import { DatesUtils } from '@core/utils/dates-utils';
import { ServiceId } from '@core/constants/service-id.const';
import { CartStatus } from '@core/constants/cart-status';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmationDialogOptions } from '@shared/schemas/confirmation-dialog-options.model';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { Flight } from '@schemas/modules/flights/flight';
import { ICart } from '@schemas/modules/cart/cart';
import { ToolsService } from '@core/utils/tools-service';

export class CartUtils {
    private static readonly TRANSPORT = 0;
    private static readonly HOTEL = 2;
    private static readonly TRANSFERS = 3;
    private static readonly RENTAL = 5;

    private static mapper: any = {
        hotel: (item: any) => ({
            startDate: item.data.checkIn,
            endDate: item.data.checkOut,
        }),
        train: (item: any) => ({
            startDate: item.data.onward.departure.dateTzFormat,
            endDate: item.data.onward.arrival.dateTzFormat,
        }),
        transfer: (item: any) => {
            const { onwardDetails } = item.data;

            return {
                startDate: onwardDetails.pickupTzDate,
                endDate: onwardDetails.dropTzDate,
            };
        },
        flight: ({ data }: any, key: any) => ({
            startDate: data[key].departureTzFormat,
            endDate: data[key].arrivalTzFormat,
        }),
        rentCar: ({ data }: any) => ({
            startDate: data.pickupTzDate,
            endDate: data.dropOffTzDate,
        }),
        activity: () => ({
            startDate: '',
            endDate: '',
        }),
    };

    public static isFailed = (status: number) => status >= CartStatus.STATUS_REJECTED;
    public static isBookingStarted = (status: number) => status > CartStatus.STATUS_CREATED;
    public static isPriceChange = (status: number) => status >= CartStatus.STATUS_PRICE_CHANGED;
    public static isBooked = (status: number) =>
        status >= CartStatus.STATUS_PROGRESS && status <= CartStatus.STATUS_CANCELLED;
    public static isStillBooked = (status: number) =>
        status >= CartStatus.STATUS_PROGRESS && status < CartStatus.STATUS_CPROGRESS;

    public static getDatesData(item: any, type: any, key?: any): any {
        return this.mapper[type](item, key);
    }

    public static isCancellableOption(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return !bookingOptions.some(
            (option) => option.status !== CartStatus.STATUS_CONFIRMED || option.cancelMode === 'not-allowed'
        );
    }

    public static hasUnavailable(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => this.isFailed(option.status));
    }

    public static hasPriceChange(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => this.isPriceChange(option.status));
    }

    public static hasBookingStarted(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => this.isBookingStarted(option.status));
    }
    public static hasNoBookingStarted(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => !this.isBookingStarted(option.status));
    }
    public static hasBooked(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => this.isBooked(option.status));
    }
    public static hasStillBooked(bookingOptions: ICartSelectedItemBookingOption[]): boolean {
        return bookingOptions.some((option) => this.isStillBooked(option.status));
    }
    public static hasStillBookedItem(cartData: ICart): boolean {
        for (const items of cartData.items) {
            if (CartUtils.hasStillBooked(items.selectedItem.bookingOptions)) {
                return true;
            }
        }
        return false;
    }
    public static areAllInError(cartData: ICart): boolean {
        return !CartUtils.hasNotFailed(cartData);
    }
    public static generateTimelineData(items: any, splitTransfers: any, translateService: TranslateService) {
        const data: any = [];
        if (!items) {
            return data;
        }

        items.map((timeline: any, idx: any) => {
            const { dataType, dataId, serviceId, summaryTitle, ordinal, tzOrderNumber, selectedItem } = timeline;
            let refId = timeline.selectedItem.id; // unique data identifier

            switch (dataType) {
                case 'flight':
                    const departureData = CartUtils.getDatesData(timeline.selectedItem, 'flight', 'departureSummary');
                    const returnDates = CartUtils.getDatesData(timeline.selectedItem, 'flight', 'returnSummary');
                    //timeline.orderDate = new Date(departureData.startDate);

                    const departureTitle = translateService.instant('FLIGHT_TRAIN.PLACE_TO_PLACE', {
                        placeFrom: timeline.selectedItem.data.departureSummary.originCity,
                        placeTo: timeline.selectedItem.data.departureSummary.destinationCity,
                    });
                    const returnTitle = translateService.instant('FLIGHT_TRAIN.PLACE_TO_PLACE', {
                        placeFrom: timeline.selectedItem.data.returnSummary.originCity,
                        placeTo: timeline.selectedItem.data.returnSummary.destinationCity,
                    });
                    const flightCommonData = {
                        idx,
                        dataId,
                        serviceId,
                        summaryTitle,
                        ordinal,
                        tzOrderNumber,
                        hash: timeline.selectedItem.id,
                        bookingOptions: selectedItem.bookingOptions,
                        refId,
                    };

                    data.push({
                        ...flightCommonData,
                        type: 'flight',
                        startDate: DatesUtils.getDateFromTzDate(departureData.startDate),
                        endDate: DatesUtils.getDateFromTzDate(departureData.endDate),
                        direction: 'departure',
                        routes: timeline.selectedItem.data.departureSummary.airportStopRoutes,
                        title: departureTitle,
                    });
                    if (returnDates.startDate && returnDates.endDate) {
                        data.push({
                            ...flightCommonData,
                            type: 'flight',
                            startDate: DatesUtils.getDateFromTzDate(returnDates.startDate),
                            endDate: DatesUtils.getDateFromTzDate(returnDates.endDate),
                            direction: 'return',
                            routes: timeline.selectedItem.data.returnSummary.airportStopRoutes,
                            title: returnTitle,
                        });
                    }

                    break;

                default:
                    let title;

                    const datesInfo = CartUtils.getDatesData(timeline.selectedItem, dataType);
                    switch (dataType) {
                        case 'train':
                            title = translateService.instant('FLIGHT_TRAIN.PLACE_TO_PLACE', {
                                placeFrom: timeline.selectedItem.data.onward.departure.location.city,
                                placeTo: timeline.selectedItem.data.onward.arrival.location.city,
                            });

                            // for train combined the onward and return by identifying same bookingID;
                            // @todo: for train data need to be combined at server while selecting trains
                            refId = selectedItem.bookingOptions[0].groupingId;

                            break;
                        case 'hotel':
                            datesInfo.startDate = timeline.startDate;
                            datesInfo.endDate = timeline.endDate;
                            break;
                        case 'rentCar':
                            title = translateService.instant('SERVICES.CAR RENTAL');
                            break;
                    }
                    const info: any = {
                        idx,
                        dataId,
                        serviceId,
                        summaryTitle,
                        ordinal,
                        tzOrderNumber,
                        hash: timeline.selectedItem.id,
                        title,
                        type: dataType.toLowerCase(),
                        startDate: DatesUtils.getDateFromTzDate(datesInfo.startDate),
                        endDate: DatesUtils.getDateFromTzDate(datesInfo.endDate),
                        activeTransfer: selectedItem.activeTransfer,
                        bookingOptions: selectedItem.bookingOptions,
                        refId,
                    };

                    if (Array.isArray(selectedItem.bookingOptions) && selectedItem.bookingOptions.length) {
                        const bookingOption = selectedItem.bookingOptions[0];
                        info.journeyType = bookingOption.journeyType;
                        info.activeTransfer = bookingOption.activeTransfer;

                        if (dataType === 'transfer' && splitTransfers) {
                            // key created to separate transfers selected in the summary page
                            // info.activeTransfer = selectedItem.activeTransfer;
                            info.journeyType = bookingOption.journeyType;
                            info.dataId = dataId + '-' + bookingOption.journeyType;
                        }
                    }
                    data.push(info);
            }
        });
        console.log({ data });
        data.sort((a: any, b: any) => a.startDate - b.startDate);
        return data;
    }

    public static formTabInfo(info: any, cart: any) {
        if (!info) {
            info = {
                inCart: false,
                price: 0,
                currency: {},
            };
        }
        info.inCart = true;
        info.price += cart.itemPrice.listPrice;
        info.additionalCharge += cart.itemPrice.additionalCharge;
        info.currency = cart.itemPrice.listPriceCur;
        info.isVirtual = cart.isVirtual;
        if (cart.inError && info.inError) {
            info.inError.cheapestListPrice += cart.inError.cheapestListPrice;
        } else {
            info.inError = ToolsService.deepCopy(cart.inError);
        }
        return info;
    }

    public static buildFlightTitleSummary(
        flight: Flight,
        translateService: TranslateService,
        isMyTrip?: boolean
    ): string {
        let flightRoute = flight.departureSummary.originCity + ' – ' + flight.departureSummary.destinationCity;

        let service: string;
        if (flight.isRoundTrip) {
            flightRoute += ' – ' + flight.returnSummary.destinationCity;
            service = translateService.instant('FLIGHT.FLIGHTS');
        } else {
            service = translateService.instant('FLIGHT.FLIGHT');
        }

        let summaryTitle = `${translateService.instant(
            flight.isRoundTrip ? 'FLIGHT_TRAIN.SELECTED' : 'FLIGHT.SELECTED_ONE_WAY',
            {
                service: service.toLowerCase(),
                serviceUpper: service,
                transportRoute: flightRoute,
            }
        )}`;
        if (isMyTrip) {
            summaryTitle = translateService.instant('SERVICES.FLIGHT');
        }

        return summaryTitle;
    }

    public static formServiceTitle(cartData: any, translateService: TranslateService, isMyTrip?: boolean) {
        if (!cartData.items) {
            return;
        }
        let service = '';
        cartData.items.forEach((item: any) => {
            switch (item.title) {
                case 'Flight':
                    item.summaryTitle = this.buildFlightTitleSummary(
                        item.selectedItem.data,
                        translateService,
                        isMyTrip
                    );
                    item.ordinal = this.TRANSPORT;
                    break;
                case 'Train #1':
                    let trainPrefix = translateService.instant('TRAIN.TRAIN');
                    let trainRoutes =
                        item.selectedItem.data.onward.departure.location.city +
                        ' – ' +
                        item.selectedItem.data.onward.arrival.location.city;
                    // eslint-disable-next-line @typescript-eslint/prefer-for-of
                    for (let i = 0; i < cartData.items.length; i++) {
                        if (cartData.items[i].title === 'Train #2') {
                            trainPrefix = translateService.instant('TRAIN.TRAINS');
                            trainRoutes += ' – ' + cartData.items[i].selectedItem.data.onward.arrival.location.city;
                            cartData.items[i].ordinal = this.TRANSPORT + 1;
                        }
                    }
                    service = translateService.instant('TRAIN.TRAINS');
                    item.summaryTitle = `${translateService.instant('FLIGHT_TRAIN.SELECTED', {
                        service: service.toLowerCase(),
                        transportRoute: trainRoutes,
                        serviceUpper: service,
                    })}`;
                    if (isMyTrip) {
                        item.summaryTitle = translateService.instant('SERVICES.TRAIN');
                    }
                    item.ordinal = this.TRANSPORT;
                    break;
                case 'Hotel':
                    item.summaryTitle = translateService.instant('HOTELS.SELECTED');
                    if (isMyTrip) {
                        item.summaryTitle = translateService.instant('SERVICES.HOTEL');
                    }
                    item.ordinal = this.HOTEL;
                    break;
                case 'Transfer':
                    let journeyType = 'onwardJourney';
                    if (
                        Array.isArray(item.selectedItem.bookingOptions) &&
                        item.selectedItem.bookingOptions.length > 0
                    ) {
                        journeyType = item.selectedItem.bookingOptions[0].journeyType;
                    }
                    const isRoundTrip = item.searchReq.searchData.transferCal.twoDates;
                    const journeyInfo = isRoundTrip
                        ? translateService.instant(
                              journeyType === 'onwardJourney' ? 'TRANSFER.OUTBOUND' : 'TRANSFER.RETURN'
                          )
                        : '';
                    if (isMyTrip) {
                        item.summaryTitle = translateService.instant('SERVICES.TRANSFER') + ' ' + journeyInfo;
                    } else {
                        item.summaryTitle = isRoundTrip
                            ? ` ${translateService.instant('TRANSFER.SELECTED_S')}`
                            : ` ${translateService.instant('TRANSFER.SELECTED')}`;
                    }
                    // if (isRoundTrip) {
                    //     journeyInfo = journeyType === "onwardJourney" ? "O" : "R";
                    // }
                    item.summaryHeaderInfo = journeyInfo.trim();
                    item.ordinal = item.title === 'Transfer #2' ? this.TRANSFERS + 1 : this.TRANSFERS;
                    break;
                case 'Car Rental':
                    item.summaryTitle = translateService.instant('CAR_RENTAL.SELECTED');
                    if (isMyTrip) {
                        item.summaryTitle = translateService.instant('SERVICES.CAR RENTAL');
                    }
                    item.ordinal = this.RENTAL;
                    break;
            }
        });

        cartData.items.sort((itemA: any, itemB: any) => itemA.ordinal - itemB.ordinal);
        return cartData;
    }

    public static formItemOrder(serviceId: ServiceId = -1, cartItems: any = [], translateService: TranslateService) {
        if (serviceId < 0 || cartItems.length === 0) {
            return;
        }

        const timelineData = CartUtils.generateTimelineData(cartItems, true, translateService);
        if (!timelineData || timelineData.length === 0) {
            return;
        }

        const serviceOrder: any = {};
        timelineData.forEach((timeline: any) => {
            const { hash, serviceID, summaryTitle, tzOrderNumber, idx, bookingOptions, refId } = timeline;
            if (serviceOrder[refId]) {
                if (serviceOrder[refId].idxs.indexOf(idx) === -1) {
                    serviceOrder[refId].idxs.push(idx);
                }
                if (serviceOrder[refId].hashes.indexOf(hash) === -1) {
                    serviceOrder[refId].hashes.push(hash);
                }
            } else {
                serviceOrder[refId] = {
                    serviceID,
                    summaryTitle,
                    tzOrderNumber,
                    idxs: [idx],
                    hashes: [hash],
                    bookingOptions,
                };
            }
        });

        return serviceOrder;
    }

    public static verifySelectedAcc(oldSelectedAcc: any, newlySelectedAcc: any): boolean {
        let sameAcc = true;

        if (
            oldSelectedAcc.selectedHotel.id !== newlySelectedAcc.id ||
            oldSelectedAcc.bookingOptions.length !== newlySelectedAcc.bookingOptions.length
        ) {
            return false;
        }

        // eslint-disable-next-line @typescript-eslint/prefer-for-of
        for (let i = 0; i < oldSelectedAcc.bookingOptions.length; i++) {
            let offerFound = false;
            // eslint-disable-next-line @typescript-eslint/prefer-for-of
            for (let j = 0; j < newlySelectedAcc.bookingOptions.length; j++) {
                //@todo: need to change
                if (
                    oldSelectedAcc.bookingOptions[i].selectedRoom.roomOfferId ===
                        newlySelectedAcc.bookingOptions[j].selectedRoom.roomOfferId &&
                    oldSelectedAcc.bookingOptions[i].selectedRoom.numberOfRooms ===
                        newlySelectedAcc.bookingOptions[j].selectedRoom.numberOfRooms
                ) {
                    offerFound = true;
                }
            }
            if (!offerFound) {
                sameAcc = offerFound;
            }
        }

        return sameAcc;
    }

    // it checks whether new options contain previously selected transfers
    public static verifySelectedTransfers(oldSelectedTransfers: any, newlySelectedTransfers: any): boolean {
        console.log(
            {
                oldSelectedTransfers,
            },
            {
                newlySelectedTransfers,
            },
            '306'
        );
        const sameTransferFound = true;

        if (
            !oldSelectedTransfers ||
            !newlySelectedTransfers ||
            oldSelectedTransfers.length !== newlySelectedTransfers.length
        ) {
            return false;
        }

        // eslint-disable-next-line @typescript-eslint/prefer-for-of
        for (let j = 0; j < oldSelectedTransfers.length; j++) {
            let found = false;

            for (let i = 0; i < newlySelectedTransfers.length && !found; i++) {
                if (oldSelectedTransfers[j].selectedItem.id === newlySelectedTransfers[i].id) {
                    found = true;
                    break;
                }
            }
            if (!found) {
                return false;
            }
        }

        return sameTransferFound;
    }

    public static sameExtras(oldExtras: any, newExtras: any) {
        const extraMap: any = {};
        let extraCount = 0;
        for (const extraId in oldExtras) {
            if (oldExtras.hasOwnProperty(extraId)) {
                extraMap[extraId] = oldExtras[extraId];
                extraCount++;
            }
        }
        let newExtraCount = 0;
        for (const extraId in newExtras) {
            if (oldExtras.hasOwnProperty(extraId)) {
                newExtraCount++;
                if (extraMap[extraId]) {
                    if (extraMap[extraId].count !== newExtras[extraId].count) {
                        return false;
                    }
                } else {
                    return false;
                }
            }
        }
        return extraCount === newExtraCount;
    }

    public static isInvalidItem(selected: any): boolean {
        return (
            selected &&
            selected.bookingOptions &&
            selected.bookingOptions.some((option: ICartSelectedItemBookingOption) => this.isFailed(option.status))
        );
    }

    public static getSameItemConfirmationDialogData(
        serviceId: ServiceId,
        selected: any,
        cartReq: any,
        translateService: TranslateService
    ): ConfirmationDialogOptions {
        let confirmStr = '';
        if (serviceId === ServiceId.FLIGHT) {
            confirmStr = translateService.instant('POPUPS.CHANGE_OPTION.MESSAGE', {
                service: translateService.instant('SERVICES.' + serviceId).toLowerCase(),
            });
        } else {
            confirmStr = translateService.instant('POPUPS.CHANGE_OPTION.MESSAGE', {
                service: '',
            });
        }

        return {
            title: translateService.instant('POPUPS.CHANGE_OPTION.TITLE'),
            content: confirmStr,
            cancelBtnTitle: translateService.instant('POPUPS.CHANGE_OPTION.CANCEL'),
            confirmBtnTitle: translateService.instant('POPUPS.CHANGE_OPTION.YES_CHANGE'),
        };
    }

    public static checkPriceChangeOnly(invalidItems: ICartItem[]) {
        let priceChangedItemsCount = 0;
        let unchangedItemsCount = 0;
        let priceChangeId = '';
        let priceChangeServId!: ServiceId;

        invalidItems.forEach((item) => {
            const status = item.selectedItem.bookingOptions[0].status;
            if (!CartUtils.isFailed(status)) {
                return;
            }

            if (status === CartStatus.STATUS_PRICE_CHANGED) {
                priceChangeServId = item.serviceId;
                priceChangeId = item.selectedItem.bookingOptions[0].optionId;
                priceChangedItemsCount++;
            } else {
                unchangedItemsCount++;
            }
        });

        return {
            // check only one error.
            showPriceChange: priceChangedItemsCount === 1 && unchangedItemsCount === 0,
            priceChangeServId,
            priceChangeId,
        };
    }

    public static getTravellersData(cartItems: ICartItem[]): { txt: string; total: number } | undefined {
        let min = 99999;
        let max = 0;

        if (!cartItems) {
            return;
        }

        cartItems.forEach((item) => {
            const total =
                //adult can be 0 in case of rental only
                (item.searchReq.passengers.adults ? item.searchReq.passengers.adults : 1) +
                item.searchReq.passengers.children +
                item.searchReq.passengers.infants;

            if (total < min) {
                min = total;
            }
            if (total > max) {
                max = total;
            }
        });

        return {
            txt: min === max ? min.toString() : '(' + min + ' - ' + max + ')',
            total: max,
        };
    }

    private static hasNotFailed(cartData: ICart): boolean {
        for (const items of cartData.items) {
            if (!CartUtils.hasUnavailable(items.selectedItem.bookingOptions)) {
                return true;
            }
        }
        return false;
    }
}
