import { Injectable } from '@angular/core';
import { ValidatorFn, AbstractControl, ValidationErrors, FormGroup } from '@angular/forms';
import { Validators, FormControl } from '@angular/forms';
import * as moment from 'moment';

import { Passenger } from '@shared/schemas/tz-customer-info';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { UserService } from '@services/modules/user/services/user.service';
import { DatesUtils } from '@core/utils/dates-utils';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { FlightTrainCartInfo, ACCInfoCartInfo, CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { TravellerType } from '@core/constants/traveller-type';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';
import { DefaultLuggage } from '@services/modules/checkout/utils/transport-luggages.utils';
import { SettingsService } from '@services/services/settings-service';

@Injectable({ providedIn: 'root' })
export class TzTravellerService {
    public updateTravellerForm$ = new Subject<boolean>();

    constructor(
        private apiAccessService: ApiAccessService,
        private userService: UserService,
        private listenersService: ListenersService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private settingsService: SettingsService
    ) {}

    get tzFormDateFormat(): string {
        return this.settingsService.locales.DATETIME_FORMATS.tzTravelerShortDate;
    }

    public static invalidControl(form: FormGroup, key: string): boolean {
        const control = form.controls[key];
        return (
            control?.invalid &&
            (control.touched ||
                (key !== 'phone' && control.dirty) ||
                (control.value && control.value !== null && control.value !== ''))
        );
    }

    /** form&returns the validation object based on the service the traveller form is used */
    public createTravellerGroup(mode: string, docNotRequired = true) {
        const isMode = (service: string) => mode === service;
        const isDefault = mode === 'profile';

        /**
         * mode((profile === flight)|rental|acc|transfer)
         * 1. firstName, 2. last name - (required for all)
         * 2. gender - (required for flight/profile/rental)
         * 3. dateOfBirth - (required for flight/profile/rental)
         * 4. nationality - (required for flight/profile)
         * 5. idCardType, idCardNumber, idCardValidity, idCardIssuedDate, idCardIssuedCountry - (required for flight/profile)
         * 6. phoneNumber - (required for transfer)
         */
        const travellerGrp: any = {
            firstName: new FormControl('', [
                Validators.required,
                Validators.maxLength(20),
                Validators.pattern(ValidatorPatterns.NAME),
            ]),
            lastName: new FormControl('', [
                Validators.required,
                Validators.maxLength(20),
                Validators.pattern(ValidatorPatterns.NAME),
            ]),
        };

        if (isMode('transfer')) {
            travellerGrp.phone = new FormControl('', Validators.required);
        }

        if (isDefault || isMode('transport') || isMode('rental')) {
            travellerGrp.gender = new FormControl('', Validators.required);
            travellerGrp.dateOfBirth = new FormControl('', Validators.required);
        }

        if (isDefault || isMode('transport')) {
            travellerGrp.middleName = new FormControl('', [
                Validators.maxLength(20),
                Validators.pattern(ValidatorPatterns.NAME),
            ]);
            travellerGrp.nationality = new FormControl('', Validators.required);
        }

        if (isDefault || !docNotRequired) {
            travellerGrp.idCardType = new FormControl('');
            travellerGrp.idCardNumber = new FormControl('', [
                Validators.maxLength(30),
                Validators.pattern(ValidatorPatterns.TRAVELLERID),
            ]);
            travellerGrp.dateOfIssue = new FormControl('');
            travellerGrp.idCardValidity = new FormControl('');
            travellerGrp.idIssuedCountry = new FormControl('');
        }

        if (!docNotRequired) {
            travellerGrp.idCardType.setValidators(Validators.required);
            travellerGrp.idCardNumber.addValidators(Validators.required);
            travellerGrp.dateOfIssue.setValidators(Validators.required);
            travellerGrp.idCardValidity.setValidators(Validators.required);
            travellerGrp.idIssuedCountry.setValidators(Validators.required);
        }

        return travellerGrp;
    }

    public dateValidation(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const momentObj = moment(control.value, this.tzFormDateFormat);
            if (!control.value || control.value === '') {
                return null;
            }

            if (control.value.length !== this.tzFormDateFormat.length) {
                return {
                    INVALID_DATE: true,
                };
            }

            if (!momentObj.isValid()) {
                return {
                    INVALID_DATE_FORMAT: 'TZ_FORM_VALIDATION.DATE.INVALID_DATE_FORMAT',
                    INVALID_MESSAGE_PARAMS: {
                        format: this.tzFormDateFormat,
                    },
                };
            }

            return null;
        };
    }

    public dobValidation(
        type: string,
        selectedAge?: number,
        travelDate = { startDate: new Date(), endDate: new Date() },
        ageLimit = { min: -1, max: 100 }
    ): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const momentObj = moment(control.value, this.tzFormDateFormat);
            const selectedDate = momentObj.toDate();
            const today = new Date();

            const age = DatesUtils.getAgeDiff(selectedDate, travelDate.endDate);
            switch (true) {
                case age > 100: {
                    return { INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.LOWER_DATE' };
                }
                case ageLimit.min !== -1 && ageLimit.min > age: {
                    return {
                        INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.MINIMUN_AGE_NEEDED',
                        INVALID_MESSAGE_PARAMS: {
                            age: ageLimit.min,
                        },
                    };
                }
                case ageLimit.max < age: {
                    return {
                        INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.MAXIMUM_AGE_ALLOWED',
                        INVALID_MESSAGE_PARAMS: {
                            age: ageLimit.max,
                        },
                    };
                }
            }

            if (type !== '') {
                switch (true) {
                    case type === TravellerType.ADULT && age < 18: {
                        return {
                            INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.ADULT_AGE_NEEDED',
                        };
                    }
                    case type === TravellerType.CHILD && (age >= 18 || age < 2): {
                        return {
                            INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.CHILD_AGE_NEEDED',
                        };
                    }
                    case type === TravellerType.INFANT && age >= 2: {
                        return {
                            INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.INFANT_AGE_NEEDED',
                        };
                    }
                }
            }

            if (selectedAge && selectedAge !== -1 && selectedAge !== age) {
                return {
                    INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.INVALID_SELECTED_DATE',
                    INVALID_MESSAGE_PARAMS: {
                        currentAge: age,
                        reqAge: selectedAge,
                    },
                };
            }

            const maxDays = 14;
            const minYears = 100;
            const startDate = travelDate.startDate;
            const minLimit = new Date(today.getFullYear() - minYears, today.getMonth(), today.getDate());
            const maxLimit = new Date(startDate.getFullYear(), startDate.getMonth(), startDate.getDate() - maxDays);
            if (Date.parse(selectedDate.toString()) > Date.parse(maxLimit.toString())) {
                return { INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.FUTURE_DATE' };
            } else if (Date.parse(selectedDate.toString()) < Date.parse(minLimit.toString())) {
                return { INVALID_DOB: 'TZ_FORM_VALIDATION.DATE.LOWER_DATE' };
            }

            control.parent?.get('dateOfIssue')?.updateValueAndValidity();

            return null;
        };
    }

    public idIssueValidation(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const momentObj = moment(control.value, this.tzFormDateFormat);
            const selectedDate = momentObj.toDate();
            const today = new Date();
            const minLimit = new Date(today.getFullYear() - 20, today.getMonth(), today.getDate());
            const selectedDOB = moment(control.parent?.get('dateOfBirth')?.value, this.tzFormDateFormat);

            if (Date.parse(selectedDate.toString()) > Date.parse(today.toString())) {
                return { INVALID_ISSUED_DATE: 'TZ_FORM_VALIDATION.DATE.FUTURE_DATE' };
            } else if (
                Date.parse(selectedDate.toString()) < Date.parse(minLimit.toString()) ||
                (selectedDOB?.isValid() && Date.parse(selectedDate.toString()) < Date.parse(selectedDOB?.toString()))
            ) {
                return { INVALID_ISSUED_DATE: 'TZ_FORM_VALIDATION.DATE.LOWER_DATE' };
            }

            return null;
        };
    }

    public idExpiryValidation(travelDate: Date): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const momentObj = moment(control.value, this.tzFormDateFormat);
            const selectedDate = momentObj.toDate();

            const today = new Date();
            let minDate = today;
            if (moment(travelDate).format(this.tzFormDateFormat) !== moment(today).format(this.tzFormDateFormat)) {
                minDate = new Date(travelDate.getFullYear(), travelDate.getMonth() + 3, travelDate.getDate());
            }

            if (Date.parse(selectedDate.toString()) < Date.parse(minDate.toString())) {
                if (
                    Date.parse(today.toString()) < Date.parse(selectedDate.toString()) &&
                    Date.parse(selectedDate.toString()) < Date.parse(travelDate.toString())
                ) {
                    return {
                        INVALID_EXPIRY_DATE: 'TZ_FORM_VALIDATION.DATE.ID_EXPIRED',
                    };
                } else if (travelDate !== today && Date.parse(today.toString()) < Date.parse(selectedDate.toString())) {
                    return {
                        INVALID_EXPIRY_DATE: 'TZ_FORM_VALIDATION.DATE.EXPIRY_ALERT',
                    };
                } else {
                    return {
                        INVALID_EXPIRY_DATE: 'TZ_FORM_VALIDATION.DATE.ID_EXPIRED',
                    };
                }
            }

            return null;
        };
    }

    public duplicateIdValidation(currentFormIdx: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const travelers = control.parent?.parent?.controls;
            if (!travelers) {
                return null;
            }

            const travellerIdMap = new Map();
            for (const [idx, traveler] of (travelers as FormGroup[]).entries()) {
                if (!traveler.value) {
                    continue;
                }

                const idCardNumberCtrl = traveler.get('idCardNumber');
                const idCardType = traveler.get('idCardType')?.value ?? '';
                const idCardNumber = idCardNumberCtrl?.value ?? '';

                if (!idCardNumberCtrl || idCardType === '' || idCardNumber === '') {
                    continue;
                }

                const idStr = `${idCardType}-${idCardNumber}`;

                if (travellerIdMap.get(idStr)) {
                    const error = { DUPLICATE_ID: 'TZ_FORM_VALIDATION.ID_CARD_NUMBER.DUPLICATE_ID' };
                    if (idx === currentFormIdx) {
                        return error;
                    }

                    idCardNumberCtrl.setErrors(error);
                } else if (idCardNumberCtrl.hasError('DUPLICATE_ID')) {
                    idCardNumberCtrl.setErrors(null);
                }

                travellerIdMap.set(idStr, true);
            }

            return null;
        };
    }

    public saveTraveller(traveller: Passenger) {
        this.apiAccessService.SavePassenger(this.userService.getSessionID(), traveller).then(
            (resp: any) => {
                console.log('passenger save success:', resp);
                traveller.id = resp.cardID;
                this.dialogService.openSnackBar({ message: this.translateService.instant('NOTIFS.DATA_SAVED') });
                this.listenersService.NotifyListeners(ListenerId.CUSTOMER_INFO_UPDATED);
                this.userService.getCustomer(true);
            },
            (failure: any) => {
                console.log('passenger save failure:', failure);
            }
        );
    }

    public updateTraveller(traveller: Passenger) {
        this.apiAccessService.UpdatePassenger(this.userService.getSessionID(), traveller).then(
            (resp: any) => {
                console.log('passenger update success:', resp);
                this.dialogService.openSnackBar({ message: this.translateService.instant('NOTIFS.DATA_UPDATED') });
                this.listenersService.NotifyListeners(ListenerId.CUSTOMER_INFO_UPDATED);
                this.userService.getCustomer(true);
            },
            (failure: any) => {
                console.log('traveller update failure:', failure);
            }
        );
    }

    public removeTraveller(travelleId: string) {
        this.apiAccessService.RemovePassenger(this.userService.getSessionID(), travelleId).then(
            (resp: any) => {
                console.log('Success on customer passenger delete', resp);
                this.dialogService.openSnackBar({ message: this.translateService.instant('NOTIFS.DATA_REMOVED') });
                this.listenersService.NotifyListeners(ListenerId.CUSTOMER_INFO_UPDATED);
                this.userService.getCustomer(true);
            },
            (failureResp: any) => {
                console.log('traveller delete failure', failureResp);
            }
        );
    }

    public newFlightTrainTravellers(cartInfo: FlightTrainCartInfo): CheckoutTraveller[] {
        const checkoutTraveller: CheckoutTraveller[] = [];
        const passengers = cartInfo.passengers;
        const totalTravellers = passengers.adults + passengers.children + passengers.infants;

        for (let idx = 1; idx <= totalTravellers; idx++) {
            const travellerType =
                idx > passengers.adults
                    ? idx > passengers.adults + passengers.children
                        ? TravellerType.INFANT
                        : TravellerType.CHILD
                    : TravellerType.ADULT;
            const newTraveller = {
                type: travellerType,
                selectedAge: -1,
                saveTraveller: true,
                updateTraveller: true,
                additionalLuggage: JSON.parse(JSON.stringify(DefaultLuggage)),
            } as CheckoutTraveller;

            if (travellerType === TravellerType.CHILD) {
                newTraveller.selectedAge = passengers.childrenAges[idx - passengers.adults - 1];
            }
            newTraveller.docNotRequired = !this.checkDocRequired(cartInfo.travelCondition, idx);
            checkoutTraveller.push(newTraveller);
        }
        return checkoutTraveller;
    }

    public newAccTravellers(cartInfo: ACCInfoCartInfo): CheckoutTraveller[] {
        const checkoutTraveller: CheckoutTraveller[] = [];

        cartInfo.roomTypes.forEach((offer, idx) => {
            checkoutTraveller.push({
                type: TravellerType.ADULT,
                roomType: offer.roomType,
                roomOfferId: offer.roomOfferId,
                optionId: offer.optionId,
            } as CheckoutTraveller);
        });
        return checkoutTraveller;
    }

    public isExpiringSoon(idCardValidity: string, travelDate: Date): boolean {
        const today = new Date();
        let minLimit = new Date(today.getFullYear(), today.getMonth() + 3, today.getDate());
        if (moment(travelDate).format(this.tzFormDateFormat) !== moment(today).format(this.tzFormDateFormat)) {
            minLimit = new Date(travelDate.getFullYear(), travelDate.getMonth() + 3, travelDate.getDate());
        }

        const momentObj = moment(idCardValidity, this.tzFormDateFormat);
        const validity = Date.parse(momentObj.toDate().toDateString());
        if (validity <= Date.parse(minLimit.toString()) && validity >= Date.parse(today.toString())) {
            this.dialogService.openConfirmationDialog({
                title: this.translateService.instant('POPUPS.ALERT_ID_INVALID.TITLE'),
                multipleContent: [this.translateService.instant('POPUPS.ALERT_ID_INVALID.MESSAGE')],
                confirmBtnTitle: this.translateService.instant('POPUPS.OK'),
            });
            return true;
        }
        return false;
    }

    public formProfileTraveller(traveller: CheckoutTraveller): CheckoutTraveller {
        const customer = this.userService.getCustomerInformation();
        for (const passenger of customer.passengers) {
            if (passenger.id === traveller.id) {
                traveller = {
                    ...traveller,
                    idCardType: passenger.idCardType,
                    idCardNumber: passenger.idCardNumber,
                    dateOfIssue: passenger.dateOfIssue,
                    idCardValidity: passenger.idCardValidity,
                    issuedCountryName: passenger.issuedCountryName,
                    idIssuedCountry: passenger.idIssuedCountry,
                };
            }
        }

        return traveller;
    }

    private checkDocRequired(travellerConditions: any, travellerId: number) {
        let doc = true;
        if (!Array.isArray(travellerConditions)) {
            return doc;
        }

        travellerConditions.some((tc: { travellerId: string; documentRequired: boolean }) => {
            if (tc.travellerId === travellerId.toString()) {
                doc = tc.documentRequired;
            }
        });

        return doc;
    }
}
