@Injectable({
    providedIn: 'root',
})
export class GeneralService {
    private stopFunction: any;

    constructor() {}

    public stopWindowListeners() {
        if (typeof this.stopFunction !== 'function') {
            return;
        }

        window.removeEventListener('load', this.stopFunction);
        window.removeEventListener('mousemove', this.stopFunction);
        window.removeEventListener('keypress', this.stopFunction);
        window.removeEventListener('mousedown', this.stopFunction);
        window.removeEventListener('click', this.stopFunction);
        window.removeEventListener('scroll', this.stopFunction);
        this.stopFunction = null;
    }

    public startWindowListeners(callbackFn?: any) {
        if (typeof callbackFn !== 'function') {
            return;
        }
        if (this.stopFunction) {
            this.startWindowListeners();
        }
        this.stopFunction = callbackFn;
        window.addEventListener('load', this.stopFunction);
        window.addEventListener('mousemove', this.stopFunction);
        window.addEventListener('keypress', this.stopFunction);
        window.addEventListener('mousedown', this.stopFunction);
        window.addEventListener('click', this.stopFunction);
        window.addEventListener('scroll', this.stopFunction);
    }

    public isMobile(): boolean {
        return false;
    }
}
