import { Directive, ElementRef, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges } from '@angular/core';

interface imgCallbackData {
    id: number;
    status: number;
    url: string;
}

@Directive({
    selector: '[tzHotelImage]',
})
export class HotelImageDirective implements OnInit, OnChanges {
    @Input() id!: number;
    @Input() tzHotelImage = '';
    @Input() provider = '';
    @Input() defaultSize = '';
    @Input() onErrorSrc = '';

    @Output() imgCallback: EventEmitter<imgCallbackData> = new EventEmitter<imgCallbackData>();

    private corruptedImgURL = 'assets/icons/web/img/corrupted.png';
    private url = '';
    private idx = 0;
    private len = 0;
    private sizes: string[] = [];
    private defSize = this.defaultSize;

    constructor(private elem: ElementRef) {}

    ngOnInit(): void {
        if (this.onErrorSrc && this.onErrorSrc !== '') {
            this.corruptedImgURL = this.onErrorSrc;
        }
        this.init(true);
    }

    ngOnChanges(changes: SimpleChanges): void {
        // used in showing gallery dynamically
        if (changes['tzHotelImage']) {
            this.init(true);
        }
    }

    private setImage = (imageURL: string) => (this.elem.nativeElement.src = imageURL);

    private setTzImage() {
        let imageURL = '';
        if (this.sizes[this.idx] === '') {
            imageURL = this.url && this.idx < this.len ? this.url.replace('{size}/', '') : this.corruptedImgURL;
        } else {
            imageURL =
                this.url && this.idx < this.len
                    ? this.url.replace('{size}', this.sizes[this.idx])
                    : this.corruptedImgURL;
        }

        if (imageURL && imageURL !== '_omit') {
            this.setImage(imageURL);
        } else {
            this.handleError();
        }
    }

    private invokeCallback = (status: number) => {
        this.imgCallback.emit({
            id: this.id,
            status,
            url: this.elem.nativeElement.src,
        });
    };

    private handleError(): void {
        if (this.idx >= this.len) {
            // set corruptedImgURL once all the alternate images are set
            if (this.corruptedImgURL !== '_omit') {
                this.setImage(this.corruptedImgURL);
            }

            this.invokeCallback(404);
            return;
        }

        this.idx++;
        this.setTzImage();
    }

    private getImagesSizes(provider: string) {
        let sizes: string[] = [];
        switch (provider) {
            case 'Hotelbeds':
                // '' We use default HB image (/ to be removed in that case)
                sizes = ['bigger', '', 'medium'];
                break;
            case 'Ostrovok':
                sizes = ['1024x768', '240x240', 'x100'];
                if (this.defaultSize === 'medium') {
                    this.defSize = '240x240';
                }
                break;
            case 'BookingCom':
                sizes = ['max500', 'max300', 'square60'];
                if (this.defaultSize === 'medium') {
                    this.defSize = 'max300';
                }
                break;
        }

        if (this.defSize) {
            if (sizes.indexOf(this.defSize) > -1) {
                sizes.splice(sizes.indexOf(this.defSize), 1);
            }

            // move the default size to index zero
            sizes.unshift(this.defSize);
        }

        return sizes;
    }

    private init(isInit: boolean): void {
        this.url = this.tzHotelImage;
        this.idx = 0;
        this.sizes = this.getImagesSizes(this.provider);
        this.len = this.sizes.length;

        if (isInit) {
            this.elem.nativeElement.onerror = () => this.handleError();
        }
        this.elem.nativeElement.onload = () => this.invokeCallback(200);

        this.setTzImage();
    }
}
