import { Injectable } from '@angular/core';
import { BrowserAnimationsModuleConfig } from '@angular/platform-browser/animations';

@Injectable({
    providedIn: 'root',
})
export class DeviceService {
    public static animatedConfig(): BrowserAnimationsModuleConfig {
        // on old IOS version animation must be disabled or we get crashes on angular (visible impact
        // dialog box do not close)
        const disableAnimations =
            !('animate' in document.documentElement) ||
            (navigator && /(iPhone OS|iPad; CPU OS) (8|9|10|11|12|13)_/.test(navigator.userAgent));

        if (disableAnimations) {
            console.log('animation disabled');
            return { disableAnimations: true };
        }
        return { disableAnimations: false };
    }
}
