import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { LoaderService } from '@services/modules/core/loader.service';
import { ServiceId } from '@core/constants/service-id.const';
import { SearchEvents } from '@core/constants/search-events';
import { SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { StateFrom } from '@core/constants/state-from.const';
import { ListenersService } from '@services/modules/core/listeners-service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { SearchHelpers } from '@services/searchEngine/services/search-helpers';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';

@Injectable({
    providedIn: 'root',
})
export class PreSearchService {
    constructor(
        private apiAccessService: ApiAccessService,
        private searchMetadataService: SearchMetadataService,
        private tripPlannerService: TripPlannerService,
        private loaderService: LoaderService,
        private searchErrorService: SearchErrorService,
        private router: Router,
        private listenersService: ListenersService,
        private cartRoutingService: CartRoutingService,
        private cartService: CartService,
        private searchDataService: SearchDataService
    ) {}

    public preSearchData(params: any, serviceSearch: any, eventId: any): Promise<any> {
        this.searchErrorService.unsetErrorFlag();
        if (
            this.router.url !== '/search' &&
            //when click on tab for new service do not move to both train and flight states (we move to flight)
            (!params.isNewService || params.serviceId !== ServiceId.TRAIN) &&
            params.serviceId !== this.cartRoutingService.getCurStateServiceId &&
            //If we are already in a transport state do not move to other transport...
            (!this.cartRoutingService.isTransportState() || !SearchHelpers.isTransport(params.serviceId))
        ) {
            this.cartRoutingService.goToState(params.serviceId, undefined, params.isNewService, StateFrom.SEARCH, {
                preSearch: true,
            });
        }
        return new Promise((resolve, reject) => {
            this.apiAccessService
                .preSearch(params, serviceSearch)
                .then(
                    (success: any) => {
                        if (!success) {
                            //this is occurring when we cancel all requests
                            //I unsuccessfully tried to figure out why we do not end up on reject case
                            return;
                        }
                        this.cartService.reInit(true).then(
                            () => this.preSearchSuccess(success, params, resolve, eventId),
                            () => this.preSearchFailure(success, params, reject, eventId)
                        );
                    },
                    (failure: any) => {
                        if (params.isNewService || params.isNewCombi) {
                            this.preSearchFailure(failure, params, reject, eventId);
                        } else {
                            this.cartService
                                .reInit()
                                .finally(() => this.preSearchFailure(failure, params, reject, eventId));
                        }
                    }
                )
                .catch(() => {
                    this.loaderService.triggerLoading.emit({ loading: false });
                });
        });
    }

    private preSearchSuccess(success: any, params: any, resolve: any, eventId: any): void {
        console.log('getting pre search success on service:' + params.serviceId);
        const serviceId: ServiceId = success.metaData.serviceId;
        this.searchMetadataService.saveSearch(success, eventId);
        if (success.metaData.complete) {
            this.tripPlannerService.setComplete(serviceId);
        }
        if (serviceId === ServiceId.TRAIN || serviceId === ServiceId.FLIGHT) {
            resolve(success);
            return;
        }
        switch (serviceId) {
            case ServiceId.ACC:
            case ServiceId.TRANSFER:
            case ServiceId.CAR_RENTAL:
                const metaData = success.metaData;
                const serviceSearchRes = success.serviceSearch;
                if (metaData.count === 0) {
                    /*this.searchErrorService.searchErr(
                        {
                            status: 400,
                            message: '',
                        },
                        serviceId,
                        SearchEvents.EVENT_SEARCH_WEB
                    );*/
                    if (this.router.url === '/search') {
                        this.loaderService.triggerLoading.emit({ loading: false });
                        this.searchErrorService.searchErr(
                            {
                                status: 200,
                                message: '',
                            },
                            serviceId,
                            SearchEvents.EVENT_SEARCH_WEB
                        );
                        return;
                    }
                } else {
                    this.searchErrorService.unsetErrorFlag();
                }

                switch (serviceId) {
                    case ServiceId.ACC:
                        if (serviceSearchRes.searchData.hotelCode) {
                            const accRepriceListener = this.listenersService.registerListener(
                                ListenerId.LISTEN_ACC_REPRICED,
                                () => {
                                    this.listenersService.unregisterListener(accRepriceListener);
                                    this.cartRoutingService.goToState(
                                        ServiceId.ACC_DETAILS,
                                        //TODO SF 9091
                                        {
                                            giataId: serviceSearchRes.searchData.hotelCode,
                                            hotelName: serviceSearchRes.searchData.hotelName,
                                        },
                                        true,
                                        StateFrom.SEARCH
                                    );
                                },
                                this
                            );
                            return;
                        } /*else {
                                            this.cartRoutingService.currentServiceId.next(serviceId);
                                        }*/
                        break;
                    case ServiceId.TRANSFER:
                        break;
                    case ServiceId.CAR_RENTAL:
                        if (serviceSearchRes.searchData.retainSearch) {
                            this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_RENT_CAR);
                        } else {
                            this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_RENT_CAR, {
                                resetMap: true,
                            });
                        }
                }
                this.cartRoutingService.goToState(serviceId, undefined, true, StateFrom.SEARCH);
                break;
            default:
                console.error('unhandled service id:' + serviceId);
        }
        resolve(success);
    }

    private preSearchFailure(failure: any, params: any, reject: any, eventId: any): void {
        console.log('getting pre search failure on service:' + params.serviceId);
        this.loaderService.triggerLoading.emit({ loading: false });
        this.tripPlannerService.setComplete(params.serviceId); // on error set result is completed

        if (this.router.url !== '/search' && failure.status === 412 && failure.error) {
            failure.error.metaData.complete = true;
            this.searchMetadataService.saveSearch(failure.error, eventId);
            this.searchDataService.resetSearch$.next();
        }
        if (
            params.serviceId === ServiceId.TRAIN ||
            params.serviceId === ServiceId.FLIGHT ||
            (failure.error && failure.error.code === 8024)
        ) {
            reject(failure);
            return;
        }
        this.searchErrorService.searchErr(
            {
                status: failure && failure.status ? failure.status : 400,
                message: '',
                invalid: failure && failure.status === 412,
            },
            params.serviceId,
            eventId
        );
        if (this.router.url !== '/search') {
            //this.cartRoutingService.currentServiceId.next(params.serviceId);
            if (this.router.url === '/results-summary' || eventId !== SearchEvents.EVENT_SEARCH_EDIT) {
                this.cartRoutingService.goToState(params.serviceId, undefined, true, StateFrom.SEARCH);
            }
        }
        return;
    }
}
