import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SettingsService } from '@services/services/settings-service';

@Pipe({
    name: 'tzDate',
    pure: false,
})
export class TzDatePipe implements PipeTransform {
    constructor(private translateService: TranslateService, private settingsService: SettingsService) {}

    transform(
        value: Date | string | number | undefined,
        format?: string,
        timezone?: string,
        locale?: string
    ): string | null {
        if (!value) {
            return '';
        }
        const datePipe: DatePipe = new DatePipe(this.translateService.currentLang);
        const settingsLocale = locale ? locale : this.settingsService.locales;
        let localeFormat: undefined | string;
        if (format) {
            localeFormat = settingsLocale.DATETIME_FORMATS[format] ? settingsLocale.DATETIME_FORMATS[format] : format;
        }

        return datePipe.transform(value, localeFormat, timezone, locale);
    }
}
