import { ChangeDetectorRef, Pipe, PipeTransform } from '@angular/core';
import { formatCurrency, getCurrencySymbol } from '@angular/common';
import { SettingsService } from '@services/services/settings-service';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
    name: 'tzCurrency',
    pure: false,
})
export class TzCurrencyPipe implements PipeTransform {
    constructor(private settingService: SettingsService) {}

    transform(
        value: number,
        currencyCode: string = this.settingService.getCurrency(),
        display: 'code' | 'symbol' | 'symbol-narrow' | string | boolean = 'symbol',
        digitsInfo: string = '1.0-0',
        locale: string = this.settingService.langCode
    ): string | null {
        return formatCurrency(value, locale, getCurrencySymbol(currencyCode, 'wide'), currencyCode, digitsInfo);
    }
}
