import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'tz-mob-main-modal-header',
    templateUrl: './mob-main-modal-header.component.html',
    styleUrls: ['./mob-main-modal-header.component.scss'],
})
export class MobMainModalHeaderComponent {
    @Input() type: 'default' | 'fullscreen' = 'default';
    @Input() isHeaderBottomDivider = true;
    @Output() closeModal: EventEmitter<void> = new EventEmitter<void>();

    get isFullscreen(): boolean {
        return this.type === 'fullscreen';
    }

    get isDefault(): boolean {
        return this.type === 'default';
    }

    public onCloseModal(): void {
        this.closeModal.emit();
    }
}
