import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { UserService } from '@services/modules/user/services/user.service';
import { Phone } from '@shared/schemas/tz-customer-info';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { first } from 'rxjs/operators';
import { timer } from 'rxjs';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-profile-info',
    templateUrl: './profile-info.component.html',
    styleUrls: ['./profile-info.component.scss'],
})
export class ProfileInfoComponent implements OnInit {
    @Input() customerInformation!: CustomerInfo;
    @Input() usageType = 'profile';

    @Output() updateCustomerInfo = new EventEmitter<{ customer: CustomerInfo }>();
    @Output() loadForm = new EventEmitter<FormGroup>();

    public formReadonly = false;
    public formGroup = new FormGroup({
        firstName: new FormControl('', [
            Validators.required,
            Validators.pattern(ValidatorPatterns.NAME),
            Validators.maxLength(100),
        ]),
        lastName: new FormControl('', [
            Validators.required,
            Validators.pattern(ValidatorPatterns.NAME),
            Validators.maxLength(100),
        ]),
        emailID: new FormControl('', [Validators.required, Validators.pattern(ValidatorPatterns.EMAIL)]),
        phone: new FormControl('', [Validators.required]),
    });
    public phoneFocused = false;

    constructor(
        private userService: UserService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        public platformService: PlatformService
    ) {}

    ngOnInit() {
        this.setDefaultFormValues();
        this.formReadonly =
            this.usageType === 'profile' &&
            this.customerInformation.profUpdated &&
            !this.platformService.isMobileDevice;
        this.loadForm.emit(this.formGroup);
    }

    public toggleEdit(): void {
        this.formReadonly = !this.formReadonly;
        if (this.formReadonly) {
            this.setDefaultFormValues();
        }

        this.formGroup.markAsPristine();
    }

    public updateProfile(): void {
        const values = this.formGroup.value;
        this.customerInformation.firstName = values.firstName;
        this.customerInformation.lastName = values.lastName;
        this.customerInformation.emailID = values.emailID;
        this.customerInformation.phone = {
            // eslint-disable-next-line id-blacklist
            number: values.phone.internationalNumber,
            countryCode: values.phone.countryCode,
            callingCode: values.phone.dialCode,
        } as Phone;

        this.updateCustomerInfo.emit({ customer: this.customerInformation });
        this.formReadonly = !this.platformService.isMobileDevice;
        this.formGroup.markAsPristine();
    }

    public onEmailFieldClicked(): void {
        if (this.formReadonly || !this.customerInformation.profUpdated) {
            return;
        }

        this.dialogService.openSnackBarDialog(this.translateService.instant('NOTIFS.MAIL_EDIT_MSG'));
    }

    public deleteAccount = () => this.userService.deleteCustomer();

    private setDefaultFormValues(): void {
        if (!this.userService.IsLogged()) {
            return;
        }

        this.formGroup.controls['firstName'].setValue(this.customerInformation.firstName);
        this.formGroup.controls['lastName'].setValue(this.customerInformation.lastName);
        this.formGroup.controls['emailID'].setValue(this.customerInformation.emailID);

        const updatedPhone = this.customerInformation.phone;
        const validPhone = updatedPhone && updatedPhone.number && updatedPhone.number !== '';

        const phoneNumber = this.formGroup.controls['phone'];
        const updatePhoneNumber =
            validPhone &&
            (!phoneNumber.value ||
                (phoneNumber.value.internationalNumber &&
                    updatedPhone.number !== phoneNumber.value.internationalNumber));

        if (updatePhoneNumber) {
            phoneNumber.setValue(this.customerInformation.phone.number);
            phoneNumber.statusChanges.pipe(first()).subscribe(() => {
                const timer$ = timer(500); // to reset the initial field dirty set by tel library
                timer$.subscribe(() => {
                    phoneNumber.markAsPristine();
                });
            });
        }
    }
}
