import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { SortByValueOption } from '@schemas/modules/core/sort-by-value.option';

@Component({
    selector: 'tz-price-range-filter',
    templateUrl: './price-range-filter.component.html',
    styleUrls: ['./price-range-filter.component.scss'],
})
export class PriceRangeFilterComponent implements OnInit {
    @Input() options: SortByValueOption[] = [];
    @Input() showLabel = false;
    @Input() sort: undefined | string;
    @Input() sortType: 'select' | 'buttonToggle' = 'select';
    @Input() skeletonLoading = false;
    @Input() disabled = false;
    @Output() sortChange: EventEmitter<string> = new EventEmitter<string>();

    constructor() {}

    ngOnInit() {
        this.sort = this.sort || 'bestValue';
    }

    public selectOption(option: SortByValueOption): void {
        this.sort = option.sort;
        this.sortChange.emit(option.sort);
    }

    public onButtonToggleChange(sort: string | undefined): void {
        this.sort = sort;
        this.sortChange.emit(sort);
    }
}
