import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { AppConfigDataService } from '@services/modules/core/dataservices/app-config-data.service';

@Injectable({
    providedIn: 'root',
})
export class DeviceIdForceResolver implements Resolve<string> {
    constructor(private appConfigDataService: AppConfigDataService) {}

    async resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<string> {
        return await this.appConfigDataService.initDeviceId(true).catch(() => '');
    }
}
