import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot, Router } from '@angular/router';
import { Injectable } from '@angular/core';
import { AppConfigDataService } from '@services/modules/core/dataservices/app-config-data.service';
import { take } from 'rxjs/operators';
import { DeviceIdForceResolver } from '@core/resolvers/device-id-force.resolver';

@Injectable({
    providedIn: 'root',
})
export class SearchPageResolver implements Resolve<void> {
    constructor(
        protected appConfigDataService: AppConfigDataService,
        protected deviceIdForceResolver: DeviceIdForceResolver
    ) {}

    async resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<void> {
        const deviceId = await this.deviceIdForceResolver.resolve(route, state);
        if (deviceId) {
            await this.appConfigDataService.setCountryIP(deviceId).pipe(take(1)).subscribe();
        }
    }
}
