import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ControlContainer, FormGroup } from '@angular/forms';
import { TzTravellerService } from '@services/services/tzTraveller.service';
import fa from '@mobiscroll/angular/dist/js/i18n/fa';

@Component({
    selector: '[formGroup] tz-mob-input, [formGroupName] tz-mob-input',
    templateUrl: './mob-input.component.html',
})
export class MobInputComponent implements OnInit {
    @Input() formReadonly = false;
    @Input() key!: string;
    @Input() fieldType: string | undefined;
    @Input() title: string | undefined;
    @Input() icon: string | undefined;
    @Input() checkValid = true;
    @Input() placeholder = '';

    @Input() autocomplete = 'off';
    @Input() autocorrect = 'off';
    @Input() autocapitalize = 'off';
    @Input() type = 'text';

    public form!: FormGroup;
    public isInValid = false;

    constructor(private controlContainer: ControlContainer) {}

    ngOnInit() {
        this.form = this.controlContainer.control as FormGroup;

        this.checkValidInput();
    }

    public checkValidInput(): void {
        this.isInValid =
            !this.formReadonly && this.checkValid && TzTravellerService.invalidControl(this.form, this.key);
    }
}
