import { AfterViewInit, Component, OnInit } from '@angular/core';
import { LandingPageService } from '@services/landing-page.service';
import { PlatformService } from '@services/platform.service';
import { GeneralSettings } from '@core/constants/general-settings';
import { SelectedServices } from '@schemas/modules/services/selected-services';
import { PageUtils } from '@core/utils/page-utils';
import { PagesUrls } from '@core/constants/pages-urls';
import { Location } from '@angular/common';
import { GeneralServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { AnimationController, Animation } from '@ionic/angular';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Component({
    selector: 'tz-mob-landing-page',
    templateUrl: './mob-landing-page.component.html',
    styleUrls: ['./mob-landing-page.component.scss'],
})
export class MobLandingPageComponent implements OnInit {
    public availableServices: GeneralServiceTab[] = GeneralSettings.availableServices;
    public isServicesSelected = false;
    public isValid = false;
    public selectedServices: SelectedServices = {
        transport: false,
        accommodation: false,
        transfer: false,
        rentCar: false,
        // activity: false,
    };
    public validAnimation?: Animation;
    public isLandingPageShown = false;

    constructor(
        public platformService: PlatformService,
        private landingPageService: LandingPageService,
        private location: Location,
        private animationCtrl: AnimationController
    ) {}

    get isHomePage(): boolean {
        const url = this.location.path();
        return PageUtils.is(url, [PagesUrls.HOME, PagesUrls.SEARCH]);
    }

    ngOnInit() {
        this.isLandingPageShown = !!StorageManagerUtil.getMobLandingPageIsShown();
        // this.availableServices = GeneralSettings.availableServices.filter((el) => el.serviceKey !== 'activity');
    }

    public onToggleService(serviceKey: string): void {
        this.validAnimation?.stop();

        if (serviceKey === 'activity') {
            return;
        }

        if (serviceKey === 'rentCar') {
            this.selectedServices.transfer = false;
        }

        if (serviceKey === 'transfer') {
            this.selectedServices.rentCar = false;
        }

        this.selectedServices[serviceKey as keyof SelectedServices] =
            !this.selectedServices[serviceKey as keyof SelectedServices];
    }

    public onNextClick(): void {
        if (Object.values(this.selectedServices).every((value) => !value)) {
            this.runValidAnimation();
            return;
        }

        StorageManagerUtil.setLandingPageIsShown();

        this.landingPageService.landingPageClosed$.next(this.selectedServices);
        this.isServicesSelected = true;
    }

    private runValidAnimation(): void {
        if (!this.validAnimation) {
            this.validAnimation = this.createAnimation();
        }

        this.validAnimation.play();
    }

    private createAnimation(): Animation {
        return this.animationCtrl
            .create()
            .addElement(document.querySelectorAll('.service'))
            .duration(500)
            .keyframes([
                { offset: 0 },
                { offset: 0.5, background: 'var(--color-main-mob-blink)', borderColor: 'var(--color-main-purple)' },
                { offset: 1 },
            ]);
    }
}
