import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { TranslateService } from '@ngx-translate/core';
import { ModalService } from '@services/modal.service';
import { LogInService } from '@services/services/log-in.service';
import { MobForgotPasswordComponent } from '../mob-forgot-password/mob-forgot-password.component';

@Component({
    selector: 'tz-mob-sign-in',
    templateUrl: './mob-sign-in.component.html',
    styleUrls: ['./mob-sign-in.component.scss'],
})
export class MobSignInComponent implements OnInit {
    public showSignin = true;
    public fromCheckout = false;
    public errorMsg = '';
    public logInForm;
    public showPassword = {
        currentPassword: false,
    };

    private data!: { loginMailId: string | undefined; showSignin: boolean; fromCheckout: boolean };

    constructor(
        private modalService: ModalService,
        private logInService: LogInService,
        private router: Router,
        private translateService: TranslateService
    ) {
        this.logInForm = LogInService.createLoginForm();
    }

    ngOnInit() {
        this.showSignin = this.data.showSignin;
        this.fromCheckout = this.data.fromCheckout;
        if (this.data.loginMailId && this.data.loginMailId !== '') {
            this.logInForm.controls['emailID'].setValue(this.data.loginMailId);
        }
    }

    public validatePassword(patternStr: string): boolean {
        const pattern = new RegExp(patternStr);
        return pattern.test(this.logInForm.value.password);
    }

    public goToPage(url: string): void {
        const targetUrl = this.router.serializeUrl(this.router.createUrlTree([url]));
        window.open(targetUrl, '_blank');
    }

    public proceedSignIn() {
        if (!this.logInForm.valid) {
            return;
        }

        this.logInService.proceedSignIn(this.showSignin, this.logInForm.value, this.modalService).then(
            () => {
                this.modalService.dismiss(true);
            },
            (err) => {
                this.errorMsg = err.error.message;
                console.log('Auth error: ', err);
            }
        );
    }

    public hello() {
        console.log('sdfasdfajsidfasjdfiasjdfoajsiodfijasdjifajs');
        this.logInService.proceedSignIn(this.showSignin, this.logInForm.value, this.modalService).then(
            () => {},
            (err) => {
                this.errorMsg = err.error.message;
                console.log('Auth error: ', err);
            }
        );
    }

    public loginWithSocialMedia = (media: string) => this.logInService.loginWithSocialMedia(media, this.modalService);

    public closePopup(skipSignIn = false): void {
        if (skipSignIn) {
            StorageManagerUtil.set('skipSignIn', true);
        }

        this.modalService.dismiss();
    }

    public toggleSignIn(): void {
        this.showSignin = !this.showSignin;
        this.modalService.updateModelTitle$.next(
            this.translateService.instant('ACCOUNT.SIGN.' + (this.showSignin ? 'SIGN_IN' : 'CREATE_AN_ACCOUNT'))
        );
    }

    public forgotPassword(): void {
        this.modalService.dismiss().then(() => {
            this.modalService.createMainModal(MobForgotPasswordComponent, {
                data: {},
                title: 'ACCOUNT.FORGOT_PASSWORD.FORGOT_PASSWORD',
            });
        });
    }
}
