import { Injectable } from '@angular/core';
import { loadStripe } from '@stripe/stripe-js/pure';

import { StripeKeys } from '@core/constants/stripe.const';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { environment } from '@environments/environment';
import { Stripe, StripeCardNumberElement } from '@stripe/stripe-js';

@Injectable({
    providedIn: 'root',
})
export class StripeService {
    public updateStripe = new Subject<void>();
    public readonly stripeFieldStyle = {
        base: {
            fontSize: '14px',
            fontWeight: 600,
            fontFamily:
                //eslint-disable-next-line max-len
                '\'Segoe UI\',-apple-system,BlinkMacSystemFont,\'Roboto\',\'Oxygen\',\'Ubuntu\',\'Fira Sans\',\'Droid Sans\',\'Helvetica Neue\',sans-serif',
            color: '#2F0A4DA',
            ':-webkit-autofill': {
                color: '#2F0A4D',
            },
            '::placeholder': {
                fontWeight: 400,
            },
        },
        invalid: {
            color: '#2F0A4D',
            iconColor: '#fa755a',
        },
    };

    public readonly stripeMobileFieldStyle = {
        base: {
            fontSize: '14px',
            fontWeight: 400,
            fontFamily:
                //eslint-disable-next-line max-len
                '\'Segoe UI\',-apple-system,BlinkMacSystemFont,\'Roboto\',\'Oxygen\',\'Ubuntu\',\'Fira Sans\',\'Droid Sans\',\'Helvetica Neue\',sans-serif',
            color: '#202124',
            ':-webkit-autofill': {
                color: '#202124',
            },
            '::placeholder': {
                fontWeight: 400,
            },
        },
        invalid: {
            color: '#202124',
            iconColor: '#fa755a',
        },
    };

    private stripe!: Stripe;
    private cardEl!: StripeCardNumberElement;
    private stripeLoaded = new BehaviorSubject<boolean>(false);

    constructor() {}

    public async loadStripeKey(): Promise<any> {
        const stripe = await loadStripe(environment.production ? StripeKeys.PROD : StripeKeys.TEST);

        if (stripe) {
            this.stripe = stripe;
            console.log('stripe key set');
            this.stripeLoaded.next(true);
        } else {
            console.log('delaying stripe loading');
            return this.loadStripeKey();
        }
    }

    public isStripLoaded(): Observable<boolean> {
        return this.stripeLoaded.asObservable();
    }

    public getStripe(): Stripe {
        return this.stripe;
    }

    public setCartElement(el: StripeCardNumberElement): void {
        this.cardEl = el;
    }

    public getCartElement(): StripeCardNumberElement {
        return this.cardEl;
    }
}
