import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { ToolsService } from '@core/utils/tools-service';

export interface AccSlider {
    id: number;
    url: string;
    active: boolean;
    status: number;
}

@Component({
    selector: 'tz-image-slider',
    templateUrl: './tz-image-slider.component.html',
    styleUrls: ['./tz-image-slider.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TzImageSliderComponent implements OnInit {
    @Input() giataId = '';
    @Input() images: string[] = [];
    @Input() imageProvider = '';
    @Input() showWarning = false;
    @Input() gallery = false;
    @Input() mobileOnlyGal = false;

    @Output() actionCb: EventEmitter<string | number> = new EventEmitter<string | number>();

    public slides: AccSlider[] = [];
    public loading = true;
    public initialLoading = true;
    public selectedImage!: AccSlider;
    public image!: AccSlider;

    private count = 0;

    constructor() {}

    ngOnInit() {
        if (this.images) {
            this.slides = this.images.map((url) =>
                // if (this.count === 0) url += 'test'; //To test

                ({
                    id: this.count++,
                    url,
                    active: false,
                    status: 0,
                })
            );
        }

        this.addNoImage(-1, 0);
    }

    public loadImg(n: number): void {
        // n - increment/decrement value
        let selectedId = this.selectedImage.id + n;
        if (selectedId < 0) {
            selectedId = this.slides[this.slides.length - 1].id;
        } else if (selectedId >= this.count) {
            selectedId = this.slides[0].id;
        }

        this.select(selectedId, n);
    }

    public selectImg = (id: number) => this.select(id, 0);

    public imgCallback(data: { id: number; status: number; url: string }): void {
        if (!data) {
            return;
        }

        this.loading = false;
        this.slides.forEach((slide, idx) => {
            slide.active = false;

            if (slide.id === data.id) {
                if (data.status === 404) {
                    slide.status = -1;
                    const nextId = slide.id + 1;
                    this.select(nextId, 1);
                    this.slides.splice(idx, 1);
                } else {
                    slide.status = 1;
                    slide.url = data.url;
                    this.setSelectedImg(slide);
                }
            }
        });
    }

    public open(): void {
        this.actionCb.emit(this.gallery ? this.selectedImage.id : this.giataId);
    }

    private setSelectedImg(slide: AccSlider): void {
        this.loading = false;
        this.initialLoading = false;
        slide.active = true;
        this.selectedImage = ToolsService.deepCopy(slide);
        /*if (elt[0].offsetParent.id === 'hotel_0') {
      ListenersService.NotifyListeners(ListenersService.LISTEN_ACC_FIRST_LOADED)
    }*/
    }

    private select(id: number, n: number): void {
        if (id >= this.count) {
            this.addNoImage(-1, 0);
            return;
        }

        let found = false;
        this.slides.forEach((slide) => {
            slide.active = false;
            if (slide.id === id) {
                found = slide.status > -1;
                if (slide.status === 0) {
                    this.image = ToolsService.deepCopy(slide);
                    this.loading = true;
                } else if (slide.status === 1) {
                    this.setSelectedImg(slide);
                }
            }
        });

        if (!found) {
            this.addNoImage(id, n);
        }
    }

    private addNoImage(id: number, n: number): void {
        // n - increment/decrement value
        if (this.slides.length === 0) {
            const slide = {
                url: 'assets/icons/web/img/corrupted.png',
            } as AccSlider;
            this.slides = [slide];
            this.setSelectedImg(slide);
        } else if (n !== 0 && id > -1 && id < this.count) {
            this.select(id + n, n);
        } else if (n !== 0 && this.selectedImage && this.selectedImage.active) {
            // activate previous selection
            this.select(this.selectedImage.id, n);
        } else if (id !== this.slides[0].id) {
            this.select(this.slides[0].id, n);
        }
    }
}
