import { Injectable } from '@angular/core';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { GeneralService } from '@services/modules/core/general-service';
import { CombServiceIds } from '@core/constants/comb-service-ids';
import { CalTypes } from '@core/constants/call-types.const';
import { CartService } from '@services/modules/cart/services/cart.service';
import { Router } from '@angular/router';
import { SettingsService } from '@services/services/settings-service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { AppConfigDataService } from '@services/modules/core/dataservices/app-config-data.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';
import { CombinationId } from '@schemas/modules/core/base-types';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { ModalService } from '@services/modal.service';
import { PlatformService } from '@services/platform.service';
import { PageUtils } from '@core/utils/page-utils';
import { ToggleContentPages } from '@core/constants/toggle-content-pages';

type searchParam = { params: any; serviceSearch?: any };

@Injectable({
    providedIn: 'root',
})
export class InactivityHandler {
    private readonly thirtyMinInMilliseconds = 30 * 60000;
    private sessionTimeout: any;
    private inactiveTimeout: any;
    private listenerTimeout: any;

    constructor(
        private trazlerAPI: ApiAccessService,
        private generalService: GeneralService,
        private cartService: CartService,
        private router: Router,
        private searchDataService: SearchDataService,
        private settingsService: SettingsService,
        private appConfigDataService: AppConfigDataService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private modalService: ModalService,
        private platformService: PlatformService
    ) {
        /*
        //used to test inactivity handler
        let self = this;
        setInterval(function(){
            if (window.sessionStorage.getItem("fake") === "true"){
                self.timeoutAlert(new Subject<{ params: any; serviceSearch?: any }>());
                window.sessionStorage.setItem("fake","false");
            }
        },2000);*/
    }

    public sessionTimer(
        sessionEndTimeInMinutes: number,
        combId: CombinationId,
        searchHandler$: Subject<searchParam>
    ): void {
        if (!sessionEndTimeInMinutes || sessionEndTimeInMinutes === 0) {
            return;
        }

        const dt = new Date(new Date().getTime() + sessionEndTimeInMinutes * 60000);
        StorageManagerUtil.set('sessionEndTime', dt.toString(), true);
        this.startSessionTimer(dt, combId, searchHandler$);
    }

    public startSessionTimer(
        sessionEndTime: Date | string | null,
        combId: CombinationId,
        searchHandler$: Subject<searchParam>
    ): void {
        sessionEndTime = sessionEndTime ? sessionEndTime : StorageManagerUtil.get('sessionEndTime', true);
        if (!sessionEndTime) {
            return;
        }

        if (this.sessionTimeout) {
            clearTimeout(this.sessionTimeout);
        }

        const timer = new Date(sessionEndTime).getTime() - new Date().getTime();
        this.sessionTimeout = setTimeout(
            () => {
                console.log('search expires');
                if (this.platformService.isMobileDevice) {
                    this.dialogService.closeAll();
                }
                this.timeoutAlert(searchHandler$);
                // sendBookingRecoveryMail(combId, handler);
                // provide some delay when timer < 0 (page was refreshed otherwise
                // alert is not displayed as we are currently displaying the loader popup
            },
            timer <= 0 ? 5000 : timer
        );

        if (timer > this.thirtyMinInMilliseconds) {
            this.startInactiveTimer(combId, searchHandler$);
        }
    }

    public clearSession(): void {
        this.generalService.stopWindowListeners();
        StorageManagerUtil.remove('sessionEndTime');
        if (this.sessionTimeout) {
            clearTimeout(this.sessionTimeout);
        }
        if (this.inactiveTimeout) {
            clearTimeout(this.inactiveTimeout);
        }
        if (this.listenerTimeout) {
            clearTimeout(this.listenerTimeout);
        }
    }

    private startInactiveTimer(combId: CombinationId, searchHandler$: Subject<searchParam>) {
        if (this.inactiveTimeout) {
            clearTimeout(this.inactiveTimeout);
        }
        this.generalService.stopWindowListeners();

        this.inactiveTimeout = setTimeout(() => {
            this.timeoutAlert(searchHandler$);
        }, this.thirtyMinInMilliseconds);

        if (this.listenerTimeout) {
            clearTimeout(this.listenerTimeout);
        }

        this.listenerTimeout = setTimeout(() => {
            this.generalService.startWindowListeners(this.startInactiveTimer.bind(this, combId, searchHandler$));
        }, 3 * 60000);
    }

    private timeoutAlert(searchHandler$: Subject<searchParam>) {
        if (
            !PageUtils.is(this.router.url, ToggleContentPages.RESULT_PAGES) ||
            //inactivity handler disabled in case of one or more service is booked
            this.cartService.isSomeServiceBooked()
        ) {
            return;
        }

        this.clearSession();
        const { update, combSearchInfos } = this.checkEnableUpdateSession();
        if (!update) {
            this.timeout();
            return;
        }

        this.checkSession().then(
            (resp: any) => {
                this.dialogService.closeAll();
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.INACTIVITY.TITLE'),
                        multipleContent: [this.translateService.instant('POPUPS.INACTIVITY.BODY')],
                        confirmBtnTitle: this.translateService.instant('POPUPS.INACTIVITY.1_NEW_SEARCH'),
                        cancelBtnTitle: this.translateService.instant('POPUPS.INACTIVITY.2_UPDATE'),
                        icon: { name: 'inactivity-newsearch-dialog', iconStyle: { 'width.px': 48, 'height.px': 48 } },
                    })
                    .afterClosed()
                    .subscribe((startNewSearch: TzDialogReturn) => {
                        if (startNewSearch === CONFIRM) {
                            if (this.platformService.isMobileDevice) {
                                this.closeAllModal();
                            } else {
                                this.router.navigate(['search']);
                            }

                            return;
                        }

                        if (!resp.fullReset) {
                            // extend the session and search missing service
                            this.sessionTimer(resp.combTimeout, this.searchDataService.getCombId(), searchHandler$);
                            const searchInfos = this.searchDataService.getSearch(resp.firstServiceId);
                            if (searchInfos) {
                                /* forceResultPage used to navigate the flight/train page
                                forcefully to result screen if the search starts from summary*/
                                const forceResultPage = true;
                                searchHandler$.next({
                                    params: { serviceId: resp.firstService, editMode: true, forceResultPage },
                                    serviceSearch: searchInfos,
                                });
                                return;
                            }
                        }
                        this.reCreateSession(combSearchInfos, searchHandler$);
                    });
            },
            (failure: any) => {
                console.error('***unknown cart error', { failure });
                this.reCreateSession(combSearchInfos, searchHandler$);
            }
        );
    }

    private checkEnableUpdateSession() {
        const combSearchInfos = StorageManagerUtil.get('searchInfos');
        console.log(combSearchInfos);
        if (!combSearchInfos) {
            return {
                update: false,
            };
        }
        const recommendedCartData = CartService.getRecommendedCartInfo(combSearchInfos.combId);
        if (recommendedCartData) {
            combSearchInfos.recommendedCartData = recommendedCartData;
        }
        delete combSearchInfos.combId;
        return {
            update: this.checkDate(combSearchInfos),
            combSearchInfos,
        };
    }

    private checkDate = (combSearchInfos: any) => {
        let calType = CalTypes.CAL_TRIP_TYPE;

        switch (combSearchInfos.context.services) {
            case CombServiceIds.FLIGHT_TRAIN:
            case CombServiceIds.FLIGHT:
            case CombServiceIds.TRAIN:
            case CombServiceIds.HOTEL:
                break;
            case CombServiceIds.TRANSFER: // transfer
                calType = CalTypes.CAL_TRANSFER_TYPE;
                break;
            case CombServiceIds.RENTAL: //rental
                calType = CalTypes.CAL_RENTAL_TYPE;
                break;
            default:
                // check for single service
                return false;
        }

        const cal = combSearchInfos.searchData[calType];
        if (!cal) {
            return false;
        }
        const now = new Date();
        const calDate = new Date(`${cal.fromDate}T${cal.fromTime}`);
        return now.getTime() < calDate.getTime();
    };

    private reCreateSession(combSearchInfos: any, searchHandler$: Subject<searchParam>) {
        // LocalStorageManager.clear(LocalStorageManager.session);
        this.settingsService.clearDeviceId();
        this.appConfigDataService.initDeviceId(true).then(() => {
            searchHandler$.next({
                params: {},
                serviceSearch: combSearchInfos,
            });
        });
    }

    private checkSession() {
        return new Promise((resolve, reject) => {
            this.trazlerAPI.resetSession(this.searchDataService.getCombId()).then(
                (resp: any) => {
                    resolve(resp);
                },
                (failure: any) => {
                    reject(failure);
                }
            );
        });
    }

    private timeout(): void {
        // LocalStorageManager.clear(LocalStorageManager.session);
        this.settingsService.clearDeviceId();
        this.dialogService.closeAll();
        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.INACTIVITY.TITLE'),
                multipleContent: [this.translateService.instant('POPUPS.INACTIVITY.BODY')],
                confirmBtnTitle: this.translateService.instant('POPUPS.INACTIVITY.1_NEW_SEARCH'),
                icon: { name: 'inactivity-dialog', iconStyle: { 'width.px': 80, 'height.px': 54 } },
            })
            .afterClosed()
            .subscribe(() => {
                if (this.platformService.isMobileDevice) {
                    this.closeAllModal();
                } else {
                    this.router.navigate(['search']);
                }

                return;
            });
    }

    private closeAllModal(): void {
        this.modalService.getTop().then((isOverlay) => {
            if (!!isOverlay) {
                this.modalService
                    .dismiss()
                    .then(() => {
                        this.closeAllModal();
                    })
                    .catch((err) => {
                        console.error('dismiss modal error:', err);
                    });
            } else {
                this.goToHome();
            }
        });
    }

    private goToHome(): void {
        setTimeout(() => {
            this.router.navigate(['search']).catch((e) => console.error(e));
        }, 300);
    }
}
