import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ComponentType } from '@angular/cdk/portal';

import { ConfirmationDialogOptions } from '@shared/schemas/confirmation-dialog-options.model';
import { DialogButtonsStyles } from '@shared/schemas/dialog.model';
import { DialogService } from '@services/dialog.service';
import { PlatformService } from '@services/platform.service';

export type TzDialogReturn = boolean | null;
export const CLOSE: TzDialogReturn = null;
export const CONFIRM: TzDialogReturn = true;
export const CANCEL: TzDialogReturn = false;

@Component({
    selector: 'tz-trazler-confirmation-dialog',
    templateUrl: './trazler-confirmation-dialog.component.html',
    styleUrls: ['./trazler-confirmation-dialog.component.scss'],
})

//export interface IDialogReturn: DialogReturn
export class TrazlerConfirmationDialogComponent implements OnInit {
    public dialogData!: ConfirmationDialogOptions;
    public dialogButtonsStyles = DialogButtonsStyles;

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: {
            component: ComponentType<any>;
            data: ConfirmationDialogOptions;
        },
        public platformService: PlatformService,
        private mdDialogRef: MatDialogRef<TrazlerConfirmationDialogComponent>,
        private dialogService: DialogService
    ) {}

    ngOnInit(): void {
        this.dialogData = this.data.data;
    }

    public onClose(): void {
        this.closeDialog(CLOSE);
    }

    public onCancel(): void {
        this.closeDialog(CANCEL);
    }

    public onConfirm(): void {
        this.closeDialog(CONFIRM);
    }

    private closeDialog(value: TzDialogReturn): void {
        this.dialogService.closeDialog(this.mdDialogRef, value);
    }
}
