import { Injectable } from '@angular/core';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { HttpRequestType } from '@core/constants/http-request-type';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class UserDataService extends ApiAccessService {
    public getUserInfo() {
        return this.apiCallObs(HttpRequestType.GET, {
            entity: 'loggedInCustomer',
        });
    }

    public updateUserInfo(customerInformation: CustomerInfo) {
        this.showLoader(true);
        return this.apiCallObs(
            HttpRequestType.PUT,
            {
                entity: 'customers',
                id: customerInformation.id,
                closeLoader: true,
                language: this.settingsService.langCode,
            },
            customerInformation
        );
    }

    public changeTzPassword(
        customerId: string,
        passwordDetail: {
            currentPassword?: string;
            newPassword: string;
            confirmPassword: string;
            updateType: string;
        }
    ) {
        const params = {
            entity: 'customers',
            action: 'changePassword',
            id: customerId,
            closeLoader: true,
            language: this.settingsService.langCode,
        };

        this.showLoader(true);
        return this.apiCallObs(HttpRequestType.PUT, params, passwordDetail);
    }

    public resetTzPassword(passwordDetail: { token: string; password: string; confirmPassword: string }) {
        const params = {
            entity: 'resetPassword',
            closeLoader: true,
            language: this.settingsService.langCode,
        };

        this.showLoader(true);
        return this.apiCallObs(HttpRequestType.PUT, params, passwordDetail);
    }

    public deleteUserAccount(customerInformation: CustomerInfo) {
        return this.apiCallObs(HttpRequestType.DELETE, {
            entity: 'customers',
            id: customerInformation.id,
        });
    }

    public forgotPassword(emailID: string) {
        const params = {
            entity: 'forgotPassword',
            emailID,
            langCode: this.settingsService.langCode,
        };

        return this.apiCallObs(HttpRequestType.POST, params);
    }

    public checkPasswordToken(token: string): Observable<{ customerMail: string }> {
        const params = {
            entity: 'checkCustomerToken',
            token,
            closeLoader: true,
        };

        this.showLoader(true);
        return this.apiCallObs(HttpRequestType.GET, params);
    }

    public subscribeToTz(emailId: string): Observable<void> {
        const params = {
            entity: 'subscribe',
            langCode: this.settingsService.langCode,
        };

        return this.apiCallObs(HttpRequestType.POST, params, { emailId });
    }
}
