import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CountryISO } from 'ngx-intl-tel-input';
import { FormGroup } from '@angular/forms';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-country-field',
    templateUrl: './tz-country-field.component.html',
})
export class TzCountryFieldComponent implements OnInit {
    @Input() formGroup = new FormGroup({});
    @Input() formCtrl = '';
    @Input() readonly = false;

    @Output() countyFocus: EventEmitter<boolean> = new EventEmitter<boolean>();

    public countryList!: any[];

    constructor(public platformService: PlatformService) {}

    ngOnInit(): void {
        this.countryList = this.getCountryList();
        this.formGroup.controls[this.formCtrl].statusChanges.subscribe((result) => {
            if (result === 'INVALID') {
                this.countryList = this.getCountryList('in');
            }
        });
    }

    private getCountryList(selectedISO?: string) {
        return Object.entries(CountryISO).map((country) => {
            const counrty = {
                name: country[0].replace(/([a-z])([A-Z])/g, '$1 $2').replace(/([A-Z])([A-Z][a-z])/g, '$1 $2'),
                code: country[1],
            };

            if (selectedISO && selectedISO === counrty.code) {
                this.formGroup.controls[this.formCtrl].setValue(counrty);
            }

            return counrty;
        });
    }
}
