import { ServiceId } from '@core/constants/service-id.const';
import { DatesUtils } from '@core/utils/dates-utils';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { TranslateService } from '@ngx-translate/core';
import { Injectable } from '@angular/core';
import { NightChoices } from '@schemas/modules/search/search';
import { DialogService } from '@services/dialog.service';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';

export interface INightCheckResult {
    ignore: boolean;
    changed: boolean;
    curChoice: NightChoices;
}

interface ICheckNightRes {
    askNightDate: boolean;
    newChoice: NightChoices;
}

@Injectable({
    providedIn: 'root',
})
export class NightChoiceService {
    private userNightPref = NightChoices.NIGHT_CHOICE_UNDEFINED;

    constructor(
        private cartService: CartService,
        private searchDataService: SearchDataService,
        private translateService: TranslateService,
        private dialogService: DialogService
    ) {}

    public checkNightArrival(): Promise<INightCheckResult> {
        return new Promise<INightCheckResult>((resolve, reject) => {
            if (this.userNightPref !== NightChoices.NIGHT_CHOICE_UNDEFINED) {
                resolve({
                    ignore: true,
                    changed: false,
                    curChoice: this.userNightPref,
                });
            } else {
                const { askNightDate, newChoice } = this.requestNightArrivalCheck();
                if (askNightDate) {
                    return this.requestDateCheck(newChoice, resolve);
                } else {
                    resolve({
                        ignore: true,
                        changed: false,
                        curChoice: this.userNightPref,
                    });
                }
            }
        });
    }

    public resetNightPreference(): void {
        this.userNightPref = NightChoices.NIGHT_CHOICE_UNDEFINED;
    }

    public requestNightArrivalCheck(): ICheckNightRes {
        const searchServiceInfos = this.searchDataService.getSearchServiceInfos();
        if (
            searchServiceInfos.services[ServiceId.FLIGHT] &&
            searchServiceInfos.services[ServiceId.ACC] &&
            this.searchDataService.hasAcc()
        ) {
            const flight = this.cartService.getCartItemData(ServiceId.FLIGHT);

            if (flight && flight.carts[0]) {
                const arrivalTimeH = parseInt(
                    DatesUtils.getTZFormatTime(
                        flight.carts[0].selectedItem.data.departureSummary.arrivalTzFormat
                    ).split(':')[0],
                    10
                );
                const arrivalDate = DatesUtils.getTZFormatDate(
                    flight.carts[0].selectedItem.data.departureSummary.arrivalTzFormat
                );
                const hotelDate = searchServiceInfos.services[ServiceId.ACC].searchData.tripCal.fromDate;

                if (arrivalTimeH >= 0 && arrivalTimeH <= 5) {
                    const newChoice =
                        arrivalDate !== hotelDate
                            ? NightChoices.NIGHT_CHOICE_CUR_DAY
                            : NightChoices.NIGHT_CHOICE_PREV_DAY;
                    if (newChoice === this.userNightPref || !this.userNightPref) {
                        return {
                            askNightDate: true,
                            newChoice,
                        };
                    }
                }
            }
        }

        return {
            askNightDate: false,
            newChoice: NightChoices.NIGHT_CHOICE_UNDEFINED,
        };
    }

    private requestDateCheck(newChoice: NightChoices, resolver: any): void {
        const msg =
            newChoice === NightChoices.NIGHT_CHOICE_CUR_DAY
                ? 'POPUPS.SEARCH_ON_ARRIVAL.BODY'
                : 'POPUPS.BOOK_BEFORE_ARRIVAL.BODY';
        const title =
            newChoice === NightChoices.NIGHT_CHOICE_CUR_DAY
                ? 'POPUPS.SEARCH_ON_ARRIVAL.TITLE'
                : 'POPUPS.BOOK_BEFORE_ARRIVAL.TITLE';
        const button1 =
            newChoice === NightChoices.NIGHT_CHOICE_CUR_DAY
                ? 'POPUPS.SEARCH_ON_ARRIVAL.1_YES'
                : 'POPUPS.BOOK_BEFORE_ARRIVAL.1_YES';
        const button2 =
            newChoice === NightChoices.NIGHT_CHOICE_CUR_DAY
                ? 'POPUPS.SEARCH_ON_ARRIVAL.2_NO'
                : 'POPUPS.BOOK_BEFORE_ARRIVAL.2_NO';

        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant(title),
                content: this.translateService.instant(msg),
                confirmBtnTitle: this.translateService.instant(button1),
                cancelBtnTitle: this.translateService.instant(button2),
            })
            .afterClosed()
            .subscribe((dialogReturn: TzDialogReturn) => {
                if (dialogReturn === CONFIRM) {
                    this.userNightPref = newChoice;
                    resolver({ ignore: false, changed: true, curChoice: this.userNightPref });
                } else {
                    this.userNightPref =
                        newChoice === NightChoices.NIGHT_CHOICE_CUR_DAY
                            ? NightChoices.NIGHT_CHOICE_PREV_DAY
                            : NightChoices.NIGHT_CHOICE_CUR_DAY;
                    resolver({
                        ignore: false,
                        changed: false,
                        curChoice: this.userNightPref,
                    });
                }
            });
    }
}
