import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { StickFooterDialogOptions } from '@shared/schemas/stick-footer-dialog-options.model';
import { ComponentType } from '@angular/cdk/portal';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-stick-footer-dialog',
    templateUrl: './stick-footer-dialog.component.html',
    styleUrls: ['./stick-footer-dialog.component.scss'],
})
export class StickyFooterDialogComponent implements OnInit {
    public dialogData!: StickFooterDialogOptions;

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: {
            component: ComponentType<any>;
            data: StickFooterDialogOptions;
        },
        private mdDialogRef: MatDialogRef<StickyFooterDialogComponent>,
        private dialogService: DialogService
    ) {}

    @HostListener('keydown.esc')
    public onEsc(): void {
        this.close(false);
    }

    ngOnInit(): void {
        this.dialogData = this.data.data;
        this.addWrapperClass();
    }

    public addWrapperClass(): void {
        const dialogRefKey = '_overlayRef';
        const overlayEl = this.mdDialogRef[dialogRefKey].overlayElement;
        overlayEl.parentElement.className += ' tz-stick-footer-dialog-wrapper';
        overlayEl.parentElement.insertBefore(overlayEl.parentElement.previousElementSibling, overlayEl);
    }

    public close(value: any): void {
        this.dialogService.closeDialog(this.mdDialogRef, value);
    }

    public cancel(): void {
        this.close(false);
    }

    public confirm(): void {
        this.close(true);
    }
}
