import { Flight } from '@schemas/modules/flights/flight';
import { FlightPackageRequestParams } from '@schemas/modules/flights/flight-package-request-params';

export class FlightUtils {
    public static getQueryParamsForFlightPackages(flight: Flight): FlightPackageRequestParams {
        const departureFlightId = flight.departureSummary.route ? flight?.departureSummary?.route[0]?.flightID : '';
        const returnFlightId = flight.returnSummary?.route ? flight?.returnSummary?.route[0]?.flightID : '';
        const flightIds = departureFlightId + '|' + returnFlightId;
        const travelClassDep = flight.departureSummary.cabin !== '' ? flight.departureSummary.cabin : 'null';
        const travelClassRet = flight.returnSummary.cabin !== '' ? flight.returnSummary.cabin : 'null';
        const travelClass = travelClassDep + '|' + travelClassRet;

        return {
            depDate: flight.departureSummary.departureTzFormat ? flight.departureSummary.departureTzFormat : '',
            entity: 'upgrade',
            flightIds,
            provider: flight.broker,
            retDate: flight.returnSummary.departureTzFormat ? flight.returnSummary.departureTzFormat : '',
            travelClass,
            tripId: flight.tripId,
            vendor: flight.vendor.split(' ').join(''),
        };
    }
}
