import { Injectable } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { ScrollService } from '@services/modules/core/scroll-service';
import { ResolveEnd, Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { StateFrom } from '@core/constants/state-from.const';
import { Trip } from '@core/constants/trip';
import { SERVICE_DEFAULT_SORT } from '@core/constants/default-sort';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SERVICE_ROUTE_MAP } from '../../../../modules/results/service-route.map';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';

export interface IRoutingIds {
    giataId?: string; //hotel
    markerKey?: string; //rental
    hotelName?: string; //hotel
    journeyType?: Trip | undefined; //transfer
    state?: any;
}

type IRoutingParams = {
    id?: string;
    from?: StateFrom;
    itemIdentifier?: string;
    newItem?: boolean; // to be removed after the check
    journeyType?: Trip;
    sort_by?: string | number;
    extra?: any;
    markerKeys?: string;
    serviceId: ServiceId;
};

type ServiceRoutingParams = {
    inValid?: boolean;
    name?: string;
    params?: IRoutingParams;
};

@Injectable({
    providedIn: 'root',
})
export class CartRoutingService {
    currentServiceId: BehaviorSubject<ServiceId> = new BehaviorSubject<ServiceId>(ServiceId.INVALID);

    constructor(
        private scrollService: ScrollService,
        private router: Router,
        private cartService: CartService,
        private searchDataService: SearchDataService
    ) {
        this.computeCurrentServiceId(window.location.pathname);
        this.listenNav();
    }

    public get getCurStateServiceId(): ServiceId {
        return this.currentServiceId.getValue();
    }

    public reload(state: any, serviceId: ServiceId, ids: IRoutingIds | undefined): void {
        this.router.navigateByUrl('fake', { skipLocationChange: true, state: { isReload: true } }).then(() => {
            let routerParams = [state.name];
            if (state.params?.id) {
                routerParams.push(state.params.id);
            } else {
                const urlSuffixes = this.searchDataService.getRoutingParam(serviceId, ids);
                if (urlSuffixes && urlSuffixes.length) {
                    routerParams = routerParams.concat(urlSuffixes);
                }
            }
            this.router.navigate(routerParams, {
                state: state.params,
            });
        });
    }

    public goToState(
        serviceId: ServiceId,
        ids: IRoutingIds | undefined,
        newItem: any,
        from: any,
        extraParams?: any
    ): void {
        const state = this.getStateAndParams(serviceId, ids, newItem, from);

        if (!state.inValid) {
            this.scrollService.scrollToTop();

            if (extraParams) {
                if (!state.params) {
                    state.params = {
                        serviceId,
                    } as IRoutingParams;
                }
                state.params.extra = extraParams;
            } else {
                if (!state.params) {
                    state.params = {
                        serviceId,
                    } as IRoutingParams;
                } else {
                    state.params.serviceId = serviceId;
                }
            }
            const urlSuffixes = this.searchDataService.getRoutingParam(serviceId, ids);
            if (
                state.name &&
                this.isSameState(
                    state.name +
                        (state.params?.id ? '/' + state.params.id : urlSuffixes ? '/' + urlSuffixes.join('/') : '')
                )
            ) {
                this.reload(state, serviceId, ids);
                return;
            }

            // V2 check later
            let routerParams = [state.name];
            if (state.params?.id) {
                routerParams.push(state.params.id);
            }
            if (urlSuffixes) {
                routerParams = routerParams.concat(urlSuffixes);
            }

            console.log('navigateto:', routerParams, state.params, !urlSuffixes || !urlSuffixes.length);

            this.router.navigate(routerParams, {
                state: state.params,
                skipLocationChange: serviceId !== ServiceId.SUMMARY && (!urlSuffixes || !urlSuffixes.length),
            });
            /*.then(() => {
                this.set(serviceId);
            })*/
        }
    }

    public changeState(serviceId: ServiceId, cartItem: any, newItem: any, from: any, extraParams?: any): void {
        switch (serviceId) {
            case ServiceId.ACC_DETAILS:
                this.goToState(ServiceId.ACC_DETAILS, { giataId: cartItem.giataID }, newItem, from, extraParams);
                console.log('cartItem in changeState fn', cartItem);
                break;
            case ServiceId.CAR_RENTAL:
                if (cartItem) {
                    this.goToState(serviceId, { markerKey: cartItem.markerKey }, newItem, from, extraParams);
                } else {
                    this.goToState(serviceId, undefined, newItem, from, extraParams);
                }
                break;
            case ServiceId.FLIGHT_TRAIN:
                if (this.cartService.getCartItemData(ServiceId.TRAIN).found) {
                    this.goToState(ServiceId.TRAIN, undefined, newItem, from, extraParams);
                } else {
                    this.goToState(ServiceId.FLIGHT, undefined, newItem, from, extraParams);
                }
                break;
            case ServiceId.TRANSFER:
                if (cartItem) {
                    this.goToState(serviceId, { journeyType: cartItem.journeyType }, newItem, from, extraParams);
                } else {
                    this.goToState(serviceId, undefined, newItem, from, extraParams);
                }
                break;
            default:
                this.goToState(serviceId, undefined, newItem, from, extraParams);
        }
    }

    public moveToSummary(): Promise<boolean> {
        const routerPromise = this.router.navigate(['results-summary']);
        routerPromise.then(() => this.set(ServiceId.SUMMARY));
        return routerPromise;
    }

    public isTransportState(): boolean {
        return this.router.url.indexOf('/travel') === 0;
    }

    private computeCurrentServiceId(pathanme: string) {
        console.log('path name is:', pathanme);
        for (const key in SERVICE_ROUTE_MAP) {
            if (
                SERVICE_ROUTE_MAP[+key as ServiceId] &&
                pathanme.includes(SERVICE_ROUTE_MAP[+key as ServiceId] as string)
            ) {
                this.set(+key as ServiceId);
                break;
            }
        }
    }

    private set(serviceId: ServiceId): void {
        console.log('set cur service id to:', serviceId);
        this.currentServiceId.next(serviceId);
    }

    private isSameState(uri: string): boolean {
        return this.router.url === '/' + encodeURI(uri);
    }

    private getStateAndParams(
        serviceId: ServiceId,
        ids: IRoutingIds | undefined,
        newItem: any,
        from: any
    ): ServiceRoutingParams {
        const params: IRoutingParams = {
            sort_by: SERVICE_DEFAULT_SORT[serviceId],
            from,
            newItem,
            serviceId,
        };

        switch (serviceId) {
            case ServiceId.ACC:
                return {
                    name: 'hotels',
                    params,
                };
            case ServiceId.ACC_DETAILS:
                let giataId = '';
                if (ids?.giataId) {
                    giataId = ids.giataId;
                }
                return {
                    name: 'hotel',
                    params: {
                        id: giataId,
                        from,
                        newItem,
                        serviceId: ServiceId.ACC,
                    },
                };
            case ServiceId.FLIGHT_TRAIN:
                if (this.cartService.getCartInfo(ServiceId.TRAIN).inCart) {
                    return { name: 'travel/trains', params };
                } else {
                    return { name: 'travel/flights', params };
                }
            case ServiceId.FLIGHT:
                return {
                    name: 'travel/flights',
                    params,
                };
            case ServiceId.TRAIN:
                return {
                    name: 'travel/trains',
                    params,
                };
            case ServiceId.CAR_RENTAL:
                if (ids && ids.markerKey) {
                    params.markerKeys = ids.markerKey;
                }

                return {
                    name: 'car-rentals',
                    params,
                };
            case ServiceId.TRANSFER:
                return {
                    name: 'transfers',
                    params: {
                        journeyType: ids && ids.journeyType ? ids.journeyType : undefined,
                        from,
                        newItem,
                        sort_by: SERVICE_DEFAULT_SORT[serviceId],
                        serviceId,
                    },
                };
            case ServiceId.SUMMARY:
                // Activate Summary tab if there is any data in cart
                return {
                    name: 'results-summary',
                    params: {
                        from,
                        newItem,
                        serviceId,
                    },
                };
        }
        return {
            inValid: true,
        };
    }

    private listenNav(): void {
        console.log('starting listen nav');
        this.router.events.forEach((event) => {
            if (event instanceof ResolveEnd) {
                console.log('nav end received', event, this.router.url);
                this.computeCurrentServiceId(event.url);
                //this.computeCurrentServiceId(this.router.url);
                /*const state = this.router.getCurrentNavigation();
                if (state && state.extras && state.extras.state) {
                    if (state.extras.state['serviceId'] !== undefined) {
                        const serviceId = state.extras.state['serviceId'];
                        if (serviceId !== ServiceId.INVALID) {
                            this.set(serviceId);
                        } else {
                            console.error('invalid service');
                        }
                    } else if (!state.extras.state['isReload']) {
                        console.error('invalid state');
                    }
                } else {
                    console.error('invalid state');
                }*/
            }
        });
    }
}
