import { Injectable } from '@angular/core';
import { window } from 'ngx-bootstrap/utils';

@Injectable({
    providedIn: 'root',
})
export class ToolsService {
    constructor() {}

    public static distance(lat1: number, lon1: number, lat2: number, lon2: number): number {
        // return distance in km

        if (lat1 === lat2 && lon1 === lon2) {
            return 0;
        } else {
            const radlat1 = (Math.PI * lat1) / 180;
            const radlat2 = (Math.PI * lat2) / 180;
            const theta = lon1 - lon2;
            const radtheta = (Math.PI * theta) / 180;
            let dist =
                Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = (dist * 180) / Math.PI;
            dist = dist * 60 * 1.1515;
            dist = dist * 1.609344;
            return dist;
        }
    }

    public static deepCopy(data: any): any {
        if (data) {
            return JSON.parse(JSON.stringify(data));
        } else {
            return null;
        }
    }

    public static hasSomeParentTheClass(element: HTMLElement, classname: string): boolean {
        if (element.className.split(' ').indexOf(classname) >= 0) {
            return true;
        }

        if (!element.parentNode) {
            return false;
        }

        return ToolsService.hasSomeParentTheClass(element.parentElement as HTMLElement, classname);
    }

    // check for empty object, return true if not empty

    public static isSet(object: object): boolean {
        return (
            object && // 👈 null and undefined check
            Object.keys(object).length !== 0 &&
            Object.getPrototypeOf(object) === Object.prototype
        );
    }

    public static getTransFormTranslateOfElement(myElement: any, direction = 'y') {
        const identifier = direction === 'y' ? 'm22' : 'm41';
        const style = window.getComputedStyle(myElement);
        const matrix = new WebKitCSSMatrix(style.transform);
        return matrix[identifier];
    }

    // used to debug who changed a field in a structure
    // display a stack when all value is set
    // pass obj and field name to monitor
    public debugSetter(debugObj: any, debugField: string | number): void {
        const val = debugObj[debugField];
        delete debugObj[debugField];

        Object.defineProperty(debugObj, debugField, {
            get() {
                const self = this;
                //console.log(self['_'+debugField]);
                return self['_' + debugField];
            },
            set(value) {
                const self = this;
                self['_' + debugField] = value;
                console.error(self['_' + debugField]);
            },
        });
        debugObj[debugField] = val;
    }
}
