import { Component, HostListener, OnInit } from '@angular/core';
import { ModalService } from '@services/modal.service';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';
import { MbscScrollerOptions } from '@mobiscroll/angular4';

@Component({
    selector: 'tz-mob-picker-modal',
    templateUrl: './mob-picker-modal.component.html',
    styleUrls: ['./mob-picker-modal.component.scss'],
})
export class MobPickerModalComponent implements OnInit {
    public scrollerOptions!: MbscScrollerOptions;
    public selectBtnText!: string;
    public cancelBtnText: string | undefined;
    public title: string | undefined;
    public selected: any[] | undefined = [];

    private data!: { wheels: MobPickerWheels[]; selected?: any[]; buttonText?: string };
    private selectedValue: any[] | undefined;

    constructor(private modalService: ModalService) {}

    @HostListener('window:popstate', ['$event'])
    dismissModal(): void {
        this.modalService.dismiss();
    }

    ngOnInit() {
        this.modalService.historyPushModal();
        this.initPiker();
        this.setSelectedValue();
    }

    public onSelect(): void {
        this.modalService.dismiss({ selected: [this.selectedValue] });
    }

    public onCancel(): void {
        this.modalService.dismiss({ cancel: true });
    }

    public onClose(): void {
        this.modalService.dismiss(null);
    }

    private initPiker(): void {
        this.scrollerOptions = {
            display: 'inline',
            width: 150,
            wheels: [
                [
                    {
                        circular: false,
                        data: this.data.wheels,
                    },
                ],
            ],
            onChange: (event: any, inst: any) => {
                this.selectedValue = event.valueText;
            },
        };
    }

    private setSelectedValue(): void {
        this.selected = this.data.selected;

        const selected = this.selected && this.selected[0] !== undefined ? this.selected[0] : this.data.wheels[0].value;

        this.selectedValue = selected;
    }
}
