import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { UserService } from '@services/modules/user/services/user.service';
import { Observable, Subscriber } from 'rxjs';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class LoginUserGuard implements CanActivate {
    constructor(private userService: UserService, private router: Router) {}
    canActivate(): Observable<boolean> | Promise<boolean> | boolean {
        return new Observable<boolean>((subscriber: Subscriber<boolean>) => {
            const askToLogin = () => {
                this.userService.openLoginModel().then(
                    () => subscriber.next(true),
                    () => {
                        this.router.navigate(['search']);
                        subscriber.next(false);
                    }
                );
            };

            if (this.userService.IsLogged()) {
                subscriber.next(true);
            } else if (StorageManagerUtil.get('trazler_customer_token', true)) {
                this.userService.getCustomer(false).then(
                    () => subscriber.next(true),
                    () => {
                        askToLogin();
                    }
                );
            } else {
                askToLogin();
            }
        });
    }
}
