import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'tz-mob-modal-page-link',
    templateUrl: './mob-modal-page-link.component.html',
    styleUrls: ['./mob-modal-page-link.component.scss'],
})
export class MobModalPageLinkComponent implements OnInit {
    @Input() isMobileIcon = false;
    @Output() clicked: EventEmitter<void> = new EventEmitter<void>();

    constructor() {}

    ngOnInit() {}

    public onGoToPage(): void {
        this.clicked.emit();
    }
}
