import { Component, Inject, OnInit } from '@angular/core';

import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { DebugInfo } from '@schemas/modules/cart/debug-cart-item';
import { CartStatus } from '@core/constants/cart-status';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { CombinationId } from '@schemas/modules/core/base-types';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-debug-cart-item',
    templateUrl: './debug-cart-item.component.html',
    styleUrls: ['./debug-cart-item.component.scss'],
})
export class DebugCartItemComponent implements OnInit {
    public combId!: CombinationId;
    public optionId!: string;
    public allowedStatuses: { id: CartStatus; name: string }[] = [
        { id: CartStatus.STATUS_REJECTED, name: 'Reject' },
        { id: CartStatus.STATUS_PRICE_CHANGED, name: 'Price Changed' },
        { id: CartStatus.STATUS_INVALID, name: 'Invalid' },
    ];
    public debugInfo: DebugInfo = {
        enabled: true,
        status: CartStatus.STATUS_REJECTED,
        newPrice: {
            value: null,
            cur: '',
        },
        imprint: true,
    };

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: {
            data: {
                combId: CombinationId;
                optionId: string;
            };
        },
        private mdDialogRef: MatDialogRef<DebugCartItemComponent>,
        private trazlerAPI: ApiAccessService,
        private dialogService: DialogService
    ) {}

    ngOnInit(): void {
        this.combId = this.data.data.combId;
        this.optionId = this.data.data.optionId;
    }

    public setDebugInfo() {
        this.trazlerAPI.setCartDebugInfo(this.combId, this.optionId, this.debugInfo).then(
            () => {
                this.closeDialog(true);
            },
            (failure: any) => {
                alert(failure.error.message);
            }
        );
    }

    public closeDialog(value: boolean | null): void {
        this.dialogService.closeDialog(this.mdDialogRef, value);
    }
}
