import { Injectable } from '@angular/core';
import { HttpEvent, HttpHandler, HttpHeaders, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable()
export class XXsrfTokenInterceptor implements HttpInterceptor {
    constructor() {}

    private static forceString(val: string | null): string {
        if (!val) {
            return '';
        }
        return val;
    }

    intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
        const headers = {
            'x-xsrf-token': XXsrfTokenInterceptor.forceString(StorageManagerUtil.get('xsfrToken', true)), // ? xsrfToken : '5oD9Lx8g',
            Authorization: XXsrfTokenInterceptor.forceString(StorageManagerUtil.get('trazler_customer_token', true)),
            DeviceId: XXsrfTokenInterceptor.forceString(StorageManagerUtil.get('deviceId', true)),
        };
        const req = request.clone({
            headers: new HttpHeaders(headers),
        });

        return next.handle(req);
    }
}
