/* eslint-disable no-bitwise */
import { ServiceId } from '@core/constants/service-id.const';
import { CombServiceIds } from '@core/constants/comb-service-ids';

export class SearchHelpers {
    public static getServicesList(servicesVal: CombServiceIds): ServiceId[] {
        const serviceList = [];

        if ((servicesVal & CombServiceIds.FLIGHT_TRAIN) > 0) {
            serviceList.push(ServiceId.FLIGHT_TRAIN);
        }
        if ((servicesVal & CombServiceIds.FLIGHT) > 0) {
            serviceList.push(ServiceId.FLIGHT);
        }
        if ((servicesVal & CombServiceIds.TRAIN) > 0) {
            serviceList.push(ServiceId.TRAIN);
        }
        if ((servicesVal & CombServiceIds.HOTEL) > 0) {
            serviceList.push(ServiceId.ACC);
        }
        if ((servicesVal & CombServiceIds.TRANSFER) > 0) {
            serviceList.push(ServiceId.TRANSFER);
        }
        if ((servicesVal & CombServiceIds.RENTAL) > 0) {
            serviceList.push(ServiceId.CAR_RENTAL);
        }

        return serviceList;
    }

    public static getServiceValue(selectedServiceObj: any): number {
        let serviceCount = 0;

        if (selectedServiceObj.transport) {
            serviceCount += CombServiceIds.FLIGHT_TRAIN;
        }
        if (selectedServiceObj.accommodation) {
            serviceCount += CombServiceIds.HOTEL;
        }
        if (selectedServiceObj.transfer) {
            serviceCount += CombServiceIds.TRANSFER;
        }
        if (selectedServiceObj.rentCar) {
            serviceCount += CombServiceIds.RENTAL;
        }

        return serviceCount;
    }

    public static getServicesListMap(servicesVal: CombServiceIds, ignoreId?: ServiceId): any {
        const serviceList: {
            [key in ServiceId]?: true;
        } = {};
        if ((servicesVal & CombServiceIds.FLIGHT_TRAIN) > 0 && !this.isTransport(ignoreId)) {
            serviceList[ServiceId.FLIGHT_TRAIN] = true;
        }
        if ((servicesVal & CombServiceIds.FLIGHT) > 0 && !this.isTransport(ignoreId)) {
            serviceList[ServiceId.FLIGHT] = true;
        }
        if ((servicesVal & CombServiceIds.TRAIN) > 0 && !this.isTransport(ignoreId)) {
            serviceList[ServiceId.TRAIN] = true;
        }
        if ((servicesVal & CombServiceIds.HOTEL) > 0 && ignoreId !== ServiceId.ACC) {
            serviceList[ServiceId.ACC] = true;
        }
        if ((servicesVal & CombServiceIds.TRANSFER) > 0 && ignoreId !== ServiceId.TRANSFER) {
            serviceList[ServiceId.TRANSFER] = true;
        }
        if ((servicesVal & CombServiceIds.RENTAL) > 0 && ignoreId !== ServiceId.CAR_RENTAL) {
            serviceList[ServiceId.CAR_RENTAL] = true;
        }

        return serviceList;
    }

    public static newToOldServices(newServices: number) {
        const selectedServices: any = {};

        selectedServices.transport = (newServices & 1) > 0;
        selectedServices.flight = (newServices & 16) > 0;
        selectedServices.train = (newServices & 32) > 0;
        selectedServices.accommodation = (newServices & 2) > 0;
        selectedServices.transfer = (newServices & 4) > 0;
        selectedServices.rentCar = (newServices & 8) > 0;

        return selectedServices;
    }

    public static getFirstServiceId(servicesVal: CombServiceIds): ServiceId {
        if ((servicesVal & CombServiceIds.FLIGHT_TRAIN) > 0) {
            return ServiceId.FLIGHT_TRAIN;
        }
        if ((servicesVal & CombServiceIds.FLIGHT) > 0) {
            return ServiceId.FLIGHT_TRAIN;
        }
        if ((servicesVal & CombServiceIds.TRAIN) > 0) {
            return ServiceId.FLIGHT_TRAIN;
        }
        if ((servicesVal & CombServiceIds.HOTEL) > 0) {
            return ServiceId.ACC;
        }
        if ((servicesVal & CombServiceIds.TRANSFER) > 0) {
            return ServiceId.TRANSFER;
        }
        if ((servicesVal & CombServiceIds.RENTAL) > 0) {
            return ServiceId.CAR_RENTAL;
        }
        return ServiceId.INVALID;
    }

    public static isTransport(servicesId?: ServiceId): boolean {
        return (
            servicesId === ServiceId.FLIGHT_TRAIN || servicesId === ServiceId.TRAIN || servicesId === ServiceId.FLIGHT
        );
    }

    public static isPrimaryService(serviceId: ServiceId): boolean {
        return this.isTransport(serviceId) || serviceId === ServiceId.ACC;
    }
}
