export enum ValidatorPatterns {
    NAME = '^([a-zA-ZÀ-ÿ]+\\s?)+$',
    ZIPCODE = '^([a-zA-ZÀ-ÿ0-9]+\\s?)+$',
    TRAVELLERID = '^([a-zA-ZÀ-ÿ0-9]+\\s?)+$',
    PASSWORD = '^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,}$',
    EMAIL = '^[a-z0-9A-Z._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$',
    STRING = `^([a-zA-ZÀ-ÿ0-9!@#$%^&*()_+\-=\[\\]{};':"\\|,.<>\/?]\\s?)+$`,
}
