import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { LogInService } from '@services/services/log-in.service';

@Component({
    selector: 'tz-sign-in',
    templateUrl: './sign-in.component.html',
    styleUrls: ['./sign-in.component.scss'],
})
export class SignInComponent implements OnInit {
    @Input() emailId?: string;

    public showSignin = true;
    public errorMsg = '';
    public logInForm;
    public showPassword = false;

    constructor(
        private activeModal: NgbActiveModal,
        private router: Router,
        private logInService: LogInService,
        private storageManagerUtil: StorageManagerUtil
    ) {
        this.logInForm = LogInService.createLoginForm();
    }

    ngOnInit(): void {
        if (this.emailId && this.emailId !== '') {
            this.logInForm.controls['emailID'].setValue(this.emailId);
        }
    }

    public validatePassword(patternStr: string): boolean {
        const pattern = new RegExp(patternStr);
        return pattern.test(this.logInForm.value.password);
    }

    public closePopup(skipSignIn = false): void {
        if (skipSignIn) {
            StorageManagerUtil.set('skipSignIn', true);
        }

        this.activeModal.dismiss({} as CustomerInfo);
    }

    public proceedSignIn(): void {
        if (!this.logInForm.valid) {
            return;
        }

        this.logInService.proceedSignIn(this.showSignin, this.logInForm.value, this.activeModal).then(
            (resp: { token: string; customer: CustomerInfo }) => {
                console.log('Auth data', resp);
            },
            (err) => {
                this.errorMsg = err.error.message;
                console.log('Auth error: ', err);
            }
        );
    }

    public loginWithSocialMedia = (media: string) => this.logInService.loginWithSocialMedia(media, this.activeModal);

    public goToPage(url: string): void {
        const targetUrl = this.router.serializeUrl(this.router.createUrlTree([url]));
        window.open(targetUrl, '_blank');
    }
}
