import { Component, OnInit } from '@angular/core';
import { environment } from '@environments/environment';
import { NavigationEnd, Router, RouterEvent } from '@angular/router';
import { filter } from 'rxjs/operators';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { UserService } from '@services/modules/user/services/user.service';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-footer',
    templateUrl: './footer.component.html',
    styleUrls: ['./footer.component.scss'],
})
export class FooterComponent implements OnInit {
    public version = '';
    public isSearchDropDownOpen = false;
    private urlFirstSegment = '';

    constructor(
        private router: Router,
        private searchDataService: SearchDataService,
        private userService: UserService,
        private scrollService: ScrollService
    ) {
        this.pageUrl();
    }

    ngOnInit(): void {
        this.version = environment.version;

        this.searchDataService.isSearchDropDownOpen$.subscribe((val: boolean) => {
            this.isSearchDropDownOpen = val;
        });
    }

    public onGoToFooterPage(url: string): void {
        if (this.urlFirstSegment === 'about') {
            this.router.navigateByUrl(url);
            return;
        }

        const targetUrl = this.router.serializeUrl(this.router.createUrlTree([url]));
        window.open(targetUrl, '_blank');
    }

    public loginRedirect(path: string): void {
        this.scrollService.scrollToTop();
        this.router.navigate([path]);
    }

    public onFooterClick(): void {
        if (!this.isSearchDropDownOpen) {
            this.searchDataService.closeSearchEditingMode$.next();
        }
    }

    private pageUrl(): void {
        this.router.events.pipe(filter((event) => event instanceof NavigationEnd)).subscribe((event) => {
            let urlSegments: string[] = [];
            if (event instanceof RouterEvent) {
                urlSegments = event.url.split('/');
            }

            if (urlSegments.length > 0) {
                this.urlFirstSegment = urlSegments[1];
            }
        });
    }
}
