import {
    Component,
    ComponentFactoryResolver,
    ComponentRef,
    HostListener,
    Input,
    OnDestroy,
    OnInit,
    ViewChild,
    ViewContainerRef,
} from '@angular/core';
import { ComponentType } from '@angular/cdk/portal';
import { ModalService } from '@services/modal.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
    selector: 'tz-mob-main-modal',
    templateUrl: './mob-main-modal.component.html',
    styleUrls: ['./mob-main-modal.component.scss'],
})
export class MobMainModalComponent implements OnInit, OnDestroy {
    @ViewChild('modalContent', {
        static: true,
        read: ViewContainerRef,
    })
    viewContainerRef: any;
    @Input() hideHeader = false;
    public title: string | undefined;
    private componentRef!: ComponentRef<any>;
    private data: any;
    private component!: ComponentType<any>;
    private destroy$: Subject<boolean> = new Subject<boolean>();
    constructor(private modalService: ModalService, private componentFactoryResolver: ComponentFactoryResolver) {}

    @HostListener('window:popstate', ['$event'])
    dismissModal(): void {
        this.closeModal();
    }

    ngOnInit() {
        this.modalService.historyPushModal();

        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.component);
        this.componentRef = this.viewContainerRef.createComponent(componentFactory);

        if (this.componentRef) {
            this.componentRef.instance.data = this.data;
        }

        this.modalService.updateModelTitle$.pipe(takeUntil(this.destroy$)).subscribe((title) => {
            this.title = title;
        });
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public closeModal(): void {
        this.modalService.dismiss();
    }
}
