import * as moment from 'moment';

export class DatesUtils {
    public static readonly savedTzTravelerFormat = 'DD/MM/YYYY';
    public static readonly updateTzTravelerFormat = 'DD-MM-YYYY';

    public static getTZFormatDate(tzDate: string): string {
        return tzDate.split('T')[0];
    }

    public static getTZFormatTime(tzDate: string) {
        return tzDate.split('T')[1];
    }

    public static getDateFromTzDate(tzDate: string): null | Date {
        if (!tzDate) {
            console.error('invalidate date received');
            return null;
        }
        const dateParts = tzDate.split(/[\-|T:]/);
        if (dateParts.length < 3) {
            console.error('invalidate date received:' + tzDate);
            return null;
        }
        return new Date(
            +dateParts[0],
            +dateParts[1] - 1,
            +dateParts[2],
            dateParts.length > 3 ? +dateParts[3] : 0,
            dateParts.length > 4 ? +dateParts[4] : 0,
            0,
            0
        );
    }

    public static getNewDate(date: string | Date): Date {
        let nd;
        if (date instanceof Date) {
            nd = new Date(date);
        } else {
            const res = date.split('-');
            if (res.length === 3) {
                // to avoid timezone conversion
                nd = new Date(parseInt(res[0], 10), parseInt(res[1], 10) - 1, parseInt(res[2], 10));
            } else {
                nd = new Date(date);
            }
        }
        return nd;
    }

    public static formCalDatesControlObj(calData: any): any {
        if (!calData || !calData.fromDate) {
            return '';
        }

        const selected = { startDate: moment(), endDate: moment() };
        selected.startDate = this.formMoment(calData.fromDate, calData.fromTime);
        if (calData.toDate) {
            selected.endDate = this.formMoment(calData.toDate, calData.toTime);
        }
        return selected;
    }

    // getAgeDiff gets the diff between two dates sent
    public static getAgeDiff(birthDate: Date, ageAt: Date) {
        let age = ageAt.getFullYear() - birthDate.getFullYear();
        const m = ageAt.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && ageAt.getDate() < birthDate.getDate())) {
            age--;
        }
        return age;
    }

    public static hourDiff(endDate: Date, startDate: Date): number {
        let diff = (endDate.getTime() - startDate.getTime()) / 1000;
        diff /= 60 * 60;
        return Math.abs(Math.round(diff));
    }

    public static formatDate(dateStr: string | Date, strFormat: string, fotmatTo: string): string {
        if (!dateStr || dateStr === '') {
            return '';
        }

        return moment(dateStr, strFormat).format(fotmatTo);
    }

    public static updateTzFormDate(date: string | Date, dateFormat?: string): string {
        if (!date || date === '') {
            return '';
        }

        return moment(date, dateFormat).format(this.updateTzTravelerFormat);
    }

    private static setTime(date: Date, timeStr: string): void {
        const time = timeStr.split(':');
        if (time.length !== 2) {
            return;
        }

        date.setHours(parseInt(time[0], 10), parseInt(time[1], 10));
    }

    private static formMoment(dateStr: string | Date, timeStr: string): moment.Moment {
        const date = this.getNewDate(dateStr);
        DatesUtils.setTime(date, timeStr);
        return moment(date);
    }
}

