import { Component, Injector, OnInit } from '@angular/core';
import { DialogService } from '@services/dialog.service';
import { CookieComponent } from '@shared/components/cookie/cookie.component';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { StorageKeys } from '@schemas/modules/core/storage-keys';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { TranslateService } from '@ngx-translate/core';
import { ModalService } from '@services/modal.service';
import { PassengerComponent } from 'src/app/modules/search/passenger/passenger.component';
import { MobCookieModalComponent } from '../mob-cookie-modal/mob-cookie-modal.component';

@Component({
    selector: 'tz-cookie-modal',
    templateUrl: './mob-cookie.component.html',
    styleUrls: ['./mob-cookie.component.scss'],
})
export class MobCookieComponent extends CookieComponent implements OnInit {
    constructor(
        private dialogService: DialogService,
        private translateService: TranslateService,
        injector: Injector,
        private modalService: ModalService
    ) {
        super(injector);
        const showCookieAlert = !StorageManagerUtil.get(StorageKeys.COOKIE, true);
        if (showCookieAlert) {
            this.modalService.createMainModal(MobCookieModalComponent, {
                data: {
                    title: translateService.instant('COOKIES.MANAGE'),
                    content: translateService.instant('COOKIES.MSG'),
                    policy: translateService.instant('COOKIES.POLICY'),
                    cancelBtnTitle: translateService.instant('COOKIES.AGREE_MIN'),
                    confirmBtnTitle: translateService.instant('COOKIES.AGREE'),
                    confirm: () => this.confirmCookie(),
                    cancel: () => this.cancelCookie(),
                },
                cssClass: 'mob-cookie-modal',
                hideHeader: true,
            });
        }
    }

    confirmCookie() {
        this.acceptCookie(StorageManagerUtil.ACCEPT_ALL);
        this.modalService.dismiss();
    }

    cancelCookie() {
        this.rejectCookie(StorageManagerUtil.ACCEPT_REQUIRED);
        this.modalService.dismiss();
    }

    ngOnInit(): void {}
}
