import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-footer-page-contact-form',
    templateUrl: './footer-page-contact-form.component.html',
    styleUrls: ['./footer-page-contact-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FooterPageContactFormComponent implements OnInit {
    @Input() signUpSuccessfully = false;
    @Input() formProcessing = false;
    @Input() hideFileUpload = false;
    @Output() formSubmit: EventEmitter<{ [key: string]: string }> = new EventEmitter<{ [key: string]: string }>();

    public form!: FormGroup;
    public files: { names: string[]; error: boolean } = { names: [], error: false };
    public formSubmitted = false;

    constructor(private formBuilder: FormBuilder, public platformService: PlatformService) {}

    public get email() {
        return this.form.get('emailID');
    }

    public get firstName() {
        return this.form.get('firstName');
    }

    public get feedBack() {
        return this.form.get('feedBack');
    }

    ngOnInit() {
        this.initForm();
    }

    public initForm(): void {
        this.form = this.formBuilder.group({
            emailID: ['', [Validators.required, Validators.pattern('^[a-z0-9A-Z._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
            firstName: ['', [Validators.required, Validators.pattern('^[a-zA-ZÀ-ÿ-s]+$')]],
            feedBack: ['', [Validators.required, Validators.minLength(40)]],
            files: [],
            firstnameLeft: [true],
            mailLeft: [true],
            messageLeft: [true],
        });
    }

    public onFormSubmit(): void {
        this.formSubmitted = true;

        if (this.form.invalid) {
            return;
        }

        this.formSubmit.emit(this.form.value);
    }

    public onChooseFile(event: Event): void {
        const target = event.target as HTMLInputElement;

        if (target && target.files && !target.files?.length) {
            return;
        }

        const files: FileList | null = target.files;

        if (files) {
            this.files = { names: [], error: false };

            const filesArr: File[] = [];
            let fileData: File;
            let key: string;
            for ([key, fileData] of Object.entries(files)) {
                filesArr.push(fileData);
            }

            const filesSize = filesArr.reduce((total: any, file: any) => total + file.size / (1000 * 1000), 0);
            if (files.length > 4 || filesSize > 20) {
                this.files.error = true;
                return;
            }

            const newFiles: { name: string; fileStr: string }[] = [];
            const filesName: string[] = [];

            filesArr.map((file: File) => {
                filesName.push(fileData.name);
                this.getFileBuffer(file).subscribe((fileBuffer: string) => {
                    newFiles.push({ fileStr: fileBuffer, name: file.name });
                });
            });

            this.files.names = filesName;

            this.form.patchValue({ files: newFiles });
        }
    }

    public onClickFile(event: Event): void {
        const target = event.target as HTMLInputElement;
        target.value = '';
    }

    public resetFileUploadData(): void {
        this.files = { names: [], error: false };
        this.form.patchValue({ files: [] });
    }

    private getFileBuffer(file: File): Observable<string> {
        return new Observable((subscriber) => {
            const reader = new FileReader();
            reader.onloadend = (e: ProgressEvent<FileReader>) => {
                if (e.target && typeof e.target.result === 'string') {
                    subscriber.next(e.target.result);
                }
            };
            reader.readAsDataURL(file);
        });
    }
}
