import { Directive, EventEmitter, HostListener, Output } from '@angular/core';

@Directive({
    selector: '[tzOnSwipe]',
})
export class OnSwipeDirective {
    @Output() swipeLeft = new EventEmitter<void>();
    @Output() swipeRight = new EventEmitter<void>();
    @Output() swipeTop = new EventEmitter<void>();
    @Output() swipeBottom = new EventEmitter<void>();

    private touchstartX = 0;
    private touchstartY = 0;

    constructor() {}

    @HostListener('touchstart', ['$event']) touchStart(event: TouchEvent): void {
        const touch = event.changedTouches[0];
        this.touchstartX = touch.screenX;
        this.touchstartY = touch.screenY;
        return;
    }

    @HostListener('touchend', ['$event']) touchEnd(event: TouchEvent): void {
        const touch = event.changedTouches[0];
        const touchendX = touch.screenX;
        const touchendY = touch.screenY;

        const xDiff = this.touchstartX - touchendX;
        const yDiff = this.touchstartY - touchendY;

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
            if (xDiff > 0) {
                this.swipeLeft.emit();
            } else {
                this.swipeRight.emit();
            }
        } else {
            if (yDiff > 0) {
                this.swipeTop.emit();
            } else {
                this.swipeBottom.emit();
            }
        }
        return;
    }
}
