    protected configService: ConfigService;
    protected searchDataService: SearchDataService;
    protected loaderService: LoaderService;

    constructor(injector: Injector, http: HttpClient) {
        super(http);
        this.settingsService = injector.get(SettingsService);
        this.listenersService = injector.get(ListenersService);
        this.configService = injector.get(ConfigService);
        this.searchDataService = injector.get(SearchDataService);
        this.loaderService = injector.get(LoaderService);
    }

    private static decodeChunk(encodedRes: string | undefined): string[] {
        if (encodedRes === undefined || encodedRes == null || encodedRes.trim().length <= 0) {
            return [];
        }

        //remove not terminated line and transform data into json readable array
        let res = encodedRes.replace(/((.*\n)*).*/, '$1');
        res = '[' + res.replace(/}\n/g, '}\n,') + ']';
        res = res.replace(/,]/, ']');

        return JSON.parse(res);
    }

    private static sendChunkData(start: number, subscriber: Subscriber<string>, encoded: string | undefined): number {
        let sent = 0;
        const decoded = ApiAccessService.decodeChunk(encoded);

        for (let i = start; i < decoded.length; i++) {
            console.log('sending: ' + JSON.stringify(decoded[i]));
            subscriber.next(decoded[i]);
            sent++;
        }

        return start + sent;
    }

    public searchFromRoute() {
        const params = {
            entity: 'searchFromRoute',
            deviceId: this.settingsService.getDeviceID(),
            routePath: window.location.pathname,
        };

        return this.apiCallAccess(HttpRequestType.GET, params);
    }

    public checkDep(combId: CombinationId, dataId: DataId, serviceId: ServiceId, editMode: boolean): Promise<any> {
        const params = {
            entity: 'checkDep',
            id: combId,
            serviceId,
            dataId,
            editMode,
            deviceId: this.settingsService.getDeviceID(),
        };

        if (!params.id) {
            return this.paramsError('invalid device id');
        } else {
            return this.apiCallAccess(HttpRequestType.POST, params);
        }
    }

    public preSearch(params: any, request: any): Promise<any> {
        params.entity = 'preSearch';
        params.deviceId = this.settingsService.getDeviceID();
        request = {
            toReset: params.toReset,
            editMode: params.editMode,
            refId: params.refId,
            partialSettings: {
                lang: this.settingsService.langCode,
                userCurReq: this.settingsService.getCurrency(),
            },
            nightChoice: params.nightChoice ? params.nightChoice : 0,
        };
        if (!params.id || (!params.serviceId && params.serviceId !== 0)) {
            return this.paramsError('invalid data id');
        } else {
            const options = {
                context: new HttpContext().set(SEARCH_RESULT_PROCESSING, true),
            };
            return this.apiCallAccessOptions(HttpRequestType.POST, params, options, request);
        }
    }

    public newCombination(request: any, tripPlanner: any): Promise<any> {
        const params = {
            entity: 'newCombination',
            id: this.settingsService.getDeviceID(),
        };

        request.searchSettings = {
            partialSettings: {
                lang: this.settingsService.langCode,
                userCurReq: this.settingsService.getCurrency(),
            },
            ctryCode: SettingsService.getIpCountry().country,
        };

        if (!params.id) {
            return this.paramsError('Invalid device id');
        } else {
            return this.apiCallAccess(HttpRequestType.POST, params, request);
        }
    }

