import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'tzDistance',
    pure: true,
})
export class DistancePipe implements PipeTransform {
    constructor() {}

    transform(distance: number): string {
        return distance < 0.1 ? Math.round(distance * 1000) + 'm' : distance.toFixed(1) + 'km';
    }
}
