import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { DialogService } from '@services/dialog.service';

import { ConfirmationDialogOptions } from '@shared/schemas/confirmation-dialog-options.model';

@Component({
    selector: 'tz-dialog',
    templateUrl: './tz-dialog.component.html',
    styleUrls: ['./tz-dialog.component.scss'],
})
export class TzDialogComponent implements OnInit {
    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: ConfirmationDialogOptions,
        private mdDialogRef: MatDialogRef<TzDialogComponent>,
        private dialogService: DialogService
    ) {}

    @HostListener('keydown.esc')
    public onEsc(): void {
        this.close(false);
    }
    ngOnInit(): void {}

    public close(value: any): void {
        this.dialogService.closeDialog(this.mdDialogRef, value);
    }

    public cancel(): void {
        this.close(false);
    }

    public confirm(): void {
        this.close(true);
    }
}
