import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ModalService } from '@services/modal.service';
import { UserDataService } from '@services/modules/user/dataservices/user.data.service';

@Component({
    selector: 'tz-mob-forgot-password',
    templateUrl: './mob-forgot-password.component.html',
    styleUrls: ['./mob-forgot-password.component.scss'],
})
export class MobForgotPasswordComponent {
    public passwordChanged = false;

    constructor(private router: Router, private userDataService: UserDataService, private modalService: ModalService) {}

    public changePassword(email: string): void {
        this.passwordChanged = true;
        this.userDataService.forgotPassword(email).subscribe(
            () => {
                this.modalService.dismiss();
            },
            (failure: any) => {
                console.log('Forgot password failure: ', failure);
            }
        );
    }

    public gotoHome(): void {
        this.router.navigate(['/search']);
    }
}
