import { Platform } from '@ionic/angular';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class PlatformService {
    private isPlatformMobile = false;
    private isPlatformMobileDevice = false;
    private isPlatformBrowser = false;
    private isPlatformMobileDevice$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
    private isPlatformMobile$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
    private isPlatformMobileBrowser$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
    private screenSize!: number;
    // set to 0 to disable, to a value below 920 to activate when screensize <= that value
    private forceMobileOnSmallDisplay = 920;

    constructor(private platform: Platform) {
        platform.ready().then(() => {
            this.screenSize = platform.width();
            this.setPlatformType();
        });

        this.platform.resize.subscribe(async () => {
            this.screenSize = platform.width();
            this.setPlatformType();
        });
    }

    get isMobileBrowser(): boolean {
        return this.isPlatformBrowser;
    }

    get isMobile(): boolean {
        return this.isPlatformMobile;
    }

    get isMobileDevice(): boolean {
        return this.isPlatformMobileDevice;
    }

    get isMobileDevice$(): BehaviorSubject<boolean> {
        return this.isPlatformMobileDevice$;
    }

    get isMobile$(): BehaviorSubject<boolean> {
        return this.isPlatformMobile$;
    }

    get isMobileBrowser$(): BehaviorSubject<boolean> {
        return this.isPlatformMobileBrowser$;
    }

    private forceMobile(): boolean {
        return !!(this.forceMobileOnSmallDisplay && this.screenSize <= this.forceMobileOnSmallDisplay);
    }

    private setPlatformType(): void {
        this.setMobileDevice();
        this.setMobileType();
        this.setMobileBrowserType();
    }

    private setMobileDevice(): void {
        if (
            this.screenSize < 920 &&
            (this.forceMobile() || this.platform.is('mobile') || this.platform.is('mobileweb'))
        ) {
            this.isPlatformMobileDevice$.next(true);
            this.isPlatformMobileDevice = true;
        } else {
            this.isPlatformMobileDevice$.next(false);
            this.isPlatformMobileDevice = false;
        }
    }

    private setMobileType(): void {
        if (this.screenSize < 920 && this.platform.is('mobile')) {
            this.isPlatformMobile$.next(true);
            this.isPlatformMobile = true;
        } else {
            this.isPlatformMobile$.next(false);
            this.isPlatformMobile = false;
        }
    }

    private setMobileBrowserType(): void {
        if (this.screenSize < 920 && this.platform.is('mobileweb')) {
            this.isPlatformMobileBrowser$.next(true);
            this.isPlatformBrowser = true;
        } else {
            this.isPlatformMobileBrowser$.next(false);
            this.isPlatformBrowser = false;
        }
    }
}
