import { Component, Input, OnInit } from '@angular/core';

@Component({
    selector: 'tz-mob-dropdown',
    templateUrl: './mob-dropdown.component.html',
})
export class MobDropdownComponent implements OnInit {
    @Input() title: string | undefined;
    @Input() leftIcon: string | undefined;
    @Input() text!: string;

    public open = false;

    constructor() {}

    ngOnInit() {}

    public dropDownClick(event: Event): void {
        this.dropDownToggle();

        console.log('dropDownClick');
    }
    public leftIconClick(event: Event): void {
        event.stopPropagation();

        console.log('leftIconClick');
    }

    public rightIconClick(event: Event): void {
        event.stopPropagation();

        this.dropDownToggle();
        console.log('rightIconClick');
    }

    private dropDownToggle(): void {
        this.open = !this.open;
    }
}
