import { Component, OnDestroy, OnInit, Renderer2 } from '@angular/core';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

import { LoaderService } from '@services/modules/core/loader.service';
import { LoaderModel, LoaderType } from '@shared/schemas/loader.model';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-loader',
    templateUrl: './loader.component.html',
    styleUrls: ['./loader.component.scss'],
})
export class TzLoaderComponent implements OnInit, OnDestroy {
    public loading = false;
    public loaderType: LoaderType = 'default';
    public loaderTitle = '';
    public loaderText = '';
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        public platformService: PlatformService,
        private loaderService: LoaderService,
        private renderer: Renderer2
    ) {
        this.triggerLoading();
    }

    ngOnInit(): void {}

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    private triggerLoading(): void {
        this.loaderService.triggerLoading.pipe(takeUntil(this.destroy$)).subscribe((loader: LoaderModel) => {
            this.loading = loader.loading;
            this.loaderType = 'default';
            this.loaderTitle = '';
            this.loaderText = '';

            this.lockScroll();

            if (loader.type) {
                this.loaderType = loader.type;
            }

            if (loader.title) {
                this.loaderTitle = loader.title;
            }

            if (loader.textKey) {
                this.loaderText = loader.textKey;
            }
        });
    }

    private lockScroll(): void {
        if (this.loading) {
            this.renderer.addClass(document.body, 'body-lock-scroll');
        } else {
            this.renderer.removeClass(document.body, 'body-lock-scroll');
        }
    }
}
