import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { IHotelNames } from '@schemas/modules/accommodations/accommodations-response';
import { NgSelectComponent } from '@ng-select/ng-select';

@Component({
    selector: 'tz-search-hotel-name',
    templateUrl: './search-hotel-name.component.html',
    styleUrls: ['./search-hotel-name.component.scss'],
})
export class SearchHotelNameComponent {
    @Input() hotelNames: IHotelNames[] = [];
    @Output() search = new EventEmitter<IHotelNames>();
    @ViewChild(NgSelectComponent) ngSelectComponent?: NgSelectComponent;

    constructor() {}

    public filterNames(term: string, item: any): boolean {
        if (!term) {
            return false;
        }

        return item.name.toLowerCase().indexOf(term.toLowerCase()) >= 0;
    }

    public searchHotel(hotelInfo: IHotelNames): void {
        this.search.emit(hotelInfo);
        this.ngSelectComponent?.blur();
    }

    public onFocus(): void {
        if (!this.ngSelectComponent?.selectedValues[0]?.name.length) {
            return;
        }

        this.ngSelectComponent.searchTerm = this.ngSelectComponent.selectedValues[0].name;
    }

    public onBlur(): void {
        if (!this.ngSelectComponent?.searchTerm?.length) {
            return;
        }

        this.ngSelectComponent.searchTerm = '';
    }
}
