import { Injectable } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
    providedIn: 'root',
})
export class TransferUtils {
    private currentSelectedTransfers = [];

    constructor(private toolsService: ToolsService, private translateService: TranslateService) {}

    public formatVehicleDesc(description: string) {
        if (!description) {
            return;
        }
        if (!description.includes('similar') && description !== '') {
            description = description + ' ' + this.translateService.instant('TRANSFER.BE.Or_similar');
        }
        return description;
    }

    public setSelectedInfoInServices(transfers: any) {
        if (!transfers) {
            transfers = [];
        }
        this.currentSelectedTransfers = ToolsService.deepCopy(transfers);
    }
}
