import { Injectable } from '@angular/core';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ServiceId } from '@core/constants/service-id.const';
import { TranslateService } from '@ngx-translate/core';
import { UserSearchService } from '@services/searchEngine/services/user-search-service';
import { StateFrom } from '@core/constants/state-from.const';
import { INightCheckResult, NightChoiceService } from '@services/modules/nightChoice/night-choice.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { DialogService } from '@services/dialog.service';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';

@Injectable({
    providedIn: 'root',
})
export class CheckInconsistentService {
    constructor(
        private nightChoiceService: NightChoiceService,
        private tabRoutingService: TabRoutingService,
        private tripPlannerService: TripPlannerService,
        private userSearchService: UserSearchService,
        private searchDataService: SearchDataService,
        private translateService: TranslateService,
        private cartService: CartService,
        private dialogService: DialogService
    ) {}

    public checkInconsistent(serviceId: ServiceId, stateParams: StateFrom, deferred?: any) {
        const promise = new Promise((resolve, reject) => {
            this.tripPlannerService.isRunning(this.searchDataService.getCombId(), resolve);
        });
        //we must wait for poller to finish or we can get invalid consistency values
        promise.then(() => {
            const nextIsSummary = stateParams === StateFrom.SUMMARY && this.tabRoutingService.hasMovedToSummary();

            if (!nextIsSummary && !this.tabRoutingService.isConsistent(serviceId)) {
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.TRIP_INCONSISTENT.TITLE'),
                        content: this.tabRoutingService.getConsistentMsg(serviceId),
                        confirmBtnTitle: this.translateService.instant('POPUPS.TRIP_INCONSISTENT.1_YES'),
                        cancelBtnTitle: this.translateService.instant('POPUPS.TRIP_INCONSISTENT.2_NO'),
                        icon: { name: 'trip-inconsistancy-dialog', iconStyle: { 'width.px': 66, 'height.px': 66 } },
                    })
                    .afterClosed()
                    .subscribe((confirmed: TzDialogReturn) => {
                        if (confirmed === CONFIRM) {
                            const toReset: { [key in ServiceId]?: true } = {};
                            toReset[serviceId] = true;
                            this.tabRoutingService.setConsistent(serviceId, true, '');
                            this.userSearchService.search({
                                forceLoader: true,
                                serviceId,
                                serviceIdMapToUpd: toReset,
                                anticipatedList: toReset,
                            });
                            if (deferred) {
                                deferred.reject();
                            }
                        } else {
                            // user does not want to change anything we set to consistent
                            this.tabRoutingService.setConsistent(serviceId, true, '');
                            if (deferred) {
                                deferred.resolve();
                            }
                            if (stateParams !== StateFrom.CHECKOUT && stateParams !== StateFrom.TAB) {
                                this.userSearchService.search({
                                    serviceId: this.tabRoutingService.setNext(serviceId).serviceId,
                                    serviceIdMapToUpd: undefined,
                                });
                            }
                        }
                    });
            } else if (serviceId === ServiceId.ACC) {
                //do not re-ask for just received results
                this.nightChoiceService.checkNightArrival().then((res: INightCheckResult) => {
                    switch (res.ignore) {
                        case true:
                            //nothing
                            break;
                        case false:
                            switch (res.changed) {
                                case true:
                                    this.userSearchService.search({
                                        serviceId: ServiceId.ACC,
                                        nightChoice: res.curChoice,
                                    });
                                    if (deferred) {
                                        deferred.reject();
                                    }
                                    break;
                                case false:
                                    //save current choice
                                    this.searchDataService.setNightChoice(res.curChoice);
                                    if (
                                        stateParams !== StateFrom.CHECKOUT &&
                                        !this.cartService.isVirtual(ServiceId.ACC)
                                    ) {
                                        this.userSearchService.searchCheckDep(
                                            ServiceId.FLIGHT,
                                            true,
                                            false,
                                            true,
                                            true
                                        );
                                    }
                                    break;
                            }
                    }
                    if (deferred) {
                        deferred.resolve();
                    }
                });
            } else if (deferred) {
                deferred.resolve();
            }
        });
    }
}
