import { Component, Inject, OnInit } from '@angular/core';
import { SnackBarDialogModel } from '@shared/schemas/snack-bar-dialog.model';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ComponentType } from '@angular/cdk/portal';
import { timer } from 'rxjs';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-tz-snack-bar',
    templateUrl: './tz-snack-bar.component.html',
    styleUrls: ['./tz-snack-bar.component.scss'],
})
export class TzSnackBarComponent implements OnInit {
    public dialogData!: SnackBarDialogModel;

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: {
            component: ComponentType<any>;
            data: SnackBarDialogModel;
        },
        private mdDialogRef: MatDialogRef<TzSnackBarComponent>,
        private dialogService: DialogService
    ) {}

    ngOnInit(): void {
        this.dialogData = this.data.data;

        if (this.dialogData.time > 0) {
            const timger = timer(this.dialogData.time);
            const subscribe = timger.subscribe((val) => {
                this.close();
                subscribe.unsubscribe();
            });
        }
    }

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }
}
