import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

@Component({
    selector: 'tz-results-top-info-block',
    templateUrl: './results-top-info-block.component.html',
    styleUrls: ['./results-top-info-block.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ResultsTopInfoBlockComponent {
    @Input() blockType: 'green' | 'blue' | 'orange' | 'red' = 'blue';
    @Input() loading = false;

    get className() {
        return 'results-top-info-block--' + this.blockType;
    }
}
