import { Pipe, PipeTransform } from '@angular/core';
import { AccommodationSummary } from '@schemas/modules/accommodations/accommodation';

@Pipe({
    name: 'sortSummary',
})
export class SortSummaryPipe implements PipeTransform {
    public transform(value: AccommodationSummary[]): AccommodationSummary[] {
        return value.sort((a: AccommodationSummary, b: AccommodationSummary) => {
            if (a.sentiment === 'positive') {
                return -1;
            } else if (a.sentiment === 'negative') {
                return 1;
            } else {
                return 0;
            }
        });
    }
}
