import { Directive, ElementRef, HostListener, Renderer2 } from '@angular/core';

@Directive({
    selector: '[tzOnFocus]',
})
export class OnFocusDirective {
    constructor(private el: ElementRef, public renderer: Renderer2) {}

    @HostListener('focus', ['$event']) onFocus(): void {
        this.renderer.addClass(this.el.nativeElement, 'tz-input-focus');
        return;
    }

    @HostListener('blur', ['$event']) onblur(): void {
        this.renderer.removeClass(this.el.nativeElement, 'tz-input-focus');
        return;
    }
}
