import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'sortArray',
})
export class SortArrayPipe implements PipeTransform {
    transform(arr: any[], args: string): any[] {
        if (!Array.isArray(arr)) {
            return arr;
        }

        arr.sort((a: any, b: any) => {
            if (a[args] < b[args]) {
                return -1;
            } else if (a[args] > b[args]) {
                return 1;
            } else {
                return 0;
            }
        });

        return arr;
    }
}
