import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FormGroup } from '@angular/forms';
import * as moment from 'moment';

import { Passenger, Phone } from '@shared/schemas/tz-customer-info';
import { TzTravellerService } from '@services/services/tzTraveller.service';
import { ToolsService } from '@core/utils/tools-service';
import { UserService } from '@services/modules/user/services/user.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { PlatformService } from '@services/platform.service';
import { SettingsService } from '@services/services/settings-service';
import { DatesUtils } from '@core/utils/dates-utils';

@Component({
    selector: 'tz-traveller',
    templateUrl: './tz-traveller.component.html',
    styleUrls: ['./tz-traveller.component.scss'],
})
export class TzTravellerComponent implements OnInit, OnChanges, OnDestroy {
    @Input() formGroup = new FormGroup({});
    @Input() formIdx = -1;
    @Input() selectedTraveller!: Passenger;
    @Input() isProfile = false;
    @Input() travellerType = '';
    @Input() travelDate = {
        startDate: new Date(),
        endDate: new Date(),
    };
    @Input() selectedAge = -1;
    @Input() ageLimit!: {
        min: number;
        max: number;
    };

    @Output() setTraveller = new EventEmitter<{ traveller: Passenger }>();
    @Output() markFormAsVisited = new EventEmitter<void>();

    public savedTraveller = false;
    public dateFormat;
    public formReadonly = false;
    public genders = ['TRAVELLER.MALE', 'TRAVELLER.FEMALE'];
    public idCardTypes = [
        this.translateService.instant('TRAVELLER.PASSPORT'),
        this.translateService.instant('TRAVELLER.ID_CARD'),
    ];
    public phoneFocused = false;
    public expiryAlertShown = false;

    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private readonly changeDetectorRef: ChangeDetectorRef,
        private tzTravellerService: TzTravellerService,
        private translateService: TranslateService,
        private settingsService: SettingsService,
        public userService: UserService,
        public platformService: PlatformService
    ) {
        this.dateFormat = this.tzTravellerService.tzFormDateFormat;
    }

    get updateTraveler(): boolean {
        const formData = this.formTravellerInfo();
        return formData.travellerUpdated;
    }

    get saveTraveler(): boolean {
        for (const value of Object.values(this.formGroup?.value)) {
            if (value !== null && value !== '') {
                return true;
            }
        }

        return false;
    }

    ngOnInit() {
        this.savedTraveller = this.selectedTraveller.id !== undefined && this.selectedTraveller.id !== '';
        this.formReadonly = this.isProfile && this.savedTraveller && !this.platformService.isMobileDevice;
        this.setDateValidators();

        this.formGroup.statusChanges.subscribe((result) => {
            if (result === 'VALID') {
                const formData = this.formTravellerInfo();
                this.setTraveller.emit({ traveller: formData });
            }
        });

        this.userService.loggedIn$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.changeDetectorRef.detectChanges();
            this.formTravellerInfo();
        });

        this.tzTravellerService.updateTravellerForm$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.changeDetectorRef.detectChanges();
            this.formTravellerInfo();
        });

        this.settingsService.selectedLanguageSubject.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.formatFieldDates();
        });
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['selectedTraveller'] && this.selectedTraveller) {
            const updateTravellerForm = this.isProfile || this.selectedTraveller.updateTravellerForm;
            this.formTravellerInfo();
            if (updateTravellerForm) {
                this.selectedTraveller.travellerUpdated = false;
                this.formGroup.reset();
                if (this.selectedTraveller.firstName && this.selectedTraveller.firstName !== '') {
                    this.setSelectedTraveller();

                    if (!this.isProfile) {
                        this.checkExpiringSoon();
                    }
                }
                this.changeDetectorRef.detectChanges();
            }
        }
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public isInValidField(controlName: string): boolean {
        return TzTravellerService.invalidControl(this.formGroup, controlName);
    }

    public toggoleEdit(): void {
        this.formReadonly = !this.formReadonly;
        this.formGroup.markAsPristine();
        this.setSelectedTraveller();
    }

    public saveTraveller(): void {
        const travellerInfo = this.formTravellerInfo();
        console.log('Values: ', travellerInfo);

        if (this.selectedTraveller.id !== '') {
            this.tzTravellerService.updateTraveller(travellerInfo);
        } else {
            this.tzTravellerService.saveTraveller(travellerInfo);
        }

        this.formReadonly = !this.platformService.isMobileDevice;
        this.formGroup.markAsPristine();
    }

    public removeTraveller(): void {
        this.tzTravellerService.removeTraveller(this.selectedTraveller.id);
    }

    public checkExpiringSoon(): void {
        if (this.expiryAlertShown) {
            return;
        }

        const formValue = this.formGroup.value;
        this.tzTravellerService.isExpiringSoon(formValue.idCardValidity, this.travelDate.endDate);
        this.expiryAlertShown = true;
    }

    public onFocus(field: string): void {
        if (this.isProfile) {
            return;
        }

        const setDirty = (controlName: string) => {
            const fieldControl = this.formGroup.get(controlName);
            if (fieldControl) {
                fieldControl.markAsDirty();
                fieldControl.markAsTouched();
            }
        };

        switch (field) {
            case 'idIssuedCountry':
                setDirty('idCardValidity');
            // eslint-disable-next-line no-fallthrough
            case 'idCardValidity':
                setDirty('dateOfIssue');
            // eslint-disable-next-line no-fallthrough
            case 'dateOfIssue':
                setDirty('idCardNumber');
            // eslint-disable-next-line no-fallthrough
            case 'idCardNumber':
                setDirty('idCardType');
            // eslint-disable-next-line no-fallthrough
            case 'idCardType':
                setDirty('nationality');
            // eslint-disable-next-line no-fallthrough
            case 'nationality':
                setDirty('dateOfBirth');
            // eslint-disable-next-line no-fallthrough
            case 'dateOfBirth':
                setDirty('gender');
            // eslint-disable-next-line no-fallthrough
            case 'gender':
                setDirty('phone');
            // eslint-disable-next-line no-fallthrough
            case 'phone':
                setDirty('lastName');
            // eslint-disable-next-line no-fallthrough
            case 'lastName':
                setDirty('middleName');
            // eslint-disable-next-line no-fallthrough
            case 'middleName':
                setDirty('firstName');
        }

        this.markFormAsVisited.emit();
    }

    private setDateValidators(): void {
        const dobCtrl = this.formGroup.get('dateOfBirth');
        if (dobCtrl) {
            dobCtrl.addValidators([
                this.tzTravellerService.dateValidation(),
                this.tzTravellerService.dobValidation(
                    this.travellerType,
                    this.selectedAge,
                    this.travelDate,
                    this.ageLimit
                ),
            ]);
        }

        const dateOfIssue = this.formGroup.get('dateOfIssue');
        if (dateOfIssue) {
            dateOfIssue.addValidators([
                this.tzTravellerService.dateValidation(),
                this.tzTravellerService.idIssueValidation(),
            ]);
        }

        const idCardValidity = this.formGroup.get('idCardValidity');
        if (idCardValidity) {
            idCardValidity.addValidators([
                this.tzTravellerService.dateValidation(),
                this.tzTravellerService.idExpiryValidation(this.travelDate.endDate),
            ]);
        }

        const idCardNumber = this.formGroup.get('idCardNumber');
        if (idCardNumber && this.formIdx !== -1) {
            idCardNumber.addValidators([this.tzTravellerService.duplicateIdValidation(this.formIdx)]);

            const idCardType = this.formGroup.get('idCardType');
            idCardType?.statusChanges.subscribe(() => {
                idCardNumber.updateValueAndValidity();
            });
        }
    }

    private formatFieldDates(): void {
        const currentFormat = this.dateFormat;
        const reqFromat = this.tzTravellerService.tzFormDateFormat;
        const formatDate = (controlStr: string): void => {
            const fieldCtrl = this.formGroup.get(controlStr);
            const fieldValue = fieldCtrl?.value;
            if (!fieldValue || fieldValue === '') {
                return;
            }

            const momentObj = moment(fieldValue, currentFormat);
            if (!momentObj.isValid()) {
                fieldCtrl.updateValueAndValidity();
                this.changeDetectorRef.detectChanges();
                return;
            }

            const date = moment(fieldValue, currentFormat).format(reqFromat);
            this.setFieldValue(controlStr, date);
        };

        formatDate('dateOfBirth');
        formatDate('dateOfIssue');
        formatDate('idCardValidity');
        this.dateFormat = reqFromat;
    }

    private setFieldValue(controlStr: string, value: any): void {
        const fieldCtrl = this.formGroup.get(controlStr);
        if (!fieldCtrl || !value) {
            return;
        }

        fieldCtrl.setValue(value);
    }

    private formDate(dateStr: string | Date, format: string): string {
        if (!dateStr || dateStr === '' || dateStr === '01/01/0001') {
            return '';
        }

        return moment(dateStr, format).format(this.dateFormat);
    }

    private setSelectedTraveller(): void {
        const traveller = this.selectedTraveller;
        const savedDateFormat = DatesUtils.savedTzTravelerFormat;

        this.setFieldValue('firstName', traveller.firstName);
        this.setFieldValue('middleName', traveller.middleName);
        this.setFieldValue('lastName', traveller.lastName);
        this.setFieldValue('gender', traveller.gender);
        this.setFieldValue('dateOfBirth', this.formDate(traveller.dateOfBirth, savedDateFormat));
        this.setFieldValue('idCardType', traveller.idCardType);
        this.setFieldValue('idCardNumber', traveller.idCardNumber);
        this.setFieldValue('dateOfIssue', this.formDate(traveller.dateOfIssue, savedDateFormat));
        this.setFieldValue('idCardValidity', this.formDate(traveller.idCardValidity, savedDateFormat));

        if (traveller.nationalityName !== '' && traveller.nationality !== '') {
            this.setFieldValue('nationality', {
                name: traveller.nationalityName,
                code: traveller.nationality,
            });
        }

        if (traveller.issuedCountryName !== '' && traveller.idIssuedCountry !== '') {
            this.setFieldValue('idIssuedCountry', {
                name: traveller.issuedCountryName,
                code: traveller.idIssuedCountry,
            });
        }

        if (traveller.phone && traveller.phone.number && traveller.phone.number !== '') {
            this.setFieldValue('phone', traveller.phone.number);
        }
    }

    private formTravellerInfo(): Passenger {
        const formValue = this.formGroup.value;
        const curFormat = this.tzTravellerService.tzFormDateFormat;

        const travellerInfo = {
            ...this.selectedTraveller,
            firstName: formValue.firstName,
            middleName: formValue.middleName,
            lastName: formValue.lastName,
            gender: formValue.gender,
            dateOfBirth: DatesUtils.updateTzFormDate(formValue.dateOfBirth, curFormat),
            idCardType: formValue.idCardType,
            idCardNumber: formValue.idCardNumber,
            dateOfIssue: DatesUtils.updateTzFormDate(formValue.dateOfIssue, curFormat),
            idCardValidity: DatesUtils.updateTzFormDate(formValue.idCardValidity, curFormat),
        } as Passenger;

        if (this.isProfile) {
            travellerInfo.docNotRequired = true;
        }

        if (formValue.phone) {
            travellerInfo.phone = {
                // eslint-disable-next-line id-blacklist
                number: formValue.phone.internationalNumber,
                countryCode: formValue.phone.countryCode,
                callingCode: formValue.phone.dialCode,
            } as Phone;
        }

        if (formValue.nationality) {
            travellerInfo.nationalityName = formValue.nationality.name;
            travellerInfo.nationality = formValue.nationality.code;
        }

        if (formValue.idIssuedCountry) {
            travellerInfo.issuedCountryName = formValue.idIssuedCountry.name;
            travellerInfo.idIssuedCountry = formValue.idIssuedCountry.code;
        }

        travellerInfo.travellerUpdated = this.updateTraveller(travellerInfo);
        return travellerInfo;
    }

    private selectedProfileTrvaller(): Passenger {
        const customer = this.userService.getCustomerInformation();
        const selectedId = this.selectedTraveller.id;
        if (selectedId === undefined || (selectedId === '' && !this.userService.IsLogged())) {
            return {} as Passenger;
        }

        for (const passenger of customer.passengers ?? []) {
            if (passenger.id === selectedId) {
                return passenger;
            }
        }

        return {} as Passenger;
    }

    private updateTraveller(formValue: Passenger): boolean {
        const savedTraveler = ToolsService.deepCopy(this.selectedProfileTrvaller()) as Passenger;
        if (!savedTraveler || !savedTraveler.id || savedTraveler.id === '') {
            return false;
        }

        const isDatesModified = (savedDate: string | Date, formDate: string | Date) =>
            !!formDate && formDate !== '' && new Date(savedDate).toDateString() !== new Date(formDate).toDateString();
        const isValueModified = (savedValue: string | undefined, formStr: string | undefined) => {
            savedValue = savedValue ?? '';
            formStr = formStr ?? '';
            return savedValue !== formStr;
        };

        return (
            isValueModified(savedTraveler.firstName, formValue.firstName) ||
            isValueModified(savedTraveler.middleName, formValue.middleName) ||
            isValueModified(savedTraveler.lastName, formValue.lastName) ||
            isValueModified(savedTraveler.gender, formValue.gender) ||
            isDatesModified(savedTraveler.dateOfBirth, formValue.dateOfBirth) ||
            isValueModified(savedTraveler.nationalityName, formValue.nationalityName) ||
            (!this.selectedTraveller.docNotRequired &&
                (isValueModified(savedTraveler.idCardType, formValue.idCardType) ||
                    isValueModified(savedTraveler.idCardNumber, formValue.idCardNumber) ||
                    isDatesModified(savedTraveler.dateOfIssue, formValue.dateOfIssue) ||
                    isDatesModified(savedTraveler.idCardValidity, formValue.idCardValidity) ||
                    isValueModified(savedTraveler.issuedCountryName, formValue.issuedCountryName)))
        );
    }
}
