import { Injectable } from '@angular/core';
import {
    HttpErrorResponse,
    HttpEvent,
    HttpHandler,
    HttpInterceptor,
    HttpRequest,
    HttpResponse,
} from '@angular/common/http';
import { Observable, Subject, throwError } from 'rxjs';
import { LoaderService, SEARCH_RESULT_PROCESSING } from '@services/modules/core/loader.service';
import { catchError, tap, map, takeUntil } from 'rxjs/operators';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { HttpCancelService } from '@core/interceptors/check-results/http-cancel.services';

@Injectable()
export class CheckResultsInterceptor implements HttpInterceptor {
    private counter = 0;

    constructor(
        private loaderService: LoaderService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private httpCancelService: HttpCancelService,
        private router: Router
    ) {}

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        if (req.context.get(SEARCH_RESULT_PROCESSING)) {
            this.counter++;
        }
        if (this.counter > 0) {
            this.loaderService.triggerCheckResults.next(true);
        }
        return next.handle(req).pipe(
            takeUntil(this.httpCancelService.onCancelPendingRequests().pipe(tap(() => this.resetCounter()))),
            map((event: HttpEvent<any>) => {
                if (event instanceof HttpResponse) {
                    this.handleResponse(req);
                }
                return event;
            }),
            catchError((error: HttpErrorResponse) => {
                console.log('http failure on:' + req.urlWithParams);
                this.handleResponse(req);
                if (req.params.get('handleRedisError')) {
                    this.handleRedisError(error);
                }

                return throwError(error);
            })
        );
    }

    public resetCounter() {
        console.log('ending counter of:', this.counter);
        this.counter = 0;
    }

    private handleResponse(req: HttpRequest<any>) {
        if (req.context.get(SEARCH_RESULT_PROCESSING)) {
            this.counter--;
        }
        if (this.counter <= 0) {
            this.loaderService.triggerCheckResults.next(false);
        }

        if (req.params.get('closeLoader')) {
            this.loaderService.triggerLoading.emit({ loading: false });
        }
    }

    private handleRedisError(error: HttpErrorResponse) {
        if (!error || error.status !== 410) {
            return;
        }

        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.INACTIVITY.TITLE'),
                multipleContent: [this.translateService.instant('POPUPS.INACTIVITY.BODY')],
                confirmBtnTitle: this.translateService.instant('POPUPS.INACTIVITY.1_NEW_SEARCH'),
                icon: { name: 'inactivity-dialog', iconStyle: { 'width.px': 80, 'height.px': 54 } },
            })
            .afterClosed()
            .subscribe(() => {
                this.router.navigate(['search']);
                return;
            });
    }
}
