import { Directive, ElementRef, Input, HostListener } from '@angular/core';

@Directive({
    selector: '[tzMaxLimit]',
})
export class MaxLimitDirective {
    @Input() tzMaxLimit = -1;
    @Input() type = '';

    private readonly invalidChars = ['-', '+', 'E', 'e', '.'];
    private readonly specialKeys = ['Backspace', 'Del', 'Delete'];
    constructor(private el: ElementRef) {}

    @HostListener('keydown', ['$event'])
    onKeyDown(event: KeyboardEvent) {
        if (this.specialKeys.includes(event.key)) {
            return;
        }

        const preventKeydown =
            (this.type === 'number' && this.invalidChars.includes(event.key)) ||
            (!document.getSelection()?.toString() &&
                this.el.nativeElement.value.length === this.tzMaxLimit &&
                isFinite(+event.key));
        if (preventKeydown) {
            event.preventDefault();
        }
    }
}
