import { Injectable } from '@angular/core';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { Observable, Subject } from 'rxjs';
import { ServiceId } from '@core/constants/service-id.const';
import { ICart } from '@schemas/modules/cart/cart';
import { ITrainsCartData } from '@schemas/modules/trains/train-cart-data';
import {
    SelectedOption,
    SupplierClassMinPrice,
    TrainSelection,
} from '@schemas/modules/trains/train-search-query-params';
import { TrainsResponseSection } from '@schemas/modules/trains/train.response';
import { TRAIN } from '@schemas/modules/trains/train';
import { SupplierClass, TrainClass } from '@schemas/modules/trains/train-class';
import { ServicePrices } from '@schemas/modules/core/service-prices';

@Injectable({
    providedIn: 'root',
})
export class TrainService extends ResultService {
    public override readonly serviceId = ServiceId.TRAIN;
    public selectedOption: SelectedOption = {};
    public updateTrainSelection: Subject<TrainSelection> = new Subject<TrainSelection>();

    public getTrainSelections(): SelectedOption {
        if (
            this.selectedOption.dataId === '' ||
            this.selectedOption.dataId !== this.searchDataService.getDataId(this.serviceId)
        ) {
            this.selectedOption = {};
        }

        return this.selectedOption;
    }

    public setTrainSelections(selection: SelectedOption): void {
        if (Object.keys(selection).length) {
            selection.dataId = this.searchDataService.getDataId(this.serviceId);
        }

        this.selectedOption = selection;
    }

    public clearTrainsSelection(): void {
        this.selectedOption = {};
    }

    public addTrainToCart(selectedOption: SelectedOption): Observable<ICart> {
        return this.addToCart(this.formAddCartReq(selectedOption));
    }

    public getSelectedTrainsFromCart(): ITrainsCartData {
        return this.getServiceCartItems();
    }

    public getSelectedTrainFromCart(id: string): ITrainsCartData | null {
        const cartData = this.getServiceCartItems();
        for (const item of cartData.items) {
            if (item.id === id) {
                return item;
            }
        }
        return null;
    }

    public getSelectedClass(
        train: TRAIN,
        trainClass: string,
        selectedTrain: TrainSelection | null
    ): TrainClass | undefined {
        const trainClasses = train.trainClasses;
        let minPriceClass: TrainClass | undefined;
        let minPrice: number | undefined;

        trainClasses.forEach((tc) => {
            if (trainClass !== tc.trainClass.tzClass) {
                return;
            }

            if (selectedTrain && trainClass === selectedTrain.tzClass) {
                if (selectedTrain.classId === tc.id) {
                    minPriceClass = tc;
                }
                return;
            }

            if (!minPrice || !minPriceClass) {
                minPrice = tc.prices.listPrice;
                minPriceClass = tc;
            } else if (tc.prices.listPrice < minPrice) {
                minPrice = tc.prices.listPrice;
                minPriceClass = tc;
            }
            return;
        });

        return minPriceClass;
    }

    public formTrainSelectionInfo(
        train: TRAIN,
        trainClass: TrainClass,
        isSelected: boolean,
        showDetails: boolean
    ): TrainSelection {
        return {
            id: train.id,
            journeyType: train.journeyType,
            fromLocation: train.departure.location.city,
            toLocation: train.arrival.location.city,
            classId: trainClass.id,
            groupId: trainClass.groupId,
            tripId: trainClass.tripID,
            tzClass: trainClass.trainClass.tzClass,
            selected: isSelected,
            tzSubClass: trainClass.trainClass.tzSubClass,
            showDetails,
        } as TrainSelection;
    }

    public isValidTicket(train: TRAIN, supplierClass: SupplierClass, subClass?: TrainClass): boolean {
        if (supplierClass.count === 0) {
            return false;
        }
        if (train.journeyType === 'onwardJourney') {
            return true;
        }
        if (subClass) {
            return !subClass.disabled;
        }
        let valid = false;
        train.trainClasses.some((tc) => {
            if (tc.trainClass.tzClass === supplierClass.class) {
                valid = !tc.disabled;
            }
            return valid;
        });
        return valid;
    }

    public getServicePrices(train: TRAIN, selectedTrain: TrainSelection | null): { [key: string]: ServicePrices } {
        const supplierClassMinPrice: SupplierClassMinPrice = {};
        train.supplierClasses.forEach((supplierClass) => {
            const tc = this.getSelectedClass(train, supplierClass.class, selectedTrain);
            if (tc) {
                supplierClassMinPrice[supplierClass.class] = tc.prices;
            }
        });
        return supplierClassMinPrice;
    }

    public filterTrainsRemoveCartItem(data: TrainsResponseSection[], cartIds: string[]): TrainsResponseSection[] {
        return data.map((trains: TrainsResponseSection) => {
            trains.data = trains.data.filter((item: TRAIN) => !cartIds.includes(item.id));
            return trains;
        });
    }

    private formAddCartReq(selectedOption: SelectedOption): any[] {
        console.log('$scope.selected train', selectedOption);
        const groupingId = `T${+new Date()}`;
        const req: any[] = [];

        [selectedOption.onward, selectedOption.return].forEach((option) => {
            if (option && option.tripId) {
                req.push({
                    id: option.id,
                    type: option.journeyType,
                    bookingOptions: [
                        {
                            classId: option.classId,
                            tripId: option.tripId,
                            groupingId,
                        },
                    ],
                });
            }
        });
        return req;
    }
}
