export enum FlightLuggageOptionTypes {
    PERSONALITEM = 'personalItem',
    CABIN = 'cabin',
    CHECKIN = 'checkIn',
}

export interface LuggagePrices {
    additionalCharge: number;
    additionalChargeNow: number;
    conversionRate: number;
    dueOnArrival: number;
    listPrice: number;
    listPriceCur: string;
    originalPrice: {
        ChargeAmt: number;
        chargeCur: string;
        chargeNowAmt: number;
        chargeNowCur: string;
        commission: {
            amt: number;
            strategy: number;
            type: number;
            value: number;
        };
        curCode: string;
        isPayLater: false;
        originalPrice: number;
        providerPrice: number;
    };
    payLaterAmt: number;
    payableAmt: number;
}

export interface FlightLuggageOption {
    byTotalWeight: boolean;
    dimensions: string;
    maxQuantity: number;
    maxWeight: number;
    prices: LuggagePrices;
    quantity: number;
    travelerTypes: string[];
    type: FlightLuggageOptionTypes;
    weightText: string;
    weightTexts: string[];
    combinationText: string;
    index: string;
    outwardIndex: string;
    returnIndex: string;
    weights: number[];
}

export interface FlightLuggage {
    cabin: {
        combinable: boolean;
        options: FlightLuggageOption[];
        replaceIncluded: boolean;
    };
    checkIn: {
        combinable: boolean;
        options: FlightLuggageOption[];
        replaceIncluded: boolean;
    };
}
