import { APP_INITIALIZER } from '@angular/core';
import { CartService } from '@services/modules/cart/services/cart.service';
import { AppConfigDataService } from '@services/modules/core/dataservices/app-config-data.service';

const initializeCartData = (cartService: CartService) => () => cartService.reInit();

const initializeDeviceId = (appConfigDataService: AppConfigDataService) => () =>
    appConfigDataService.initDeviceId(false);

export const INITIALIZATION_PROVIDERS = [
    {
        provide: APP_INITIALIZER,
        useFactory: initializeDeviceId,
        deps: [AppConfigDataService],
        multi: true,
    },
    {
        provide: APP_INITIALIZER,
        useFactory: initializeCartData,
        deps: [CartService],
        multi: true,
    },
];
