import { Injectable, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material/dialog';
import { ComponentType } from '@angular/cdk/portal';
import { BehaviorSubject, Observable, Subject, timer } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { TzDialogComponent } from '@shared/components/dialogs/tz-dialog/tz-dialog.component';
// eslint-disable-next-line max-len
import { TrazlerConfirmationDialogComponent } from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { MainDialogOptions } from '@shared/schemas/main-dialog-options.model';
import { MainDialogComponent } from '@shared/components/dialogs/main-dialog/main-dialog.component';
import { StickFooterDialogOptions } from '@shared/schemas/stick-footer-dialog-options.model';
import { StickyFooterDialogComponent } from '@shared/components/dialogs/stick-footer-dialog/stick-footer-dialog.component';
import { MainDialogSize } from '@shared/schemas/dialog.model';
import { ConfirmationDialogOptions } from '@shared/schemas/confirmation-dialog-options.model';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { TzSnackBarComponent } from '@shared/components/dialogs/tz-snack-bar/tz-snack-bar.component';
import { TzSnackBarDialogComponent } from '@shared/components/dialogs/tz-snack-bar-dialog/tz-snack-bar-dialog.component';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { PlatformService } from './platform.service';
import { FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { IHotelNames } from '@schemas/modules/accommodations/accommodations-response';

@Injectable({
    providedIn: 'root',
})
export class DialogService {
    private conf = {
        autoFocus: true,
        height: 'auto',
        width: 'auto',
        panelClass: 'app-dialog',
        data: {},
    };

    constructor(
        private dialog: MatDialog,
        private dialogRef: MatDialogRef<TzDialogComponent>,
        private platformService: PlatformService
    ) {}

    public openDialog<T>(component: ComponentType<T> | TemplateRef<T>, conf: MatDialogConfig): MatDialogRef<any> {
        if (!this.platformService.isMobileDevice) {
            return this.dialog.open(component, conf);
        }

        const animationClass = ['animate__dialogPanel', 'animate__dialogEnter'];
        if (!conf.panelClass) {
            conf.panelClass = animationClass;
        } else if (typeof conf.panelClass === 'string') {
            conf.panelClass = [conf.panelClass, ...animationClass];
        } else {
            conf.panelClass = [...conf.panelClass, ...animationClass];
        }

        return this.dialog.open(component, conf);
    }

    public closeAll() {
        console.log('closing all open popups');
        this.dialog.closeAll();
    }

    public closeDialog<T>(mdDialogRef: MatDialogRef<T>, dialogResult?: any): void {
        if (!this.platformService.isMobileDevice) {
            mdDialogRef.close(dialogResult);
            return;
        }

        mdDialogRef.removePanelClass('animate__dialogEnter');
        mdDialogRef.addPanelClass('animate__dialogExit');

        const timer$ = timer(500); // wait for panel close animation to finish
        timer$.subscribe(() => {
            mdDialogRef.close(dialogResult);
        });
    }

    public open<T>(
        component: ComponentType<T> | TemplateRef<T>,
        data?: any,
        conf: MatDialogConfig = {}
    ): MatDialogRef<any> {
        this.conf.data = {
            component,
            data,
        };
        if (data?.isDefaultDialog === true) {
            this.conf.panelClass = 'default-app-dialog';
        }
        conf = {
            ...this.conf,
            ...conf,
        };
        return this.openDialog(component, conf);
    }

    openSizePopup<T>(component: any, sizedata: any): MatDialogRef<any> {
        return this.openDialog(component, {
            width: '600px',
            data: sizedata,
            disableClose: false,
        });
    }

    openAuthenticityPopup<T>(component: any): MatDialogRef<any> {
        return this.openDialog(component, {
            disableClose: false,
        });
    }

    // tvb-dialog

    public openFilterDialog<T>(
        component: ComponentType<T>,
        data: {
            filter: BehaviorSubject<FilterReferenceItem>;
            resultsCount: Subject<number>;
            searchHotel?: {
                hotelNames: IHotelNames[];
                searchHotel$?: Subject<IHotelNames>;
            };
        }
    ): MatDialogRef<T> {
        const conf = {
            autoFocus: false,
            height: '100%',
            width: '448px',
            panelClass: 'trazler-filter-dialog',
            position: {
                left: '0',
            },
            data,
        };
        return this.dialog.open(component, conf);
    }

    public openAccommodationReviewsDialog<T>(
        component: ComponentType<T>,
        accommodationReviews: AccommodationReviews | undefined | null
    ): MatDialogRef<T> {
        const conf = {
            height: '100%',
            width: '424px',
            panelClass: 'trazler-accommodation-reviews-dialog',
            position: {
                left: '0',
            },
            data: { accommodationReviews },
        };
        return this.dialog.open(component, conf);
    }

    public openBasketTimelineDialog<T>(component: ComponentType<T>, cartItems: ICartItem[]): MatDialogRef<T> {
        const conf = {
            height: '100%',
            width: '448px',
            panelClass: 'trazler-filter-dialog',
            position: {
                left: '0',
            },
            data: { cartItems },
        };

        return this.dialog.open(component, conf);
    }

    public openAccommodationDialog<T>(component: ComponentType<T>, galleyConf: MatDialogConfig): MatDialogRef<T> {
        return this.openDialog(component, galleyConf);
    }

    public openServiceDialog<T>(component: ComponentType<T>, modelConf: MatDialogConfig): MatDialogRef<T> {
        return this.openDialog(component, modelConf);
    }

    public confirmedTrazlerDialog(): Observable<any> {
        return this.dialogRef.afterClosed().pipe(
            take(1),
            map((res) => res)
        );
    }

    public openConfirmationDialog(
        confirmationDialogData: ConfirmationDialogOptions,
        options: { mainDialogSize: MainDialogSize; dialogPanelClasses?: string[] } = {
            mainDialogSize: MainDialogSize.DEFAULT,
        }
    ): MatDialogRef<TrazlerConfirmationDialogComponent> {
        const className = MainDialogSize.LARGE === options.mainDialogSize ? 'tz-main-dialog-large' : 'tz-main-dialog';

        const config: MatDialogConfig = {
            data: { data: confirmationDialogData },
            panelClass: [className, ...(options.dialogPanelClasses ?? [])],
            maxWidth: options.mainDialogSize,
            width: options.mainDialogSize,
            maxHeight: '566px',
            disableClose: true,
        };

        return this.openDialog(TrazlerConfirmationDialogComponent, config);
    }

    public openSnackBarDialog(
        message: string,
        options: { time: number } = { time: 4000 }
    ): MatDialogRef<TzSnackBarDialogComponent> {
        const config: MatDialogConfig = {
            data: { data: { message, time: options.time } },
            panelClass: 'tz-snack-bar-dialog',
            backdropClass: 'tz-snack-bar-backdrop',
            maxWidth: '365px',
            width: '365px',
            minHeight: '60px',
            disableClose: true,
        };

        return this.openDialog(TzSnackBarDialogComponent, config);
    }

    public openMainDialog(
        dialogData: MainDialogOptions,
        options: { mainDialogSize: MainDialogSize } = { mainDialogSize: MainDialogSize.DEFAULT }
    ) {
        const className = MainDialogSize.LARGE === options.mainDialogSize ? 'tz-main-dialog-large' : 'tz-main-dialog';

        const config: MatDialogConfig = {
            data: { data: dialogData },
            panelClass: className,
            maxWidth: options.mainDialogSize,
            width: options.mainDialogSize,
            maxHeight: '566px',
            disableClose: true,
        };

        return this.openDialog(MainDialogComponent, config);
    }

    public openStickFooterDialog(dialogData: StickFooterDialogOptions) {
        const config: MatDialogConfig = {
            data: { data: dialogData },
            panelClass: 'tz-stick-footer-dialog',
            maxWidth: '100vw',
            minWidth: '920px',
            width: '100%',
            disableClose: true,
        };

        return this.openDialog(StickyFooterDialogComponent, config);
    }

    public openSnackBar(data: { message: string; time?: number }): void {
        data.time = data.time ?? 4000;
        const config: MatDialogConfig = {
            data: { data },
            panelClass: ['tz-snack-bar-dialog', 'tz-snack-bar'],
            backdropClass: 'tz-snack-bar-backdrop',
            maxWidth: '365px',
            width: '365px',
            minHeight: '60px',
            disableClose: true,
        };

        this.openDialog(TzSnackBarComponent, config);
    }

    public getDialogById(id: string): MatDialogRef<any> | undefined {
        return this.dialog.getDialogById(id);
    }
}
