import { ChangeDetectionStrategy, Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TZ_ICONS } from '@shared/components/tz-icon/tz-icons';

@Component({
    selector: 'tz-icon',
    templateUrl: './tz-icon.component.html',
    styleUrls: ['./tz-icon.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
})
export class TzIconComponent implements OnInit {
    @Input() icon: string | null = null;
    @Input() iconContent: string | null = null;
    @Input() className = '';
    public imageUrl = '';

    constructor() {}

    ngOnInit(): void {
        if (this.iconContent) {
            return;
        }
        if (this.icon) {
            this.imageUrl = TZ_ICONS[this.icon] ? TZ_ICONS[this.icon] : this.icon + '.svg';
        }
    }
}
