import { Injectable } from '@angular/core';
import { ListenersService } from '@services/modules/core/listeners-service';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ListenerId } from '@core/constants/listener-id.const';
import { CartRemoveService } from '@services/modules/cart/services/cart-remove-service';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ServiceId } from '@core/constants/service-id.const';
import { DialogService } from '@services/dialog.service';
import {
    CANCEL,
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { EngineRuleService } from '@services/searchEngine/services/engine-rule-service';

interface IDataFailure {
    remove: true;
    status: number;
    cal: any;
    confirmCb: (newCal: any) => void;
    message?: string;
    checkContinue?: boolean;
    field: string;
}

interface IFailure {
    invalid: boolean;
    //actionId: number;
    addService?: boolean;
    dataFailure?: IDataFailure;
}

export interface ISearchErr {
    failure: IFailure;
    serviceId: ServiceId;
    cb: (serviceId: ServiceId, shouldEdit: boolean) => void;
}

@Injectable({
    providedIn: 'root',
})
export class SearchErrorService {
    private translateFieldMap: {
        [key in ServiceId]?: any;
    } = {};
    private inErrorFlag = false;
    private curFailureData: ISearchErr = {} as ISearchErr;

    constructor(
        private listenersService: ListenersService,
        private tabRoutingService: TabRoutingService,
        private searchDataService: SearchDataService,
        private removeService: CartRemoveService,
        private router: Router,
        private translateService: TranslateService,
        private dialogService: DialogService,
        private engineRuleService: EngineRuleService
    ) {
        this.initializeListenerAndMap();
    }

    public unsetErrorFlag(): void {
        this.inErrorFlag = false;
        this.curFailureData = {} as ISearchErr;
    }

    public setErrorFlag(): void {
        this.inErrorFlag = true;
    }

    public getInErrorFlag(): boolean {
        return this.inErrorFlag;
    }

    public getCurServiceErr(serviceId: ServiceId): ISearchErr | null {
        if (this.curFailureData && this.curFailureData.serviceId === serviceId) {
            return this.curFailureData;
        }
        return null;
    }

    public searchErr(failure: any, serviceId: ServiceId, actionId: any): void {
        if (this.router.url !== '/results-summary') {
            const isValid = this.engineRuleService.isValidSearch(serviceId);
            let invalid = false;
            if (!isValid.isValid) {
                invalid = true;
                failure.field = isValid.field;
            }
            this.listenersService.NotifyListeners(ListenerId.LISTEN_SEARCH_ERR, {
                serviceId,
                failure: {
                    //actionId,
                    dataFailure: failure,
                    invalid,
                },
            } as ISearchErr);
        }
        this.tabRoutingService.setSearchError(serviceId);
    }

    public newError(searchErrData: ISearchErr): void {
        /* if (
            false &&
            this.curFailureData &&
            this.curFailureData.serviceId === searchErrData.serviceId &&
            this.curFailureData?.failure?.dataFailure?.status === searchErrData.failure.dataFailure?.status
        ) {
            this.curFailureData = searchErrData;
            //error already exists we do not want to trigger again workflow
            //just update it with new data and return
            console.error('skipping error');
            return;
        }*/
        console.error('triggering error');
        this.curFailureData = searchErrData;
        const isValidSearch = this.engineRuleService.isValidSearch(this.curFailureData.serviceId);
        this.setErrorFlag();
        if (!isValidSearch.isValid && this.curFailureData.cb) {
            this.curFailureData.cb(this.curFailureData.serviceId, true);
            return;
        }

        this.searchErrHdl(false);
    }

    public searchErrHdl(checkContinue: boolean): void {
        const failure = this.curFailureData.failure;

        //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, false);
        const isCombination: boolean = this.searchDataService.isCombination();
        const actionBtn = this.translateService.instant(
            isCombination ? 'POPUPS.MISSING_SEARCH_PARAMS.2_CONTINUE' : 'POPUPS.MISSING_SEARCH_PARAMS.3_NEW_SEARCH'
        );
        if (failure && failure.dataFailure && failure.dataFailure.status === 8024) {
            this.promptAdjustTime(failure.dataFailure, this.curFailureData.serviceId, this.curFailureData.cb);
        } else if (
            failure &&
            failure.dataFailure &&
            ((failure.dataFailure.message && failure.dataFailure.message !== '') || failure.dataFailure.status === 412)
        ) {
            if (failure.dataFailure.status === 412) {
                if (checkContinue) {
                    if (failure.invalid) {
                        let field: any = failure.dataFailure.field;
                        if (
                            this.translateFieldMap[this.curFailureData.serviceId as ServiceId] &&
                            this.translateFieldMap[this.curFailureData.serviceId as ServiceId][
                                failure.dataFailure.field
                            ]
                        ) {
                            field =
                                this.translateFieldMap[this.curFailureData.serviceId as ServiceId][
                                    failure.dataFailure.field
                                ];
                        }
                        this.dialogService
                            .openConfirmationDialog({
                                title: this.translateService.instant('POPUPS.MISSING_SEARCH_PARAMS.TITLE'),
                                content: this.translateService.instant('POPUPS.MISSING_SEARCH_PARAMS.BODY', {
                                    errField: this.translateService.instant('MissingField.' + field),
                                    andOthers: this.translateService.instant('MissingField.andOthers'),
                                }),
                                confirmBtnTitle: this.translateService.instant('POPUPS.MISSING_SEARCH_PARAMS.1_ADJUST'),
                                cancelBtnTitle: actionBtn,
                                showCloseBtn: true,
                            })
                            .afterClosed()
                            .subscribe((dialogReturn: TzDialogReturn) => {
                                if (dialogReturn === CANCEL) {
                                    this.removeAnswerHandling(
                                        dialogReturn,
                                        this.curFailureData.serviceId,
                                        this.curFailureData.cb
                                    );
                                } else {
                                    if (this.curFailureData.cb) {
                                        this.curFailureData.cb(this.curFailureData.serviceId, true);
                                    }
                                }
                            });
                    } else {
                        this.dialogService
                            .openConfirmationDialog({
                                title: this.translateService.instant('POPUPS.PROCESS_SEARCH.TITLE'),
                                content: this.translateService.instant('POPUPS.PROCESS_SEARCH.BODY'),
                                confirmBtnTitle: this.translateService.instant('POPUPS.PROCESS_SEARCH.1_ADJUST'),
                                cancelBtnTitle: this.translateService.instant(
                                    isCombination
                                        ? 'POPUPS.PROCESS_SEARCH.2_CONTINUE'
                                        : 'POPUPS.PROCESS_SEARCH.3_NEW_SEARCH'
                                ),
                                showCloseBtn: true,
                            })
                            .afterClosed()
                            .subscribe((dialogReturn: any) => {
                                if (dialogReturn === CANCEL) {
                                    this.removeAnswerHandling(
                                        dialogReturn,
                                        this.curFailureData.serviceId,
                                        this.curFailureData.cb
                                    );
                                } else {
                                    if (this.curFailureData.cb) {
                                        this.curFailureData.cb(this.curFailureData.serviceId, true);
                                    }
                                }
                            });
                    }
                } else if (this.curFailureData.cb) {
                    this.curFailureData.cb(this.curFailureData.serviceId, true);
                }
            } else {
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.NO_RESULTS.TITLE'),
                        content: this.translateService.instant('POPUPS.PROCESS_SEARCH.BODY'),
                        confirmBtnTitle: this.translateService.instant('GENERAL.NoResults1'),
                        showCloseBtn: false,
                    })
                    .afterClosed()
                    .subscribe((dialogReturn: TzDialogReturn) => {
                        if (this.curFailureData.cb) {
                            this.curFailureData.cb(this.curFailureData.serviceId, true);
                        }
                    });
            }
        } else {
            if (this.router.url === 'reCreateTrip') {
                this.router.navigate(['/search']);
                setTimeout(() => {
                    this.dialogService
                        .openConfirmationDialog({
                            title: this.translateService.instant('POPUPS.NO_RESULTS.TITLE'),
                            content: this.translateService.instant('POPUPS.NO_RESULTS.BODY'),
                            confirmBtnTitle: this.translateService.instant('POPUPS.NO_RESULTS.1_OK'),
                            showCloseBtn: false,
                            icon: { name: 'no-results-dialog', iconStyle: { 'width.px': 64, 'height.px': 70 } },
                        })
                        .afterClosed()
                        .subscribe((dialogReturn: TzDialogReturn) => {});
                }, 1000);
            } else if (this.router.url === '/search') {
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.NO_RESULTS.TITLE'),
                        content: this.translateService.instant('POPUPS.NO_RESULTS.BODY'),
                        confirmBtnTitle: this.translateService.instant('POPUPS.NO_RESULTS.1_OK'),
                        showCloseBtn: false,
                        icon: { name: 'no-results-dialog', iconStyle: { 'width.px': 64, 'height.px': 70 } },
                    })
                    .afterClosed()
                    .subscribe((dialogReturn: TzDialogReturn) => {});
            } else if (failure && failure.invalid) {
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.NO_GOOD_OPTIONS.TITLE'),
                        content: this.translateService.instant(
                            isCombination ? 'POPUPS.NO_GOOD_OPTIONS.BODY_COMBINATION' : 'POPUPS.NO_GOOD_OPTIONS.BODY'
                        ),
                        cancelBtnTitle: this.translateService.instant(
                            isCombination ? 'POPUPS.NO_GOOD_OPTIONS.2_CONTINUE' : 'POPUPS.NO_GOOD_OPTIONS.3_NEW_SEARCH'
                        ),
                        confirmBtnTitle: this.translateService.instant('POPUPS.NO_GOOD_OPTIONS.1_ADJUST'),
                        showCloseBtn: false,
                        icon: { name: 'no-results-dialog', iconStyle: { 'width.px': 64, 'height.px': 70 } },
                    })
                    .afterClosed()
                    .subscribe((dialogReturn: TzDialogReturn) => {
                        this.removeAnswerHandling(dialogReturn, this.curFailureData.serviceId, this.curFailureData.cb);
                    });
            } else {
                this.dialogService
                    .openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.ADD_SERVICE_NO_RESULTS.TITLE'),
                        content: this.translateService.instant(
                            isCombination
                                ? 'POPUPS.ADD_SERVICE_NO_RESULTS.BODY_CONTINUE'
                                : 'POPUPS.ADD_SERVICE_NO_RESULTS.BODY_NEW_SEARCH'
                        ),
                        cancelBtnTitle: this.translateService.instant(
                            isCombination
                                ? 'POPUPS.ADD_SERVICE_NO_RESULTS.2_CONTINUE'
                                : 'POPUPS.ADD_SERVICE_NO_RESULTS.3_NEW_SEARCH'
                        ),
                        showCloseBtn: false,
                        confirmBtnTitle: this.translateService.instant('POPUPS.ADD_SERVICE_NO_RESULTS.1_ADJUST'),
                        icon: { name: 'no-results-dialog', iconStyle: { 'width.px': 64, 'height.px': 70 } },
                    })
                    .afterClosed()
                    .subscribe((dialogReturn: TzDialogReturn) => {
                        this.removeAnswerHandling(dialogReturn, this.curFailureData.serviceId, this.curFailureData.cb);
                    });
            }
        }
    }

    private removeAnswerHandling(
        dialogReturn: TzDialogReturn,
        serviceId: ServiceId,
        onUpdateCb: (serviceId: ServiceId, shouldEdit: boolean) => void
    ): void {
        if (dialogReturn === CONFIRM) {
            if (onUpdateCb) {
                onUpdateCb(serviceId, true);
            }
        } else {
            this.unsetErrorFlag();
            if (!this.searchDataService.isCombination()) {
                this.router.navigate(['/search']);
                return;
            }
            this.removeService.removeCartData(serviceId, undefined, true).subscribe(
                () => {
                    if (onUpdateCb) {
                        onUpdateCb(serviceId, false);
                    }
                },
                () => {
                    if (onUpdateCb) {
                        onUpdateCb(serviceId, false);
                    }
                }
            );
        }
    }

    private searchErrNotifyHdl(failure: ISearchErr): void {
        // discard subj
        this.newError(failure);
    }

    private initializeListenerAndMap(): void {
        this.listenersService.registerListener(ListenerId.LISTEN_SEARCH_ERR, this.searchErrNotifyHdl.bind(this));
        this.translateFieldMap[ServiceId.TRANSFER] = {
            to: 'from',
            address: 'to',
        };
        this.translateFieldMap[ServiceId.CAR_RENTAL] = {
            to: 'pickup',
            address: 'dropoff',
            persons: 'driver',
        };
    }

    private promptAdjustTime(failure: IDataFailure, serviceId: any, onUpdateCb?: any): void {
        let cancelTxt = 'POPUPS.ADJUST_RENTAL_SEARCH.3_NO';
        if (failure.remove) {
            cancelTxt = 'POPUPS.ADJUST_RENTAL_SEARCH.2_CONTINUE';
        }
        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.ADJUST_RENTAL_SEARCH.TITLE'),
                confirmBtnTitle: this.translateService.instant('POPUPS.ADJUST_RENTAL_SEARCH.1_YES'),
                cancelBtnTitle: this.translateService.instant(cancelTxt),
                closeBtnTitle: failure.remove ? this.translateService.instant('POPUPS.ADJUST_RENTAL_SEARCH.3_NO') : '',
                content: this.translateService.instant('POPUPS.ADJUST_RENTAL_SEARCH.BODY', {
                    openingHour: failure.cal.fromTime,
                }),
                icon: { name: 'adjust-rental-search-dialog', iconStyle: { 'width.px': 44, 'height.px': 44 } },
            })
            .afterClosed()
            .subscribe((dialogReturn: TzDialogReturn) => {
                if (dialogReturn === CONFIRM) {
                    if (failure.confirmCb) {
                        failure.confirmCb(failure.cal);
                    }
                } else if (dialogReturn === CANCEL && failure.remove) {
                    this.removeAnswerHandling(false, serviceId, onUpdateCb);
                } else {
                    if (onUpdateCb) {
                        onUpdateCb(serviceId, true);
                    }
                }
            });
    }
}
