import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { XXsrfTokenInterceptor } from '@core/interceptors/xsrf/x-xsrf-token.interceptor';
import { ErrorHandlerInterceptor } from '@core/interceptors/error-handler/error-handler.interceptor';
import { CheckResultsInterceptor } from '@core/interceptors/check-results/check-results.interceptor';
import { HttpCancelService } from '@core/interceptors/check-results/http-cancel.services';

export const INTERCEPTORS = [
    HttpCancelService,
    {
        provide: HTTP_INTERCEPTORS,
        useClass: XXsrfTokenInterceptor,
        multi: true,
    },
    {
        provide: HTTP_INTERCEPTORS,
        useClass: ErrorHandlerInterceptor,
        multi: true,
    },
    {
        provide: HTTP_INTERCEPTORS,
        useClass: CheckResultsInterceptor,
        multi: true,
    },
];
