    constructor(private listenersService: ListenersService, private generalService: GeneralService) {}

    public static adjustScrollButtonPosition(resultContainer: ElementRef): number {
        const minimumThreshold = 63;
        const minimumBtnRight = 22;
        if (!resultContainer) {
            return minimumBtnRight;
        }

        const containerStyle = window.getComputedStyle(resultContainer.nativeElement);
        const containerRightMargin = +containerStyle.getPropertyValue('margin-left').replace('px', '');

        if (containerRightMargin > minimumThreshold && containerRightMargin - minimumThreshold > minimumBtnRight) {
            return containerRightMargin - minimumThreshold;
        } else {
            return minimumBtnRight;
        }
    }

    public scrollTo(scrollId: string, scrollTop: number, smooth: boolean) {
        if (!scrollId || scrollId === '') {
            return;
        }

        const scrollEl = document.getElementById(scrollId) as HTMLElement;
        if (!scrollEl) {
            return;
        }

        requestAnimationFrame(() => {
            scrollEl.style.scrollMarginTop = `${scrollTop}px`;
            scrollEl.scrollIntoView({
                behavior: smooth ? 'smooth' : 'auto',
                block: 'start',
            });
        });
    }

    public scrollToVisibleArea(
        el: HTMLElement | ElementRef | string,
        fixedElement: HTMLElement | ElementRef | string,
        options: {
            scrollTop?: number;
            scrollMarginBottom?: number;
            scrollBlock?: 'start' | 'end' | 'center' | 'nearest';
            includeElementsHeight?: string[];
        } = {},
        smooth = true
    ): void {
        let element;
        if (typeof el === 'string') {
            element = document.getElementById(el);
        } else {
            element = el instanceof HTMLElement ? el : el.nativeElement;
        }

        let fixElement;
        if (typeof fixedElement === 'string') {
            fixElement = document.getElementById(fixedElement);
        } else {
            fixElement = fixedElement instanceof HTMLElement ? fixedElement : fixedElement.nativeElement;
        }

        const scrollEl = element as HTMLElement;
        const fixedEl = fixElement as HTMLElement;

        if (!scrollEl || !fixedEl) {
            return;
        }

        let elementsHeight = 0;
        if (options.includeElementsHeight) {
            options.includeElementsHeight.map((countEl) => {
                const countElement = document.getElementById(countEl);

                if (countElement) {
                    elementsHeight = elementsHeight + countElement.offsetHeight;
                }
            });
        }

        const addScrollTop = options.scrollTop ? options.scrollTop : 0;
        const scrollTop =
            window.innerHeight - fixedEl.offsetHeight - scrollEl.offsetHeight - elementsHeight - addScrollTop;

        if (this.isScrolledElementView(element, { elAddBottom: fixedEl.offsetHeight })) {
            return;
        }

        requestAnimationFrame(() => {
            if (scrollTop) {
                scrollEl.style.scrollMarginTop = `${scrollTop}px`;
            }

            if (options.scrollMarginBottom) {
                scrollEl.style.scrollMarginBottom = `${options.scrollMarginBottom}px`;
            }

            scrollEl.scrollIntoView({
                behavior: smooth ? 'smooth' : 'auto',
                block: options.scrollBlock ? options.scrollBlock : 'start',
            });
        });
    }

    public scrollToTop(): void {
        const element = document.getElementsByClassName('wrapper');
        if (element.length && element[0]) {
            element[0].scrollTo({ top: 0 });
        }
    }

    public scrollToElement(el: HTMLElement | ElementRef | string, options: Partial<ScrollIntoViewOptions> = {}): void {
        const scrollOptions = {
            ...this.defaultScrollingOptions,
            ...options,
        };
        let element;
        if (typeof el === 'string') {
            element = document.getElementById(el);
        } else {
            element = el instanceof HTMLElement ? el : el.nativeElement;
        }
        element.scrollIntoView(scrollOptions);
    }

    public scrollToServiceContainer() {
        this.scrollToElement('result-wrapper', { block: 'start' });
    }

    public isScrolledElementView(
        el: HTMLElement | ElementRef | string,
        options: { elAddTop?: number; elAddBottom?: number } = {}
    ): boolean {
        let element;
        if (typeof el === 'string') {
            element = document.getElementById(el);
        } else {
            element = el instanceof HTMLElement ? el : el.nativeElement;
        }

        const scrollEl = element as HTMLElement;
        if (!scrollEl) {
            return false;
        }

        const rect = scrollEl.getBoundingClientRect();
        const elAddTop = options.elAddTop ? options.elAddTop : 0;
        const elAddBottom = options.elAddBottom ? options.elAddBottom : 0;
        const elemTop = rect.top + elAddTop;
        const elemBottom = rect.bottom + elAddBottom;

        return elemTop >= 0 && elemBottom <= window.innerHeight;
    }

