import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ToolsService } from '@core/utils/tools-service';
import { Injectable } from '@angular/core';
import { CombinationId, DataId } from '@schemas/modules/core/base-types';
import { ServiceId } from '@core/constants/service-id.const';

@Injectable({
    providedIn: 'root',
})
export class PollerService {
    private pollerIntervalId: any = -1;
    private pollInterval = 5; // poll interval in seconds
    private pollerIds: any = {};

    constructor(private trazlerAPI: ApiAccessService, private toolsService: ToolsService) {
        this.pollerIntervalId = this.poll();
    }

    public unregisterPoller(registeredData: any) {
        console.log('delete poller id :' + registeredData);
        if (!registeredData || !this.pollerIds[registeredData.dataId]) {
            return;
        }
        // deactivate callback for unregistered listener
        this.pollerIds[registeredData.dataId].cb[registeredData.cbIdx] = null;

        //check if one callback is left
        for (const cb of this.pollerIds[registeredData.dataId].cb) {
            if (cb !== null) {
                return;
            }
        }
        // no callback left clean up structure
        this.unregisterAllPoller(registeredData.dataId);
    }

    public registerPoller(combId: CombinationId, dataId: DataId, servicesId: ServiceId, cb: any) {
        if (!dataId || dataId === '') {
            console.error('DataId is mandatory');
            return null;
        }

        if (!cb) {
            console.error('callback not set');
        }
        console.log('registering poller for service :' + servicesId);
        if (this.pollerIds[dataId]) {
            this.pollerIds[dataId].cb.push(cb);
        } else {
            this.pollerIds[dataId] = {
                dataId,
                combId,
                cb: [cb],
                servicesId,
            };
            this.onPoll();
        }

        return {
            dataId,
            cbIdx: this.pollerIds[dataId].cb.length - 1,
        };
    }

    private onPoll(): void {
        if (ToolsService.isSet(this.pollerIds)) {
            for (const dataId in this.pollerIds) {
                if (this.pollerIds.hasOwnProperty(dataId)) {
                    if (this.pollerIds[dataId].dataId === '' || this.pollerIds[dataId].combId === '') {
                        console.error('DataId/combId is mandatory');
                        continue;
                    }
                    if (this.pollerIds[dataId].pollOngoing) {
                        continue;
                    }
                    this.pollerIds[dataId].pollOngoing = true;
                    console.log('service poller on dataId:' + dataId);
                    this.trazlerAPI.pollService(this.pollerIds[dataId].dataId, this.pollerIds[dataId].combId).then(
                        (metaData: any) => {
                            if (this.pollerIds[dataId]) {
                                this.pollerIds[dataId].pollOngoing = false;
                                if (metaData.complete === true) {
                                    // in case poller get unselected in between
                                    if (this.pollerIds[dataId]) {
                                        for (const cb of this.pollerIds[dataId].cb) {
                                            if (cb) {
                                                cb(metaData, null);
                                            }
                                        }
                                        this.unregisterAllPoller(dataId);
                                    }
                                } else {
                                    console.log('service poller data not ready');
                                }
                            }
                        },
                        (error: any) => {
                            this.pollerIds[dataId].pollOngoing = false;
                            console.log(error);
                            if (this.pollerIds[dataId]) {
                                for (const cb of this.pollerIds[dataId].cb) {
                                    if (cb) {
                                        cb(null, error);
                                    }
                                }
                                this.unregisterAllPoller(dataId);
                            }
                        }
                    );
                }
            }
        }
    }

    private poll() {
        return setInterval(() => {
            this.onPoll();
        }, this.pollInterval * 1000);
    }

    private unregisterAllPoller(dataId: string) {
        delete this.pollerIds[dataId];
    }
}
