import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';

import { ICart } from '@schemas/modules/cart/cart';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceIdToName } from '@core/constants/service-ids-name.const';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CombinationId } from '@schemas/modules/core/base-types';
import { ServiceId } from '@core/constants/service-id.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { StateFrom } from '@core/constants/state-from.const';
import { CartStatus } from '@core/constants/cart-status';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { DialogService } from '@services/dialog.service';
import { ToolsService } from '@core/utils/tools-service';
import { TranslateService } from '@ngx-translate/core';
import { ListenersService } from '@services/modules/core/listeners-service';

import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { LoaderService } from '@services/modules/core/loader.service';

@Injectable({
    providedIn: 'root',
})
export class ReCheckCartItem implements Resolve<ICart> {
    constructor(
        private cartService: CartService,
        private router: Router,
        private trazlerAPI: ApiAccessService,
        private cartRoutingService: CartRoutingService,
        private dialogService: DialogService,
        private toolsService: ToolsService,
        private translateService: TranslateService,
        protected listenersService: ListenersService,
        private searchDataService: SearchDataService,
        private loaderService: LoaderService
    ) {}

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ICart> | Promise<ICart> | ICart {
        return this.cartService.reInit().then((cartData: any) => {
            if (!cartData || !cartData.items || Object.keys(cartData.items).length === 0) {
                this.router.navigate(['results-summary']);
                return {} as ICart;
            }

            return this.beforeCheckout(cartData).then(
                (value: ICart) => value,
                () => {
                    this.router.navigate(['results-summary']);
                    return {} as ICart;
                }
            );
        });
    }

    private validCart(cartData: any): boolean {
        if (!cartData.items || cartData.items.length === 0) {
            return false;
        }

        return !cartData.items.some((ci: any) => CartUtils.isInvalidItem(ci.selectedItem));
    }

    private getInvalidItems(cartData: any): ICartItem[] {
        return cartData.items.filter((ci: ICartItem) =>
            ci.selectedItem.bookingOptions.some((option) => CartUtils.isFailed(option.status))
        );
    }

    private recheckCart(cartData: any, combId: CombinationId, deviceId: string, deferred: any): void {
        this.cartService.recheckCartItem(combId, deviceId, false).then(
            (success: ICart) => {
                // this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, false);
                let i = 0;
                let needsACCRateCheck = false;
                for (; i < success.items.length; i++) {
                    if (
                        success.items[i].serviceId === ServiceId.ACC &&
                        success.items[i].selectedItem.recheckBefore === '0001-01-01T00:00:00Z'
                    ) {
                        needsACCRateCheck = true;
                    }

                    if (success.items[i].serviceId === ServiceId.FLIGHT) {
                        this.listenersService.NotifyListeners(
                            ListenerId.LISTEN_RATE_CHECK,
                            success.items[i].selectedItem.data
                        );
                    }
                }

                if (needsACCRateCheck) {
                    this.cartRoutingService.changeState(ServiceId.ACC_DETAILS, success.items[i], false, StateFrom.NONE);
                    this.dialogService.openSnackBar({ message: this.translateService.instant('NOTIFS.RECHECK') });
                }

                this.cartService.setCart(success);
                if (success.bookingStatus === CartStatus.STATUS_PRICE_CHANGED) {
                    this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, true);
                    deferred.reject();
                    return;
                }

                deferred.resolve(success as ICart);
            },
            (error: any) => {
                // this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, false);
                /*                let msg = '';

                const showError = error.error.showError ? error.error.showError : false;
                if (error.error.showError) {
                    msg = error.error.message + '. ' + error.error.error;
                }

                if (error.error.errors === 275) {
                    error.error.errors = ServiceId.TRAIN;
                }*/
                if (ServiceIdToName[error.error.errors as ServiceId]) {
                    const serviceId = error.error.errors as ServiceId;
                    const invalidItems = cartData.items.filter((ci: any) => ci.serviceId === serviceId);
                    if (invalidItems.length > 0) {
                        this.cartService.reprice(combId).then(
                            (cartDataRepriced: any) => {
                                this.showInvalidCartDialog(
                                    cartDataRepriced,
                                    invalidItems,
                                    invalidItems[0],
                                    ServiceIdToName[serviceId]
                                );
                            },
                            () => {}
                        );
                    }
                }

                this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, true);
                this.loaderService.triggerLoading.emit({ loading: false });
                deferred.reject();
            }
        );
    }

    private showPriceChangeDialog(
        priceChangeServId: ServiceId,
        priceChangeId: string,
        serviceName: string | undefined
    ): void {
        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.PRICE_CHANGED.TITLE'),
                multipleContent: [this.translateService.instant('POPUPS.PRICE_CHANGED.BODY', { service: serviceName })],
                cancelBtnTitle: this.translateService.instant('POPUPS.PRICE_CHANGED.2_CANCEL'),
                confirmBtnTitle: this.translateService.instant('POPUPS.PRICE_CHANGED.1_ACCEPT'),
                icon: { name: 'decision-required-dialog', iconStyle: { 'width.px': 64, 'height.px': 61.53 } },
            })
            .afterClosed()
            .subscribe((acceptPriceChange: TzDialogReturn) => {
                if (acceptPriceChange !== CONFIRM) {
                    return;
                }

                this.cartService.applyPriceChange(priceChangeId).subscribe((success) => {
                    this.router.navigate(['/checkout']);
                });
            });
    }

    private showInvalidCartDialog(
        cartData: any,
        invalidItems: ICartItem[],
        cartItem: ICartItem,
        serviceName: string | undefined
    ) {
        const showSkip =
            cartData.items.length > invalidItems.length &&
            cartItem.serviceId !== ServiceId.FLIGHT &&
            cartItem.serviceId !== ServiceId.TRAIN &&
            cartItem.serviceId !== ServiceId.ACC;

        const messageKey = 'POPUPS.' + (showSkip ? 'INVALID_ITEM.BODY' : 'INVALID_ITEM.UPDATE_ONLY');
        const { showPriceChange, priceChangeServId, priceChangeId } = CartUtils.checkPriceChangeOnly(invalidItems);

        if (showPriceChange) {
            this.showPriceChangeDialog(priceChangeServId, priceChangeId, serviceName);
            return;
        }

        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.INVALID_ITEM.TITLE'),
                multipleContent: [this.translateService.instant(messageKey, { service: serviceName })],
                cancelBtnTitle: showSkip ? this.translateService.instant('POPUPS.INVALID_ITEM.SKIP') : undefined,
                confirmBtnTitle: this.translateService.instant('POPUPS.INVALID_ITEM.UPDATE'),
                icon: { name: 'decision-required-dialog', iconStyle: { 'width.px': 64, 'height.px': 61.53 } },
            })
            .afterClosed()
            .subscribe((removeCart: TzDialogReturn) => {
                if (removeCart === CONFIRM) {
                    this.cartRoutingService.changeState(cartItem.serviceId, cartItem, false, StateFrom.NONE);
                    return;
                }

                if (!showSkip) {
                    return;
                }

                let optionId = '';
                if (cartItem.serviceId === ServiceId.TRANSFER) {
                    optionId = cartItem.selectedItem.bookingOptions[0].optionId;
                }

                this.cartService.removeCartData(cartItem.serviceId, optionId).subscribe((success) => {
                    this.router.navigate(['/checkout']);
                });
            });
    }

    private beforeCheckout(cartData: any): Promise<any> {
        return new Promise((resolve, reject) => {
            if (!cartData || !cartData.items) {
                return reject('cart empty');
            }

            if (!this.validCart(cartData)) {
                let invalidItems = this.getInvalidItems(cartData);
                invalidItems = invalidItems.filter((v, i, a) => a.findIndex((t) => t.serviceId === v.serviceId) === i);
                if (invalidItems.length > 0) {
                    const serviceName = invalidItems.reduce(
                        (servicesTxt, item) =>
                            servicesTxt + (servicesTxt !== '' ? ', ' : '') + ServiceIdToName[item.serviceId],
                        ''
                    );
                    this.showInvalidCartDialog(cartData, invalidItems, invalidItems[0], serviceName);
                }
                return reject('invalid cart');
            }

            this.recheckCart(cartData, cartData.combId, cartData.deviceId, {
                resolve,
                reject,
            });
        });
    }
}
