import { Injectable } from '@angular/core';
import { Currency } from '@schemas/modules/core/currency';
import { BehaviorSubject } from 'rxjs';
import { StorageKeys } from '@schemas/modules/core/storage-keys';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class CurrencyService {
    private readonly defaultCurCode = 'EUR';
    private readonly allowedCurrencies: {
        [key in string]: Currency;
    } = {
        EUR: {
            code: 'EUR',
            name: 'Euro',
            trazlerSymbol: '€',
            symbol: '€',
            selectedLable: '€ EUR',
        },
        USD: {
            code: 'USD',
            name: 'US dollar',
            trazlerSymbol: 'US$',
            symbol: '$',
            selectedLable: '$ USD',
        },
    };

    private readonly selectedCur: BehaviorSubject<Currency>;

    constructor() {
        this.selectedCur = this.initCurrency();
    }

    get currencyCode(): string {
        return this.selectedCur.getValue().code;
    }

    get currency(): Currency {
        return this.selectedCur.getValue();
    }

    get selectedCurrencySubject(): BehaviorSubject<Currency> {
        return this.selectedCur;
    }

    get currencies(): Currency[] {
        const data: Currency[] = [];
        for (const curCode in this.allowedCurrencies) {
            if (this.allowedCurrencies[curCode]) {
                data.push(this.allowedCurrencies[curCode]);
            }
        }
        return data;
    }

    // set current cur as cur provided, default cur if no newCur provided
    public setCurrency(curCode?: string) {
        if (!curCode) {
            curCode = this.defaultCurCode;
        }

        if (curCode === this.currencyCode) {
            return;
        }

        const cur = this.allowedCurrencies[curCode];
        if (cur) {
            StorageManagerUtil.set(StorageKeys.CURRENCY_CODE, cur.code);
            this.selectedCur.next(cur);
        }
    }

    public setCartCurrency(curCode: string): void {
        StorageManagerUtil.set('cartCurrency', curCode);
        this.setCurrency(curCode);
    }

    private initCurrency(): BehaviorSubject<Currency> {
        let curCode = StorageManagerUtil.get(StorageKeys.CURRENCY_CODE);
        if (!curCode) {
            curCode = this.defaultCurCode;
        }

        const cartCur = StorageManagerUtil.get('cartCurrency');
        if (cartCur !== null && cartCur !== '') {
            curCode = cartCur;
        }

        return new BehaviorSubject(this.allowedCurrencies[curCode]);
    }
}
