import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { GeneralServiceTab, ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { TabUtils } from '@services/modules/service-tab/tab.utils';
import { Router } from '@angular/router';
import { ServiceId } from '@core/constants/service-id.const';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { PagesUrls } from '@core/constants/pages-urls';
import { PageUtils } from '@core/utils/page-utils';
import { TrainService } from '@services/modules/transport/trains/trains.service';
import { Subscription } from 'rxjs';
import { FlightsService } from '@services/modules/transport/flights/flights.service';

@Component({
    selector: 'tz-mob-service-tab',
    templateUrl: './mob-service-tab.component.html',
    styleUrls: ['./mob-service-tab.component.scss'],
})
export class MobServiceTabComponent implements OnInit, OnDestroy {
    @Input() service: ServiceTab | undefined;
    @Input() generalServiceTab: GeneralServiceTab | undefined;
    @Input() removable = true;
    @Input() selected = false;
    @Input() active = false;
    @Input() isResultTab = false;
    @Input() public cartSummary: CheckoutCartServiceSummary[] = [];
    @Output() toggleServiceTab: EventEmitter<void> = new EventEmitter<void>();
    @Output() removeServiceTab: EventEmitter<void> = new EventEmitter();
    @Output() clickServiceTab: EventEmitter<void> = new EventEmitter();

    public cartLabel!: string;
    public isDisplayedMinimalPriceFromPreSearch = false;
    public isDisplayedNoResult = false;
    public isDisplayedCartDataPrice = false;
    public serviceId = ServiceId;
    private isFlightPath!: boolean;
    private isTrainPath!: boolean;
    private subscription$: Subscription = new Subscription();

    constructor(private router: Router, private trainService: TrainService, private flightService: FlightsService) {}

    get serviceTextOpacity(): boolean {
        const isFirstPage = PageUtils.is(this.router.url, [PagesUrls.HOME, PagesUrls.SEARCH]);
        return (
            (!isFirstPage && !this.service?.isInError) ||
            (this.isEnable && !this.service?.isInError) ||
            (!isFirstPage && !this.isEnable)
        );
    }

    get isEnable(): boolean {
        return this.service && this.service.enable ? this.service?.enable : false;
    }

    get icon(): string {
        if (this.service) {
            if (this.service.serviceId === ServiceId.FLIGHT_TRAIN) {
                if (this.service.selectedService && this.active) {
                    if (this.service.secondMobIcon) {
                        const isFlightPath = PageUtils.is(this.router.url, PagesUrls.RESULTS_FLIGHTS);
                        return isFlightPath ? this.service.mobIcon : this.service.secondMobIcon;
                    }
                }
                const isTrain = !!this.cartSummary.find(
                    (cartItem: CheckoutCartServiceSummary) => cartItem.serviceId === ServiceId.TRAIN
                );
                if (isTrain) {
                    return this.active ? 'mob-train-service-tab' : 'mob-train-purple-service-tab';
                }
            }
            return this.service.mobIcon;
        }

        if (this.generalServiceTab) {
            return this.generalServiceTab.mobIcon;
        }

        return '';
    }

    get label(): string {
        if (
            this.service?.serviceId === ServiceId.FLIGHT_TRAIN &&
            this.cartSummary.find((cartItem: CheckoutCartServiceSummary) => cartItem.serviceId === ServiceId.TRAIN)
        ) {
            return (this.cartLabel = 'SERVICES.TRAIN');
        }

        if (this.service) {
            return this.service.mobLabel;
        }

        if (this.generalServiceTab) {
            return this.generalServiceTab.mobLabel;
        }

        return '';
    }

    get dataTestName(): string {
        if (this.service) {
            return this.service.name;
        }

        if (this.generalServiceTab) {
            return this.generalServiceTab.serviceKey;
        }

        return '';
    }

    get getSelectedItemPrice(): number {
        if (this.service) {
            if (this.isDisplayedCartDataPrice) {
                return this.service.selectedItemPrice;
            }

            if (this.service?.serviceId === ServiceId.FLIGHT_TRAIN && this.isDisplayedMinimalPriceFromPreSearch) {
                this.isFlightPath = PageUtils.is(this.router.url, PagesUrls.RESULTS_FLIGHTS);
                this.isTrainPath = PageUtils.is(this.router.url, PagesUrls.RESULTS_TRAINS);
                if (this.isTrainPath) {
                    return this.trainService?.minTrainPrice;
                }

                if (this.isFlightPath) {
                    return this.flightService.minFlightsPrice;
                }
            }

            return this.service.minPrice;
        }

        return 0;
    }

    get getSelectedItemPriceCur(): string {
        if (this.service) {
            return this.service.selectedItemPriceCur;
        }

        return '';
    }

    get getMinPriceCur(): string {
        if (this.service) {
            return this.service.minPriceCur;
        }

        return '';
    }

    public ngOnInit(): void {
        if (this.isResultTab) {
            this.initializeTabData();
        }
    }

    public onSelectService(): void {
        if (this.isResultTab) {
            this.clickServiceTab.emit();
        }

        if (!this.isResultTab) {
            this.toggleServiceTab.emit();
        }
    }

    public onRemoveService(): void {
        this.removeServiceTab.emit();
    }

    public ngOnDestroy(): void {
        this.subscription$.unsubscribe();
    }

    private initializeTabData(): void {
        if (this.service) {
            this.isDisplayedMinimalPriceFromPreSearch = TabUtils.displayMinimalPriceFromPreSearch(this.service);
            this.isDisplayedCartDataPrice = TabUtils.displayPriceFromCart(this.service);
            this.isDisplayedNoResult = TabUtils.displayNoResult(this.service);
        }
    }
}
