import { Pipe, PipeTransform } from '@angular/core';
import { FlightSummary } from '@schemas/modules/flights/flight-summary.model';
import { TranslateService } from '@ngx-translate/core';

@Pipe({
    name: 'layoverDescription',
})
export class LayoverDescriptionPipe implements PipeTransform {
    constructor(private translateService: TranslateService) {}

    transform(summary: FlightSummary): string {
        const count = summary.airportStopRoutes && summary.airportStopRoutes.length;
        let finalText = '';
        let city1 = '';
        let city2 = '';
        let airport1 = '';
        let airport2 = '';
        const bookAndText = this.translateService.instant('GENERAL.AND');

        if (count) {
            city1 = summary.airportStopRoutes[0]?.city;
            city2 = summary.airportStopRoutes[1]?.city;
            airport1 = summary.airportStopRoutes[0]?.airport;
            airport2 = summary.airportStopRoutes[1]?.airport;
        }

        switch (count) {
            case 1:
                if (summary.hasLayover === 2) {
                    finalText = this.translateService.instant('NIGHT_LAYOVER_IN') + ' ' + city1 + ', ' + airport1;
                }
                finalText = city1 + ', ' + airport1;
                break;
            case 2:
                finalText = city1 + ', ' + airport1 + ' ' + bookAndText + ' ' + city2 + ', ' + airport2;
                break;
            case 3:
                finalText = city1 + ', ' + city2 + ' ' + bookAndText + ' ' + summary.airportStopRoutes[2].city;
                break;
        }

        return finalText;
    }
}
