import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { PlatformService } from '@services/platform.service';
import { Router } from '@angular/router';
import { UserService } from '@services/modules/user/services/user.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { ServiceId } from '@core/constants/service-id.const';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
    selector: 'tz-main-header',
    templateUrl: './main-header.component.html',
    styleUrls: ['./main-header.component.scss'],
})
export class MainHeaderComponent implements OnInit, OnDestroy {
    public loggedInInfo!: CustomerInfo;

    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private router: Router,
        private cartRoutingService: CartRoutingService,
        public userService: UserService,
        public platformService: PlatformService
    ) {}

    ngOnInit() {
        this.getLoggedInCustomer();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public logOut(): void {
        this.userService.SignOut();

        if (this.router.url.includes('/customer') || this.router.url.includes('/bookings')) {
            this.router.navigate(['/search'], { skipLocationChange: false });
        } else {
            this.reloadAccDetails();
        }
    }

    public reloadAccDetails() {
        if (this.router.url.includes('/hotel/')) {
            this.cartRoutingService.reload(
                {
                    ...window.history.state,
                    name: '/hotel',
                    params: { id: this.router.url.split('/')[3] },
                },
                ServiceId.ACC_DETAILS,
                undefined
            );
        }
    }

    private getLoggedInCustomer(): void {
        if (this.userService.IsLogged()) {
            this.loggedInInfo = this.userService.getCustomerInformation();
        } else if (StorageManagerUtil.get('trazler_customer_token', true)) {
            this.userService.getCustomer(true).then(
                (customer: CustomerInfo) => {
                    this.loggedInInfo = customer;
                },
                (err: any) => {
                    this.loggedInInfo = {} as CustomerInfo;
                    console.log('Error fetching customer info: ', err);
                }
            );
        } else {
            this.loggedInInfo = {} as CustomerInfo;
        }

        this.userService.loggedIn$.pipe(takeUntil(this.destroy$)).subscribe((isLogged) => {
            if (isLogged) {
                this.loggedInInfo = this.userService.getCustomerInformation();
            }
        });
    }
}
