import { Component } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { ModalService } from '@services/modal.service';
import { LogInService } from '@services/services/log-in.service';
import { Router } from '@angular/router';
import { UserService } from '@services/modules/user/services/user.service';
import { MobSignInComponent } from '@shared/components/mobile/mob-sign-in/mob-sign-in.component';

@Component({
    selector: 'tz-mob-auth',
    templateUrl: './mob-auth.component.html',
    styleUrls: ['./mob-auth.component.scss'],
})
export class MobAuthComponent {
    public showSignin = true;
    public fromCheckout = false;
    public errorMsg = '';

    constructor(
        private menu: MenuController,
        private modalService: ModalService,
        private logInService: LogInService,
        private router: Router,
        private userService: UserService
    ) {}

    public openSignIn(): void {
        this.userService.openLoginModel().then((res) => {
            if (res) {
                this.closeSignInMenu();
            }
        });
    }

    public openSignUp(loginMailId?: string, fromCheckout?: boolean): void {
        this.modalService
            .createMainModal(MobSignInComponent, {
                data: { loginMailId, showSignin: false, fromCheckout },
                title: 'ACCOUNT.SIGN.SIGN_UP',
            })
            .then(() => {});
    }

    public loginWithSocialMedia = (media: string) => this.logInService.loginWithSocialMedia(media, this.modalService);

    public onClosePage(): void {
        this.closeSignInMenu();
    }

    private closeSignInMenu(): void {
        this.menu.close('authMenu').then(() => {});
    }
}
