import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-sign-up-form',
    templateUrl: './sign-up-form.component.html',
    styleUrls: ['./sign-up-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SignUpFormComponent implements OnInit {
    @Input() formProcessing = false;
    @Input() buttonTxt = 'FOOTER.SUBSCRIBE';
    @Output() formSubmit: EventEmitter<string> = new EventEmitter<string>();

    public form!: FormGroup;
    public formSubmitted = false;

    constructor(public platformService: PlatformService, private formBuilder: FormBuilder) {}

    public get email() {
        return this.form.get('email');
    }

    ngOnInit() {
        this.initForm();
    }

    public onFormSubmit(): void {
        this.formSubmitted = true;

        if (this.form.invalid) {
            return;
        }

        this.formSubmit.emit(this.form.value.email);
    }

    private initForm(): void {
        this.form = this.formBuilder.group({
            email: ['', [Validators.required, Validators.pattern(ValidatorPatterns.EMAIL)]],
        });
    }
}
