import { Resolve, Router, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable, Subscriber } from 'rxjs';
import { Injectable } from '@angular/core';

import { UserDataService } from '@services/modules/user/dataservices/user.data.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { PlatformService } from '@services/platform.service';
import { ModalService } from '@services/modal.service';
import { MobResetPasswordComponent } from '@shared/components/mobile/mob-reset-password/mob-reset-password.component';

@Injectable({
    providedIn: 'root',
})
export class PasswordTokenValidation implements Resolve<string> {
    constructor(
        private userDataService: UserDataService,
        private router: Router,
        private translateService: TranslateService,
        private dialogService: DialogService,
        private platformService: PlatformService,
        private modalService: ModalService
    ) {}

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<string> {
        return new Observable<string>((subscriber: Subscriber<string>) => {
            const token = route.params['token'];
            const invalidToken = () => {
                this.router.navigate(['search']).then(() => {
                    this.dialogService.openSnackBarDialog(this.translateService.instant('NOTIFS.INVALID_TOKEN'));
                });
                subscriber.error('Invalid token');
                subscriber.complete();
            };

            if (!token || token === '') {
                invalidToken();
                return;
            }

            this.userDataService.checkPasswordToken(token).subscribe(
                (response) => {
                    if (this.platformService.isMobileDevice) {
                        this.router.navigate(['search']).then(() => {
                            this.modalService.createMainModal(MobResetPasswordComponent, {
                                data: { token },
                                title: 'ACCOUNT.PASSWORD_',
                            });
                        });
                    } else {
                        subscriber.next(response.customerMail);
                    }
                    subscriber.complete();
                },
                () => invalidToken()
            );
        });
    }
}
