export enum CartStatus {
    STATUS_PLACED = -1,
    STATUS_CREATED = 0,
    STATUS_PROGRESS = 1,
    STATUS_CONFIRMED = 2,
    STATUS_COMPLETED = 3,
    STATUS_CPROGRESS = 4,
    STATUS_CANCELLED = 5,
    STATUS_HOLD = 6,
    STATUS_REJECTED = 7,
    STATUS_PRICE_CHANGED = 8,
    STATUS_INVALID = 9,
}

export const STATUS_COLORS: {
    [key in CartStatus]?: string;
} = {};
STATUS_COLORS[CartStatus.STATUS_PLACED] = 'status-progress';
STATUS_COLORS[CartStatus.STATUS_CREATED] = 'status-progress';
STATUS_COLORS[CartStatus.STATUS_PROGRESS] = 'status-progress';
STATUS_COLORS[CartStatus.STATUS_CONFIRMED] = 'status-confirmed';
STATUS_COLORS[CartStatus.STATUS_COMPLETED] = 'status-confirmed';
STATUS_COLORS[CartStatus.STATUS_CPROGRESS] = 'status-cancelling';
STATUS_COLORS[CartStatus.STATUS_CANCELLED] = 'status-canceled';
STATUS_COLORS[CartStatus.STATUS_REJECTED] = 'status-canceled';
STATUS_COLORS[CartStatus.STATUS_INVALID] = 'status-canceled';

//WARNING: These values must stay in sync with server cache side values!
export enum PaymentCaptureMode {
    NOT_DONE = -1,
    NORMAL_MODE = 0,
    AUTO_MODE = 1,
    MANUAL_MODE = 2,
}

