import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';

@Component({
    selector: 'tz-dropdown',
    templateUrl: './dropdown.component.html',
    styleUrls: ['./dropdown.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DropdownComponent implements OnInit, OnChanges {
    @Input() label = '';
    @Input() selectedOptionKey = '';
    @Input() options: any[] = [];
    @Input() optionKey = '';
    @Input() labelKey = '';
    @Input() labelSelectKey = '';
    @Input() labelStartKey = '';
    @Input() labelIconKey = '';
    @Input() dropArrowDownIcon = 'drop-arrow-down-fill';
    @Input() dropArrowUpIcon = 'drop-arrow-up-fill';
    @Input() dropdownType: 'text' | 'block' = 'text';
    @Input() placement = 'bottom-left';
    @Input() className = '';
    @Input() svgStyle: { [key: string]: string | number } = {};
    @Output() selectChange: EventEmitter<any> = new EventEmitter<any>();

    public selectedOptionsLabel = '';

    ngOnInit(): void {
        if (!this.labelSelectKey) {
            this.labelSelectKey = this.labelKey;
        }

        this.setSelectedValue();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['selectedOptionKey']) {
            this.setSelectedValue();
        }
    }

    public onSelectChange(option: any): void {
        this.selectedOptionKey = option[this.optionKey];
        this.selectedOptionsLabel = option[this.labelSelectKey];
        this.selectChange.emit(option);
    }

    public setSelectedValue(): void {
        this.options.map((option) => {
            if (option[this.optionKey] === this.selectedOptionKey) {
                this.selectedOptionsLabel = option[this.labelSelectKey];
            }
        });
    }
}
