import { Injectable } from '@angular/core';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ServiceId, ServiceIdStr } from '@core/constants/service-id.const';
import { SearchEvents } from '@core/constants/search-events';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { SERVICE_DEFAULT_SORT } from '@core/constants/default-sort';
import { IPreSearchResult, ISSIBase } from '@schemas/modules/search/search';
import { MetaDataExt } from '@schemas/modules/search/meta';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { CombinationId } from '@schemas/modules/core/base-types';
import { CombServiceIds } from '@core/constants/comb-service-ids';

export interface ITripQuery {
    combId: CombinationId;
    servicesIds: CombServiceIds;
    deviceId: string;
    lang: string;
}

@Injectable({
    providedIn: 'root',
})
export class SearchMetadataService {
    constructor(
        private searchDataService: SearchDataService,
        private apiAccessService: ApiAccessService,
        private tabRoutingService: TabRoutingService,
        private cartRoutingService: CartRoutingService
    ) {}

    public parseTripUrl(url: string): ITripQuery {
        const queryParams = new URLSearchParams(url);
        return {
            combId: queryParams.get('combId') ? queryParams.get('combId') : '',
            deviceId: queryParams.get('deviceId') ? queryParams.get('deviceId') : '',
            lang: queryParams.get('lang') ? queryParams.get('lang') : '',
            servicesIds: queryParams.get('services') ? queryParams.get('services') : CombServiceIds.INVALID,
        } as ITripQuery;
    }

    public openTrip(tripQuery: ITripQuery): Promise<any> {
        this.searchDataService.saveInfos(
            {
                combId: tripQuery.combId,
                searchData: {},
                passengers: {
                    rooms: 1,
                    infantAges: [] as number[],
                    childrenAges: [] as number[],
                    youthAges: [] as number[],
                },
                context: {
                    services: tripQuery.servicesIds,
                    runMode: this.searchDataService.RUN_FAST,
                    travelType: 'roundTrip',
                },
            },
            false
        );
        return this.fillFastRunMeta().then(() => {
            this.cartRoutingService.changeState(ServiceId.ACC, undefined, false, undefined);
        });
    }

    public fillFastRunMeta(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            this.apiAccessService.getAllMetaRes(this.searchDataService.getCombId()).then(
                (metaRes: { [key in ServiceIdStr]: IPreSearchResult }) => {
                    this.searchDataService.resetSearchResults();
                    let serviceId: ServiceIdStr;

                    for (serviceId in metaRes) {
                        if (metaRes.hasOwnProperty(serviceId)) {
                            this.saveSearch(metaRes[serviceId], SearchEvents.EVENT_TRIP_PLANNER);
                        }
                    }
                    this.tabRoutingService.initRouting(this.searchDataService.searchInfos.context.services, false);
                    resolve();
                },
                (error: any) => {
                    console.error(error);
                    reject(error);
                }
            );
        });
    }

    public saveTripPlannerSearchInfos(res: ISSIBase, serviceId: ServiceId) {
        this.searchDataService.saveServicesInfos(res, serviceId);
    }

    public saveSearch(res: IPreSearchResult, eventId?: any): void {
        if (!res || !res.metaData) {
            return;
        }
        console.log('Saving trip planner info for :' + res.metaData.serviceId, { res });
        // if (res.metaData.complete) {
        //    this.tripPlannerService.setComplete(res.metaData.serviceId);
        // }
        res.serviceSearch.passengers.total =
            res.serviceSearch.passengers.adults +
            res.serviceSearch.passengers.children +
            res.serviceSearch.passengers.infants;
        this.searchDataService.saveServicesInfos(res.serviceSearch, res.metaData.serviceId);
        if (res.metaData.serviceId === ServiceId.FLIGHT || res.metaData.serviceId === ServiceId.TRAIN) {
            this.searchDataService.saveServicesInfos(res.serviceSearch, ServiceId.FLIGHT_TRAIN);
        }
        this.saveMeta(res.metaData, eventId);
    }

    private saveMeta(metaData: MetaDataExt, eventId: any): void {
        console.log('Saving metadata for :' + metaData.serviceId);

        switch (metaData.serviceId) {
            case ServiceId.TRAIN:
            case ServiceId.FLIGHT:
                const params = {
                    dataId: metaData.dataId,
                    resultLength: metaData.count,
                    nbOnwards: metaData.nbOnwards,
                    nbReturns: metaData.nbReturns,
                    bestValueFlight: metaData.bestValueFlight ? metaData.bestValueFlight : '',
                    recommendedItemPrice: metaData.recommendedItemPrice,
                    recommendedItemPriceCur: metaData.recommendedItemPriceCur,
                    serviceType: metaData.serviceId === ServiceId.TRAIN ? 'train' : 'transport',
                    sortBy: SERVICE_DEFAULT_SORT[metaData.serviceId as ServiceId],
                };

                this.searchDataService.saveResult(metaData.serviceId, params, eventId);
                break;

            case ServiceId.CAR_RENTAL:
                this.searchDataService.saveResult(
                    ServiceId.CAR_RENTAL,
                    {
                        dataId: metaData.dataId,
                        resultLength: metaData.count,
                        recommendedItemPrice: metaData.recommendedItemPrice,
                        recommendedItemPriceCur: metaData.recommendedItemPriceCur,
                        showMap: metaData.showMap,
                        sortBy: SERVICE_DEFAULT_SORT[metaData.serviceId as ServiceId],
                    },
                    eventId
                );
                break;

            case ServiceId.TRANSFER:
                this.searchDataService.saveResult(
                    ServiceId.TRANSFER,
                    {
                        dataId: metaData.dataId,
                        resultLength: metaData.count,
                        serviceType: metaData.serviceType,
                        onwardResultsLength: metaData.nbOnwards,
                        returnResultsLength: metaData.nbReturns,
                        recommendedItemPrice: metaData.recommendedItemPrice,
                        recommendedItemPriceCur: metaData.recommendedItemPriceCur,
                        sortBy: SERVICE_DEFAULT_SORT[metaData.serviceId as ServiceId],
                    },
                    eventId
                );
                break;

            case ServiceId.ACC:
                // req.dataID = metaData.dataID;
                this.searchDataService.saveResult(
                    ServiceId.ACC,
                    {
                        dataId: metaData.dataId,
                        recommendedItemPrice: metaData.recommendedItemPrice,
                        recommendedItemPriceCur: metaData.recommendedItemPriceCur,
                        count: metaData.count,
                        resultLength: metaData.count,
                        nbOnwards: metaData.nbOnwards,
                        nbReturns: metaData.nbReturns,
                        complete: metaData.complete,
                        sortBy: SERVICE_DEFAULT_SORT[metaData.serviceId as ServiceId],
                        info: metaData.info, // @todo: [Remove] temporarily added to see original count of provider
                    },
                    eventId
                );
                break;
            default:
        }
    }
}
