import { Injectable } from '@angular/core';
import { EngineRuleService } from '@services/searchEngine/services/engine-rule-service';
import { ToolsService } from '@core/utils/tools-service';
import { LinkedFieldInfo, SearchAddress } from '@schemas/modules/core/search-address';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { StorageKeys } from '@schemas/modules/core/storage-keys';

@Injectable({
    providedIn: 'root',
})
export class RecentSearchService {
    private readonly VERSION = 1.2;
    private readonly MAX_RECENT = 3;

    private recentAddress: any = {
        version: this.VERSION,
        data: {},
    };

    constructor(private engineRuleService: EngineRuleService, private toolsService: ToolsService) {
        this.initRecentSearch();
    }

    private static getUniqueKey(addr: SearchAddress): string {
        if (!addr) {
            return '';
        }

        return addr.placeId + addr.placeType + addr.code + addr.name;
    }

    private static isSameAddress(addr1: SearchAddress, addr2: SearchAddress): boolean {
        return RecentSearchService.getUniqueKey(addr1) === RecentSearchService.getUniqueKey(addr2);
    }

    private static isOutsideRange(loc: SearchAddress, dep: LinkedFieldInfo): boolean {
        if (!dep.lat || !dep.lng || !loc.latitude || !loc.longitude) {
            return false;
        }

        return ToolsService.distance(loc.latitude, loc.longitude, dep.lat, dep.lng) > dep.radius;
    }

    public setRecentAddress(selectedLocation: SearchAddress, providerBool: boolean, filterType: string): void {
        if (!selectedLocation) {
            return;
        }

        const fieldName = selectedLocation.inputName;
        const provider = providerBool.toString();

        if (!this.recentAddress.data[provider]) {
            this.recentAddress.data[provider] = {};
        }

        if (!this.recentAddress.data[provider][fieldName]) {
            this.recentAddress.data[provider][fieldName] = {};
        }

        if (!this.recentAddress.data[provider][fieldName][filterType]) {
            this.recentAddress.data[provider][fieldName][filterType] = [];
        }

        const recentContent = this.recentAddress.data[provider][fieldName][filterType];
        if (!this.isInCache(selectedLocation, fieldName, provider, filterType)) {
            selectedLocation.isRecent = true;
            recentContent.unshift(selectedLocation);
        } else {
            recentContent.forEach((address: SearchAddress) => {
                address.recentSelected = false;
                address.recentSelected = RecentSearchService.isSameAddress(address, selectedLocation);
            });

            this.recentAddress.data[provider][fieldName][filterType] = recentContent;
        }
    }

    public storeRecentAddress(searchInfos: any, services: { [key: string]: boolean }, travelType: string): void {
        const conf = EngineRuleService.buildSearchFieldsObj(services, travelType);
        if (!conf) {
            return;
        }

        for (const confContent of conf) {
            for (const fieldName in confContent) {
                if (!confContent.hasOwnProperty(fieldName)) {
                    continue;
                }

                switch (fieldName) {
                    case 'from':
                    case 'to':
                    case 'address':
                        if (
                            searchInfos.searchData[fieldName] &&
                            EngineRuleService.checkLoc(searchInfos.searchData[fieldName], false)
                        ) {
                            this.validateAddress(
                                fieldName,
                                confContent[fieldName].provider,
                                confContent[fieldName].type
                            );
                        }
                        break;
                    default:
                }
            }
        }

        this.removeInvalidAddresses();
        this.limitMaxAddressLength();
        StorageManagerUtil.set(StorageKeys.RECENT_ADDR, this.recentAddress);
    }

    public getRecentAddress(
        provider: string,
        filterType: string,
        fieldName: string,
        linkedField?: LinkedFieldInfo
    ): SearchAddress[] {
        const noAddressFound =
            !this.recentAddress.data[provider] ||
            !this.recentAddress.data[provider][fieldName] ||
            !this.recentAddress.data[provider][fieldName][filterType];
        if (noAddressFound) {
            return [];
        }

        const recent = [];
        const recentContent = ToolsService.deepCopy(this.recentAddress.data[provider][fieldName][filterType]);

        for (const k in recentContent) {
            if (recentContent[k]) {
                const nonValidatedAddress =
                    recentContent[k].isRecent || !recentContent[k].isCached || recentContent[k].inputName !== fieldName;
                if (nonValidatedAddress) {
                    continue;
                }

                const sameLocation = linkedField && linkedField.placeId === recentContent[k].placeId;
                const outsideRange =
                    linkedField &&
                    linkedField.radius !== 0 &&
                    RecentSearchService.isOutsideRange(recentContent[k], linkedField);
                if (sameLocation || outsideRange) {
                    continue;
                }

                recent.push(recentContent[k]);
            }
        }
        return recent;
    }

    private validateAddress(fieldName: string, provider: string, filterType: string): void {
        const addressCached =
            this.recentAddress.data[provider] &&
            this.recentAddress.data[provider][fieldName] &&
            this.recentAddress.data[provider][fieldName][filterType];
        if (addressCached) {
            const recentContent = this.recentAddress.data[provider][fieldName][filterType];

            for (const recent of recentContent) {
                if (recent && recent.isRecent) {
                    recent.isRecent = false;
                }
            }
        } else {
            console.error('validating an address which is not in recent:' + fieldName);
        }
    }

    private removeInvalidAddresses(): void {
        for (const provider in this.recentAddress.data) {
            if (!this.recentAddress.data.hasOwnProperty(provider)) {
                continue;
            }

            for (const fieldName in this.recentAddress.data[provider]) {
                if (!this.recentAddress.data[provider].hasOwnProperty(fieldName)) {
                    continue;
                }

                for (const filterType in this.recentAddress.data[provider][fieldName]) {
                    if (!this.recentAddress.data[provider][fieldName].hasOwnProperty(filterType)) {
                        continue;
                    }

                    let recentContent = this.recentAddress.data[provider][fieldName][filterType];
                    for (const recent of recentContent) {
                        if (!recent || !recent.recentSelected) {
                            continue;
                        }

                        const selectedAddress = ToolsService.deepCopy(recent);
                        delete selectedAddress.recentSelected;

                        const temp = [selectedAddress];
                        recentContent.forEach((address: SearchAddress) => {
                            if (!RecentSearchService.isSameAddress(address, selectedAddress)) {
                                temp.push(address);
                            }
                        });
                        recentContent = temp;

                        this.recentAddress.data[provider][fieldName][filterType] = recentContent;
                    }

                    for (let i = 0; i < recentContent.length; i++) {
                        if (!recentContent[i]) {
                            delete recentContent[i];
                        } else if (recentContent[i].isRecent) {
                            delete recentContent[i];
                        } else {
                            recentContent[i].isCached = true;
                        }
                    }
                }
            }
        }
    }

    private isInCache(
        selectedLocation: SearchAddress,
        fieldName: string,
        provider: string,
        filterType: string
    ): boolean {
        const recentContent = this.recentAddress.data[provider][fieldName][filterType];
        for (let i = 0; i < recentContent.length; i++) {
            if (!recentContent[i] || !recentContent[i].isCached) {
                delete recentContent[i];
            } else if (RecentSearchService.isSameAddress(recentContent[i], selectedLocation)) {
                // recentContent[i] = selectedLocation;
                return true;
            }
        }

        return false;
    }

    private limitMaxAddressLength(): void {
        for (const provider in this.recentAddress.data) {
            if (!this.recentAddress.data.hasOwnProperty(provider)) {
                continue;
            }

            for (const fieldName in this.recentAddress.data[provider]) {
                if (!this.recentAddress.data[provider].hasOwnProperty(fieldName)) {
                    continue;
                }

                for (const filterType in this.recentAddress.data[provider][fieldName]) {
                    if (!this.recentAddress.data[provider][fieldName].hasOwnProperty(filterType)) {
                        continue;
                    }

                    const recentContent = this.recentAddress.data[provider][fieldName][filterType];
                    for (let i = 0; i < recentContent.length; ) {
                        if (!recentContent[i]) {
                            recentContent.splice(i, 1);
                        } else {
                            i++;
                        }
                    }

                    if (recentContent.length > this.MAX_RECENT) {
                        recentContent.splice(this.MAX_RECENT, recentContent.length);
                    }
                }
            }
        }
    }

    private initRecentSearch(): void {
        const recentAddrTmp = StorageManagerUtil.get(StorageKeys.RECENT_ADDR);
        if (!recentAddrTmp) {
            return;
        }

        if (recentAddrTmp && recentAddrTmp.version && recentAddrTmp.version === this.VERSION) {
            this.recentAddress = recentAddrTmp;
            this.limitMaxAddressLength();
        }
    }
}
