import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DialogService } from '@services/dialog.service';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { environment } from '@environments/environment';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { UserService } from '@services/modules/user/services/user.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModalService } from '@services/modal.service';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class LogInService {
    constructor(
        private trazlerAPI: ApiAccessService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private userService: UserService
    ) {}

    public static createLoginForm(): FormGroup {
        return new FormGroup({
            emailID: new FormControl('', [Validators.required, Validators.pattern(ValidatorPatterns.EMAIL)]),
            password: new FormControl('', [
                Validators.required,
                Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,}$'),
            ]),
        });
    }

    public static guid(): string {
        const getRd = () =>
            Math.floor((1 + Math.random()) * 0x10000)
                .toString(16)
                .substring(1);

        return getRd() + getRd() + '-' + getRd() + '-' + getRd() + '-' + getRd() + '-' + getRd() + getRd() + getRd();
    }

    public loginWithSocialMedia(media: string, activeModal: NgbActiveModal | ModalService) {
        const dataKey = LogInService.guid();
        this.trazlerAPI.GenerateLoginToken(dataKey).then(
            (data: string) => {
                StorageManagerUtil.set('trazler_socialLogin_token', dataKey, true);
                StorageManagerUtil.set('trazler_socialLogin_close_window', 'true', true);

                const serverUrl = `${
                    environment.baseUrl
                }/socialMediaAuth/${media}?time=${new Date().getMilliseconds()}&host=${
                    environment.uiUrl
                }/&dataKey=${dataKey}&loginToken=${data}`;

                const popupWinWidth = screen.width > 600 ? 600 : screen.width;
                const popupWinHeight = 500;
                const left = (screen.width - popupWinWidth) / 2;
                const top = (screen.height - popupWinHeight) / 4;
                const win = window.open(
                    serverUrl,
                    'popup',
                    'left=' +
                        left +
                        ',top=' +
                        top +
                        ',menubar=no,status=no,titlebar=yes,toolbar=no,scrollbars=no,location=no,width=' +
                        popupWinWidth +
                        ',height=' +
                        popupWinHeight
                );

                const timer = setInterval(() => {
                    if (!win) {
                        clearInterval(timer);
                        return;
                    }

                    if (win.closed) {
                        this.makeSignInForSocialMedia(activeModal);
                        StorageManagerUtil.remove('trazler_socialLogin_close_window');
                        clearInterval(timer);
                    }
                }, 1000);
            },
            (err) => {
                console.log('Login token error: ', err);
            }
        );
    }

    public proceedSignIn(
        signIn: boolean,
        customer: { emailID: string; password: string },
        activeModal: NgbActiveModal | ModalService
    ): Promise<any> {
        if (signIn) {
            return this.signIn(customer, activeModal);
        }

        return this.registerAccount(customer, activeModal);
    }

    private makeSignInForSocialMedia(activeModal: NgbActiveModal | ModalService): void {
        const socialLoginToken = StorageManagerUtil.get('trazler_socialLogin_token', true);
        if (socialLoginToken === null) {
            return;
        }
        StorageManagerUtil.remove('trazler_socialLogin_token');

        this.trazlerAPI.SocialMediaSignIn(socialLoginToken).then(
            (resp: { token: string; customer: CustomerInfo }) => {
                StorageManagerUtil.set('trazler_customer_token', resp.token, true);
                this.userService.getCustomer(true);
                if (activeModal instanceof NgbActiveModal) {
                    activeModal.close(resp.customer);
                } else {
                    activeModal.dismiss(resp.customer);
                }
            },
            (err) => {
                console.log('Auth error: ', err);
            }
        );
    }

    private signIn(customer: { emailID: string; password: string }, activeModal: NgbActiveModal | ModalService) {
        const signIn = this.trazlerAPI.SignIn(customer);
        signIn.then(
            (data: { token: string }) => {
                StorageManagerUtil.set('trazler_customer_token', data.token, true);

                this.userService.getCustomer(true).then(
                    (customerInfo: CustomerInfo) => {
                        if (activeModal instanceof NgbActiveModal) {
                            activeModal.close(customerInfo);
                        } else {
                            activeModal.dismiss(customerInfo);
                        }
                    },
                    (err: any) => {
                        console.log('Error fetching customer info: ', err);
                    }
                );
            },
            (err) => {
                console.log('Auth error: ', err);
            }
        );
        return signIn;
    }

    private registerAccount(
        customer: { emailID: string; password: string },
        activeModal: NgbActiveModal | ModalService
    ) {
        const register = this.trazlerAPI.Register(customer);
        register.then(
            () => {
                activeModal.dismiss({} as CustomerInfo);
                this.dialogService.openConfirmationDialog({
                    title: this.translateService.instant('POPUPS.REGISTRATION_MAIL.TITLE'),
                    multipleContent: [
                        this.translateService.instant('POPUPS.REGISTRATION_MAIL.BODY', {
                            emailaddr: customer.emailID,
                        }),
                    ],
                    confirmBtnTitle: this.translateService.instant('POPUPS.REGISTRATION_MAIL.BTN'),
                    icon: { name: 'registration-dialog', iconStyle: { 'width.px': 55.5, 'height.px': 55.49 } },
                });
            },
            (err) => {
                console.log('Auth error: ', err);
            }
        );

        return register;
    }
}
