import { LANGUAGES } from '@core/constants/languages.const';
import { StorageKeys } from '@schemas/modules/core/storage-keys';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root',
})
export class StorageManagerUtil {
    public static readonly LOCAL = 0;
    public static readonly SESSION = 1;
    public static readonly ACCEPT_UNDEFINED = null;
    public static readonly ACCEPT_REQUIRED = 1;
    public static readonly ACCEPT_ALL = 2;
    private static readonly languages = LANGUAGES;
    private static readonly getHooks: any = {
        selected_language: (language: any) => {
            if (language === null) {
                if (window.location.host !== 'trazler.com') {
                    const browserLanguages = StorageManagerUtil.getBrowserLocales({ languageCodeOnly: true });
                    language = StorageManagerUtil.languages.list.find((lang: any) =>
                        browserLanguages ? lang.code === browserLanguages[0] : false
                    );
                    console.log({ language });
                }
                if (!language) {
                    language = StorageManagerUtil.languages.list[0];
                }
            }
            return language;
        },
    };
    private static readonly cookieMap: { [key in string]: { p?: number; s?: number; r: boolean; d?: string } } = {
        searchInfos: { p: StorageManagerUtil.SESSION, r: true }, // r true when required, false when optional
        searchInfosLocal: { p: StorageManagerUtil.LOCAL, r: true },
        trazler_customer_token: { p: StorageManagerUtil.LOCAL, r: true },
        deviceId: { p: StorageManagerUtil.SESSION, r: true },
        xsfrToken: { p: StorageManagerUtil.SESSION, r: true },
        cookie_accept: { p: StorageManagerUtil.LOCAL, r: true },
        //'XSRF-TOKEN': {p: cookie, r: true}, handled by Angular
        selected_currency: { p: StorageManagerUtil.LOCAL, r: true },
        verifiedMail: { p: StorageManagerUtil.SESSION, r: true },
        cartCurrency: { p: StorageManagerUtil.SESSION, r: true },
        bookingId: { p: StorageManagerUtil.SESSION, r: true },
        booking_DetailID: { s: StorageManagerUtil.LOCAL, r: true },
        _prevState: { p: StorageManagerUtil.SESSION, r: true },
        latestBookingId: { p: StorageManagerUtil.SESSION, r: true },
        skipSignIn: { p: StorageManagerUtil.SESSION, r: true },
        cardMiniInfo: { p: StorageManagerUtil.SESSION, r: true },
        preSearchInfos: { p: StorageManagerUtil.SESSION, r: true },
        searchServiceInfos: { p: StorageManagerUtil.SESSION, r: true },
        searchResults: { p: StorageManagerUtil.SESSION, r: true },
        sessionEndTime: { p: StorageManagerUtil.SESSION, r: true },
        pwdPrevPath: { p: StorageManagerUtil.SESSION, r: true },
        _login_redirect: { p: StorageManagerUtil.SESSION, r: true },
        trazler_admin_token: { p: StorageManagerUtil.LOCAL, r: true },
        trazler_socialLogin: { p: StorageManagerUtil.LOCAL, r: true },
        trazler_socialLogin_token: { p: StorageManagerUtil.LOCAL, r: true },
        trazler_socialLogin_close_window: { p: StorageManagerUtil.LOCAL, r: true },
        rental_map_center: { p: StorageManagerUtil.SESSION, r: true },
        hotel_map_center: { p: StorageManagerUtil.SESSION, r: true },
        activeTransfer: { p: StorageManagerUtil.SESSION, r: true },
        searchInfos_l: { p: StorageManagerUtil.LOCAL, r: true },
        _walk_through: { p: StorageManagerUtil.LOCAL, r: true },
        selected_language: { p: StorageManagerUtil.LOCAL, r: true, d: 'EUR' }, //d : default value when cookie rejected
        recentAddr: { p: StorageManagerUtil.LOCAL, r: true, d: '{}' },
        _recommended: { p: StorageManagerUtil.LOCAL, r: true, d: '{}' },
        ip_country: { p: StorageManagerUtil.LOCAL, r: true, d: '{}' },
        installTzAlertShown: { p: StorageManagerUtil.SESSION, r: true },
        checkoutFormData: { p: StorageManagerUtil.LOCAL, r: true },
        resultSortBy0: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy1: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy2: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy3: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy4: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy5: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy6: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy7: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy8: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy101: { p: StorageManagerUtil.SESSION, r: true },
        resultSortBy999: { p: StorageManagerUtil.SESSION, r: true },
        mob_landing_page_shown: { p: StorageManagerUtil.LOCAL, r: true },
        destination: { p: StorageManagerUtil.SESSION, r: true }, // store current destination data (for page refresh)
    };
    public cookieAcceptStatus: number | null = StorageManagerUtil.ACCEPT_UNDEFINED;

    constructor() {
        this.cookieAcceptStatus = StorageManagerUtil.get(StorageKeys.COOKIE, true);
    }

    public static clear(storageType: number) {
        switch (storageType) {
            case StorageManagerUtil.LOCAL:
                localStorage.clear();
                break;
            case StorageManagerUtil.SESSION:
                sessionStorage.clear();
                break;
            default:
                console.error('unknown storage type to clear');
        }
    }

    public static remove(key: string) {
        switch (StorageManagerUtil.cookieMap[key].p) {
            case StorageManagerUtil.SESSION:
                return window.sessionStorage.removeItem(key);
            case StorageManagerUtil.LOCAL:
                return window.localStorage.removeItem(key);
            /*case cookie:
                return $cookies.remove(key);*/
            default:
                console.error('unknown cookie key ' + key);
        }
    }

    public static set(key: string, value: any, noJson?: boolean): void {
        const cookSetting = StorageManagerUtil.cookieMap[key];
        if (!cookSetting) {
            console.error('invalid cookie key:' + key);
        }
        if (!cookSetting.r) {
            return;
        }
        return StorageManagerUtil.setInternal(key, value, cookSetting, noJson);
    }

    public static setInternal(key: string, value: any, cookSetting: any, noJson?: boolean): void {
        const decodeFunc = noJson ? StorageManagerUtil.noop : JSON.stringify;
        switch (cookSetting.p) {
            case StorageManagerUtil.SESSION:
                return window.sessionStorage.setItem(key, decodeFunc(value));
            case StorageManagerUtil.LOCAL:
                return window.localStorage.setItem(key, decodeFunc(value));
            /*case cookie:
                return $cookies.put(key, value);*/
            default:
                console.error('unknown cookie key ' + key);
        }
    }

    public static getCookie(): any | null {
        if (window.location.search) {
            return this.ACCEPT_ALL;
        }
        return StorageManagerUtil.get(StorageKeys.COOKIE, true);
    }

    public static get(key: string, noJson?: boolean): any | null {
        const cookSetting = StorageManagerUtil.cookieMap[key];

        if (!cookSetting) {
            console.error('invalid cookie key:' + key);
        }
        if (!cookSetting.r) {
            return cookSetting.d ? cookSetting.d : null;
        }

        return StorageManagerUtil.getInternal(key, cookSetting, noJson);
    }

    public static getMobLandingPageIsShown(): any | null {
        return StorageManagerUtil.get(StorageKeys.MOB_LANDING_PAGE_SHOWN, true);
    }

    public static setLandingPageIsShown(): void {
        return StorageManagerUtil.set(StorageKeys.MOB_LANDING_PAGE_SHOWN, 1, true);
    }

    private static getBrowserLocales(options = {}) {
        const defaultOptions = {
            languageCodeOnly: false,
        };

        const opt = {
            ...defaultOptions,
            ...options,
        };

        const browserLocales = navigator.languages === undefined ? [navigator.language] : navigator.languages;

        if (!browserLocales) {
            return undefined;
        }

        return browserLocales.map((locale) => {
            const trimmedLocale = locale.trim();

            return opt.languageCodeOnly ? trimmedLocale.split(/-|_/)[0] : trimmedLocale;
        });
    }

    private static getInternal(key: string, cookSetting: any, noJson?: boolean): any | string | null {
        let val: string | null = null;

        switch (cookSetting.p) {
            case StorageManagerUtil.SESSION:
                val = window.sessionStorage.getItem(key);
                break;
            case StorageManagerUtil.LOCAL:
                val = window.localStorage.getItem(key);
                break;
            /*case cookie:
                return $cookies.get(key);*/
            default:
                console.error('unknown cookie key ' + key);
        }

        if (val && !noJson) {
            val = JSON.parse(val);
        }
        if (StorageManagerUtil.getHooks[key] && typeof StorageManagerUtil.getHooks[key] === 'function') {
            return StorageManagerUtil.getHooks[key](val);
        } else {
            return val;
        }
    }

    private static noop = (val: any): any => val;

    public setCookie(value: any): void {
        this.cookieAcceptStatus = value;

        return StorageManagerUtil.set(StorageKeys.COOKIE, value, true);
    }

    //setOptional and getOptional are checking the cookie acceptance
    // use these functions to access cookie values which are not required
    public setOptional(key: string, value: any, noJson?: boolean): void {
        const cookSetting = StorageManagerUtil.cookieMap[key];

        if (!cookSetting) {
            console.error('invalid cookie key:' + key);
        }
        if (!cookSetting.r && this.cookieAcceptStatus !== StorageManagerUtil.ACCEPT_ALL) {
            return;
        }

        return StorageManagerUtil.setInternal(key, value, cookSetting, noJson);
    }

    public getOptional(key: string, noJson?: boolean): any {
        const cookSetting = StorageManagerUtil.cookieMap[key];

        if (!cookSetting) {
            console.error('invalid cookie key:' + key);
        }
        if (!cookSetting.r && this.cookieAcceptStatus !== StorageManagerUtil.ACCEPT_ALL) {
            return cookSetting.d;
        }

        return StorageManagerUtil.getInternal(key, cookSetting, noJson);
    }
}
