import { Component, OnInit } from '@angular/core';

import { StripeService } from '@services/stripe.service';
import { PlatformService } from '@services/platform.service';
import { ScrollService } from '@services/modules/core/scroll-service';
import { Event, NavigationEnd, Router } from '@angular/router';
import { ToggleContentPages } from '@core/constants/toggle-content-pages';
import { UserService } from '@services/modules/user/services/user.service';
import { PageUtils } from '@core/utils/page-utils';

@Component({
    selector: 'tz-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
    public showMobFooter = false;
    public showMobNavBar = false;
    public showMobAboutFooter = false;

    private fitMainContent = false;
    constructor(
        private router: Router,
        private scrollService: ScrollService,
        private stripeService: StripeService,
        public platformService: PlatformService,
        public userService: UserService
    ) {
        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                this.onStateChange(event.url);
                this.fitMainContent = PageUtils.is(event.url, ToggleContentPages.DESKTOP_SMALL_PAGES);
            }
        });
    }

    get minBodyHeight(): string {
        if (!this.platformService.isMobileDevice) {
            const vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);
            return !this.fitMainContent && vh < 1200 ? '100vh' : 'calc(100vh - 520px)';
        }

        return 'unset';
    }

    ngOnInit(): void {
        const queryParams = new URLSearchParams(window.location.search);
        const deviceId = queryParams.get('deviceId');
        if (!deviceId) {
            this.stripeService.loadStripeKey();
        }
    }

    public onScroll(scrollElement: HTMLDivElement) {
        this.scrollService.onScroll$.next(scrollElement);
    }

    private onStateChange(url: string): void {
        //PageUtils.autoTest();
        this.showMobAboutFooter =
            this.platformService.isMobileDevice && PageUtils.is(url, ToggleContentPages.MOB_ABOUT_FOOTER);
        this.showMobFooter =
            this.platformService.isMobileDevice &&
            (this.showMobAboutFooter || PageUtils.is(url, ToggleContentPages.MOB_FOOTER));
        this.showMobNavBar =
            this.platformService.isMobile &&
            !this.platformService.isMobileBrowser &&
            PageUtils.is(url, ToggleContentPages.MOB_NAV_BAR);
    }
}
