import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';
import { DialogService } from '@services/dialog.service';
import { CombinationId } from '@schemas/modules/core/base-types';
import { DebugCartItemComponent } from '@shared/components/debug-cart-item/debug-cart-item.component';
import { CartStatus, STATUS_COLORS } from '@core/constants/cart-status';
import { CartService } from '@services/modules/cart/services/cart.service';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { PlatformService } from '@services/platform.service';
import { Router } from '@angular/router';

@Component({
    selector: 'tz-cart-item-actions-bar',
    templateUrl: './cart-item-actions-bar.component.html',
    styleUrls: ['./cart-item-actions-bar.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CartItemActionsBarComponent implements OnInit, OnChanges {
    @Input() buttons: string[] = [];
    @Input() combId: CombinationId | undefined;
    @Input() optionId: string | undefined;
    @Input() status!: CartStatus;
    @Output() changeHotel: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeOptions: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeRooms: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeTickets: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeTransfer: EventEmitter<Transfer> = new EventEmitter<Transfer>();
    @Output() changeCarRental: EventEmitter<void> = new EventEmitter<void>();
    @Output() debug: EventEmitter<void> = new EventEmitter<void>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() remove: EventEmitter<void> = new EventEmitter<void>();
    @Output() removeOption: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<boolean> = new EventEmitter<boolean>(false);

    public hasButton = false;
    public debugButton = false;
    public changeButton = false;
    public changeHotelButton = false;
    public changeOptionsButton = false;
    public changeRoomsButton = false;
    public changeRoomButton = false;
    public changeTicketButton = false;
    public changeTicketsButton = false;
    public changeTransferButton = false;
    public changeCarRentalButton = false;
    public modifyButton = false;
    public removeOptionButton = false;
    public removeButton = false;
    public removeAllButton = false;
    public updateButton = false;
    public cancelButton = false;
    public cancelAllButton = false;
    public cartStatusStr = '';
    public statusColor = '';
    public acceptNewPriceButton = false;

    constructor(
        private dialogService: DialogService,
        private cartService: CartService,
        public platformService: PlatformService,
        private router: Router
    ) {}

    ngOnInit(): void {
        this.initializeButtons();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['status'] || changes['buttons']) {
            this.initializeButtons();
        }
    }

    public onChangeOptions(): void {
        this.changeOptions.emit();
    }

    public onChangeRooms(): void {
        this.changeRooms.emit();
    }

    public onChangeTickets(): void {
        this.changeTickets.emit();
    }

    public onChangeHotel(): void {
        this.changeHotel.emit();
    }

    public onCancel(cancelAll = false): void {
        this.cancel.emit(cancelAll);
    }

    public onDebug(): void {
        this.dialogService
            .open(DebugCartItemComponent, {
                combId: this.combId,
                optionId: this.optionId,
            })
            .afterClosed()
            .subscribe((result) => {
                console.log(`Dialog result: ${result}`);
                this.debug.emit();
            });
    }

    public onRemove(): void {
        this.remove.emit();
    }

    public onRemoveOption(): void {
        this.removeOption.emit();
    }

    public onChangeTransfer(): void {
        this.changeTransfer.emit();
    }

    public onChangeCarRental(): void {
        this.changeCarRental.emit();
    }

    public onModify(): void {
        this.modify.emit();
    }

    public acceptPriceChange(): void {
        if (this.optionId === undefined) {
            return;
        }

        this.cartService.applyPriceChange(this.optionId).subscribe();

        if (this.platformService.isMobileDevice) {
            this.router.navigate(['/checkout']);
        }
    }

    private initializeButtons() {
        this.hasButton = false;
        if (this.buttons) {
            this.changeOptionsButton = this.buttons.includes('changeOptions') && (this.hasButton = true);
            this.changeButton = this.buttons.includes('change') && (this.hasButton = true);
            this.debugButton = this.buttons.includes('debug') && (this.hasButton = true);
            this.removeOptionButton = this.buttons.includes('removeOption') && (this.hasButton = true);
            this.removeButton = this.buttons.includes('remove') && (this.hasButton = true);
            this.removeAllButton = this.buttons.includes('removeAll') && (this.hasButton = true);
            this.changeTicketButton = this.buttons.includes('changeTicket') && (this.hasButton = true);
            this.changeTicketsButton = this.buttons.includes('changeTickets') && (this.hasButton = true);
            this.changeHotelButton = this.buttons.includes('changeHotel') && (this.hasButton = true);
            this.changeRoomsButton = this.buttons.includes('changeRooms') && (this.hasButton = true);
            this.changeRoomButton = this.buttons.includes('changeRoom') && (this.hasButton = true);
            this.changeTransferButton = this.buttons.includes('changeTransfer') && (this.hasButton = true);
            this.changeCarRentalButton = this.buttons.includes('changeCarRental') && (this.hasButton = true);
            this.modifyButton = this.buttons.includes('modify') && (this.hasButton = true);
            this.cancelButton = this.buttons.includes('cancel') && (this.hasButton = true);
            this.cancelAllButton = this.buttons.includes('cancelAll') && (this.hasButton = true);
            this.updateButton = this.buttons.includes('update') && (this.hasButton = true);
        }
        this.cartStatusStr = '';
        this.acceptNewPriceButton = this.status === CartStatus.STATUS_PRICE_CHANGED;
        if (this.status && this.status !== CartStatus.STATUS_PLACED && !this.acceptNewPriceButton) {
            this.cartStatusStr = 'BOOKINGS.STATUS.' + this.status;
            const color = STATUS_COLORS[this.status];
            if (color) {
                this.statusColor = color;
            }
        }
    }
}
