import { Component, Inject } from '@angular/core';
import { FlightTerm } from '@schemas/modules/flights/flight';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-flight-terms-dialog',
    templateUrl: './flight-terms-dialog.component.html',
    styleUrls: ['./flight-terms-dialog.component.scss'],
})
export class FlightTermsDialogComponent {
    public flightTerms: FlightTerm[] = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<FlightTermsDialogComponent>,
        private dialogService: DialogService
    ) {
        this.flightTerms = ToolsService.deepCopy(data.terms);
    }

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }
}
