import { Injectable } from '@angular/core';

import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { TranslateService } from '@ngx-translate/core';
import { UserDataService } from '../dataservices/user.data.service';
import { BehaviorSubject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { SignInComponent } from '@shared/components/sign-in/sign-in.component';
import { PlatformService } from '@services/platform.service';
import { ModalService } from '@services/modal.service';
import { MobSignInComponent } from '@shared/components/mobile/mob-sign-in/mob-sign-in.component';
import { DialogService } from '@services/dialog.service';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class UserService {
    public loggedIn$ = new BehaviorSubject<boolean>(false);
    public subscribeProcessing = false;

    private customerInformation!: CustomerInfo;

    constructor(
        private translateService: TranslateService,
        private userDataService: UserDataService,
        private bootstrapModel: NgbModal,
        private router: Router,
        private platformService: PlatformService,
        private modalService: ModalService,
        private dialogService: DialogService
    ) {}

    get formProfileName(): string {
        if (this.IsLogged()) {
            const loggedInCustomer = this.getCustomerInformation();
            if (loggedInCustomer.firstName) {
                const customerName = `${loggedInCustomer.firstName} ${
                    loggedInCustomer.lastName ? loggedInCustomer.lastName : ''
                }`;

                return customerName.length > 20 ? customerName.substring(0, 20) + '...' : customerName;
            }

            if (!loggedInCustomer.firstName && loggedInCustomer.emailID) {
                return loggedInCustomer.emailID;
            }
        }

        return this.translateService.instant('SIGN_IN');
    }

    get firstLettersFromName(): string | null {
        if (this.IsLogged()) {
            const loggedInCustomer = this.getCustomerInformation();
            let str = '';

            if (loggedInCustomer.firstName) {
                str += loggedInCustomer.firstName.charAt(0);
            }

            if (loggedInCustomer.lastName) {
                str += loggedInCustomer.lastName.charAt(0);
            }

            return str ? str : null;
        }

        return null;
    }

    public setCustomerInformation(sessionCustomer: CustomerInfo): void {
        this.customerInformation = sessionCustomer;
        this.loggedIn$.next(this.IsLogged());
    }

    public getSessionID(): string {
        return this.customerInformation.id;
    }

    public getCustomerInformation(): CustomerInfo {
        if (this.IsLogged()) {
            return JSON.parse(JSON.stringify(this.customerInformation)) as CustomerInfo;
        }

        return {} as CustomerInfo;
    }

    public IsLogged(): boolean {
        return (
            this.customerInformation && this.customerInformation.id !== undefined && this.customerInformation.id !== ''
        );
    }

    public SignOut(): void {
        StorageManagerUtil.remove('trazler_customer_token');
        this.setCustomerInformation({} as CustomerInfo);
        this.loggedIn$.next(false);
    }

    public getCustomer(noReject: boolean): Promise<CustomerInfo> {
        return new Promise((resolve, reject) => {
            if (!StorageManagerUtil.get('trazler_customer_token', true)) {
                if (noReject) {
                    resolve({} as CustomerInfo);
                } else {
                    reject();
                }
                return;
            }

            this.userDataService.getUserInfo().subscribe(
                (customer: CustomerInfo) => {
                    console.log('Customer profile: ', customer);
                    this.setCustomerInformation(customer);
                    resolve(customer);
                },
                (err: any) => {
                    console.log('Error fetching customer info: ', err);

                    if (noReject) {
                        resolve({} as CustomerInfo);
                    } else {
                        reject(err);
                    }
                }
            );
        });
    }

    public updateCustomerInfo(customerInformation: CustomerInfo): Promise<CustomerInfo> {
        return new Promise((resolve, reject) => {
            console.log('profile = ', customerInformation);
            this.userDataService.updateUserInfo(customerInformation).subscribe(
                (success: any) => {
                    // $rootScope.showPrimaryAlert(true, "Data successfully saved");
                    // console.log('success', success);
                    // this.getCustomer(true);
                    // resolve({} as CustomerInfo);
                    this.customerInformation = customerInformation;
                },
                (failure) => {
                    console.log(failure);
                    /*if (failure.data.errors && failure.data.errors.phoneNumber && failure.data.errors.phoneNumber !== "") {
                        $rootScope.showPrimaryAlert(true, failure.data.errors.phoneNumber);
                    } else {
                        $rootScope.showPrimaryAlert(true, "Unable to save Data");
                    }*/
                    reject(failure);
                }
            );
        });
    }

    public deleteCustomer(): void {
        this.dialogService
            .openConfirmationDialog({
                title: `${this.translateService.instant('ACCOUNT.DELETE.TITLE')}?`,
                multipleContent: [this.translateService.instant('ACCOUNT.DELETE.MESSAGE')],
                cancelBtnTitle: this.translateService.instant('GENERAL.CANCEL'),
                confirmBtnTitle: this.translateService.instant('ACCOUNT.DELETE.TITLE'),
            })
            .afterClosed()
            .subscribe((proceedDelete: TzDialogReturn) => {
                if (proceedDelete !== CONFIRM) {
                    return;
                }

                this.userDataService.deleteUserAccount(this.customerInformation).subscribe(() => {
                    this.SignOut();
                    this.dialogService.openSnackBar({
                        message: this.translateService.instant('NOTIFS.DELETE_ANSWER'),
                    });

                    this.router.navigate(['/search'], { skipLocationChange: false });
                });
            });
    }

    public openLoginModel(loginMailId?: string, fromCheckout?: boolean): Promise<CustomerInfo> {
        if (this.platformService.isMobileDevice) {
            return new Promise((resolve, reject) => {
                this.modalService
                    .createMainModal(MobSignInComponent, {
                        data: { loginMailId, showSignin: true, fromCheckout },
                        title: 'ACCOUNT.SIGN.SIGN_IN',
                    })
                    .then((modal: any) => {
                        if (modal.data) {
                            resolve(modal.data);
                        } else {
                            reject();
                        }
                    });
            });
        }

        const signInModalRef = this.bootstrapModel.open(SignInComponent, {
            modalDialogClass: this.router.url.includes('/checkout') ? 'checkout-login-modal' : 'login-modal',
            centered: true,
        });

        if (loginMailId && loginMailId !== '') {
            signInModalRef.componentInstance.emailId = loginMailId;
        }

        return signInModalRef.result;
    }

    public subscribeToTrazler(email: string): void {
        if (this.subscribeProcessing) {
            return;
        }

        this.subscribeProcessing = true;
        this.userDataService.subscribeToTz(email).subscribe(
            () => {
                this.subscribeProcessing = false;

                this.dialogService.openConfirmationDialog({
                    title: this.translateService.instant('FOOTER.Thank You!'),
                    content: this.translateService.instant('NOTIFS.SUBSCRIBE_SUCCESS'),
                    showCloseBtn: true,
                });
            },
            (error) => {
                this.subscribeProcessing = false;
                this.dialogService.openMainDialog({ content: error.error.message, showCloseBtn: true });
            }
        );
    }
}
