import { Injector } from '@angular/core';
import { Component } from '@angular/core';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { NavigationStart, Router } from '@angular/router';
@Component({
    selector: 'tz-cookie',
    templateUrl: './cookie.component.html',
    styleUrls: ['./cookie.component.scss'],
})
export class CookieComponent {
    public cookie: any = {};
    public showCookie: boolean;
    public storageManagerUtil: StorageManagerUtil;
    public st: any;
    private router?: Router;

    constructor(injector: Injector) {
        // needed to access static values of StorageManagerUtil in html
        this.st = StorageManagerUtil;

        this.storageManagerUtil = injector.get(StorageManagerUtil);
        this.showCookie = !StorageManagerUtil.getCookie();
        if (this.showCookie) {
            this.router = injector.get(Router);
            this.listenCookieChange();
        }
    }

    public acceptCookie(value: number | null): void {
        this.storageManagerUtil.setCookie(value);
        this.showCookie = false;
    }

    public rejectCookie(value: number | null): void {
        this.storageManagerUtil.setCookie(value);
        this.showCookie = false;
    }

    private listenCookieChange() {
        this.router?.events.forEach((event) => {
            if (!this.showCookie) {
                return;
            }
            if (event instanceof NavigationStart) {
                if (StorageManagerUtil.getCookie() !== StorageManagerUtil.ACCEPT_UNDEFINED) {
                    this.showCookie = false;
                    return;
                }
            }
        });
    }
}
