import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'ratingToText',
})
export class RatingToTextPipe implements PipeTransform {
    transform(rating: number): string {
        const ratingsMap = [
            {
                range: [0, 6],
                textKey: 'NO_RATING',
            },
            {
                range: [6, 8],
                textKey: 'GOOD',
            },
            {
                range: [8, 9],
                textKey: 'VERY_GOOD',
            },
            {
                range: [9, 9.5],
                textKey: 'EXCELLENT',
            },
            {
                range: [9.5, 10],
                textKey: 'FABULOUS',
            },
        ];

        let overAllText = 'NO_RATING';

        ratingsMap.map((ratingItem) => {
            const [bottom, top] = ratingItem.range;
            if (rating >= bottom && rating <= top) {
                overAllText = ratingItem.textKey;
            }
        });

        return 'HOTELS.REVIEWS.RATINGS.' + overAllText;
    }
}
