import { TranslateService } from '@ngx-translate/core';
import { AdditionalLuggage, SelectedBaggageInfo, SelectedBagOption } from '@schemas/modules/checkout/transport-luggage';
import { FlightLuggageOption, FlightLuggageOptionTypes } from '@schemas/modules/flights/flight-luggage.model';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';

export const DefaultLuggage = {
    totalPrice: 0,
    cabin: { selected: '', bagsCount: 0, options: {} },
    checkIn: { selected: '', bagsCount: 0, options: {} },
};

export class TransportLuggagesUtil {
    public static getTravellerName(
        formvalue: { firstName: string; middleName: string; lastName: string },
        idx: number,
        translateService: TranslateService
    ): string {
        if (!formvalue.firstName || !formvalue.lastName) {
            if (idx === 0) {
                return translateService.instant('TRAVELLER.PRIMARY_TRAVELER');
            } else {
                return `${translateService.instant('TRAVELLER.TRAVELER')} ${idx + 1}`;
            }
        }

        return `${formvalue.firstName} ${formvalue.middleName ? formvalue.middleName : ''} ${formvalue.lastName}`;
    }

    public static optionPresent(additionalLuggage: AdditionalLuggage, bagType: string, idx: string): boolean {
        return (
            additionalLuggage &&
            (bagType === FlightLuggageOptionTypes.CABIN || bagType === FlightLuggageOptionTypes.CHECKIN) &&
            additionalLuggage[bagType].options.hasOwnProperty(idx)
        );
    }

    public static disableLuggageSelection(
        additionalLuggage: AdditionalLuggage,
        bagType: string,
        option: FlightLuggageOption,
        maxAllowed: number
    ): boolean {
        return (
            additionalLuggage &&
            ((this.optionPresent(additionalLuggage, bagType, option.index) &&
                this.getOption(additionalLuggage, bagType, option.index).count === option.maxQuantity) ||
                ((bagType === FlightLuggageOptionTypes.CABIN || bagType === FlightLuggageOptionTypes.CHECKIN) &&
                    additionalLuggage[bagType].bagsCount === maxAllowed))
        );
    }

    public static getOption(additionalLuggage: AdditionalLuggage, bagType: string, idx: string): SelectedBagOption {
        return bagType === FlightLuggageOptionTypes.CABIN
            ? additionalLuggage.cabin.options[idx]
            : additionalLuggage.checkIn.options[idx];
    }

    public static formLuggageCombination(
        additionalLuggage: AdditionalLuggage,
        option: FlightLuggageOption,
        bagType: string
    ): AdditionalLuggage {
        if (!additionalLuggage) {
            return JSON.parse(JSON.stringify(DefaultLuggage));
        }

        if (bagType !== FlightLuggageOptionTypes.CHECKIN && bagType !== FlightLuggageOptionTypes.CABIN) {
            return additionalLuggage;
        }

        const title = option.weightText !== '' ? option.weightText + ' ' + option.dimensions : option.dimensions;
        const bag =
            this.optionPresent(additionalLuggage, bagType, option.index) &&
            this.getOption(additionalLuggage, bagType, option.index).count !== 0
                ? this.getOption(additionalLuggage, bagType, option.index)
                : ({
                      count: 0,
                      totalPrice: 0,
                      optionPrice: option.prices,
                      title,
                      weight: option.maxWeight,
                  } as SelectedBagOption);

        additionalLuggage.totalPrice += option.prices.listPrice;
        additionalLuggage[bagType].bagsCount++;

        bag.count++;
        bag.totalPrice += option.prices.listPrice;

        bag.depIdx = option.outwardIndex;
        bag.retIdx = option.returnIndex;
        if (!option.outwardIndex) {
            bag.depIdx = option.index;
        }

        additionalLuggage[bagType].options[option.index] = bag;
        return additionalLuggage;
    }

    public static removeLuggageCombination(
        additionalLuggage: AdditionalLuggage,
        option: FlightLuggageOption,
        bagType: string
    ): AdditionalLuggage {
        if (!additionalLuggage) {
            return JSON.parse(JSON.stringify(DefaultLuggage));
        }

        if (bagType !== FlightLuggageOptionTypes.CHECKIN && bagType !== FlightLuggageOptionTypes.CABIN) {
            return additionalLuggage;
        }

        const bag = this.getOption(additionalLuggage, bagType, option.index);
        if (!bag) {
            return additionalLuggage;
        }

        if (bag.count === 0) {
            bag.depIdx = '';
            bag.retIdx = '';
            bag.title = '';
            bag.weight = 0;
            return additionalLuggage;
        }

        additionalLuggage.totalPrice -= option.prices.listPrice;
        additionalLuggage[bagType].bagsCount--;

        bag.count--;
        bag.totalPrice -= option.prices.listPrice;

        additionalLuggage[bagType].options[option.index] = bag;
        if (bag.count === 0) {
            delete additionalLuggage[bagType].options[option.index];
        }

        return additionalLuggage;
    }

    public static selectLuggageOption(
        additionalLuggage: AdditionalLuggage,
        option: FlightLuggageOption | null,
        bagType: string,
        tzCurrencyPipe: TzCurrencyPipe
    ): AdditionalLuggage {
        if (!additionalLuggage) {
            return JSON.parse(JSON.stringify(DefaultLuggage));
        }

        if (bagType !== FlightLuggageOptionTypes.CHECKIN && bagType !== FlightLuggageOptionTypes.CABIN) {
            return additionalLuggage;
        }

        additionalLuggage[bagType].options = {};
        additionalLuggage[bagType].bagsCount = 0;
        additionalLuggage[bagType].selected = option ? option.index : '';
        additionalLuggage[bagType].selectedBaggageStr = '';
        additionalLuggage[bagType].selectedBaggageInfo = {} as SelectedBaggageInfo;
        additionalLuggage.totalPrice = 0;

        if (option) {
            const title = option.combinationText;
            additionalLuggage[bagType].options[option.index] = {
                count: option.quantity,
                totalPrice: option.prices.listPrice,
                optionPrice: option.prices,
                depIdx: option.outwardIndex,
                retIdx: option.returnIndex,
                title,
                weight: option.maxWeight,
            } as SelectedBagOption;

            additionalLuggage.totalPrice = option.prices.listPrice;
            additionalLuggage[bagType].bagsCount = option.quantity;
            additionalLuggage[bagType].byTotalWeight = option.byTotalWeight;

            const weightTexts = option.weightTexts ? option.weightTexts.join(', ') : '';
            const currency = tzCurrencyPipe.transform(option.prices.listPrice, option.prices.listPriceCur);
            const dimensions = option.dimensions ?? '';
            additionalLuggage[bagType].selectedBaggageStr = weightTexts + dimensions + ' - ' + currency;
            additionalLuggage[bagType].selectedBaggageInfo = {
                weightTexts: option.weightTexts,
                dimension: dimensions,
                price: currency ?? '',
            };
        }

        return additionalLuggage;
    }

    public static getSelectedLuggage(
        additionalLuggage: AdditionalLuggage,
        bagType: FlightLuggageOptionTypes.CABIN | FlightLuggageOptionTypes.CHECKIN
    ): SelectedBaggageInfo | undefined {
        const luggageStr = additionalLuggage?.[bagType]?.selectedBaggageStr;
        if (luggageStr && luggageStr !== '') {
            return additionalLuggage[bagType].selectedBaggageInfo;
        }

        return;
    }

    public static formatWeightText(weightTexts: string[], noSinglePc = false): string {
        const weightText = weightTexts.join(' + ');
        return weightText.replace(new RegExp(weightTexts.length > 1 || noSinglePc ? '1 pc x ' : 'pc ', 'gi'), '');
    }
}
