    public static formSearchFieldOrder(
        selectedServiceConf: any,
        fieldOrderType: string,
        isEditMode: boolean
    ): SearchConfigOrder[] {
        const ordered: SearchConfigOrder[] = [];

        for (let confIdx = 0; confIdx < selectedServiceConf.length; confIdx++) {
            for (const field in selectedServiceConf[confIdx]) {
                if (!selectedServiceConf[confIdx].hasOwnProperty(field) || !selectedServiceConf[confIdx][field]) {
                    continue;
                }

                const fieldConfig = selectedServiceConf[confIdx][field];
                if (field === 'persons' || fieldConfig[fieldOrderType] === undefined) {
                    continue;
                }

                const fieldType = SearchDataService.getFieldType(field);
                let styles = [fieldType];
                if (Array.isArray(fieldConfig.classes)) {
                    styles = [...styles, ...fieldConfig.classes];
                }

                ordered[fieldConfig[fieldOrderType]] = {
                    field,
                    confIdx,
                    fieldType,
                    class: styles,
                    fieldOrder: fieldConfig[fieldOrderType],
                    tabOrder: isEditMode ? fieldConfig.editOrder : fieldConfig.tabOrder,
                } as SearchConfigOrder;
            }
        }

        return ordered;
    }

