import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

@Component({
    selector: 'tz-svg-icon',
    templateUrl: './tz-svg-icon.component.html',
    styleUrls: ['./tz-svg-icon.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TzSvgIconComponent {
    @Input() path = '/assets/icons/web/icons/';
    @Input() pathMob = '/assets/icons/web/icons/mob/';
    @Input() icon: string | undefined;
    @Input() iconType = 'svg';
    @Input() className: string | undefined;
    @Input() stretch = false;
    @Input() mobile = false;
    @Input() styles: { [key: string]: string | number } = {};
}
