import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { TranslateService } from '@ngx-translate/core';
import { DialogService } from '@services/dialog.service';
import { ModalService } from '@services/modal.service';
import { UserDataService } from '@services/modules/user/dataservices/user.data.service';

@Component({
    selector: 'tz-mob-reset-password',
    templateUrl: './mob-reset-password.component.html',
    styleUrls: ['./mob-reset-password.component.scss'],
})
export class MobResetPasswordComponent implements OnInit {
    public formGroup = new FormGroup({
        newPassword: new FormControl('', [Validators.required, Validators.pattern(ValidatorPatterns.PASSWORD)]),
        reEnteredNewPassword: new FormControl('', [
            Validators.required,
            Validators.pattern(ValidatorPatterns.PASSWORD),
        ]),
    });
    public showPassword = {
        newPassword: false,
        confirmPassword: false,
    };
    public valid = false;
    private data!: { token: string };

    constructor(
        private userDataService: UserDataService,
        private router: Router,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private modalService: ModalService
    ) {}

    ngOnInit() {
        this.formGroup.controls['newPassword'].addValidators([this.triggerEnteredPassword()]);
        this.formGroup.controls['reEnteredNewPassword'].addValidators([this.updatereEnteredPassword()]);
    }

    public validatePassword(patternStr: string): boolean {
        const pattern = new RegExp(patternStr);
        return pattern.test(this.formGroup.controls['newPassword'].value);
    }

    public changeTzPassword(): void {
        const formValue = this.formGroup.value;
        const passwordData = {
            token: this.data.token,
            password: formValue.newPassword,
            confirmPassword: formValue.reEnteredNewPassword,
        };
        this.userDataService.resetTzPassword(passwordData).subscribe(
            () => {
                this.valid = true;
            },
            (failure) => {
                this.router.navigate(['search']).then(() => {
                    this.dialogService.openSnackBarDialog(this.translateService.instant('NOTIFS.INVALID_TOKEN'));
                });
            }
        );
    }

    private triggerEnteredPassword(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            control.parent?.get('reEnteredNewPassword')?.updateValueAndValidity();
            return null;
        };
    }

    private updatereEnteredPassword(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const newPassword = control.parent?.get('newPassword')?.value;

            if (control.valid && newPassword && newPassword !== '' && control.value !== newPassword) {
                return { NEW_PASSWORD_DID_NOT_MATCH: true };
            }

            return null;
        };
    }
}
