import { Injectable, Injector } from '@angular/core';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { Observable, of } from 'rxjs';
import { Flight, FlightTerm } from '@schemas/modules/flights/flight';
import { HttpRequestType } from '@core/constants/http-request-type';
import { ServiceId } from '@core/constants/service-id.const';
import { FlightPackagesResponse } from '@schemas/modules/flights/flight-packages.response';
import { map, tap } from 'rxjs/operators';
import { ICart } from '@schemas/modules/cart/cart';
import { FlightUtils } from '@services/modules/transport/flights/services/flight.utils';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
// eslint-disable-next-line max-len
import { FlightTermsDialogComponent } from 'src/app/modules/results/transport/flights/components/flight-terms-dialog/flight-terms-dialog.component';
// eslint-disable-next-line max-len
import { MobFlightTermsConditionsModalComponent } from 'src/app/modules/results/transport/flights/components/mob-flight-terms-conditions-modal/mob-flight-terms-conditions-modal.component';
@Injectable({
    providedIn: 'root',
})
export class FlightsService extends ResultService {
    public override readonly serviceId = ServiceId.FLIGHT;
    public minFlightsPrice!: number;

    private flightTermsCache: { [key: string]: FlightTerm[] } = {};

    public getPackages(selectedFlight: any): Observable<Flight[]> {
        const queryParam = {
            entity: 'upgrade',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(this.serviceId),
            currency: this.settingsService.getCurrency(),
            hideLoading: false,
            lang: this.settingsService.langCode,
            refreshFilter: false,
            serviceId: this.serviceId,
        };
        const params = FlightUtils.getQueryParamsForFlightPackages(selectedFlight);
        return this.apiCall(HttpRequestType.POST, queryParam, params).pipe(
            map((response: FlightPackagesResponse) => response.results)
        );
    }

    public addFlightToCart(flight: Flight): Observable<ICart> {
        const options = [
            {
                bookingOptions: [{}],
                sameTrip: false,
                id: flight.id,
            },
        ];
        return this.addToCart(options);
    }

    public getSelectedFLightsFromCart(): IFlightsCartData {
        return this.getServiceCartItems();
    }

    public showFlightTrems(flight: Flight, mobile?: boolean): void {
        const openFlightTerms = (terms: FlightTerm[]) => {
            terms = terms && terms.length > 0 ? terms : [];
            if (terms.length === 0) {
                flight.termsUnavailable = true;
            }
            if (mobile) {
                this.modalService.createMainModal(MobFlightTermsConditionsModalComponent, {
                    data: { terms },
                    cssClass: 'mob-terms-modal',
                    title: this.translateService.instant('FLIGHT_TRAIN.CONDITIONS'),
                });
            } else {
                this.dialogService.openServiceDialog(FlightTermsDialogComponent, {
                    width: undefined,
                    maxHeight: '90vh',
                    panelClass: 'tz-terms-dialog-large',
                    maxWidth: '940px',
                    autoFocus: false,
                    data: { terms },
                });
            }
        };
        if (flight.terms && flight.terms.length > 0) {
            openFlightTerms(flight.terms);
            return;
        }

        this.getFlightTerms(flight).subscribe(
            (terms) => {
                openFlightTerms(terms);
            },
            (failure: any) => {
                openFlightTerms([]);
                console.log('Unable to get flight terms got ', failure);
            }
        );
    }

    public showFlightCheckinAllowance(allowanceType: number): void {
        if (allowanceType !== 1 && allowanceType !== 2) {
            return;
        }

        const checkinKey = allowanceType === 1 ? 'FREE' : 'AIRPORT';
        this.dialogService.openConfirmationDialog({
            title: this.translateService.instant(`POPUPS.CHECKIN.TITLE_${checkinKey}`),
            multipleContent: [this.translateService.instant(`POPUPS.CHECKIN.${checkinKey}`)],
            confirmBtnTitle: this.translateService.instant('POPUPS.OK'),
        });
    }

    private getFlightTerms(flight: Flight): Observable<FlightTerm[]> {
        if (this.flightTermsCache[flight.id]) {
            return of(this.flightTermsCache[flight.id]);
        }

        const params = {
            entity: 'getFlightSupplierInfo',
            provider: flight.broker,
            itemId: flight.id,
            dataId: this.searchDataService.getDataId(this.serviceId),
            deviceId: this.settingsService.getDeviceID(),
            closeLoader: true,
        };

        this.showLoader(true, 'LOADER_MESSAGES.TERMS_FLIGHT');
        return this.apiCall(HttpRequestType.POST, params, params).pipe(
            tap((terms) => {
                this.flightTermsCache[flight.id] = terms;
            })
        );
    }
}
