import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, Output, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PhoneNumberFormat } from 'ngx-intl-tel-input';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { CountryISO } from 'ngx-intl-tel-input';

@Component({
    selector: 'tz-phone',
    templateUrl: './tz-phone.component.html',
})
export class TzPhoneComponent implements AfterViewInit, OnDestroy {
    @Input() formGroup = new FormGroup({});
    @Input() readonly = false;
    @Input() cssClass?: string = '';
    @Output() inputFocused: EventEmitter<boolean> = new EventEmitter<boolean>();
    @ViewChild('ngxIntlTelInputComponent', { read: ElementRef, static: true }) ngxTelInput?: ElementRef;

    public PhoneNumberFormat = PhoneNumberFormat;
    public countryISO = CountryISO;

    private phoneValid = false;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor() {}

    public ngAfterViewInit(): void {
        const controller = this.formGroup.controls['phone'];
        if (this.ngxTelInput) {
            const nativeElementOfInput: HTMLInputElement = this.ngxTelInput.nativeElement.querySelector('#phone');
            fromEvent(nativeElementOfInput, 'focus')
                .pipe(takeUntil(this.destroy$))
                .subscribe(() => {
                    this.inputFocused.emit(true);
                });

            fromEvent(nativeElementOfInput, 'blur')
                .pipe(takeUntil(this.destroy$))
                .subscribe(() => {
                    this.inputFocused.emit(false);
                });

            fromEvent(nativeElementOfInput, 'input')
                .pipe(takeUntil(this.destroy$))
                .subscribe(() => {
                    if (typeof controller.value === 'string') {
                        nativeElementOfInput.value = controller.value;
                    }
                });
        }

        controller.statusChanges.subscribe((result) => {
            if (result === 'VALID' && !this.phoneValid) {
                controller.setValue(controller.value.internationalNumber);
                this.phoneValid = true;
            } else {
                this.phoneValid = false;
            }
        });
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }
}
