    private searchPoller(combId: CombinationId, isNewCombi: boolean, anticipatedList: any, refId?: string) {
        return this.tripPlannerService.poller(combId, isNewCombi, anticipatedList, this.hdlPollerErr.bind(this), refId);
    }

    private reloadServiceInfoFromStorage(): void {
        if (this.searchDataService.searchInfos.context) {
            this.tabRoutingService.initRouting(this.searchDataService.searchInfos.context.services, false);
        }

        if (this.searchDataService.searchInfos.combId !== '') {
            this.checkDepService.reCheckDep();
        }
    }

    private isHomePage(): boolean {
        return this.router.url === '/search';
    }
