import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { Event, NavigationEnd, Router } from '@angular/router';
import { PlatformService } from '@services/platform.service';
import { PageUtils } from '@core/utils/page-utils';
import { PagesUrls } from '@core/constants/pages-urls';
import { UserService } from '@services/modules/user/services/user.service';

type IHeaderType = 'transparent' | 'primary' | 'secondary' | 'hide';

@Component({
    selector: 'tz-mob-header',
    templateUrl: './mob-header.component.html',
    styleUrls: ['./mob-header.component.scss'],
})
export class MobHeaderComponent implements OnInit {
    @Output() signOut: EventEmitter<void> = new EventEmitter<void>();

    public headerType: IHeaderType = 'primary';

    constructor(
        public platformService: PlatformService,
        private menu: MenuController,
        private router: Router,
        public userService: UserService
    ) {
        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                this.onStateChange(event.url);
            }
        });
    }

    ngOnInit() {}

    public onOpenMenu(): void {
        this.menu.enable(true, 'mainMobMenu').then(() => {});
        this.menu.open('mainMobMenu').then(() => {});
    }

    public onOpenProfileMenu(): void {
        if (this.userService.IsLogged()) {
            this.menu.enable(true, 'profileMobMenu').then(() => {});
            this.menu.open('profileMobMenu').then(() => {});
        } else {
            this.menu.enable(true, 'authMenu').then(() => {});
            this.menu.open('authMenu').then(() => {});
        }
    }

    public onSignOut(): void {
        this.signOut.emit();
    }

    private onStateChange(url: string): void {
        if (PageUtils.is(url, [PagesUrls.HOME, PagesUrls.SEARCH])) {
            this.headerType = 'transparent';
        } else if (PageUtils.is(url, PagesUrls.ABOUT_ABOUT)) {
            this.headerType = 'secondary';
        } else if (PageUtils.is(url, [PagesUrls.GET_STARTED])) {
            this.headerType = 'hide';
        } else {
            this.headerType = 'primary';
        }
    }
}
