import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'tz-mob-button',
    templateUrl: './mob-button.component.html',
    styleUrls: ['./mob-button.component.scss'],
})
export class MobButtonComponent implements OnInit {
    @Input() title = '';
    @Input() selected = '';
    @Input() btnType: 'purple' | 'yellow' | 'white' | 'dropdown' = 'purple';
    @Input() disabled: 'gray-disabled' | 'yellow-disabled' | undefined;
    @Input() isDropdownOpen = false;
    @Output() clicked: EventEmitter<void> = new EventEmitter<void>();

    constructor() {}

    ngOnInit() {}

    public onClick(): void {
        this.clicked.emit();
    }
}
