import { Component, OnInit } from '@angular/core';
import { SettingsService } from '@services/services/settings-service';
import { ILanguage } from '@schemas/modules/core/ILanguage';
import { LANGUAGES } from '@core/constants/languages.const';
import { CurrencyService } from '@services/services/currency-service';
import { MenuController } from '@ionic/angular';
import { UserService } from '@services/modules/user/services/user.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { ILandingPagesUrls } from '@schemas/modules/component-schemas/urls/landing-pages-urls';
import { LandingPagesUrls } from '@core/constants/pages-urls';
import { ModalService } from '@services/modal.service';
import { Currency } from '@schemas/modules/core/currency';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';

@Component({
    selector: 'tz-main-mob-menu',
    templateUrl: './main-mob-menu.component.html',
    styleUrls: ['./main-mob-menu.component.scss'],
})
export class MainMobMenuComponent implements OnInit {
    public languages: ILanguage[] = LANGUAGES.list;
    public currencies!: Currency[];
    public selectedLanguage!: ILanguage;
    public selectedCurrency!: Currency;
    public destroy$: Subject<boolean> = new Subject<boolean>();
    public isLogged = false;
    public landingPagesUrls: ILandingPagesUrls = LandingPagesUrls;
    public userLogoInfo: string | null = null;

    constructor(
        private settingsService: SettingsService,
        private currencyService: CurrencyService,
        private menu: MenuController,
        private router: Router,
        public userService: UserService,
        private modalService: ModalService
    ) {
        this.userService.loggedIn$.pipe(takeUntil(this.destroy$)).subscribe((isLogged) => {
            this.isLogged = isLogged;
            console.log('isLogged', isLogged);
        });

        this.settingsService.selectedLanguageSubject.subscribe((selectedLanguage) => {
            this.selectedLanguage = selectedLanguage;
        });

        this.currencyService.selectedCurrencySubject.subscribe((selectedLanguage) => {
            this.selectedCurrency = selectedLanguage;
        });
    }

    ngOnInit() {
        this.currencies = this.currencyService.currencies;
    }

    public onOpenProfileMenu(): void {
        if (this.userService.IsLogged()) {
            this.menu.enable(true, 'profileMobMenu').then(() => {});
            this.menu.open('profileMobMenu').then(() => {});
        } else {
            this.navToGetStarted();
        }
    }

    public onCloseMenu(): void {
        this.menu.close('mainMobMenu').then(() => {});
    }

    public navToGetStarted(): void {
        this.router.navigate(['get-started']);
    }

    public onOpenLangPicker(): void {
        const wheels: MobPickerWheels[] = [];

        this.languages.map((lang) => {
            wheels.push({ display: lang.name, value: lang.i18nCode });
        });

        const selected = this.selectedLanguage.i18nCode;

        this.modalService.openPickerModal({ wheels, selected: [selected] }, {}).then((modal) => {
            if (modal.data?.selected) {
                this.languages.map((lang) => {
                    if (modal.data?.selected && modal.data.selected[0] === lang.i18nCode) {
                        this.settingsService.setLanguage(lang);
                    }
                });
            }
        });
    }

    public onOpenCurrencyPicker(): void {
        const wheels: MobPickerWheels[] = [];

        this.currencies.map((lang) => {
            wheels.push({ display: lang.name, value: lang.code });
        });

        const selected = this.selectedCurrency.code;

        this.modalService.openPickerModal({ wheels, selected: [selected] }, {}).then((modal) => {
            if (modal.data?.selected) {
                this.currencyService.setCurrency(modal.data.selected[0]);
            }
        });
    }
}
