import {
    Component,
    ComponentFactoryResolver,
    ComponentRef,
    Inject,
    OnInit,
    ViewChild,
    ViewContainerRef,
} from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MainDialogOptions } from '@shared/schemas/main-dialog-options.model';
import { ComponentType } from '@angular/cdk/portal';
import { DialogButtonsStyles } from '@shared/schemas/dialog.model';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-main-dialog',
    templateUrl: './main-dialog.component.html',
    styleUrls: ['./main-dialog.component.scss'],
})
export class MainDialogComponent implements OnInit {
    @ViewChild('dialogContent', {
        static: true,
        read: ViewContainerRef,
    })
    viewContainerRef: any;
    componentRef: ComponentRef<any> | null = null;

    public dialogData!: MainDialogOptions;
    public dialogButtonsStyles = DialogButtonsStyles;

    constructor(
        @Inject(MAT_DIALOG_DATA)
        public data: {
            component: ComponentType<any>;
            data: MainDialogOptions;
        },
        private mdDialogRef: MatDialogRef<MainDialogComponent>,
        private componentFactoryResolver: ComponentFactoryResolver,
        private dialogService: DialogService
    ) {}

    ngOnInit(): void {
        this.dialogData = this.data.data;

        if (this.dialogData.component) {
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.dialogData.component);
            this.componentRef = this.viewContainerRef.createComponent(componentFactory);

            if (this.componentRef) {
                this.componentRef.instance.data = this.dialogData.data;
            }
        }
    }

    public onCancel(): void {
        this.closeDialog(false);
    }

    private closeDialog(value: boolean): void {
        this.dialogService.closeDialog(this.mdDialogRef, value);
    }
}
