import { Component, OnInit, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { TzTravellerService } from '@services/services/tzTraveller.service';

const ERROR_PREFIX = 'TZ_FORM_VALIDATION.';

@Component({
    selector: 'tz-form-error',
    templateUrl: './tz-form-error.component.html',
    styleUrls: ['./tz-form-error.component.scss'],
})
export class TzFormErrorComponent implements OnInit {
    @Input() form!: FormGroup;
    @Input() fieldType = '';
    @Input() key!: string;

    constructor() {}

    get isInvalid(): boolean {
        return TzTravellerService.invalidControl(this.form, this.key);
    }

    get errorMessage(): string {
        const control = this.form.get(this.key);
        if (control === null) {
            return '';
        }

        if (control.hasError('required')) {
            return `${ERROR_PREFIX}REQUIRED`;
        }

        switch (this.fieldType) {
            case 'FIRST_NAME':
            case 'MIDDLE_NAME':
            case 'LAST_NAME':
            case 'ID_CARD_NUMBER':
            case 'PROFILE_FIRST_MIDDLE_NAME':
            case 'PROFILE_LAST_NAME':
            case 'HOLDERS_NAME':
            case 'COMPANY_NAME':
            case 'VAT_NO':
            case 'ADDRESS':
            case 'ZIP_CODE':
            case 'CITY_NAME': {
                if (control.hasError('maxlength')) {
                    return `${ERROR_PREFIX}${this.fieldType}.MAX_LENGTH`;
                }

                if (control.hasError('pattern')) {
                    return `${ERROR_PREFIX}${this.fieldType}.INVALID_CHARACTER`;
                }

                if (control.hasError('DUPLICATE_ID')) {
                    return control.getError('DUPLICATE_ID');
                }

                break;
            }
            case 'PHONE': {
                return `${ERROR_PREFIX}INVALID_PHONE_NUMBER`;
            }
            case 'DOB':
            case 'ISSUED_DATE':
            case 'EXPIRY_DATE': {
                switch (true) {
                    case control.hasError('INVALID_DATE'):
                        return `${ERROR_PREFIX}INVALID`;

                    case control.hasError('INVALID_DATE_FORMAT'):
                        return control.getError('INVALID_DATE_FORMAT');

                    case this.fieldType === 'DOB' && control.hasError('INVALID_DOB'):
                        return control.getError('INVALID_DOB');

                    case this.fieldType === 'ISSUED_DATE' && control.hasError('INVALID_ISSUED_DATE'):
                        return control.getError('INVALID_ISSUED_DATE');

                    case this.fieldType === 'EXPIRY_DATE' && control.hasError('INVALID_EXPIRY_DATE'):
                        return control.getError('INVALID_EXPIRY_DATE');
                }
                break;
            }
            case 'EMAIL': {
                if (control.hasError('EMAIL_DID_NOT_MATCH')) {
                    return `${ERROR_PREFIX}${this.fieldType}.NOT_MATCH`;
                }

                if (control.hasError('pattern')) {
                    return `${ERROR_PREFIX}${this.fieldType}.INVALID_FORMAT`;
                }

                break;
            }
            default: {
                if (control.hasError('pattern')) {
                    return `${ERROR_PREFIX}INVALID`;
                }
            }
        }

        return `${ERROR_PREFIX}INVALID`;
    }

    get invalidMessageParams(): any {
        const control = this.form.get(this.key);
        if (control?.hasError('INVALID_MESSAGE_PARAMS')) {
            return control.getError('INVALID_MESSAGE_PARAMS');
        }
    }

    ngOnInit() {}
}
