import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import { BestValType } from '@schemas/modules/search/search';

@Component({
    selector: 'tz-recommended-list-tem-description',
    templateUrl: './recommended-list-tem-description.component.html',
    styleUrls: ['./recommended-list-tem-description.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RecommendedListTemDescriptionComponent implements OnInit {
    @Input() bestValType: BestValType | undefined = undefined;
    public label = 'GENERAL.RECOMMENDED';
    public bgClass = 'bg-recommended';

    constructor() {}
    ngOnInit(): void {
        switch (this.bestValType) {
            case BestValType.QUICKEST: {
                this.label = 'GENERAL.QUICKEST';
                this.bgClass = 'bg-quickest';
                break;
            }
            case BestValType.CHEAPEST: {
                this.label = 'GENERAL.CHEAPEST';
                this.bgClass = 'bg-cheapest';
                break;
            }
        }
    }
}
