import { ServiceId } from '@core/constants/service-id.const';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';

export class TabUtils {
    public static displayMinimalPriceFromPreSearch(tab: ServiceTab): boolean {
        return (
            tab.selectedService &&
            ((tab.preSearch && !tab.loader) || (tab.enable && (!tab.inCart || tab.isInError))) &&
            tab.minPrice > 0 &&
            tab.serviceId !== ServiceId.SUMMARY
        );
    }

    public static displayPriceFromCart(tab: ServiceTab): boolean {
        return tab.enable && tab.inCart && !tab.preSearch && !tab.loader && !tab.isInError;
    }

    public static displayNoResult(tab: ServiceTab): boolean {
        if (
            tab.serviceId === 1 &&
            tab.selectedService &&
            !tab.loader &&
            tab.preSearch &&
            tab.resultCount === 0 &&
            tab.minPrice === 0
        ) {
            console.error(
                'no res:',
                tab.selectedService,
                tab.loader,
                tab.resultCount,
                tab.minPrice,
                tab.preSearch,
                tab.selectedService && !tab.loader && tab.preSearch && tab.resultCount === 0 && tab.minPrice === 0
            );
        }
        return tab.selectedService && !tab.loader && tab.preSearch && tab.resultCount === 0 && tab.minPrice === 0;
    }
}
