import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { SettingsService } from '@services/services/settings-service';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { LANGUAGES } from '@core/constants/languages.const';

@Injectable({
    providedIn: 'root',
})
export class TripIdResolver implements Resolve<string> {
    constructor(private settingsService: SettingsService, private searchMetadataService: SearchMetadataService) {}

    async resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<string> {
        const tripQuery = this.searchMetadataService.parseTripUrl(window.location.search);
        for (const langEntry of LANGUAGES.list) {
            if (langEntry.code === tripQuery.lang) {
                this.settingsService.setLanguage(langEntry);
                break;
            }
        }
        this.settingsService.setDeviceId(tripQuery.deviceId);
        return await this.searchMetadataService.openTrip(tripQuery).then(
            () => tripQuery.deviceId,
            () => tripQuery.deviceId
        );
    }
}
