import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { ISearchStatus, UserSearchService } from '@services/searchEngine/services/user-search-service';
import { AppConfigDataService } from '@services/modules/core/dataservices/app-config-data.service';
import { LANGUAGES } from '@core/constants/languages.const';
import { SettingsService } from '@services/services/settings-service';

@Injectable({
    providedIn: 'root',
})
export class RouteIdResolver implements Resolve<string> {
    constructor(
        private userSearchService: UserSearchService,
        private appConfigDataService: AppConfigDataService,
        private settingsService: SettingsService
    ) {}

    async resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<string> {
        for (const langEntry of LANGUAGES.list) {
            if (langEntry.i18nCode === route.url[0].path) {
                this.settingsService.setLanguage(langEntry);
                break;
            }
        }
        console.log('****** resolve route id called', this.settingsService.langCode);
        return await this.appConfigDataService.initDeviceId(true).then(
            (deviceId) =>
                this.userSearchService.searchFromRoute().then(
                    (searchStatus: ISearchStatus) => deviceId,
                    (searchStatus: ISearchStatus) => deviceId
                ),
            () => ''
        );
    }
}
