import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { StorageKeys } from '@schemas/modules/core/storage-keys';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { LANGUAGES } from '@core/constants/languages.const';
import { ILanguage } from '@schemas/modules/core/ILanguage';
import { TranslateService } from '@ngx-translate/core';
import { CurrencyService } from './currency-service';
import { EN_LOCALE } from '@core/utils/locales/en';
import { FR_LOCALE } from '@core/utils/locales/fr';
import { EN_US_LOCALE } from '@core/utils/locales/en-us';

@Injectable({
    providedIn: 'root',
})
export class SettingsService {
    private readonly selectedLanguage: BehaviorSubject<ILanguage>;
    private deviceId: string | undefined | null;
    // in discover mode we cannot book
    // user is currently viewing search results shared
    // we move to non discover mode when edit search is used
    private discoverMode = false;
    private xsfrToken = '';

    private appLocales: any = {
        'en-gb': EN_LOCALE,
        'fr-fr': FR_LOCALE,
        'en-us': EN_US_LOCALE,
    };

    /*private readonly defaultCur: Currency = {
        code: 'EUR',
        symbol: '€',
        name: 'Euro',
        trazlerSymbol: '€',
    };*/

    constructor(private translateService: TranslateService, private currencyService: CurrencyService) {
        this.selectedLanguage = this.initLanguage();
        this.getDeviceID();
        if (this.deviceId && this.deviceId.indexOf('nb_ID') === 0) {
            this.discoverMode = true;
        }
    }

    get langCode(): string {
        return this.selectedLanguage.getValue().code;
    }

    get locales(): any {
        return this.appLocales[this.i18nCode];
    }

    get isDiscoverMode(): boolean {
        return this.discoverMode;
    }

    get i18nCode(): string {
        return this.selectedLanguage.getValue().i18nCode;
    }

    get selectedLanguageObject(): ILanguage {
        return this.selectedLanguage.getValue();
    }

    get selectedLanguageSubject(): BehaviorSubject<ILanguage> {
        return this.selectedLanguage;
    }

    public static getIpCountry() {
        const ipTzCountry = StorageManagerUtil.get('ip_country');
        return ipTzCountry ? ipTzCountry : {};
    }

    public getCurrency(): string {
        return this.currencyService.currencyCode;
    }

    public getDeviceID(): string {
        if (this.deviceId) {
            return this.deviceId;
        }
        const queryParams = new URLSearchParams(window.location.search);

        const deviceId = queryParams.get('deviceId');
        if (deviceId) {
            this.storeDeviceId({ deviceID: deviceId });
            return deviceId;
        }
        const deviceID = StorageManagerUtil.get('deviceId', true);
        if (deviceID !== null) {
            this.setDeviceId(deviceID);
            return deviceID;
        }

        return '';
    }

    public setDeviceId(deviceId: string, discoverMode?: boolean) {
        this.discoverMode = !!discoverMode;
        this.deviceId = deviceId;
    }

    public storeDeviceId(response: any) {
        StorageManagerUtil.set('deviceId', response.deviceID, true);
        StorageManagerUtil.set('xsfrToken', response.xsfrToken, true);
        this.xsfrToken = response.xsfrToken;
        this.setDeviceId(response.deviceID, response.discoverMode);
        const sf: any = StorageManagerUtil.get('searchInfosLocal');
        if (sf) {
            sf.combId = '';
            StorageManagerUtil.set('searchInfos', sf);
        }
    }

    public clearDeviceId(): void {
        StorageManagerUtil.remove('deviceId');
        this.deviceId = null;
    }

    public setLanguage(selectedLanguage: ILanguage) {
        const lang = LANGUAGES.list.find((language: ILanguage) => language.i18nCode === selectedLanguage.i18nCode);
        const selectedLanguageObject = lang ? lang : LANGUAGES.list[0];
        StorageManagerUtil.set(StorageKeys.LANG, selectedLanguage);
        this.translateService.use(selectedLanguageObject.code).subscribe(() => {
            this.selectedLanguage.next(selectedLanguageObject);
        });
    }

    private initLanguage(): BehaviorSubject<ILanguage> {
        const selectedLanguage = StorageManagerUtil.get(StorageKeys.LANG);
        let selectedLanguageObject: ILanguage | undefined;
        if (selectedLanguage) {
            selectedLanguageObject = LANGUAGES.list.find(
                (language: ILanguage) => language.i18nCode === selectedLanguage.i18nCode
            );
            if (!selectedLanguageObject) {
            }
        } else {
            selectedLanguageObject = LANGUAGES.list.find((language: ILanguage) => language.code === LANGUAGES.default);
        }
        if (!selectedLanguageObject) {
            selectedLanguageObject = LANGUAGES.list[0];
        }
        this.translateService.setDefaultLang(LANGUAGES.default);
        this.translateService.use(selectedLanguageObject.code);
        return new BehaviorSubject(selectedLanguageObject);
    }
}
