import { Component, Input, OnInit } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { FlightTerm } from '@schemas/modules/flights/flight';

@Component({
    selector: 'tz-mob-flight-terms-conditions-modal',
    templateUrl: './mob-flight-terms-conditions-modal.component.html',
    styleUrls: ['./mob-flight-terms-conditions-modal.component.scss'],
})
export class MobFlightTermsConditionsModalComponent implements OnInit {
    @Input() data!: { terms: any };

    public flightTerms: FlightTerm[] = [];

    constructor() {}

    ngOnInit(): void {
        this.flightTerms = ToolsService.deepCopy(this.data.terms);
    }
}
