import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { UserService } from '@services/modules/user/services/user.service';
import { BillingAddress } from '@shared/schemas/tz-customer-info';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-billing-info',
    templateUrl: './billing-info.component.html',
    styleUrls: ['./billing-info.component.scss'],
})
export class BillingInfoComponent implements OnInit {
    @Input() customerInformation!: CustomerInfo;
    @Input() isProfile = false;
    @Input() formGroup = new FormGroup({});

    @Output() updateCustomerInfo = new EventEmitter<{ customer: CustomerInfo }>();
    @Output() loadForm = new EventEmitter<FormGroup>();

    public billingAddress!: BillingAddress;
    public formReadonly = false;

    constructor(private fb: FormBuilder, private userService: UserService, public platformService: PlatformService) {}

    ngOnInit() {
        if (this.isProfile) {
            this.billingAddress = this.customerInformation.billingAddress;
            this.formReadonly = this.billingAddress.addressUpdated && !this.platformService.isMobileDevice;
            this.buildBillingForm();
        }
        this.loadForm.emit(this.formGroup);
    }

    public toggoleEdit(): void {
        this.formReadonly = !this.formReadonly;
        this.setDefaultFormValues();
        this.formGroup.markAsPristine();
    }

    public updateBilling(): void {
        const values = this.formGroup.value;
        const billingAddress = {
            companyName: values.companyName,
            vatNo: values.vatNo,
            address: values.address,
            zipCode: values.zipCode,
            city: values.city,
            country: values.country.name,
            countryCode: values.country.code,
            updateBillingAddress: true,
        } as BillingAddress;
        this.customerInformation.billingAddress = billingAddress;

        this.updateCustomerInfo.emit({ customer: this.customerInformation });
        this.formReadonly = !this.platformService.isMobileDevice;
        this.billingAddress.addressUpdated = true;
        this.formGroup.markAsPristine();
    }

    private setFieldOptions(controlStr: string, value: any): void {
        const fieldCtrl = this.formGroup.controls[controlStr];

        if (!value || value === '' || !fieldCtrl) {
            return;
        }

        fieldCtrl.setValue(value);
    }

    private setDefaultFormValues(): void {
        if (this.userService.IsLogged()) {
            this.setFieldOptions('companyName', this.billingAddress.companyName);
            this.setFieldOptions('vatNo', this.billingAddress.vatNo);
            this.setFieldOptions('address', this.billingAddress.address);
            this.setFieldOptions('zipCode', this.billingAddress.zipCode);
            this.setFieldOptions('city', this.billingAddress.city);

            if (this.billingAddress.countryCode && this.billingAddress.countryCode !== '') {
                this.setFieldOptions('country', this.billingAddress.country);
            }
        }
    }

    private buildBillingForm(): void {
        this.formGroup = this.fb.group({
            companyName: new FormControl('', [Validators.pattern(ValidatorPatterns.STRING), Validators.maxLength(100)]),
            vatNo: new FormControl('', [Validators.pattern(ValidatorPatterns.STRING), Validators.maxLength(20)]),
            address: new FormControl('', [Validators.pattern(ValidatorPatterns.STRING), Validators.maxLength(100)]),
            zipCode: new FormControl('', [Validators.pattern(ValidatorPatterns.ZIPCODE), Validators.maxLength(10)]),
            city: new FormControl('', [Validators.pattern(ValidatorPatterns.STRING), Validators.maxLength(100)]),
            country: new FormControl(''),
        });
        this.setDefaultFormValues();
    }
}
