import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'tz-counter',
    templateUrl: './counter.component.html',
    styleUrls: ['./counter.component.scss'],
})
export class CounterComponent implements OnInit {
    @Input() count = 0;
    @Input() maxValue: number | undefined;
    @Input() minValue = 0;
    @Input() className = '';
    @Input() disabled = false;
    @Output() countChange: EventEmitter<number> = new EventEmitter<number>();
    @Output() maxValueLimitReached: EventEmitter<void> = new EventEmitter<void>();

    constructor() {}

    ngOnInit() {}

    public add(): void {
        if (this.maxValue === undefined || this.maxValue > 0) {
            this.count++;
            this.emitCounterValue();
        } else {
            this.maxValueLimitReached.emit();
        }
    }

    public substract() {
        if (this.minValue === this.count) {
            return;
        }

        if (this.count > 0) {
            this.count--;
            this.emitCounterValue();
        }
    }

    private emitCounterValue(): void {
        this.countChange.emit(this.count);
    }
}
