import { Injectable } from '@angular/core';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceId } from '@core/constants/service-id.const';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SearchHelpers } from '@services/searchEngine/services/search-helpers';
import { InconsistentIds } from '@core/constants/comb-service-ids';
import { TranslateService } from '@ngx-translate/core';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';

const DEP_DIRTY = {
    CLEAN: false,
    DIRTY: true,
};

@Injectable({
    providedIn: 'root',
})
export class TabRoutingService {
    private servicesToBook: any[] = [];
    private curServiceIdx = -1;
    private checkDepDirty = DEP_DIRTY.CLEAN;
    private movedToSummary = false;
    private servicesOrder: any = {};

    constructor(
        private cartService: CartService,
        private searchDataService: SearchDataService,
        private translateService: TranslateService,
        private listenersService: ListenersService
    ) {
        this.setServicesOrder();
        this.listenersService.registerListener(ListenerId.LISTEN_CART_CHANGED, this.refreshBookingStatus, this);
    }

    private static isInError(searchResults: any, serviceId?: ServiceId) {
        /* if (serviceId === ServiceId.FLIGHT_TRAIN) {
    serviceId = ServiceId.FLIGHT;
    }
    if (searchResults[serviceId] && searchResults[serviceId].count === 0) {
    if (serviceId === ServiceId.FLIGHT_TRAIN) {
    return this.isInError(ServiceId.TRAIN);
    } else {
    return true
    }
    }*/
        return false;
    }

    public refreshBookingStatus() {
        const cartItems = this.cartService.getCartData();
        this.resetBookingStatus();
        if (cartItems && cartItems.items) {
            for (const cartItem of cartItems.items) {
                if (!cartItem.isVirtual) {
                    this.setBooked(cartItem.serviceId);
                }
            }
        }
    }

    public setBooked(bookedServiceId?: ServiceId) {
        if (bookedServiceId !== undefined && typeof bookedServiceId === 'number') {
            if (bookedServiceId === ServiceId.FLIGHT || bookedServiceId === ServiceId.TRAIN) {
                bookedServiceId = ServiceId.FLIGHT_TRAIN;
            }
            const idx = this.getIdxFromServiceId(bookedServiceId);
            if (idx !== -1) {
                this.servicesToBook[idx].booked = true;
            }
            this.curServiceIdx = -1;
        }
        return this.setNext(-1);
    }

    public setConsistent(
        serviceId: any,
        val: any,
        infos: any,
        inconsistentId: number = InconsistentIds.NOT_SPECIFIC
    ): void {
        console.log('consistent for service id ' + serviceId + ' set to:' + val);
        if (serviceId !== undefined) {
            if (typeof serviceId !== 'number') {
                serviceId = parseInt(serviceId, 10);
            }
            const idx = this.getIdxFromServiceId(serviceId);
            if (idx !== -1) {
                this.servicesToBook[idx].consistent = val;
                if (!val) {
                    this.servicesToBook[idx].consistentInfos = infos;
                    this.servicesToBook[idx].inconsistentId = inconsistentId;
                } else {
                    this.servicesToBook[idx].consistentInfos = '';
                    this.servicesToBook[idx].inconsistentId = InconsistentIds.NOT_SPECIFIC;
                }
            }
        }
    }

    public getConsistentMsg(serviceId: any): string {
        if (serviceId !== undefined && typeof serviceId === 'number') {
            const idx = this.getIdxFromServiceId(serviceId);
            if (idx !== -1) {
                switch (this.servicesToBook[idx].inconsistentId) {
                    case InconsistentIds.ADD_TRANSFER_TO_1001:
                        return this.translateService.instant('POPUPS.TRIP_INCONSISTENT.RECOMPUTE_CAR_RENTAL_TRANS');
                    case InconsistentIds.ADD_TRANSFER_TO_1101:
                        return this.translateService.instant('POPUPS.TRIP_INCONSISTENT.RECOMPUTE_CAR_RENTAL_ACC');
                }
            }
            return this.translateService.instant('POPUPS.TRIP_INCONSISTENT.BODY', {
                services: 'services',
                service: this.translateService.instant('SERVICES.' + serviceId).toLowerCase(),
            });
        }
        return '';
    }

    public addService(serviceId: ServiceId): void {
        this.initRouting(this.searchDataService.addService(serviceId), false);
    }

    public initRouting(services: any, reset: any): void {
        this.initRoutingInternal(
            SearchHelpers.getServicesList(services),
            this.searchDataService.getAllSearchResults(),
            reset
        );
    }

    // change state to next one
    public setNext(bookedServiceId?: ServiceId) {
        this.curServiceIdx = this.getNext(bookedServiceId);
        if (this.servicesToBook.length > this.curServiceIdx) {
            return this.getCurService(this.curServiceIdx);
        }
    }

    // return next service id after provided one
    // return ServiceId.INVALID if next is not a combination service

    public getNextInOrder(serviceId: ServiceId): ServiceId {
        let serviceIdx = this.getIdxFromServiceId(serviceId);
        let found = false;

        if (serviceIdx === -1) {
            serviceIdx = 0;
            found = true;
            serviceId = ServiceId.INVALID;
        }

        if (serviceId === ServiceId.FLIGHT || serviceId === ServiceId.TRAIN) {
            serviceId = ServiceId.FLIGHT_TRAIN;
        }

        while (this.servicesToBook.length > serviceIdx) {
            if (this.servicesToBook[serviceIdx].serviceId === ServiceId.SUMMARY) {
                return ServiceId.INVALID;
            }
            if (this.servicesToBook[serviceIdx].serviceId === serviceId) {
                found = true;
            } else if (found && !this.cartService.isServicesBooked(this.servicesToBook[serviceIdx].serviceId)) {
                return this.servicesToBook[serviceIdx].serviceId;
            }
            serviceIdx++;
        }
        // never reached normally
        return ServiceId.INVALID;
    }

    public getServiceInfos(serviceId: ServiceId) {
        const idx = this.getIdxFromServiceId(serviceId);
        if (idx !== -1) {
            return this.servicesToBook[idx];
        }
    }

    // set all bookingOptions status based on cart
    // used by trip planner to set statuses after the server
    // created cart on user behalf

    public setDirtyCheckDep(): void {
        this.checkDepDirty = DEP_DIRTY.DIRTY;
    }

    public setCleanCheckDep(): void {
        this.checkDepDirty = DEP_DIRTY.CLEAN;
    }

    public isCheckDepDirty(): boolean {
        return this.checkDepDirty;
    }

    public hasMovedToSummary(): boolean {
        return this.movedToSummary;
    }

    public setMovedToSummary(status: boolean): void {
        this.movedToSummary = status;
    }

    public setSearchError(serviceId: ServiceId): void {
        for (const serviceToBook of this.servicesToBook) {
            if (serviceToBook.serviceId === serviceId) {
                serviceToBook.searchError = true;
                return;
            }
        }
    }

    public unsetBookedDependencies(serviceIdMapToUpd: any) {
        if (serviceIdMapToUpd) {
            for (const service of this.servicesToBook) {
                if (
                    serviceIdMapToUpd[service.serviceId] ||
                    (service.serviceId === ServiceId.FLIGHT_TRAIN &&
                        (serviceIdMapToUpd[ServiceId.FLIGHT] || serviceIdMapToUpd[ServiceId.TRAIN]))
                ) {
                    service.booked = false;
                }
            }
        }
    }

    // when checkAll is set we return list of dependent services, booked or not
    public hasDependencies(curServiceId: ServiceId, checkAll: boolean) {
        let idx = checkAll ? this.getIdxFromServiceId(curServiceId) : 0;
        if (idx === -1) {
            idx = 0;
        }
        if (curServiceId === ServiceId.ACC || curServiceId === ServiceId.FLIGHT || curServiceId === ServiceId.TRAIN) {
            if (curServiceId === ServiceId.FLIGHT || curServiceId === ServiceId.TRAIN) {
                curServiceId = ServiceId.FLIGHT_TRAIN;
            }
        }

        const serviceList = [];
        while (this.servicesToBook.length > idx) {
            if (curServiceId !== this.servicesToBook[idx].serviceId && (checkAll || this.servicesToBook[idx].booked)) {
                serviceList.push(this.servicesToBook[idx].serviceId);
            }
            idx++;
        }
        return serviceList;
    }

    public removeService(serviceId: ServiceId) {
        if (serviceId === ServiceId.FLIGHT || serviceId === ServiceId.TRAIN) {
            serviceId = ServiceId.FLIGHT_TRAIN;
        }
        for (let i = 0; i < this.servicesToBook.length; i++) {
            if (this.servicesToBook[i].serviceId === serviceId) {
                this.servicesToBook.splice(i, 1);
                return;
            }
        }
    }

    public areAllBooked(): boolean {
        let transportFound = false;
        let ignoreTransport = false;

        for (const serviceToBook of this.servicesToBook) {
            if (SearchHelpers.isTransport(serviceToBook.serviceId)) {
                if (ignoreTransport) {
                    continue;
                }
                if (transportFound && !serviceToBook.booked && !TabRoutingService.isInError(serviceToBook.serviceId)) {
                    return false;
                } else {
                    transportFound = true;
                    ignoreTransport = serviceToBook.booked || TabRoutingService.isInError(serviceToBook.serviceId);
                }
            }
            if (
                serviceToBook.serviceId !== ServiceId.SUMMARY &&
                serviceToBook.serviceId !== ServiceId.BOOKING &&
                (!serviceToBook.booked || TabRoutingService.isInError(serviceToBook.serviceId))
            ) {
                return false;
            }
        }

        return !(transportFound && !ignoreTransport);
    }

    public isConsistent(inconsistentServiceId: ServiceId): boolean {
        if (inconsistentServiceId !== undefined && typeof inconsistentServiceId === 'number') {
            const idx = this.getIdxFromServiceId(inconsistentServiceId);
            if (idx !== -1) {
                return this.servicesToBook[idx].consistent;
            }
        }
        return false;
    }

    private resetBookingStatus(): void {
        for (const serviceToBook of this.servicesToBook) {
            serviceToBook.booked = false;
        }
    }

    private initRoutingInternal(serviceList: any, searchResults: any, reset: any) {
        this.buildRouting(serviceList);
        if (reset) {
            this.setMovedToSummary(false);
        } else {
            const bookedService = this.cartService.getUserValidatedServices() as any;
            let last = -1;

            for (let i = 0; i < this.servicesToBook.length; i++) {
                this.servicesToBook[i].searchError = TabRoutingService.isInError(
                    searchResults,
                    this.servicesToBook[i].serviceId
                );
                if (
                    bookedService[this.servicesToBook[i].serviceId] ||
                    (this.servicesToBook[i].serviceId === ServiceId.FLIGHT_TRAIN &&
                        (bookedService[ServiceId.TRAIN] || bookedService[ServiceId.FLIGHT]))
                ) {
                    this.servicesToBook[i].booked = true;
                    if (last === i - 1) {
                        last = i;
                    }
                } else {
                    if (last === -1) {
                        last = i;
                    }
                }
            }
            this.curServiceIdx = last;
        }
    }

    private getIdxFromServiceId(serviceId: any): number {
        for (let i = 0; i < this.servicesToBook.length; i++) {
            if (
                this.servicesToBook[i].serviceId === serviceId ||
                (this.servicesToBook[i].serviceId === ServiceId.FLIGHT_TRAIN &&
                    (serviceId === ServiceId.FLIGHT || serviceId === ServiceId.TRAIN))
            ) {
                return i;
            }
        }
        console.error('get service idx not found for serviceId:' + serviceId);
        return -1;
    }

    private getCurService(serviceIdx: ServiceId) {
        if (this.servicesToBook[serviceIdx]) {
            return this.servicesToBook[serviceIdx];
        }
        console.error('get cur service not found for curServiceIdx:' + serviceIdx);
    }

    private getNext(bookedServiceId?: ServiceId) {
        let serviceIdx = 0; //this.curServiceIdx < 0 ? 0 : this.curServiceIdx; //movedToSummary ? curServiceIdx : 0;
        // skip service which are at the same priority level of current one or next service is already booked
        // this is done typically for train / flights, one of them should be used not both
        while (
            this.servicesToBook.length > serviceIdx &&
            this.servicesToBook[serviceIdx].booked &&
            (this.cartService.isServicesBooked(this.servicesToBook[serviceIdx].serviceId) ||
                this.servicesToBook[serviceIdx].consistent ||
                this.servicesToBook[serviceIdx].serviceId === bookedServiceId)
        ) {
            if (this.movedToSummary && this.servicesToBook[serviceIdx].serviceId === ServiceId.SUMMARY) {
                break;
            }
            serviceIdx++;
        }

        return serviceIdx;
    }

    private buildRouting(serviceList: any[]) {
        const serviceToOrder: any[] = [];

        for (const service of serviceList) {
            if (this.servicesOrder[service] !== undefined) {
                if (!serviceToOrder[this.servicesOrder[service]]) {
                    serviceToOrder[this.servicesOrder[service]] = [];
                }
                serviceToOrder[this.servicesOrder[service]].push(parseInt(service, 10));
            } else {
                console.log('Error: unknown service: ' + service);
            }
        }

        // build ordered list of services to book
        this.servicesToBook = [];
        for (const orderingService of serviceToOrder) {
            if (orderingService !== undefined) {
                for (const serviceId of orderingService) {
                    this.servicesToBook.push({
                        serviceId,
                        booked: false,
                        searchError: false,
                        consistent: true,
                        consistentInfos: '',
                    });
                }
            }
        }
        // add summary and bookingOptions service at the end if there is at least one service to book
        if (this.servicesToBook.length > 0) {
            this.servicesToBook.push({
                serviceId: ServiceId.SUMMARY,
                booked: false,
                consistent: true,
                consistentInfos: '',
            });
            this.servicesToBook.push({
                serviceId: ServiceId.BOOKING,
                booked: false,
                consistent: true,
                consistentInfos: '',
            });
        }

        this.curServiceIdx = -1;
    }

    private setServicesOrder(): void {
        this.servicesOrder[ServiceId.FLIGHT] = 0;
        this.servicesOrder[ServiceId.FLIGHT_TRAIN] = 0;
        this.servicesOrder[ServiceId.TRAIN] = 0;
        this.servicesOrder[ServiceId.ACC] = 1;
        this.servicesOrder[ServiceId.TRANSFER] = 2;
        this.servicesOrder[ServiceId.CAR_RENTAL] = 3;
    }
}
