/*
Service for pre-Search minimal data
 */

import { Injectable } from '@angular/core';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { ServiceId } from '@core/constants/service-id.const';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SearchEvents } from '@core/constants/search-events';
import { PollerService } from '@services/modules/core/poller.service';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { ToolsService } from '@core/utils/tools-service';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';
import { CombinationId, DataId } from '@schemas/modules/core/base-types';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class TripPlannerService {
    private activeCombId: CombinationId = '';
    private preSearchInfos?: any = null;
    private runningRequest: any = {};
    private notifyWaiters: { [key: CombinationId]: [(params?: any) => void] } = {}; // list of waiters to notify when poller has finished
    private onErrorCb: any;
    private accPollerId: any = null;
    private flightPollerId: any = null;

    constructor(
        private tabRoutingService: TabRoutingService,
        private listenersService: ListenersService,
        private apiAccessService: ApiAccessService,
        private cartService: CartService,
        private pollerService: PollerService,
        private searchDataService: SearchDataService,
        private searchMetadataService: SearchMetadataService
    ) {
        this.initiatePreSearchInfo();
    }

    private static isTransport(serviceId: number): boolean {
        return serviceId === ServiceId.TRAIN || serviceId === ServiceId.FLIGHT;
    }

    public reInit(combId: any, searchResults: any) {
        this.activeCombId = combId;
        this.preSearchInfos = {
            services: {},
            combId,
            complete: true,
        };
        for (const serviceId in searchResults) {
            if (searchResults[serviceId]) {
                this.preSearchInfos.services[serviceId] = searchResults[serviceId];
                this.preSearchInfos.services[serviceId].complete = true;
                this.preSearchInfos.services[serviceId].count = searchResults[serviceId].resultLength;
                this.tabRoutingService.setConsistent(serviceId, true, '');
            }
        }
        this.set(this.preSearchInfos);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
    }

    // refId is used to uniquely identify messages from this trip planner request
    // and more importantly get messages after trip planner ended
    // when refId is provided use it, otherwise create a fresh one
    public poller(combId: CombinationId, isNewCombi: boolean, anticipatedList: any, onError: any, refId?: string) {
        if (onError) {
            this.onErrorCb = onError;
        }
        this.preSearchInfos.complete = false;
        if (isNewCombi) {
            this.clear(combId);
        } else {
            this.activeCombId = combId;
        }
        // set complete to false to display loader upfront
        // not all anticipatedList will end up being recomputed
        console.log('anticipated list', {
            anticipatedList,
        });
        for (const serviceId in anticipatedList) {
            if (anticipatedList.hasOwnProperty(serviceId)) {
                if (parseInt(serviceId, 10) === ServiceId.FLIGHT_TRAIN) {
                    this.preSearchInfos.services[ServiceId.FLIGHT] = {
                        complete: false,
                    };
                    this.preSearchInfos.services[ServiceId.TRAIN] = {
                        complete: false,
                    };
                } else {
                    this.preSearchInfos.services[serviceId] = {
                        complete: false,
                    };
                }
            }
        }
        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
        console.log('trip planner poller requested for combId:' + combId);

        // unique number to identify this poller request
        if (!refId) {
            refId = '' + Math.floor(Math.random() * 1000000);
        }
        this.preSearchInfos.refId = refId;
        this.callTripPlanner(combId, -1, false, refId, this.firstPoller.bind(this, refId), this.pollErr.bind(this));
        return refId;
    }

    public get(serviceId: ServiceId): any {
        let res: any = {
            enable: false,
            loader: false,
            complete: false,
        };
        if (!this.preSearchInfos) {
            return res;
        }

        // handle transport case with tab listening to both flight and train
        // TODO prefer best user choice
        if (serviceId === ServiceId.FLIGHT_TRAIN) {
            if (
                this.preSearchInfos.services[ServiceId.FLIGHT] &&
                this.preSearchInfos.services[ServiceId.FLIGHT].complete
            ) {
                serviceId = ServiceId.FLIGHT;
            } else if (
                this.preSearchInfos.services[ServiceId.TRAIN] &&
                this.preSearchInfos.services[ServiceId.TRAIN].complete
            ) {
                serviceId = ServiceId.TRAIN;
            } else {
                serviceId = ServiceId.FLIGHT;
            }
        }
        const info = this.preSearchInfos.services[serviceId];
        if (info) {
            res = {
                enable: !info.complete || info.count > 0,
                loader: !info.complete && !this.preSearchInfos.complete,
                price: info.recommendedItemPrice,
                cur: info.recommendedItemPriceCur,
                complete: info.complete,
            };
        }
        return res;
    }

    // used when a service is removed
    public removeServiceSearchInfos(serviceId: ServiceId) {
        delete this.preSearchInfos.services[serviceId];
        if (serviceId === ServiceId.FLIGHT || serviceId === ServiceId.FLIGHT_TRAIN) {
            delete this.preSearchInfos.services[ServiceId.TRAIN];
        }
        if (serviceId === ServiceId.TRAIN || serviceId === ServiceId.FLIGHT_TRAIN) {
            delete this.preSearchInfos.services[ServiceId.FLIGHT];
        }
        this.set(this.preSearchInfos);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
    }

    public isRunning(combId: string, deferred?: (params?: any) => void): boolean | void {
        if (!deferred) {
            return this.runningRequest[combId] === true;
        }
        if (this.runningRequest[combId]) {
            console.log('store waiter, for poller ends');
            if (!this.notifyWaiters[combId]) {
                this.notifyWaiters[combId] = [deferred];
            } else {
                this.notifyWaiters[combId].push(deferred);
            }
        } else {
            deferred();
        }
    }

    public setCartData(combId: CombinationId, serviceId: ServiceId, dataId: DataId, cartReq: any): Observable<any> {
        if (this.isRunning(combId)) {
            return new Observable((subscriber) => {
                console.log('starting notifier waiter');
                const resolver = () => {
                    console.log('poller notification received');
                    this.cartService
                        .setCartData(combId, serviceId, dataId, cartReq)
                        .pipe(take(1))
                        .subscribe(
                            (res: any) => {
                                console.log('cart service finished in success');
                                subscriber.next(res);
                                subscriber.complete();
                            },
                            (res: any) => {
                                console.log('cart service finished in error');
                                subscriber.error(res);
                                subscriber.complete();
                            }
                        );
                };
                this.isRunning(combId, resolver);
            });
        } else {
            return this.cartService.setCartData(combId, serviceId, dataId, cartReq);
        }
    }

    public registerPoller(combId: CombinationId, dataId: DataId, serviceId: any, cb: any) {
        console.log('register poller for service id:' + serviceId + ' on data id:' + dataId);
        switch (serviceId) {
            case ServiceId.ACC:
                if (this.accPollerId) {
                    if (this.accPollerId.dataId === dataId) {
                        return this.pollerService.registerPoller(combId, dataId, ServiceId.ACC, cb);
                    } else {
                        return this.pollerService.registerPoller(
                            combId,
                            dataId,
                            ServiceId.ACC,
                            this.hdlPollerResult.bind(this, ServiceId.ACC, cb)
                        );
                    }
                } else {
                    this.accPollerId = this.pollerService.registerPoller(
                        combId,
                        dataId,
                        ServiceId.ACC,
                        this.hdlPollerResult.bind(this, ServiceId.ACC, cb)
                    );
                    return this.accPollerId;
                }
            case ServiceId.FLIGHT:
                if (this.flightPollerId) {
                    if (this.flightPollerId.dataId === dataId) {
                        return this.pollerService.registerPoller(combId, dataId, ServiceId.FLIGHT, cb);
                    } else {
                        return this.pollerService.registerPoller(
                            combId,
                            dataId,
                            ServiceId.FLIGHT,
                            this.hdlPollerResult.bind(this, ServiceId.FLIGHT, cb)
                        );
                    }
                } else {
                    this.flightPollerId = this.pollerService.registerPoller(
                        combId,
                        dataId,
                        ServiceId.FLIGHT,
                        this.hdlPollerResult.bind(this, ServiceId.FLIGHT, cb)
                    );
                    return this.flightPollerId;
                }
        }

        return null;
    }

    public unregisterPoller(pollerId: any) {
        this.pollerService.unregisterPoller(pollerId);
    }

    public setComplete(serviceId: ServiceId): void {
        if (this.preSearchInfos && this.preSearchInfos.services[serviceId]) {
            this.preSearchInfos.services[serviceId].complete = true;
            if (serviceId === ServiceId.FLIGHT && this.preSearchInfos.services[ServiceId.TRAIN]) {
                this.preSearchInfos.services[ServiceId.TRAIN].complete = true;
            }
            this.set(this.preSearchInfos);
            this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
        }
    }

    public clear(combId: CombinationId) {
        this.activeCombId = combId;
        this.preSearchInfos = {
            services: {},
            combId,
            complete: false,
        };
        this.set(this.preSearchInfos);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
    }

    private firstPoller(refId: any, decodedRes: any) {
        if (decodedRes.completed) {
            if (this.activeCombId) {
                delete this.runningRequest[this.activeCombId];
            }
        }
        this.pollResult(refId, decodedRes);
    }

    private initiatePreSearchInfo(): void {
        const infos: any = StorageManagerUtil.get('preSearchInfos');
        if (infos) {
            this.cartService.InitDone.then(() => {
                this.preSearchInfos = infos;
                this.syncCarts(this.cartService.getCartData());
                this.set(this.preSearchInfos);
                if (this.preSearchInfos && !this.preSearchInfos.complete) {
                    const combId = this.preSearchInfos.combId;
                    this.clear(combId);
                    this.fetch(combId, this.preSearchInfos.refId);
                }
                this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
            });
        } else {
            this.preSearchInfos = {
                services: {},
            };
        }
    }

    private set(searchInfos: any): void {
        this.preSearchInfos = searchInfos;
        StorageManagerUtil.set('preSearchInfos', this.preSearchInfos);
        //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, !this.isCompleted());
    }

    private isCompleted(): boolean {
        if (!this.preSearchInfos || this.preSearchInfos.complete === undefined) {
            return true;
        }

        //On mobile, we want to hide Progress bar when all services are completed including
        // flight upgrade for instance

        for (const serviceId in this.preSearchInfos.services) {
            if (!this.preSearchInfos.services[serviceId].complete) {
                if (
                    parseInt(serviceId, 10) !==
                    ServiceId.FLIGHT /*|| $rootScope.stateName === "home.results.travel.flights"*/
                ) {
                    return false;
                }
            }
        }
        return this.preSearchInfos.complete;
    }

    // validate takes true/false based on retry/new-fetch.
    private fetch(combId: CombinationId, refId: string) {
        if (!this.isValidCombId(combId)) {
            return;
        }
        this.apiAccessService.checkTmpPreSearch(
            combId,
            (res: any) => {
                console.log(res);
                this.decodeRes(res, combId);
                if (!res.completed) {
                    // get further results
                    this.callTripPlanner(
                        this.activeCombId,
                        res.lastVal,
                        false,
                        refId,
                        this.pollResult,
                        this.pollErr.bind(this)
                    );
                }
                this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
            },
            (failure: any) => {
                console.log(failure);
                // @Todo: handle error
            }
        );
    }

    // isValidCombId is validated to check current combination is invoked or not.
    // And also to avoid unnecessary api calls
    private isValidCombId(combId: CombinationId) {
        return this.activeCombId === combId;
    }

    private callTripPlanner(
        activeCombId: CombinationId,
        lastVal: number,
        loader: any,
        refId: string,
        _pollResult: any,
        _pollErr: any
    ) {
        if (!this.runningRequest[activeCombId]) {
            this.runningRequest[activeCombId] = true;
            const mySubscriber = this.apiAccessService
                .tripPlannerChecker(activeCombId, lastVal, loader, refId)
                .subscribe(
                    (success: any) => {
                        console.log('next:' + success);
                        _pollResult(success);
                    },
                    (failure: any) => {
                        console.log('next failure:' + failure);
                        mySubscriber.unsubscribe();
                    },
                    () => {
                        console.log('complete');
                        mySubscriber.unsubscribe();
                    }
                );
        } else {
            console.log('skipping trip planner checker call');
        }
    }

    private pollResult(refId: any, res: any) {
        switch (true) {
            case res.serviceList !== null:
                // eslint-disable-next-line
                for (const serviceId in res.serviceList) {
                    this.preSearchInfos.services[serviceId] = {
                        complete: false,
                        count: 0,
                    };
                }
                this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
                break;
            case res.tripPlannerSearchInfos !== undefined:
                //set search data with values from server (TRAZ-8527)
                //console.log('getting search infos:'+res.tripPlannerSearchInfos);
                this.searchMetadataService.saveTripPlannerSearchInfos(
                    res.tripPlannerSearchInfos,
                    res.tripPlannerSearchInfos.serviceId
                );
                //trigger refresh of edit bar content with new values
                this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_EXT_CHANGE);
                break;
            case res.results !== null:
                console.log('got tmp search res:', res);
                this.setPreSearchInfos(res.results, this.activeCombId, res.completed);
                this.set(this.preSearchInfos);
                switch (res.results.metaData.serviceId) {
                    case ServiceId.ACC:
                        this.accPollerId = res.results.metaData.dataId;
                        this.pollerService.registerPoller(
                            this.activeCombId,
                            res.results.metaData.dataId,
                            ServiceId.ACC,
                            this.hdlPollerResult.bind(this, ServiceId.ACC, null)
                        );
                        break;
                    case ServiceId.FLIGHT:
                        this.flightPollerId = res.results.metaData.dataId;
                        this.pollerService.registerPoller(
                            this.activeCombId,
                            res.results.metaData.dataId,
                            ServiceId.FLIGHT,
                            this.hdlPollerResult.bind(this, ServiceId.FLIGHT, null)
                        );
                        break;
                }
                this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
            // eslint-disable-next-line no-fallthrough
            default:
                if (!res.completed) {
                    // get further results
                    this.cartService.reInit(true).then(() => {
                        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
                    });
                } else {
                    console.log('getting complete status');
                    for (const serviceId in this.preSearchInfos.services) {
                        if (this.preSearchInfos.services[serviceId].count === 0) {
                            this.preSearchInfos.services[serviceId].complete = true;
                        }
                    }
                    this.preSearchInfos.complete = true;
                    if (res.status === 410) {
                        this.hdlErrorCB();
                    } else {
                        //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, !this.isCompleted());
                        this.cartService.reInit(true).then(
                            (cartData: any) => {
                                this.syncCarts(cartData);
                                this.listenersService.NotifyListeners(
                                    ListenerId.LISTEN_REPRICED,
                                    this.preSearchInfos.complete
                                );
                                this.end(this.activeCombId);
                                this.listenersService.NotifyListeners(ListenerId.LISTEN_CART_CHANGED, true);
                            },
                            (failure: any) => {
                                console.error(failure);
                                this.end(this.activeCombId);
                                this.listenersService.NotifyListeners(ListenerId.LISTEN_CART_CHANGED, true);
                            }
                        );
                    }
                }
        }
    }

    private end(combId: CombinationId) {
        delete this.runningRequest[combId];

        // notify waiters if any
        if (this.notifyWaiters[combId]) {
            console.log('notify waiters of poller ends');
            for (const notifyWaiter of this.notifyWaiters[combId]) {
                console.log('notifying');
                notifyWaiter();
            }
            delete this.notifyWaiters[combId];
        }
    }

    // remove from preSearchInfos cart that does not exists
    private syncCarts(cartData: any) {
        const existsServices: any = {};
        let modified = false;

        if (cartData && cartData.items) {
            for (const cartItem of cartData.items) {
                existsServices[cartItem.serviceId] = true;
            }
        }

        for (const serviceId in this.preSearchInfos.services) {
            if (!existsServices[serviceId]) {
                modified = true;
                if (this.preSearchInfos.services[serviceId]) {
                    this.preSearchInfos.services[serviceId].recommendedItemPrice = 0;
                    this.preSearchInfos.services[serviceId].nbOnwards = 0;
                    this.preSearchInfos.services[serviceId].nbReturns = 0;
                    this.preSearchInfos.services[serviceId].count = 0;
                }
            }
        }
        if (modified) {
            this.set(this.preSearchInfos);
        }
    }

    private hdlErrorCB() {
        if (this.onErrorCb) {
            const promise = this.onErrorCb();
            if (promise) {
                Promise.resolve(promise).then(
                    () => {
                        this.end(this.activeCombId);
                    },
                    () => {
                        this.end(this.activeCombId);
                    }
                );
            } else {
                this.end(this.activeCombId);
            }
        } else {
            this.end(this.activeCombId);
        }
    }

    private pollErr(err: any) {
        console.error('error on pre tmp search received:' + err);
        if (this.activeCombId) {
            delete this.runningRequest[this.activeCombId];
        }
        for (const serviceId in this.preSearchInfos.services) {
            if (
                !this.preSearchInfos.services[serviceId].complete &&
                this.preSearchInfos.services[serviceId].count === 0
            ) {
                this.preSearchInfos.services[serviceId] = {
                    complete: true,
                    count: 0,
                };
            }
        }

        // on validation error from server side update the service search in local
        const res = err.error;
        if (res && res.results !== null) {
            const results = res.results;
            results.serviceSearch.failureCode = res.failureCode;
            results.serviceSearch.modifyData = res.adjustData;
            this.setPreSearchInfos(results, this.activeCombId, true);
        }

        this.set(this.preSearchInfos);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, this.preSearchInfos.complete);
        this.hdlErrorCB();
    }

    private hdlPollerResult(servicesId: any, cb: any, metaData: any, error: any) {
        const consoleString =
            'get poller result for service id:' +
            servicesId +
            ' complete is:' +
            (metaData ? metaData.complete : undefined);
        console.log(consoleString);
        if (metaData && metaData.complete) {
            switch (servicesId) {
                case ServiceId.ACC:
                    this.accPoller(metaData);
                    // eslint-disable-next-line @typescript-eslint/no-unused-expressions
                    cb && cb(metaData, null);
                    break;
                case ServiceId.FLIGHT:
                    this.flightPoller();
                    // eslint-disable-next-line @typescript-eslint/no-unused-expressions
                    cb && cb(metaData, null);
                    break;
                default:
            }
        } else {
            // eslint-disable-next-line @typescript-eslint/no-unused-expressions
            cb && cb(null, error);
        }
    }

    private flightPoller() {
        const data = this.searchDataService.getSearchResult(ServiceId.FLIGHT);
        if (data) {
            data.complete = true;
        }
        this.searchDataService.saveResult(ServiceId.FLIGHT, data, SearchEvents.EVENT_SEARCH_WEB);
        this.cartService.reInit(true);
        this.setComplete(ServiceId.FLIGHT);
    }

    private accPoller(metaData: any) {
        metaData.resultLength = metaData.count;
        delete metaData.count;
        const data = this.searchDataService.getSearchResult(ServiceId.ACC);
        if (data && data.minimalPrice < metaData.minimalPrice) {
            metaData.minimalPrice = data.minimalPrice;
        }
        metaData.sortBy = data.sortBy;
        console.log('Current poll minimum price', metaData.minimalPrice);

        this.searchDataService.saveResult(ServiceId.ACC, metaData, SearchEvents.EVENT_SEARCH_WEB);
        this.searchDataService.updSearchResult(
            ServiceId.ACC,
            {
                minimalPrice: metaData.minimalPrice,
            },
            SearchEvents.EVENT_FILTER
        );
        this.setComplete(ServiceId.ACC);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_ACC_REPRICED, this.preSearchInfos.complete);
    }

    private setPreSearchInfos(results: any, combId: any, completed: any) {
        this.preSearchInfos.services[results.metaData.serviceId] = ToolsService.deepCopy(results.metaData);
        this.preSearchInfos.services[results.metaData.serviceId].complete = results.metaData.complete;
        this.preSearchInfos.complete = completed;
        this.preSearchInfos.combId = combId;
        this.searchMetadataService.saveSearch(results);
        if (results.metaData.complete) {
            this.setComplete(results.metaData.serviceId);
        }
        this.tabRoutingService.setConsistent(results.metaData.serviceId, true, '');
        if (
            completed &&
            results.metaData.count === 0 &&
            TripPlannerService.isTransport(results.metaData.serviceId) &&
            results.metaData.secondDataId
        ) {
            const serviceId = results.metaData.serviceId === ServiceId.TRAIN ? ServiceId.FLIGHT : ServiceId.TRAIN;
            this.apiAccessService.getMetaRes(combId, serviceId, results.metaData.secondDataId).then(
                (res: any) => {
                    if (res.metaData.complete) {
                        this.setComplete(results.metaData.serviceId);
                    }
                    this.searchMetadataService.saveSearch(res);
                },
                (error: any) => {
                    console.error(error);
                }
            );
        }
    }

    private decodeRes(res: any, combId: CombinationId) {
        console.log('[tmpSearch][check]', { res });

        for (const serviceId in res.metaResMap) {
            if (res.metaResMap[serviceId] != null) {
                this.setPreSearchInfos(res.metaResMap[serviceId], combId, res.completed);
            } else {
                this.preSearchInfos.services[serviceId] = {
                    complete: false,
                    count: 0,
                };
            }
        }
        this.set(this.preSearchInfos);
    }
}
