import { Injectable, Injector } from '@angular/core';
import { CartService } from '@services/modules/cart/services/cart.service';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { Router } from '@angular/router';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ServiceId } from '@core/constants/service-id.const';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { Observable, Subscriber } from 'rxjs';
import { take } from 'rxjs/operators';
import { CartDataService } from '@services/modules/cart/dataservices/cart.data.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { UserSearchService } from '@services/searchEngine/services/user-search-service';
import { MatDialogRef } from '@angular/material/dialog';
// eslint-disable-next-line max-len
import { TrazlerConfirmationDialogComponent } from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CartStatus } from '@core/constants/cart-status';

@Injectable({
    providedIn: 'root',
})
export class CartRemoveService {
    constructor(
        private cartService: CartService,
        private tabRoutingService: TabRoutingService,
        private router: Router,
        private tripPlannerService: TripPlannerService,
        private searchDataService: SearchDataService,
        private trazlerAPI: ApiAccessService,
        private cartRouterService: CartRoutingService,
        private cartDataService: CartDataService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private injector: Injector
    ) {}

    private static isPartial(cartData: any, serviceIdToRemove: ServiceId, optionId: string | undefined): boolean {
        let nbToRemove = 0;
        if (serviceIdToRemove === ServiceId.TRAIN || serviceIdToRemove === ServiceId.TRANSFER) {
            return false;
        }
        for (const item of cartData.items) {
            if (item.serviceId === serviceIdToRemove) {
                if (optionId !== undefined && optionId !== '') {
                    for (const option of item.selectedItem.bookingOptions) {
                        if (option.optionId !== optionId) {
                            return true;
                        }
                    }
                }
                nbToRemove++;
            }
            if (nbToRemove > 1) {
                return true;
            }
        }
        return false;
    }

    private static isLastToRemove(cartData: any, serviceIdToRemove: ServiceId): boolean {
        let nbTransfersToRemove = 0;
        for (const item of cartData.items) {
            if (item.serviceId !== serviceIdToRemove) {
                return false;
            }
            if (serviceIdToRemove === ServiceId.TRANSFER) {
                nbTransfersToRemove++;
            }
        }
        return nbTransfersToRemove <= 1;
    }

    /*
    isLastToBook: returns true if after current item removal all remaining items
    are under booking (or cancelled) and none is unavailable
     */
    private static wasLastToBook(cartData: any, serviceIdToRemove: ServiceId, optionId: string | undefined): boolean {
        let otherItemsFound = false;
        for (const item of cartData.items) {
            if (item.serviceId === serviceIdToRemove && optionId === '') {
                continue;
            }

            for (const option of item.selectedItem.bookingOptions) {
                if (item.serviceId !== serviceIdToRemove || option.optionId !== optionId) {
                    if (option.status < CartStatus.STATUS_PROGRESS || option.status > CartStatus.STATUS_CANCELLED) {
                        return false;
                    } else {
                        otherItemsFound = true;
                    }
                }
            }
        }

        // if no item remains we return false
        return otherItemsFound;
    }

    private static isLast(
        cartData: any,
        serviceIdToRemove: ServiceId,
        optionId: string | undefined,
        checkIfLastToBook?: boolean
    ): { isLast: boolean; isPartial: boolean; wasLastToBook: boolean } {
        const isPartial = CartRemoveService.isPartial(cartData, serviceIdToRemove, optionId);
        if (isPartial) {
            // if we remove partially an item it means that this is not last item to remove
            return {
                isLast: false,
                isPartial: true,
                wasLastToBook:
                    !!checkIfLastToBook && CartRemoveService.wasLastToBook(cartData, serviceIdToRemove, optionId),
            };
        }
        const isLast = CartRemoveService.isLastToRemove(cartData, serviceIdToRemove);
        return {
            isLast,
            isPartial: false,
            wasLastToBook:
                !!checkIfLastToBook && CartRemoveService.wasLastToBook(cartData, serviceIdToRemove, optionId),
        };
    }

    public removeCartData(
        serviceId: ServiceId,
        optionId: string | undefined,
        removeService?: boolean
    ): Observable<boolean | void> {
        // dataType, result, idx, transferJourneyType, tripType
        return new Observable<boolean | void>((subscriber: Subscriber<boolean | void>) => {
            this.cartService
                .removeCartData(serviceId, optionId)
                .pipe(take(1))
                .subscribe(
                    (success: any) => {
                        this.onRemoveSuccess(serviceId, subscriber, removeService);
                    },
                    (failure: any) => {
                        this.cartService.resetSelected(serviceId);
                        console.log('CART RESULT FAIL', failure);
                        subscriber.error(failure);
                    }
                );
        });
    }

    public removeTheService(
        serviceId: ServiceId,
        optionId?: string | undefined,
        checkBooking?: boolean
    ): Promise<boolean> {
        //we want to remove all train entries on remove, we discard optionId content
        if (serviceId === ServiceId.TRAIN) {
            optionId = undefined;
        }
        const cartData = this.cartService.getCartData();
        let cartItem: ICartItem | undefined;
        if (cartData) {
            for (let i = 0; cartData.items.length > i; i++) {
                const cItem = cartData.items[i];
                if (
                    serviceId === cItem.serviceId ||
                    (serviceId === ServiceId.FLIGHT_TRAIN &&
                        (cItem.serviceId === ServiceId.FLIGHT || cItem.serviceId === ServiceId.TRAIN))
                ) {
                    cartItem = cItem;
                    serviceId = cItem.serviceId;
                    break;
                }
            }
        }
        const isVirtual = !cartItem || (cartItem && cartItem.isVirtual);
        const isLast = CartRemoveService.isLast(cartData, serviceId, optionId, checkBooking);

        return new Promise<boolean>((resolve, reject) => {
            if (isVirtual && !isLast.isLast) {
                this.callRemoveService(serviceId, optionId, resolve, reject, false);
            } else {
                this.getServiceRemovalDialogObservableRef(serviceId, isLast)
                    .afterClosed()
                    .subscribe((confirm: boolean) => {
                        if (confirm) {
                            this.callRemoveService(serviceId, optionId, resolve, reject, isLast.wasLastToBook);
                        } else {
                            resolve(false);
                        }
                    });
            }
        });
    }

    private onRemoveSuccess(serviceId: ServiceId, subscriber: Subscriber<boolean>, removeService?: boolean) {
        const currentCartData = this.cartService.getCartData();

        if (!currentCartData.items || currentCartData.items.length === 0) {
            this.router.navigate(['/search']);
            subscriber.next(true);
            return;
        }
        this.tabRoutingService.refreshBookingStatus();

        if (removeService) {
            this.removeService(serviceId).subscribe(
                (removed: any) => {
                    if (removed) {
                        this.tripPlannerService.removeServiceSearchInfos(serviceId);
                    }
                    subscriber.next();
                },
                (failure: any) => {
                    console.error('failure on ctx service update' + failure);
                    subscriber.error(failure);
                }
            );
        } else {
            subscriber.next(true);
        }
    }

    private checkCartHasService(serviceId: ServiceId) {
        const data = this.cartService.getCartData();
        return data.items && data.items.filter((ci: any) => ci.serviceId === serviceId).length > 0;
    }

    /*
    removeService: returns true when service is removed, false otherwise (items remains on cart)
     */
    private removeService(serviceId: ServiceId): Observable<boolean> {
        return new Observable((subscriber) => {
            if (this.checkCartHasService(serviceId)) {
                // if cart has the service, keep the service
                return subscriber.next(false);
            }
            const services = this.searchDataService.removeService(serviceId);
            this.cartDataService
                .updateServiceCtx(this.searchDataService.getCombId(), {
                    activeServices: services,
                })
                .pipe(take(1))
                .subscribe(
                    () => {
                        this.tabRoutingService.removeService(serviceId);
                        subscriber.next(true);
                    },
                    (error: any) => {
                        subscriber.error(error);
                    }
                );
        });
    }

    private callRemoveService(
        serviceId: ServiceId,
        optionId: string | undefined,
        resolve: any,
        reject: any,
        wasLastToBook: boolean // when true navigate according to result
    ): void {
        this.removeCartData(serviceId, optionId, true).subscribe(
            (isLastService: boolean | void) => {
                if (wasLastToBook) {
                    this.moveToBooking()
                        .afterClosed()
                        .subscribe((confirm: boolean) => {
                            if (confirm) {
                                this.router.navigate(['/checkout']);
                            }
                            resolve(true);
                        });
                } else {
                    if (isLastService) {
                        this.router.navigate(['/search']);
                    } else {
                        this.injector.get(UserSearchService).searchCheckDep(serviceId, false, false, false, true);
                    }
                }
                resolve(true);
            },
            (err) => {
                reject(err);
            }
        );
    }

    private getInterpolate(serviceId: ServiceId): object {
        return {
            serviceName: this.translateService.instant('SERVICES.' + serviceId).toLowerCase(),
            tabName: this.getTabName(serviceId),
        };
    }

    private moveToBooking(): MatDialogRef<TrazlerConfirmationDialogComponent> {
        return this.dialogService.openConfirmationDialog({
            title: this.translateService.instant('POPUPS.FINALIZE_BOOK.TITLE'),
            content: this.translateService.instant('POPUPS.FINALIZE_BOOK.BODY'),
            confirmBtnTitle: this.translateService.instant('POPUPS.FINALIZE_BOOK.1_DELETE'),
            cancelBtnTitle: this.translateService.instant('POPUPS.FINALIZE_BOOK.2_MODIFY'),
        });
    }

    private getServiceRemovalDialogObservableRef(
        serviceId: ServiceId,
        isLast: { isLast: boolean; isPartial: boolean; wasLastToBook: boolean }
    ): MatDialogRef<TrazlerConfirmationDialogComponent> {
        const title = this.translateService.instant('POPUPS.REMOVE_CART_ITEM.TITLE');
        const confirmBtnTitle = this.translateService.instant('POPUPS.REMOVE_CART_ITEM.DELETE');
        const cancelBtnTitle = this.translateService.instant('POPUPS.REMOVE_CART_ITEM.CANCEL');
        let content: string;

        switch (true) {
            case isLast.isLast:
                content = this.translateService.instant(
                    'POPUPS.REMOVE_CART_ITEM.LAST_BODY',
                    this.getInterpolate(serviceId)
                );
                break;
            case isLast.isPartial:
                content = this.translateService.instant(
                    'POPUPS.REMOVE_CART_ITEM.PARTIAL_BODY',
                    this.getInterpolate(serviceId)
                );
                break;
            default:
                content = this.translateService.instant('POPUPS.REMOVE_CART_ITEM.BODY', this.getInterpolate(serviceId));
        }

        return this.dialogService.openConfirmationDialog({
            content,
            title,
            confirmBtnTitle,
            cancelBtnTitle,
            icon: { name: 'remove-cart-dialog', iconStyle: { 'width.px': 40, 'height.px': 40 } },
        });
    }

    private getTabName(serviceId: ServiceId): string {
        switch (serviceId) {
            case ServiceId.FLIGHT:
                return this.translateService.instant('SEARCH.TAB.FLIGHT_TRAINS');
            case ServiceId.TRAIN:
                return this.translateService.instant('SEARCH.TAB.FLIGHT_TRAINS');
            case ServiceId.ACC:
                return this.translateService.instant('SERVICES.HOTEL');
            case ServiceId.TRANSFER:
                return this.translateService.instant('SEARCH.TAB.TRANSFERS');
            case ServiceId.CAR_RENTAL:
                return this.translateService.instant('SEARCH.TAB.CAR_RENTALS');
            case ServiceId.FLIGHT_TRAIN:
                return this.translateService.instant('SEARCH.TAB.FLIGHT_TRAINS');
            default:
                return '';
        }
    }

    private isInCart(cartItems: any, serviceId: ServiceId): boolean {
        return cartItems.some((cItem: any) => cItem.serviceId === serviceId);
    }
}
