import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { Injectable } from '@angular/core';
import { HttpRequestType } from '@core/constants/http-request-type';
import { ICountryIP } from '@schemas/modules/core/country-ip';
import { Observable, throwError } from 'rxjs';
import { map } from 'rxjs/operators';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Injectable({
    providedIn: 'root',
})
export class AppConfigDataService extends ApiAccessService {
    public initDeviceId(forceNew: boolean): Promise<string> {
        return new Promise((resolve, reject) => {
            const deviceId = this.settingsService.getDeviceID();
            if (forceNew || !deviceId) {
                this.fetchDeviceID().then(
                    (response: any) => {
                        if (response.deviceID) {
                            this.settingsService.storeDeviceId(response);
                            resolve(response.deviceID);
                        } else {
                            console.error(response);
                            reject('device id fetch failed:' + response);
                        }
                    },
                    (error: any) => {
                        console.error(error);
                        resolve('');
                    }
                );
            } else {
                resolve(deviceId);
            }
        });
    }

    public setCountryIP(deviceId: string): Observable<ICountryIP> {
        const params = {
            entity: 'userConnection',
            deviceID: deviceId,
        };
        if (!deviceId) {
            return throwError('invalid data id');
        } else {
            return this.apiCallObs(HttpRequestType.GET, params).pipe(
                map((data: ICountryIP) => {
                    StorageManagerUtil.set('ip_country', data);
                    return data;
                })
            );
        }
    }

    private fetchDeviceID() {
        const params = {
            entity: 'deviceID',
        };
        return this.apiCallAccess(HttpRequestType.GET, params);
    }
}
