import { Injectable } from '@angular/core';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ServiceId } from '@core/constants/service-id.const';
import { HttpRequestType } from '@core/constants/http-request-type';
import { CombinationId, DataId } from '@schemas/modules/core/base-types';
import { ICart } from '@schemas/modules/cart/cart';
import { Observable, throwError } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class CartDataService extends ApiAccessService {
    public addCartSummary(combId: CombinationId, serviceId: ServiceId, dataId: DataId, req: any): any {
        const params = {
            entity: 'cart',
            id: combId,
            action: 'add',
            curCode: this.settingsService.getCurrency(),
            lang: this.settingsService.langCode,
            deviceId: this.settingsService.getDeviceID(),
            serviceId,
            dataId,
            closeLoader: true,
        };
        if (!params.id) {
            throwError('invalid device id');
        } else {
            this.showLoader(true);
            return this.apiCallObs(HttpRequestType.POST, params, req);
        }
    }

    public removeCartSummary(serviceId: ServiceId, optionId: string): Observable<ICart> {
        const params = {
            entity: 'cart',
            id: this.searchDataService.getCombId(),
            action: 'remove',
            curCode: this.settingsService.getCurrency(),
            deviceId: this.settingsService.getDeviceID(),
            serviceId,
            optionId: optionId ? optionId : '',
            closeLoader: true,
        };
        if (!params.id) {
            return throwError('invalid data id');
        } else {
            this.showLoader(true);
            return this.apiCallObs(HttpRequestType.POST, params, {});
        }
    }

    public updateServiceCtx(combId: CombinationId, serviceList: any): Observable<any> {
        const params = {
            entity: 'updCombinationCtx',
            id: combId,
            deviceId: this.settingsService.getDeviceID(),
        };

        if (!params.id) {
            return throwError('invalid device id');
        } else {
            return this.apiCallObs(HttpRequestType.POST, params, serviceList);
        }
    }

    public clearCart(): Observable<{ message: string }> {
        const params = {
            entity: 'cart',
            deviceId: this.settingsService.getDeviceID(),
        };
        if (!params.deviceId) {
            return throwError('invalid data id');
        } else {
            return this.apiCallObs(HttpRequestType.POST, params, {});
        }
    }

    public acceptPriceChange(serviceId: ServiceId, optionId: string): Observable<ICart> {
        const params = {
            entity: 'cart',
            id: this.searchDataService.getCombId(),
            action: 'acceptPriceChange',
            curCode: this.settingsService.getCurrency(),
            deviceId: this.settingsService.getDeviceID(),
            serviceId,
            optionId,
            closeLoader: true,
        };

        if (!params.id) {
            return throwError('invalid data id' + params);
        } else {
            this.showLoader(true);
            return this.apiCallObs(HttpRequestType.POST, params, {});
        }
    }

    public recheckCartItem(combID: CombinationId, deviceId: string, hideLoader = false) {
        const params = {
            entity: 'cart',
            id: combID,
            curCode: this.settingsService.getCurrency(),
            deviceId, //device id must not be set from the global value, we must use device id of that cart
            action: 'reCheckCartItem',
            closeLoader: !hideLoader,
            langCode: this.settingsService.langCode,
        };

        if (!hideLoader) {
            this.showLoader(true, 'LOADER_MESSAGES.RECHECK_CART.MESSAGE', 'LOADER_MESSAGES.RECHECK_CART.TITLE');
        }

        return this.apiCallAccess(HttpRequestType.POST, params);
    }
}
