import { Injectable } from '@angular/core';
import { HttpClient, HttpContext, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { HttpRequestType } from '@core/constants/http-request-type';

interface IRequestOptions {
    body?: any;
    headers?:
        | HttpHeaders
        | {
              [header: string]: string | string[];
          };
    context?: HttpContext;
    observe?: 'body';
    params?:
        | HttpParams
        | {
              [param: string]: string | number | boolean | ReadonlyArray<string | number | boolean>;
          };
    reportProgress?: boolean;
    responseType: 'arraybuffer' | 'json';
    withCredentials?: boolean;
}

@Injectable({
    providedIn: 'root',
})
export class BaseService {
    constructor(private http: HttpClient) {}

    protected request(
        method: HttpRequestType,
        url: string,
        queryParams?: {
            [param: string]: string | number | boolean;
        },
        data?: object,
        httpOptions?: any
    ): Observable<any> {
        return this.runRequest(method, url, true, queryParams, data, httpOptions);
    }

    protected requestNoCredential(
        method: HttpRequestType,
        url: string,
        queryParams?: {
            [param: string]: string | number | boolean;
        },
        data?: object,
        httpOptions?: any
    ) {
        return this.runRequest(method, url, false, queryParams, data, httpOptions);
    }

    private runRequest(
        method: HttpRequestType,
        url: string,
        credentials: boolean,
        queryParams?: {
            [param: string]: string | number | boolean;
        },
        data?: object,
        httpOptions?: any
    ): Observable<any> {
        let options: IRequestOptions = {
            responseType: 'json',
            withCredentials: credentials,
        };
        if (queryParams) {
            options.params = queryParams;
        }
        if (data) {
            options.body = data;
        }
        if (httpOptions) {
            options = {
                ...options,
                ...httpOptions,
            };
        }
        return this.http.request(method, url, options);
    }
}
