import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { StateFrom } from '@core/constants/state-from.const';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { ServiceId } from '@core/constants/service-id.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { SearchEvents } from '@core/constants/search-events';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { LoaderService } from '@services/modules/core/loader.service';
import { PreSearchService } from '@services/modules/core/dataservices/pre-search.service';
import { BehaviorSubject } from 'rxjs';

export interface IHasTransport {
    has: boolean | null;
}

@Injectable({
    providedIn: 'root',
})
export class FlightTrainSearchService {
    public searchComplete: any = {};
    public twoTransports: BehaviorSubject<IHasTransport> = new BehaviorSubject({ has: null } as IHasTransport);

    private states: any = {};

    private waitedServiceId: number | undefined;

    private readonly resultData: any = {
        [ServiceId.FLIGHT]: {},
        [ServiceId.TRAIN]: {},
    };

    constructor(
        private router: Router,
        private apiAccessService: ApiAccessService,
        private searchMetadataService: SearchMetadataService,
        private listenersService: ListenersService,
        private cartService: CartService,
        private tripPlannerService: TripPlannerService,
        private searchDataService: SearchDataService,
        private searchErrorService: SearchErrorService,
        private cartRoutingService: CartRoutingService,
        private loaderService: LoaderService,
        private preSearchService: PreSearchService
    ) {
        this.init();
    }

    // help private to send back the reverse of current service id (if train send flight and reverse)
    private static reverseId(serviceId: ServiceId): ServiceId.FLIGHT | ServiceId.TRAIN {
        return serviceId === ServiceId.TRAIN ? ServiceId.FLIGHT : ServiceId.TRAIN;
    }

    // Based on the search parameter check the preferred service result to show
    private static checkPreference(serviceSearch: any, serviceId: ServiceId) {
        const from = serviceSearch.searchData.from;
        const to = serviceSearch.searchData.to;
        if (serviceId === ServiceId.TRAIN) {
            return (
                from.placeType === 'TRAIN' ||
                ((from.placeType === 'CITY' || from.placeType === '') && to.placeType === 'TRAIN')
            );
        }
        // Always flight has first priority, check whether it is other than train
        return (
            from.placeType !== 'TRAIN' &&
            !((from.placeType === 'CITY' || from.placeType === '') && to.placeType === 'TRAIN')
        );
    }

    public searchFlightTrain(serviceSearch: any, eventId: SearchEvents, combRef: any, params: any) {
        this.searchDataService.saveResult(ServiceId.FLIGHT, null);
        this.searchDataService.saveResult(ServiceId.TRAIN, null);
        const waitedServiceId =
            eventId === SearchEvents.EVENT_SEARCH
                ? -1
                : this.router.url === this.states[ServiceId.TRAIN]
                ? ServiceId.TRAIN
                : ServiceId.FLIGHT;
        this.resetSearchComplete(waitedServiceId);

        //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, true);
        this.search(ServiceId.FLIGHT, serviceSearch, eventId, combRef, params);
        this.search(ServiceId.TRAIN, serviceSearch, eventId, combRef, params);
    }

    public hasResults(serviceId: ServiceId): boolean {
        const prevRes = this.searchDataService.getSearchResult(serviceId);
        return !!(prevRes && prevRes.resultLength > 0);
    }

    public setData(data: any, serviceId: number): void {
        this.resetData();
        data.forEach((obj: any) => {
            if (!this.resultData[serviceId][obj.id]) {
                this.resultData[serviceId][obj.id] = obj;
            } else {
                console.error('Duplicated data for:' + obj.id);
            }
        });
    }

    public hasTwoTransport(): IHasTransport {
        return this.twoTransports.getValue();
    }

    private hasNullResults(serviceId: ServiceId): boolean {
        return this.searchDataService.getSearchResult(serviceId) === null;
    }

    private resetData(): void {
        this.resultData[ServiceId.FLIGHT] = {};
        this.resultData[ServiceId.TRAIN] = {};
    }

    private resetSearchComplete(waitedId: ServiceId) {
        this.searchComplete[ServiceId.FLIGHT] = false;
        this.searchComplete[ServiceId.TRAIN] = false;
        this.twoTransports.next({ has: null });
        this.waitedServiceId = waitedId;
    }

    private allCompleted() {
        return this.searchComplete[ServiceId.FLIGHT] && this.searchComplete[ServiceId.TRAIN];
    }

    private setTransportHas() {
        if (this.hasNullResults(ServiceId.FLIGHT) || this.hasNullResults(ServiceId.TRAIN)) {
            this.twoTransports.next({ has: null });
        } else {
            this.twoTransports.next({ has: this.hasResults(ServiceId.FLIGHT) && this.hasResults(ServiceId.TRAIN) });
        }
    }

    private initSearchComplete(): void {
        // @Todo: Check if result length is zero also we are marking not complete. Have to validate
        this.searchComplete[ServiceId.FLIGHT] = !this.hasNullResults(ServiceId.FLIGHT);
        this.searchComplete[ServiceId.TRAIN] = !this.hasNullResults(ServiceId.TRAIN);
        this.setTransportHas();
    }

    private init(): void {
        this.resetSearchComplete(-1);
        this.initSearchComplete();
        this.states[ServiceId.TRAIN] = '/travel/trains';
        this.states[ServiceId.FLIGHT] = '/travel/flights';
        this.states[ServiceId.FLIGHT_TRAIN] = '/travel/flights';
    }

    private isTransportState(): boolean {
        return (
            this.router.url.indexOf(this.states[ServiceId.TRAIN]) >= 0 ||
            this.router.url.indexOf(this.states[ServiceId.FLIGHT]) >= 0
        );
    }

    private checkRes(serviceSearch: any, metaData: any): ServiceId {
        if (FlightTrainSearchService.checkPreference(serviceSearch, metaData.serviceId)) {
            return metaData.serviceId;
        }
        const id = FlightTrainSearchService.reverseId(metaData.serviceId);
        if (this.allCompleted() && !this.hasResults(id)) {
            return metaData.serviceId;
        }
        return -1;
    }

    private checkZeroRes(serviceId: ServiceId, statusCode: number) {
        this.setTransportHas();
        if (!this.allCompleted()) {
            return -1;
        }
        if (!this.hasResults(ServiceId.FLIGHT) && !this.hasResults(ServiceId.TRAIN)) {
            // no result for previous result, we stay where we are
            if (this.router.url === '/search') {
                this.searchErrorService.searchErr(
                    {
                        status: statusCode,
                        message: '',
                    },
                    ServiceId.FLIGHT,
                    SearchEvents.EVENT_SEARCH_WEB
                );
                /*setTimeout(
                    () => {
                        this.searchErrorService.searchErr(
                            {
                                status: statusCode,
                                message: '',
                            },
                            ServiceId.FLIGHT,
                            SearchEvents.EVENT_SEARCH_WEB
                        );
                    },
                    1000,
                    false
                );*/
            } else {
                /*this.listenersService.NotifyListeners(ListenerId.LISTEN_NO_RESULT, {
                    addService: this.router.url === '/results/results-summary',
                    serviceId: ServiceId.FLIGHT_TRAIN,
                    status: statusCode,
                    message: '',
                });*/
                this.searchErrorService.searchErr(
                    {
                        status: statusCode,
                        message: '',
                    },
                    ServiceId.FLIGHT,
                    SearchEvents.EVENT_SEARCH_WEB
                );
            }
            this.listenersService.NotifyListeners(ListenerId.RESET_SEARCH_DATA, true);
            this.tripPlannerService.setComplete(ServiceId.FLIGHT);
            this.tripPlannerService.setComplete(ServiceId.TRAIN);
            return -1;
        }
        // we change state there is no result for current one
        return FlightTrainSearchService.reverseId(serviceId);
    }

    private onTravelSearchSuccess(metaData: any, eventId: any, serviceSearch: any, forceResultPage: boolean): any {
        let moveToServiceId = -1;

        if (eventId === SearchEvents.EVENT_SEARCH) {
            // we searched from home page or from combination we need to move to first service with results
            if (metaData.count > 0) {
                moveToServiceId = this.checkRes(serviceSearch, metaData);
            } else {
                moveToServiceId = this.checkZeroRes(metaData.serviceId, 200);
            }
        } else {
            if (!forceResultPage && !this.isTransportState()) {
                // user change state, do not move somewhere else
                return;
            }
            // we search from edit, we stay on current state unless there is no result for it
            if (metaData.count > 0) {
                moveToServiceId = this.checkRes(serviceSearch, metaData);
            } else {
                moveToServiceId = this.checkZeroRes(metaData.serviceId, 200);
            }
        }

        if (moveToServiceId === -1) {
            return -1;
        }

        if (this.router.url !== this.states[moveToServiceId]) {
            this.cartRoutingService.goToState(moveToServiceId, undefined, true, StateFrom.SUMMARY);
            return;
        }
        // we refresh current service data
        switch (moveToServiceId) {
            case ServiceId.FLIGHT:
                this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_FLIGHT, true);
                break;
            case ServiceId.TRAIN:
                this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_TRAIN, true);
                break;
            default:
                console.error('unhandled service id:' + moveToServiceId);
        }
    }

    private search(serviceId: ServiceId, serviceSearch: any, eventId: any, combRef: any, params: any) {
        console.log('starting search for transport;' + serviceId);
        params.id = this.searchDataService.getCombId();
        params.serviceId = serviceId;
        this.preSearchService.preSearchData(params, serviceSearch, eventId).then(
            (success: any) => {
                console.log('getting success for transport:' + serviceId);
                this.searchComplete[serviceId] = true;
                this.setTransportHas();
                /*this.searchMetadataService.saveSearch(success);
                if (success.metaData.complete) {
                    this.tripPlannerService.setComplete(success.metaData.serviceId);
                }*/
                if (
                    this.onTravelSearchSuccess(
                        success.metaData,
                        eventId,
                        success.serviceSearch,
                        params.forceResultPage
                    ) === -1
                ) {
                    if (this.allCompleted() && !this.isTransportState()) {
                        if (eventId !== SearchEvents.EVENT_SEARCH) {
                            if (this.router.url === '/results-summary') {
                                this.cartRoutingService.goToState(ServiceId.FLIGHT, undefined, true, StateFrom.SUMMARY);
                            } else if (this.router.url !== '/search') {
                                this.cartRoutingService.goToState(ServiceId.FLIGHT, undefined, true, StateFrom.SEARCH);
                            }
                        } else {
                            this.loaderService.triggerLoading.emit({ loading: false });
                        }
                    }
                } else {
                    this.searchErrorService.unsetErrorFlag();
                }
            },
            (error: any) => {
                console.log('getting failure for transport;' + serviceId);
                this.searchComplete[serviceId] = true;
                this.twoTransports.next({ has: false });
                //this.tripPlannerService.setComplete(serviceId); // on error set result is completed
                if (this.allCompleted()) {
                    console.log('getting all transport completed');
                    const state = this.checkZeroRes(serviceId, error.status);
                    if (state === -1 && this.router.url !== '/search') {
                        this.cartRoutingService.goToState(ServiceId.FLIGHT, undefined, true, StateFrom.SUMMARY);
                    }
                }
                console.log(`ERROR:${serviceId}`, error);

                //this.loaderService.triggerLoading.emit({ loading: false });
            }
        );
    }
}
