import { Injectable } from '@angular/core';
import { ModalController, AnimationController } from '@ionic/angular';
import { MobMainModalComponent } from '@shared/components/modals/mob-main-modal/mob-main-modal.component';
import { ComponentType } from '@angular/cdk/portal';
import { ModalOptions } from '@ionic/core';
import { MobMainDetailsModalComponent } from '@shared/components/modals/mob-main-details-modal/mob-main-details-modal.component';
import { Subject } from 'rxjs';
import { MobPickerModalComponent } from '@shared/components/modals/mob-picker-modal/mob-picker-modal.component';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';
import { ModalOpenOptions } from '@core/constants/modal';

@Injectable({
    providedIn: 'root',
})
export class ModalService {
    public updateModelTitle$ = new Subject<string>();

    constructor(private modalController: ModalController, private animationCtrl: AnimationController) {}

    public create(opts: ModalOptions, options: ModalOpenOptions): Promise<HTMLIonModalElement> {
        const iconModel = this.modalController.create(opts);
        this.addSlideAnimation(iconModel, options);
        return iconModel;
    }

    public async createMainModal<T>(
        component: ComponentType<any>,
        props: { data?: any; title?: string; cssClass?: string | string[]; hideHeader?: boolean },
        options: { openOptions: ModalOpenOptions } = { openOptions: ModalOpenOptions.TOP }
    ): Promise<T> {
        const modal = await this.create(
            {
                component: MobMainModalComponent,
                componentProps: {
                    component,
                    data: props.data,
                    title: props.title,
                    hideHeader: props.hideHeader,
                },
                cssClass: props.cssClass ? props.cssClass : 'mob-main-modal',
            },
            options.openOptions
        );
        await modal.present();

        modal.onWillDismiss().then(() => {
            this.historyBack();
        });

        modal.onDidDismiss().then(() => {
            this.historyPushIfHasModal();
        });

        return (await modal.onDidDismiss()) as T;
    }

    public async createDetailsPage(
        component: ComponentType<any>,
        props: {
            data?: any;
            title?: string;
            hideHeader?: boolean;
            leftClose?: boolean;
            modalCustomClasses?: string[];
            isHeaderBottomDivider?: boolean;
        },
        options: { openOptions: ModalOpenOptions } = { openOptions: ModalOpenOptions.LEFT }
    ): Promise<any> {
        const modal = await this.create(
            {
                component: MobMainDetailsModalComponent,
                componentProps: {
                    component,
                    data: props.data,
                    title: props.title,
                    hideHeader: props.hideHeader,
                    isHeaderBottomDivider: props.isHeaderBottomDivider,
                },
                cssClass: ['mob-modal-fullscreen', ...(props.modalCustomClasses ?? [])],
            },
            options.openOptions
        );
        await modal.present();

        modal.onWillDismiss().then(() => {
            this.historyBack();
        });

        modal.onDidDismiss().then(() => {
            this.historyPushIfHasModal();
        });

        return await modal.onDidDismiss();
    }

    public async openSummeryModal(
        component: ComponentType<any>,
        props: { data?: any },
        options: { openOptions: ModalOpenOptions } = { openOptions: ModalOpenOptions.TOP }
    ): Promise<any> {
        const modal = await this.create(
            {
                component,
                componentProps: { data: props.data },
                cssClass: 'mob-summery-modal',
            },
            options.openOptions
        );

        await modal.present();

        modal.onWillDismiss().then(() => {
            this.historyBack();
        });

        return await modal.onDidDismiss();
    }

    public async openPickerModal(
        data: { wheels: MobPickerWheels[]; selected?: any[] },
        props: { title?: string; cssClass?: string | string[]; selectBtnText?: string; cancelBtnText?: string },
        options: { openOptions: ModalOpenOptions } = { openOptions: ModalOpenOptions.TOP }
    ): Promise<{ data?: { selected?: any[]; cancel?: boolean }; role?: string; id?: string }> {
        const modal = await this.create(
            {
                component: MobPickerModalComponent,
                componentProps: {
                    data,
                    title: props.title,
                    selectBtnText: props.selectBtnText ? props.selectBtnText : 'GENERAL.DONE',
                    cancelBtnText: props.cancelBtnText,
                },
                cssClass: props.cssClass ? props.cssClass : 'mob-picker-modal',
            },
            options.openOptions
        );
        await modal.present();

        modal.onWillDismiss().then(() => {
            this.historyBack();
        });

        modal.onDidDismiss().then(() => {
            this.historyPushIfHasModal();
        });

        return await modal.onDidDismiss();
    }

    public async openDatePickerModal(
        component: ComponentType<any>,
        props: { data?: any; title?: string; cssClass?: string | string[]; selectBtnText?: string },
        options: { openOptions: ModalOpenOptions } = { openOptions: ModalOpenOptions.TOP }
    ): Promise<any> {
        const modal = await this.create(
            {
                component,
                componentProps: {
                    data: props.data,
                    title: props.title,
                    selectBtnText: props.selectBtnText ? props.selectBtnText : 'GENERAL.DONE',
                },
                cssClass: props.cssClass ? props.cssClass : 'mob-picker-modal',
            },
            options.openOptions
        );
        await modal.present();

        modal.onWillDismiss().then(() => {
            this.historyBack();
        });

        modal.onDidDismiss().then(() => {
            this.historyPushIfHasModal();
        });

        return await modal.onDidDismiss();
    }

    public dismiss(data?: any, role?: string, id?: string): Promise<boolean> {
        return this.modalController.dismiss(data, role, id);
    }

    public getTop(): Promise<any> {
        return this.modalController.getTop();
    }

    public historyPushModal(): void {
        history.pushState({ modal: true }, '');
    }

    private historyBack(): void {
        if (window.history.state.modal) {
            history.back();
        }
    }

    private historyPushIfHasModal(): void {
        this.getTop().then((modal) => {
            if (modal) {
                history.pushState({ modal: true }, '');
            }
        });
    }

    private addSlideAnimation(
        ionModel: Promise<HTMLIonModalElement>,
        options: ModalOpenOptions = ModalOpenOptions.LEFT
    ): void {
        ionModel.then((baseEl) => {
            const root = baseEl.shadowRoot;
            const backDrop = root?.querySelector('ion-backdrop');
            const model = root?.querySelector('.modal-wrapper');
            if (!backDrop || !model) {
                return;
            }

            const backdropAnimation = this.animationCtrl
                .create()
                .addElement(backDrop)
                .fromTo('opacity', '0.01', 'var(--backdrop-opacity)');

            const wrapperAnimation = this.animationCtrl
                .create()
                .addElement(model)
                .keyframes(this.modalAnimations(options));

            const modelEnterAnimation = () =>
                this.animationCtrl
                    .create()
                    .addElement(baseEl)
                    .easing('ease')
                    .duration(300)
                    .addAnimation([backdropAnimation, wrapperAnimation]);

            baseEl.enterAnimation = modelEnterAnimation;
            baseEl.leaveAnimation = () => modelEnterAnimation().direction('reverse');
        });
    }

    private modalAnimations(options: ModalOpenOptions): { offset: number; opacity: string; transform: string }[] {
        const modalOpenOptions: {
            [key: string]: { offset: number; opacity: string; transform: string }[];
        } = {};

        modalOpenOptions[ModalOpenOptions.LEFT] = [
            { offset: 0, opacity: '1', transform: 'translateX(100%)' },
            { offset: 1, opacity: '1', transform: 'translateX(0)' },
        ];

        modalOpenOptions[ModalOpenOptions.RIGHT] = [
            { offset: 0, opacity: '1', transform: 'translateX(-100%)' },
            { offset: 1, opacity: '1', transform: 'translateX(0)' },
        ];

        modalOpenOptions[ModalOpenOptions.TOP] = [
            { offset: 0, opacity: '1', transform: 'translateY(100%)' },
            { offset: 1, opacity: '1', transform: 'translateY(0)' },
        ];

        modalOpenOptions[ModalOpenOptions.BOTTOM] = [
            { offset: 0, opacity: '1', transform: 'translateY(-100%)' },
            { offset: 1, opacity: '1', transform: 'translateY(0)' },
        ];

        return modalOpenOptions[options];
    }
}
