import { ChangeDetectionStrategy, Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { ICart } from '@schemas/modules/cart/cart';

const serviceIcons = {
    0: {
        icon: 'tz-icon-service-hotel',
        ordinal: 2,
    },
    2: {
        icon: 'service-icon-car',
        ordinal: 4,
    },
    3: {
        icon: 'tz-icon-service-transfer',
        ordinal: 3,
    },
    5: {
        icon: 'tz-icon-service-flight',
        ordinal: 1,
    },
    6: {
        icon: 'service-icon-train',
        ordinal: 1,
    },
};

@Component({
    selector: 'tz-mob-cart-summary-button',
    templateUrl: './mob-cart-summary-button.component.html',
    styleUrls: ['./mob-cart-summary-button.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobCartSummaryButtonComponent implements OnChanges {
    @Input() cartData!: ICart;
    @Input() summaryExpanded = false;

    public services: { icon: string; ordinal: number }[] = [];

    ngOnChanges(changes: SimpleChanges) {
        if (changes['cartData'] && this.cartData) {
            const serviceArr: { icon: string; ordinal: number }[] = [];
            for (const item of this.cartData.items) {
                const sid = item.serviceId;
                if (sid !== 0 && sid !== 2 && sid !== 3 && sid !== 5 && sid !== 6) {
                    continue;
                }

                serviceArr.push(serviceIcons[sid]);
            }

            serviceArr.sort((a, b) => a.ordinal - b.ordinal);
            this.services = [...new Set(serviceArr)];
        }
    }
}
