import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { RouteReuseStrategy } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { environment } from '@environments/environment';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LayoutModule } from './modules/layout/layout.module';
import { ServiceWorkerModule } from '@angular/service-worker';
import { TranslateModule } from '@ngx-translate/core';
import { TRANSLATION_CONFIG } from './config/translation.config';
import { LayoutModule as CDKLayoutModule } from '@angular/cdk/layout';
import { HttpClientModule } from '@angular/common/http';
import { INTERCEPTORS } from '@core/interceptors';
import { MatDialogModule, MatDialogRef } from '@angular/material/dialog';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { CurrencyPipe, DatePipe, registerLocaleData } from '@angular/common';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { INITIALIZATION_PROVIDERS } from '@core/initialize/app.initialize';
import localeFr from '@angular/common/locales/fr';
import localeEnUs from '@angular/common/locales/en-US-POSIX';
import { VirtualScrollerModule } from 'ngx-virtual-scroller';
import { CookieService } from 'ngx-cookie-service';
import { MobLandingPageModule } from './modules/mob-landing-page/mob-landing-page.module';
import { LocaleProvider } from '@services/services/locale.provider';
import { TzDatePipe } from '@shared/pipes/tz-date.pipe';
import { DeviceService } from '@services/device.service';

registerLocaleData(localeFr, 'fr');
registerLocaleData(localeEnUs, 'en-US');

@NgModule({
    declarations: [AppComponent],
    imports: [
        AppRoutingModule,
        BrowserModule,
        FormsModule,
        ReactiveFormsModule,
        BrowserAnimationsModule.withConfig(DeviceService.animatedConfig()),
        IonicModule.forRoot(),
        CDKLayoutModule,
        LayoutModule,
        HttpClientModule,
        MatDialogModule,
        InfiniteScrollModule,
        VirtualScrollerModule,
        TranslateModule.forRoot(TRANSLATION_CONFIG),
        ServiceWorkerModule.register('ngsw-worker.js', {
            enabled: environment.production,
            // Register the ServiceWorker as soon as the application is stable
            // or after 30 seconds (whichever comes first).
            registrationStrategy: 'registerWhenStable:30000',
        }),
        MobLandingPageModule,
    ],
    providers: [
        CookieService,
        CurrencyPipe,
        DatePipe,
        TzCurrencyPipe,
        TzDatePipe,
        INTERCEPTORS,
        ...INITIALIZATION_PROVIDERS,
        {
            provide: MatDialogRef,
            useValue: {},
        },
        {
            provide: RouteReuseStrategy,
            useClass: IonicRouteStrategy,
        },
        LocaleProvider,
    ],
    bootstrap: [AppComponent],
})
export class AppModule {}
