import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'tz-footer-pages-header',
    templateUrl: './footer-pages-header.component.html',
    styleUrls: ['./footer-pages-header.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class FooterPagesHeaderComponent implements OnInit {
    @Input() public content: string | undefined;
    @Input() public className = '';
    public isAboutRoute = false;

    constructor(private router: Router) {}

    public ngOnInit(): void {
        this.isAboutRoute = this.router.url.split('/').pop()?.toLowerCase() === 'about';
    }
}
