import { Injectable } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';

// eslint-disable-next-line max-len
@Injectable({
    providedIn: 'root',
})
export class ResultsSharedServices {
    private blockPage: Subject<boolean> = new Subject<boolean>();

    get pageBlocker(): Subject<boolean> {
        return this.blockPage;
    }
}
