import { Injectable, Injector } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ListenerId } from '@core/constants/listener-id.const';
import { ScrollService } from '@services/modules/core/scroll-service';
import { TranslateService } from '@ngx-translate/core';
import { ToolsService } from '@core/utils/tools-service';
import { CartStatus } from '@core/constants/cart-status';
import { SearchHelpers } from '@services/searchEngine/services/search-helpers';
import { HttpClient } from '@angular/common/http';
import { Observable, Subscriber, throwError } from 'rxjs';
import { CartDataService } from '@services/modules/cart/dataservices/cart.data.service';
import { ICart } from '@schemas/modules/cart/cart';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { catchError, map, tap } from 'rxjs/operators';
import { DialogService } from '@services/dialog.service';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CombinationId, DataId } from '@schemas/modules/core/base-types';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { CurrencyService } from '@services/services/currency-service';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { StorageKeys } from '@schemas/modules/core/storage-keys';
import { CartTransfers } from '@schemas/modules/cart/cart-transfers';

@Injectable({
    providedIn: 'root',
})
export class CartService extends ApiAccessService implements CanActivate {
    private cartData: any = {};
    private readonly defaultSelected: {
        [key in ServiceId]?: any;
    } = {};

    private initDone = new Promise(() => {});

    private cartRepriced = new Promise<void>((resolve, reject) => {
        resolve();
    });
    private emptyCart: any = {
        items: [],
        itemPrice: {},
    };

    constructor(
        private trazlerAPI: ApiAccessService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private scrollService: ScrollService,
        private cartDataService: CartDataService,
        private currencyService: CurrencyService,
        private router: Router,
        injector: Injector,
        http: HttpClient
    ) {
        super(injector, http);
        this.init();
    }

    public get InitDone(): Promise<unknown> {
        return this.initDone;
    }

    public static getRecommendedCartInfo(combId: string): any | null {
        const info = StorageManagerUtil.get(StorageKeys.RECOMMENDED);
        if (info) {
            if (info.combId === combId) {
                return info.recommended;
            }
        }
        return null;
    }

    private static setRecommended(combId: CombinationId, recommended: boolean): void {
        if (recommended) {
            const data = {
                combId,
                recommended,
            };
            StorageManagerUtil.set(StorageKeys.RECOMMENDED, data);
        }
    }

    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree {
        console.log('can activate called');
        if (this.noVirtual()) {
            return true;
        }
        console.error('forbidden to access summary in this state');
        return this.router.parseUrl('/search');
    }

    // silentMode == true => no loader displayed
    public reInit(silentMode?: boolean): Promise<any> {
        console.log('reinit cart called');
        return this.init(silentMode);
    }

    public reprice(combId: CombinationId, updateLang = false): Promise<any> {
        this.cartRepriced = new Promise<any>((resolve, reject): any => {
            if (this.cartData.cartCurrency && this.cartData.cartCurrency !== '' && !updateLang) {
                resolve(this.cartData);
                return this.cartRepriced;
            }

            this.trazlerAPI.repriceCart(combId).then(
                (success: any) => {
                    this.setCart(success);
                    resolve(success);
                    //this.listenersService.NotifyListeners(ListenerId.LISTEN_CART_CHANGED);
                },
                () => {
                    this.setCart(ToolsService.deepCopy(this.emptyCart));
                    reject();
                }
            );
        });

        return this.cartRepriced;
    }

    public getCartData(serviceId = -1, cartData?: any): any {
        if (!cartData) {
            cartData = ToolsService.deepCopy(this.cartData);
        }

        if (!cartData || !cartData.items) {
            return;
        }

        if (serviceId < 0) {
            return CartUtils.formServiceTitle(cartData, this.translateService);
        }

        let tmp = ToolsService.deepCopy(cartData);
        tmp.items = [];
        tmp.itemPrice = {
            additionalCharge: 0,
            additionalChargeNow: 0,
            dueOnArrival: 0,
            listPrice: 0,
            listPriceCur: 'EUR',
            payLaterAmt: 0,
            payableAmt: 0,
        };

        for (let i = 0; i < cartData.items.length; i++) {
            if (cartData.items[i].serviceId === serviceId && !cartData.items[i].isVirtual) {
                tmp.items.push(cartData.items[i]);
                if (i === 0) {
                    tmp.itemPrice.listPriceCur = cartData.items[i].itemPrice.listPriceCur;
                }
                tmp.itemPrice.payableAmt += cartData.items[i].itemPrice.payableAmt;
                tmp.itemPrice.payLaterAmt += cartData.items[i].itemPrice.payLaterAmt;
                tmp.itemPrice.listPrice += cartData.items[i].itemPrice.listPrice;
                tmp.itemPrice.dueOnArrival += cartData.items[i].itemPrice.dueOnArrival;
                tmp.itemPrice.additionalChargeNow += cartData.items[i].itemPrice.additionalChargeNow;
                tmp.itemPrice.additionalCharge += cartData.items[i].itemPrice.additionalCharge;
            }
        }

        tmp = CartUtils.formServiceTitle(tmp, this.translateService);
        tmp.itemsOrder = CartUtils.formItemOrder(serviceId, tmp.items, this.translateService);
        tmp.validated = tmp.items.length > 0;

        return tmp;
    }

    public isAllServiceOptionBooked(servicesId: ServiceId): boolean {
        return this.isServiceBooked(servicesId, true);
    }

    public isServicesBooked(servicesId: ServiceId): boolean {
        return this.isServiceBooked(servicesId, false);
    }

    public isEmpty(): boolean {
        return !(this.cartData && this.cartData.items && this.cartData.items.length);
    }

    public isSomeServiceBooked(): boolean {
        return CartUtils.hasStillBookedItem(this.cartData);
    }

    public getUserValidatedServices(): any {
        const bookedServices: {
            [key in ServiceId]?: true;
        } = {};
        if (this.cartData.items) {
            for (const cartItem of this.cartData.items) {
                if (!cartItem.isVirtual) {
                    bookedServices[cartItem.serviceId as ServiceId] = true;
                }
            }
        }
        return bookedServices;
    }

    public getTransfers(cartDataTest: ICart): CartTransfers[] {
        const hasMultipleServices = cartDataTest.items.some((item) => item.serviceId !== ServiceId.TRANSFER);
        if (hasMultipleServices) {
            return [];
        }

        const transfers: CartTransfers[] = [];
        const cartData = this.getCartData(ServiceId.TRANSFER);
        cartData.items.forEach((item: ICartItem) => {
            const info = {
                date: new Date(item.selectedItem.data.onwardDetails.pickupTzDate),
                title: item.summaryHeaderInfo,
            };

            transfers.push(info);
        });

        return transfers;
    }

    public getCartPersonsStr(cartData: ICart): string {
        let personsStr = '';
        const travellersData = CartUtils.getTravellersData(cartData.items);
        if (travellersData) {
            personsStr =
                travellersData.txt +
                ' ' +
                this.translateService
                    .instant('TRAVELLER.' + (travellersData.total > 1 ? 'PEOPLE' : 'PERSON'))
                    .toLowerCase();
        }

        return personsStr;
    }

    //  cartReq is a Array of selected items, (use to add onward&return of same service)
    public setCartData(combId: CombinationId, serviceId: ServiceId, dataId: DataId, cartReq: any): Observable<any> {
        return new Observable((subscriber) => {
            const selected = this.getSelected(serviceId, {
                journeyType: 'all',
                id: undefined,
            });
            this.scrollService.scrollToTop();
            if (!this.sameItem(selected, serviceId, cartReq) && !CartUtils.isInvalidItem(selected)) {
                const confirmationDialogOptions = CartUtils.getSameItemConfirmationDialogData(
                    serviceId,
                    selected,
                    cartReq,
                    this.translateService
                );
                this.scrollService.disableScroll();
                setTimeout(() => {
                    this.dialogService
                        .openConfirmationDialog(confirmationDialogOptions)
                        .afterClosed()
                        .subscribe((dialogReturn: TzDialogReturn) => {
                            this.scrollService.enableScroll();
                            if (dialogReturn === CONFIRM) {
                                return this.setCartDataInternal(combId, serviceId, dataId, cartReq, subscriber);
                            } else {
                                subscriber.error({
                                    status: 304,
                                });
                            }
                        });
                }, 0);
            } else {
                this.setCartDataInternal(combId, serviceId, dataId, cartReq, subscriber);
            }
        });
    }

    public isVirtual(serviceId: ServiceId): boolean {
        if (this.cartData.items) {
            for (const cartItem of this.cartData.items) {
                if (
                    cartItem.serviceId === serviceId ||
                    (serviceId === ServiceId.FLIGHT_TRAIN &&
                        (cartItem.serviceId === ServiceId.FLIGHT || cartItem.serviceId === ServiceId.TRAIN))
                ) {
                    return cartItem.isVirtual;
                }
            }
        }
        return true;
    }

    public getCartItemData(serviceId: ServiceId, legInfo?: any): any {
        const result: any = {
            found: false,
            carts: [],
        };

        if (!this.cartData || !Array.isArray(this.cartData.items) || this.cartData.items.length === 0) {
            return result;
        }

        this.cartData.items.some((cartItem: any): any => {
            if (legInfo && legInfo.journeyType) {
                switch (serviceId) {
                    case ServiceId.FLIGHT_TRAIN:
                        if (cartItem.serviceId === ServiceId.FLIGHT) {
                            break;
                        }
                    /* falls through */
                    case ServiceId.TRAIN:
                        if (cartItem.serviceId === ServiceId.TRAIN) {
                            if (legInfo.journeyType === 'all') {
                                result.found = true;
                                result.carts.push(cartItem);
                                return false;
                            } else if (cartItem.selectedItem.type === legInfo.journeyType) {
                                result.found = true;
                                result.carts.push(cartItem);
                                return true;
                            }
                        } else {
                            return false;
                        }
                        break;
                    case ServiceId.TRANSFER:
                        if (cartItem.serviceId === ServiceId.TRANSFER) {
                            if (legInfo.journeyType === 'all') {
                                result.found = true;
                                result.carts.push(cartItem);
                            } else if (
                                (!legInfo.id || cartItem.selectedItem.activeTransfer === legInfo.id) &&
                                cartItem.selectedItem.data.journeyType === legInfo.journeyType
                            ) {
                                result.found = true;
                                result.carts.push(cartItem);
                                return true;
                            }
                            return false;
                        }
                }
            }
            if (cartItem.serviceId === serviceId) {
                result.found = true;
                result.carts.push(cartItem);
                return true;
            }

            if (
                serviceId === ServiceId.FLIGHT_TRAIN &&
                (cartItem.serviceId === ServiceId.FLIGHT ||
                    (cartItem.serviceId === ServiceId.TRAIN &&
                        (!legInfo || !legInfo.journeyType || legInfo.journeyType !== 'all')))
            ) {
                result.found = true;
                result.carts.push(cartItem);
                return true;
            }
        });

        return result;
    }

    public getSelected(serviceId: ServiceId, legInfo?: any): any {
        if (!legInfo) {
            legInfo = {
                journeyType: 'all',
                id: undefined,
            };
        }
        const cartData = this.getCartItemData(serviceId, legInfo);

        if (!cartData.found) {
            return this.getEmptySelected(serviceId);
        }
        const item = cartData.carts[0].selectedItem.data;

        const cartLight = {
            inCart: true,
            isVirtual: cartData.carts[0].isVirtual,
            bookingOptions: ToolsService.deepCopy(cartData.carts[0].selectedItem.bookingOptions),
        };
        let res: any;
        switch (serviceId) {
            case ServiceId.FLIGHT:
                return {
                    ...cartLight,
                    onwardTripId: item.departureSummary.tripID,
                    returnTripId: item.returnSummary.tripID,
                    id: item.id,
                    item,
                    dataId: cartData.carts[0].dataId,
                    hasUpgrades: ToolsService.deepCopy(cartData.carts[0].selectedItem.hasUpgrades),
                };

            case ServiceId.TRAIN:
                res = {
                    ...cartLight,
                    selectedTrainOnwardId: cartData.carts[0].selectedItem.id,
                    selectedTrainReturnId: '',
                    selectedOnwardClassId: item.onward.id,
                    selectedReturnClassId: '',
                    journey_id: item.onward.groupId,
                    tripId: item.onward.tripID,
                    dataId: cartData.carts[0].dataId,
                    items: cartData.carts,
                };
                if (cartData.carts.length > 1) {
                    res.selectedTrainReturnId = cartData.carts[1].selectedItem.id;
                    res.selectedReturnClassId = cartData.carts[1].selectedItem.data.onward.id;
                    res.retTripId = cartData.carts[1].selectedItem.data.onward.tripID;
                }
                return res;

            case ServiceId.ACC:
                return {
                    ...cartLight,
                    selectedHotel: item,
                    cartPrice: cartData.carts[0].itemPrice,
                    adults: cartData.carts[0].searchReq.passengers.adults,
                };

            case ServiceId.CAR_RENTAL:
                return {
                    ...cartLight,
                    id: item.id,
                    rentalData: item,
                    // packageInfo: cartData.cart.selectedItem.rentalPackageInfo,
                };

            case ServiceId.TRANSFER:
                res = {
                    ...cartLight,
                    transferData: cartData,
                };
                return res;
        }
    }

    public getEmptySelected(serviceId: ServiceId): any {
        return ToolsService.deepCopy(this.defaultSelected[serviceId]);
    }

    /*
from should be set to one of the value of StateFrom constants
*/

    public getCartInfo(serviceId: ServiceId): any {
        let info: any = {
            inCart: false,
            price: 0,
            name: '',
            currency: {},
        };

        if (!this.cartData || !Array.isArray(this.cartData.items) || this.cartData.items.length === 0) {
            return info;
        }

        if (serviceId === ServiceId.SUMMARY) {
            info.inCart = true;
            info.price = this.cartData.itemPrice.listPrice;
            info.currency = this.cartData.itemPrice.listPriceCur;
            info.additionalCharge = this.cartData.itemPrice.additionalCharge;
            return info;
        }

        const { found, carts } = this.getCartItemData(serviceId, {
            journeyType: 'all',
            id: undefined,
        });
        if (found) {
            carts.forEach((cart: any) => {
                info = CartUtils.formTabInfo(info, cart);
            });
        }
        return info;
    }

    public resetSelected(serviceId: ServiceId): any {
        return this.getSelected(serviceId);
    }

    public removeCartData(serviceId: ServiceId, optionId: any): Observable<boolean | void> {
        // dataType, result, idx, transferJourneyType, tripType
        return this.cartDataService.removeCartSummary(serviceId, optionId).pipe(
            map((response: ICart) => {
                console.log('CART REMOVED SUCCESS', response);
                this.setCart(response);
                this.resetSelected(serviceId);
                return true;
            }),
            catchError((err) => {
                console.log('CART RESULT FAIL', err);
                this.resetSelected(serviceId);
                return throwError(err);
            })
        );
    }

    public clearCart(): Observable<{ message: string }> {
        return this.cartDataService.clearCart().pipe(
            tap((response: { message: string }) => {
                this.resetAllSelected();
                this.setCart(ToolsService.deepCopy(this.emptyCart));
                StorageManagerUtil.remove('checkoutFormData');
                StorageManagerUtil.remove('skipSignIn');
                StorageManagerUtil.remove('cartCurrency');
            })
        );
    }

    public recheckCartItem(combId: CombinationId, deviceId: string, hideLoader: boolean): Promise<any> {
        return this.cartDataService.recheckCartItem(combId, deviceId, hideLoader);
    }

    public applyPriceChange(optionId: string): Observable<ICart> {
        return new Observable<ICart>((subscriber: Subscriber<ICart>) => {
            const serviceId = this.getInvalidItemServiceId(optionId);
            if (serviceId === undefined || serviceId === ServiceId.INVALID) {
                subscriber.next();
                return;
            }

            this.cartDataService.acceptPriceChange(serviceId, optionId).subscribe(
                (response) => {
                    this.setCart(response);
                    this.resetSelected(serviceId);
                    this.listenersService.NotifyListeners(ListenerId.LISTEN_REPRICED, true);
                    subscriber.next(response);
                },
                (failure: any) => {
                    this.resetSelected(serviceId);
                    console.log('CART RESULT FAIL', failure);
                    subscriber.next(failure);
                }
            );
        });
    }

    public setCart(c: any): void {
        if (c) {
            CartService.setRecommended(c.combId, c.recommended);
            this.cartData = c;
        }
        //this.listenersService.NotifyListeners(ListenerId.LISTEN_CART_CHANGED);
    }

    public noVirtual(): any {
        if (this.cartData.items) {
            for (const cartItem of this.cartData.items) {
                if (cartItem.isVirtual) {
                    return false;
                }
            }
            return true;
        }
        return false;
    }

    private isServiceBooked(servicesId: ServiceId, checkAllOptions: boolean): boolean {
        const valid = Array.isArray(this.cartData.items) && this.cartData.items.length > 0;
        if (!valid) {
            return valid;
        }
        const checkFunc = (ci: any) => {
            if (
                ci.serviceId === servicesId ||
                (SearchHelpers.isTransport(servicesId) && SearchHelpers.isTransport(ci.serviceId))
            ) {
                if (checkAllOptions) {
                    return ci.selectedItem.bookingOptions.every(
                        (option: any) =>
                            option.status === CartStatus.STATUS_PROGRESS ||
                            option.status === CartStatus.STATUS_CONFIRMED
                    );
                }
                return ci.selectedItem.bookingOptions.some(
                    (option: any) =>
                        option.status === CartStatus.STATUS_PROGRESS || option.status === CartStatus.STATUS_CONFIRMED
                );
            }
        };

        if (checkAllOptions) {
            return this.cartData.items.every(checkFunc);
        }
        return this.cartData.items.some(checkFunc);
    }

    private getInvalidItemServiceId(optionId: string): ServiceId | undefined {
        const cartItems = this.cartData.items as ICartItem[];
        for (const item of cartItems) {
            for (const option of item.selectedItem.bookingOptions) {
                if (option.optionId === optionId) {
                    return item.serviceId;
                }
            }
        }

        return;
    }

    private resetAllSelected(): void {
        this.defaultSelected[ServiceId.FLIGHT] = {
            onwardTripId: '',
            returnTripId: '',
            id: '',
            inCart: false,
        };
        this.defaultSelected[ServiceId.TRAIN] = {
            selectedTrainOnwardId: '',
            selectedTrainReturnId: '',
            selectedOnwardClassId: '',
            selectedReturnClassId: '',
            inCart: false,
            journey_id: '',
            tripId: '',
            retTripId: '',
        };
        this.defaultSelected[ServiceId.ACC] = {
            selectedHotel: {
                id: '',
                rooms: [],
            },
            inCart: false,
        };
        this.defaultSelected[ServiceId.TRANSFER] = {
            selectedTransfers: [],
            inCart: false,
        };
        this.defaultSelected[ServiceId.CAR_RENTAL] = {
            id: '',
            inCart: false,
            rentalData: undefined,
            packageInfo: {
                extrasText: '',
            },
        };
    }

    private setAllSelected(): void {
        this.getSelected(ServiceId.FLIGHT);
        this.getSelected(ServiceId.TRANSFER);
        this.getSelected(ServiceId.TRAIN);
        this.getSelected(ServiceId.ACC);
        this.getSelected(ServiceId.CAR_RENTAL);
    }

    //silentMode == true => no loader displayed
    private init(silentMode?: boolean): Promise<any> {
        // init default selected
        this.resetAllSelected();
        this.initDone = new Promise<unknown>((resolve, reject) => {
            // get cart list from server
            this.trazlerAPI.getCartList(silentMode).then(
                (success: any) => {
                    console.log('new cart data content received');

                    this.setCart(success);
                    if (this.cartData.cartCurrency && this.cartData.cartCurrency !== '') {
                        this.currencyService.setCartCurrency(this.cartData.cartCurrency);
                    }
                    // v2 check later
                    //this.settingsService
                    this.setAllSelected();
                    resolve(success);
                    this.listenersService.NotifyListeners(ListenerId.LISTEN_CAL_REFRESH);
                },
                () => {
                    this.setCart(ToolsService.deepCopy(this.emptyCart));
                    resolve(this.cartData);
                }
            );
        });
        return this.initDone;
    }

    private setCartDataInternal(
        combId: CombinationId,
        serviceId: ServiceId,
        dataId: DataId,
        cartReq: any,
        observable: Subscriber<any>
    ): any {
        return this.cartDataService.addCartSummary(combId, serviceId, dataId, cartReq).subscribe(
            (success: any) => {
                this.setCart(success);
                observable.next(success);
            },
            (failure: any) => {
                observable.error(failure);
            }
        );
    }

    private sameItem(selected: any, serviceId: ServiceId, cartReq: any): any {
        // V2 check later
        if (serviceId === ServiceId.FLIGHT) {
            const selectedService = this.getSelected(ServiceId.TRAIN, {
                journeyType: 'all',
                id: undefined,
            });
            if (!selectedService.isVirtual && selectedService.inCart) {
                return false;
            }
        } else if (serviceId === ServiceId.TRAIN) {
            const selectedService = this.getSelected(ServiceId.FLIGHT, {
                journeyType: 'all',
                id: undefined,
            });
            if (!selectedService.isVirtual && selectedService.inCart) {
                return false;
            }
        }
        if (
            !selected.inCart ||
            selected.isVirtual
            // @todo check whether status check is needed
            // || selected.cartItemStatus > CartStatus.StatusCreated
        ) {
            return true;
        }

        switch (serviceId) {
            case ServiceId.ACC:
                return CartUtils.verifySelectedAcc(selected, cartReq[0]);
            case ServiceId.TRANSFER:
                return CartUtils.verifySelectedTransfers(selected.transferData.carts, cartReq);
            case ServiceId.CAR_RENTAL:
                return (
                    cartReq[0].id === selected.id &&
                    cartReq[0].bookingOptions[0].rentalPackageInfo.name ===
                        selected.bookingOptions[0].rentalPackageInfo.name &&
                    CartUtils.sameExtras(
                        cartReq[0].bookingOptions[0].rentalPackageInfo.extrasSelected,
                        selected.bookingOptions[0].rentalPackageInfo.extrasSelected
                    )
                );
            case ServiceId.FLIGHT:
                return cartReq[0].id === selected.id;
            case ServiceId.TRAIN:
                return (
                    cartReq[0].bookingOptions[0].tripId === selected.tripId &&
                    cartReq[0].bookingOptions[0].classId === selected.selectedOnwardClassId &&
                    (cartReq.length <= 1 ||
                        (cartReq[1].bookingOptions[0].tripId === selected.retTripId &&
                            cartReq[1].bookingOptions[0].classId === selected.selectedReturnClassId))
                );
            default:
                console.error('unknown service id:' + serviceId);
        }
    }
}
