import { Injectable, Injector } from '@angular/core';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ServiceId } from '@core/constants/service-id.const';
import { HttpRequestType } from '@core/constants/http-request-type';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, throwError } from 'rxjs';
import { PaginationQueryParams } from '@schemas/modules/core/pagination-query-params';
import { CartService } from '@services/modules/cart/services/cart.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { CartRemoveService } from '@services/modules/cart/services/cart-remove-service';
import { take, tap } from 'rxjs/operators';
import { DataId } from '@schemas/modules/core/base-types';
import { CheckInconsistentService } from '@services/searchEngine/services/check-consistent-service';
import { ISearchErr, SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { StateFrom } from '@core/constants/state-from.const';
import { UserSearchService } from '@services/searchEngine/services/user-search-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { MapResults, SearchMapRequest } from '@schemas/modules/result-map/results-map.model';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { TzFocusType } from '@core/constants/keyboard-key-types.const';
import { TranslateService } from '@ngx-translate/core';
import { DialogService } from '@services/dialog.service';
import { ResultsSharedServices } from '@services/modules/results/services/results.shared.services';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { CartStatus } from '@core/constants/cart-status';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { ModalService } from '@services/modal.service';
import { ICart } from '@schemas/modules/cart/cart';

@Injectable({
    providedIn: 'root',
})
export class ResultService extends ApiAccessService {
    public readonly serviceId: ServiceId = ServiceId.INVALID;
    public refId: string | undefined;
    public dataId: DataId | undefined;
    public selectedServices: number[] = [];
    public minTrainPrice!: number;

    private paginateFrom = -1;

    constructor(
        injector: Injector,
        http: HttpClient,
        private cartService: CartService,
        private tripPlannerService: TripPlannerService,
        private cartRemoveService: CartRemoveService,
        private checkInconsistentService: CheckInconsistentService,
        private searchErrService: SearchErrorService,
        private userSearchService: UserSearchService,
        private resultsSharedServices: ResultsSharedServices,
        public translateService: TranslateService,
        public dialogService: DialogService,
        public modalService: ModalService
    ) {
        super(injector, http);
    }

    public searchServiceData(serviceId: ServiceId, queryParams: any, checkConsistency: boolean, params?: any) {
        const state = window.history.state;
        const from = state ? state.from : '';
        this.paginateFrom = -1;

        if (this.noResult(serviceId)) {
            const curErr = this.searchErrService.getCurServiceErr(serviceId);

            let dataErr: ISearchErr;
            if (curErr === null) {
                dataErr = {
                    serviceId,
                    failure: {
                        invalid: false,
                        addService: from === StateFrom.SUMMARY,
                    },
                    cb: this.noResultMoveNext.bind(this),
                };
            } else {
                dataErr = curErr;
                dataErr.cb = this.noResultMoveNext.bind(this);
            }

            const searchInfos = this.searchDataService.getSearch(serviceId);
            if (searchInfos && searchInfos.failureCode === 8024 && searchInfos.modifyData) {
                dataErr.failure.dataFailure = {
                    remove: true,
                    status: searchInfos.failureCode,
                    cal: searchInfos.modifyData,
                    confirmCb: this.userSearchService.updateRentalSearch,
                    field: '',
                };
            }
            this.searchErrService.newError(dataErr);
            return throwError(dataErr);
        }

        const searchParams = {
            entity: 'searchResults',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(serviceId),
            currency: this.settingsService.getCurrency(),
            hideLoading: false,
            lang: this.settingsService.langCode,
            refreshFilter: false,
            serviceId,
            handleRedisError: true,
            ...queryParams,
            ...params,
        };

        return this.apiCall(HttpRequestType.GET, searchParams).pipe(
            tap(
                () => {
                    if (checkConsistency) {
                        this.checkInconsistentService.checkInconsistent(serviceId, from);
                    }

                    this.hideLoader();
                },
                (failure) => {
                    console.log('fail', failure);
                    if (failure && failure.error.code === 24) {
                        //DataExpired
                        this.userSearchService.reSearch(serviceId);
                    } else {
                        this.hideLoader();
                    }
                }
            )
        );
    }

    public filterServiceData(serviceId: ServiceId, queryParams: any, filters: any): Observable<any> {
        this.paginateFrom = -1;
        const filterParams = {
            entity: 'filter',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(serviceId),
            boosterSeats: 0,
            childSeats: 0,
            currency: this.settingsService.getCurrency(),
            hideLoading: false,
            journeyType: 'onwardJourney',
            lang: this.settingsService.langCode,
            refreshFilter: false,
            serviceId,
            handleRedisError: true,
            ...queryParams,
        };
        return this.apiCall(HttpRequestType.POST, filterParams, filters);
    }

    public searchMapResults(serviceId: ServiceId, queryParams: any, request: SearchMapRequest): Observable<MapResults> {
        const params = {
            entity: 'searchMap',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(serviceId),
            currency: this.settingsService.getCurrency(),
            lang: this.settingsService.langCode,
            serviceId,
            handleRedisError: true,
            ...queryParams,
        };

        return this.apiCallObs(HttpRequestType.POST, params, request);
    }

    public findResultById(serviceId: ServiceId, queryParams: any): Observable<Accommodation> {
        const params = {
            entity: 'findById',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(serviceId),
            currency: this.settingsService.getCurrency(),
            lang: this.settingsService.langCode,
            serviceId,
            handleRedisError: true,
            closeLoader: true,
            ...queryParams,
        };
        this.showLoader(true);

        return this.apiCallObs(HttpRequestType.GET, params);
    }

    public apiCallOptions(requestMethod: HttpRequestType, params: any, httpOptions: any, data?: any) {
        const urlAndQueryParams = this.buildUrlAndQueryParams(params);
        const { url, queryParams } = urlAndQueryParams;
        return this.request(requestMethod, url, queryParams, data, httpOptions);
    }

    public apiCall(requestMethod: HttpRequestType, params: any, data?: any) {
        return this.apiCallOptions(requestMethod, params, undefined, data);
    }

    public paginate(serviceId: ServiceId, params: any): Observable<any[]> {
        const { limit, start, section, sortBy, onwardId, returnId } = params;

        if (this.paginateFrom === start) {
            return throwError(`pagination error got duplicate start index: ${start}`);
        }
        this.paginateFrom = start;

        const paginationQueryParams: PaginationQueryParams = {
            dataId: this.searchDataService.getDataId(serviceId),
            lang: this.settingsService.langCode,
            currency: this.settingsService.getCurrency(),
            onwardId,
            returnId,
            serviceId,
            limit,
            start,
            sortBy,
        };

        if (section) {
            paginationQueryParams.section = section;
        }

        const paginationRequestParams: any = {
            entity: 'paginate',
            id: this.searchDataService.getCombId(),
            ...paginationQueryParams,
        };

        return this.apiCall(HttpRequestType.GET, paginationRequestParams);
    }

    public checkServiceFailedAndNotify(serviceId: ServiceId, journeyType?: string): void {
        const cartData = this.cartService.getCartData(serviceId) as ICart;
        if (!cartData || !cartData.items || !cartData.totalImprintAmt) {
            return;
        }

        let hasFailedOption = false;
        for (const item of cartData.items) {
            if (journeyType && item.selectedItem.data.journeyType !== journeyType) {
                continue;
            }

            hasFailedOption =
                item.selectedItem.bookingOptions.some(
                    (option) =>
                        option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED
                ) || hasFailedOption;
        }

        if (hasFailedOption) {
            this.dialogService.openConfirmationDialog({
                title: this.translateService.instant('POPUPS.CONTACT_SUPPORT.TITLE'),
                multipleContent: [this.translateService.instant('POPUPS.CONTACT_SUPPORT.MESSAGE')],
                confirmBtnTitle: this.translateService.instant('POPUPS.OK'),
            });
        }
    }

    public getServiceCartItems(validOnly = true): any {
        const cartData = this.cartService.getCartData(this.serviceId);
        if (!cartData || !validOnly) {
            return cartData;
        }

        const validCartItems: ICartItem[] = [];
        cartData.items?.forEach((item: ICartItem) => {
            const valid = item.selectedItem.bookingOptions.some(
                (option: ICartSelectedItemBookingOption) =>
                    !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
            );

            if (valid) {
                validCartItems.push(item);
            }
        });

        cartData.items = validCartItems;
        return cartData;
    }

    public addToCart(cartReq: any): Observable<any> {
        const combinationId = this.searchDataService.getCombId();
        const dataId = this.searchDataService.getDataId(this.serviceId);
        const serviceId = this.serviceId;

        console.log('set page blocker to true');
        this.resultsSharedServices.pageBlocker.next(true);
        return this.tripPlannerService.setCartData(combinationId, serviceId, dataId, cartReq).pipe(
            take(1),
            tap(
                () => {
                    console.log('set page blocker to false');
                    this.resultsSharedServices.pageBlocker.next(false);
                },
                (error) => {
                    this.resultsSharedServices.pageBlocker.next(false);
                }
            )
        );
    }

    public removeSelectedItem(optionId: string, removeService: boolean = false): Observable<void | boolean> {
        return this.cartRemoveService.removeCartData(this.serviceId, optionId, removeService);
    }

    public handlingData<T>(data: T[], recommendedID: string, cartID: string | null): T[] {
        data.map((item: any) => {
            item.recommended = item.id === recommendedID && item.id !== cartID;
        });

        return data;
    }

    public filterDataRemoveCartItem<T>(data: T[], cartId: string): T[] {
        return data.filter((item: any) => item.id !== cartId);
    }

    /*  private showLoaderMessage(serviceId: number): void {
        const messageKey = 'LOADER_MESSAGES.' + ResultService.getLoaderMassageKey(serviceId);
        this.loaderService.triggerLoading.emit({loading: true, textKey: messageKey});
    }*/

    private noResultMoveNext(serviceId: ServiceId, shouldEdit: boolean): void {
        if (shouldEdit) {
            console.log('should edit search');
            this.listenersService.NotifyListeners(ListenerId.LISTEN_ENABLE_EDIT, true);
            this.listenersService.NotifyListeners(ListenerId.LISTEN_FOCUS_NEXT, {
                id: 'tabIdx1',
                moveTo: TzFocusType.MOVE_TO_INVALID,
            });
        } else {
            this.userSearchService.searchCheckDep(serviceId, false, false, false, false);
        }
    }

    private hideLoader(): void {
        this.loaderService.triggerLoading.emit({ loading: false });
    }

    private noResult(serviceId: ServiceId): boolean {
        const data = this.searchDataService.getSearchResult(serviceId);
        return !data || data.resultLength === 0;
    }
}
