import { Injectable } from '@angular/core';
import { ListenerId } from '@core/constants/listener-id.const';

@Injectable({
    providedIn: 'root',
})
export class ListenersService {
    private listeners: any = {};
    private listenerId = ListenerId.LISTEN_INVALID;

    // can be a subj, cb or an array of subj with one callback
    public registerListener(subj: ListenerId | ListenerId[], cb: any, scope?: any): number {
        if ((typeof subj !== 'number' && !Array.isArray(subj)) || typeof cb !== 'function') {
            console.error('invalid listener request on subj:' + subj);
            return -1;
        }
        this.listenerId++;
        this.listeners[this.listenerId] = {
            subj,
            cb,
            scope,
        };

        return this.listenerId;
    }

    public unregisterListener(id: ListenerId): void {
        if (id === ListenerId.LISTEN_INVALID) {
            return;
        }
        delete this.listeners[id];
    }

    public NotifyListeners(subj: ListenerId, data?: any): void {
        for (const k in this.listeners) {
            if (typeof this.listeners[k].subj === 'number') {
                if (this.listeners[k].subj === subj) {
                    if (this.listeners[k].scope) {
                        this.listeners[k].cb.call(this.listeners[k].scope, data, subj);
                    } else {
                        this.listeners[k].cb(data, subj);
                    }
                }
            } else {
                for (const subject of this.listeners[k].subj) {
                    if (subject === subj) {
                        if (this.listeners[k].scope) {
                            this.listeners[k].cb.call(this.listeners[k].scope, data, subj);
                        } else {
                            this.listeners[k].cb(data, subj);
                        }
                    }
                }
            }
        }
    }
}
