import { Injectable } from '@angular/core';
import { SearchEvents } from '@core/constants/search-events';
import { Router } from '@angular/router';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ServiceId } from '@core/constants/service-id.const';
import { SERVICE_DEFAULT_SORT } from '@core/constants/default-sort';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ListenerId } from '@core/constants/listener-id.const';
import { ListenersService } from '@services/modules/core/listeners-service';
import { SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { PreSearchService } from '@services/modules/core/dataservices/pre-search.service';

@Injectable({
    providedIn: 'root',
})
export class RentalSearchService {
    constructor(
        private router: Router,
        private apiAccessService: ApiAccessService,
        private listenersService: ListenersService,
        private searchMetadataService: SearchMetadataService,
        private searchDataService: SearchDataService,
        private searchErrService: SearchErrorService,
        private tripPlannerService: TripPlannerService,
        private cartRoutingService: CartRoutingService,
        private preSearchService: PreSearchService
    ) {}

    public SearchRental(
        serviceSearch: any,
        eventId: SearchEvents,
        dataAvailable: any,
        params: any,
        onUpdateSearchCb: (newCal: any) => void
    ) {
        params.id = this.searchDataService.getCombId();
        params.serviceId = ServiceId.CAR_RENTAL;
        params.dataId = this.searchDataService.getDataId('rentCar');
        console.log(serviceSearch, '*** checking changes search  *** ');
        //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, true);
        this.preSearchService.preSearchData(params, serviceSearch, eventId).then(
            (success: any) => {},
            (failure: any) => {
                //this.listenersService.NotifyListeners(ListenerId.LISTEN_PROGRESS_BAR, false);
                console.log('FAILURE', failure);
                if (failure.error && failure.error.code === 8024) {
                    //
                    const cal = failure.error.params.errData;
                    const dataFailure: any = {
                        status: failure.error.status,
                        message: failure.error.message,
                    };
                    if (cal) {
                        dataFailure.remove = this.router.url !== '/search';
                        dataFailure.status = failure.error.code;
                        dataFailure.cal = cal;
                        dataFailure.confirmCb = onUpdateSearchCb;
                    }

                    const results = failure.error.params.data;
                    results.serviceSearch.failureCode = failure.error.code;
                    results.serviceSearch.modifyData = cal;
                    if (results.metaData.complete) {
                        this.tripPlannerService.setComplete(ServiceId.CAR_RENTAL);
                    }
                    this.searchMetadataService.saveSearch(results, eventId);
                    this.searchErrService.searchErr(dataFailure, ServiceId.CAR_RENTAL, eventId);
                }

                /* if (failure.status === 412 && failure.data) {
                        if (failure.data.metaData.complete) {
                            this.tripPlannerService.setComplete(ServiceId.CAR_RENTAL);
                        }
                        this.searchMetadataService.saveSearch(failure.data, eventId);
                    }*/
                /* if (eventId !== SearchEvents.EVENT_SEARCH_EDIT && !this.isHomePage()) {
                        this.gotoRentalPage();
                    }*/
                //this.apiAccessService.handleHttpError(failure);
            }
        );
        /*.catch(() => {
                this.loaderService.triggerLoading.emit({ loading: false });
            });*/
    }

    /* private gotoRentalPage(): void {
        this.cartRoutingService.currentServiceId.next(ServiceId.CAR_RENTAL);
        this.router.navigate(['/results/car-rentals'], {
            queryParams: {
                sort_by: SERVICE_DEFAULT_SORT[ServiceId.CAR_RENTAL],
            },
        });
    }

    private isHomePage(): boolean {
        return this.router.url === '/search';
    }*/
}
