import { CombinationId, DataId, Date, Time, TzDateTime } from '@schemas/modules/core/base-types';
import { CombServiceIds } from '@core/constants/comb-service-ids';
import { ILocation } from '@schemas/modules/core/location.model';
import { ServiceId } from '@core/constants/service-id.const';
import { MetaDataExt } from '@schemas/modules/search/meta';
import { CurCode } from '@schemas/modules/core/currency';
import { LangCode } from '@schemas/modules/core/ILanguage';
import { Trip } from '@core/constants/trip';

export enum NightChoices {
    NIGHT_CHOICE_UNDEFINED = 0,
    NIGHT_CHOICE_PREV_DAY = 1,
    NIGHT_CHOICE_CUR_DAY = 2,
}

export interface ICode {
    Code: string;
    IsCity: boolean;
}

export interface ITrainDistanceRoute {
    CodeInfo: ICode[];
    Distance: number;
}

export interface ISearchData {
    from: ILocation | void;
    to: ILocation | void;
    address: ILocation | void;
    tripType: Trip;
    meetingCal: ICalendar | void;
    tripCal: ICalendar | void;
    transferCal: ICalendar | void;
    rentalCal: ICalendar | void;
    retainSearch: boolean;
    hotelCode: string | void;
    activeTransfer: string | void;
    nightChoice: NightChoices | void;
    journeyToUpdate: string;
    transferDataID: DataId | void;
    trainSupplierList: string[];
    trainDistanceRoute: ITrainDistanceRoute[];
}

export interface IEndUserInfos {
    ip: string;
    agent: string;
    host: string;
}

export interface IPartialUserSettings {
    userCurReq: CurCode;
    lang: LangCode;
}

export interface ILangSettings {
    code: LangCode;
    i18nCode: LangCode;
}

export interface IUserSettings extends IEndUserInfos, IPartialUserSettings {
    langSettings: ILangSettings;
    ctryCode: string;
}

export interface IPassengers {
    adults: number;
    children: number;
    childrenAges: number[];
    infants: number;
    infantAges: number[];
    youth: number;
    youthAges: number[];
    rooms: number;
    driversAge: number;
    childSeat: boolean;
    boosterSeat: boolean;
    total?: number; //not sent by server
    showBoosterSeat?: boolean; //not sent by server
    showChildSeat?: boolean; //not sent by server
}

export enum RunMode {
    RUN_NORMAL = 0, // services are chosen one by one by customer
    RUN_FAST = 1, // we automatically get for all selected service the best value and provide a direct booking capacity to user
    RUN_FAMILY = 2, // to be defined - unused
    RUN_BUSINESS = 3, // to be defined - unused
}

export interface IContext {
    runningMode: RunMode;
    services: CombServiceIds;
    travelType: Trip;
}

export interface ISSIBase {
    searchData: ISearchData;
    settings?: IUserSettings;
    dataId?: DataId;
    combID?: CombinationId;
    servId?: ServiceId;
    passengers: IPassengers;
    context: IContext;
}

export interface IPreSearchResult {
    metaData: MetaDataExt;
    serviceSearch: ISSIBase;
}

export interface ICalendar {
    fromDate: Date; //2006-01-02
    fromTime: Time;
    toDate: Date; //2006-01-02
    toTime: Time;
    twoDates: boolean; // when true the calendar has the fromDate activated (round trip)
    computeTransferTime: CombServiceIds; // internal field, contains a single service
    arrival: TzDateTime; //Arrival date time on intermediary place (typically rental arrival in hotel)
    departure: TzDateTime; //Departure date time from intermediary place (typically rental departure from hotel)
    resultSet?: boolean;
}

export interface SearchInfoInterface {
    to: string;
    from?: string;
    adults: number;
    children: number;
    people: number;
    dateRange?: string | null;
    date?: string | null;
    totalPersons?: number;
    rooms?: number;
}

export enum BestValType {
    RECOMMENDED = '0',
    CHEAPEST = '1',
    QUICKEST = '2',
}

export type bestValTypeMapping = {
    [key in BestValType]?: boolean;
};
