import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { UserService } from '@services/modules/user/services/user.service';
import { Subject, takeUntil } from 'rxjs';

@Component({
    selector: 'tz-profile-mob-menu',
    templateUrl: './profile-mob-menu.component.html',
    styleUrls: ['./profile-mob-menu.component.scss'],
})
export class ProfileMobMenuComponent implements OnInit {
    @Output() signOut: EventEmitter<void> = new EventEmitter<void>();
    public isLogged = false;
    public destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private menu: MenuController, public userService: UserService) {
        this.userService.loggedIn$.pipe(takeUntil(this.destroy$)).subscribe((isLogged) => {
            this.isLogged = isLogged;
        });
    }

    ngOnInit() {}

    public onCloseProfileMenu(): void {
        this.menu.close('profileMobMenu').then(() => {});
    }

    public onSignOut(): void {
        this.signOut.emit();
    }
}
