import { Injectable } from '@angular/core';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchHelpers } from '@services/searchEngine/services/search-helpers';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ServiceId } from '@core/constants/service-id.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { TranslateService } from '@ngx-translate/core';
import { DialogService } from '@services/dialog.service';
import {
    CANCEL,
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';

@Injectable({
    providedIn: 'root',
})
export class CheckDepService {
    constructor(
        private trazlerAPI: ApiAccessService,
        private cartService: CartService,
        private tabRoutingService: TabRoutingService,
        private translateService: TranslateService,
        private searchDataService: SearchDataService,
        private listenersService: ListenersService,
        private dialogService: DialogService
    ) {}

    private static canTriggerInconsistency(triggerServiceId: ServiceId, modifiedServiceId: ServiceId) {
        switch (triggerServiceId) {
            case ServiceId.FLIGHT_TRAIN:
            case ServiceId.FLIGHT:
            case ServiceId.TRAIN:
            case ServiceId.ACC:
                return !SearchHelpers.isTransport(modifiedServiceId);
            case ServiceId.TRANSFER:
            case ServiceId.CAR_RENTAL:
                return !SearchHelpers.isPrimaryService(modifiedServiceId);
        }
        return true;
    }

    //checkDep
    // returns:
    //      hasDep if there are dependencies
    //      serviceIdMapToUpd list of dependencies inconsistent
    //      toRest: true - we need to search for new dependencies values ( user confirmed the reset or all are in pre-selection)
    //              false - we must not recompute value but move to next service
    //      isPreCheck: when true we are checking the impact, when false operation is done we get its impact

    public checkDep(serviceId: ServiceId, isUpdComb: boolean, isNewService: boolean, fromPrimary: any) {
        return new Promise((resolve, reject) => {
            const dependenciesList = this.tabRoutingService.hasDependencies(serviceId, true);

            if (dependenciesList.length) {
                this.trazlerAPI
                    .checkDep(
                        this.searchDataService.getCombId(),
                        this.searchDataService.getDataId(serviceId),
                        serviceId,
                        isUpdComb
                    )
                    .then(
                        (res: any) => {
                            this.tabRoutingService.setCleanCheckDep();
                            this.checkDepSuccess(res, resolve, serviceId, isUpdComb, isNewService, fromPrimary);
                        },
                        (error: any) => {
                            reject(error);
                        }
                    );
            } else {
                resolve({
                    hasDep: false,
                    toReset: !!isUpdComb,
                });
            }
        });
    }

    public checkUpdCombination(curServiceId: ServiceId, serviceSearch: any): Promise<unknown> {
        return new Promise((resolve, reject) => {
            const dependenciesList = this.tabRoutingService.hasDependencies(curServiceId, true);

            if (dependenciesList.length) {
                const dataId = this.searchDataService.getDataId(curServiceId);
                this.trazlerAPI
                    .updServiceSearch(
                        this.searchDataService.getCombId(),
                        dataId,
                        this.getTransportServicesId(curServiceId),
                        true,
                        serviceSearch
                    )
                    .then(
                        (res: any) => {
                            this.tabRoutingService.setCleanCheckDep();
                            this.checkDepSuccess(
                                res,
                                resolve,
                                curServiceId,
                                true,
                                false,
                                SearchHelpers.isPrimaryService(curServiceId)
                            );
                        },
                        (error: any) => {
                            reject(error);
                        }
                    );
            } else {
                resolve({
                    hasDep: false,
                    toReset: true,
                });
            }
        });
    }

    // check what is the inconsistent status (used on page refresh and when a service has been added)
    public reCheckDep() {
        const firstServiceId = this.getTransportServicesId(
            SearchHelpers.getFirstServiceId(this.searchDataService.services)
        );

        return this.checkDep(firstServiceId, false, false, SearchHelpers.isPrimaryService(firstServiceId)).then(
            () => {
                this.listenersService.NotifyListeners(ListenerId.LISTEN_CONSISTENCIES);
            },
            () => {}
        );
    }

    private getTransportServicesId(servicesId: ServiceId) {
        if (servicesId === ServiceId.FLIGHT_TRAIN) {
            if (this.cartService.getCartItemData(ServiceId.TRAIN, 'all').found) {
                return ServiceId.TRAIN;
            } else {
                return ServiceId.FLIGHT;
            }
        }
        return servicesId;
    }

    private checkDepSuccess(
        res: any,
        resolver: any,
        serviceId: any,
        isUpdComb: any,
        isNewService: any,
        fromPrimary: any
    ) {
        let serviceListTxt = '';
        let details = '';
        const serviceIdMapToUpd: any = {};
        let hasModified = false;
        const preCheckInconsistent: any[] = [];
        let noPartial = false;
        let mainChange: any = null;
        // when only from field modified on transport we want to ignore all inconsistencies
        let ignoreAll = res && res.onlyFromField; // when true no inconsistencies will be reported
        let roomChanged = false; // set to true when number of rooms is modified TRAZ-6613

        if (isUpdComb) {
            hasModified = true;
        }
        let validatedModified = 0;
        if (res && res.servicesStatus) {
            let sep = '';
            // eslint-disable-next-line @typescript-eslint/prefer-for-of
            for (let i = 0; i < res.servicesStatus.length; i++) {
                let serviceInfo = '';

                if (res.servicesStatus[i].modified) {
                    hasModified = true;
                    serviceInfo = this.getInconsistentInfos(res.servicesStatus[i]);
                } else {
                    this.tabRoutingService.setConsistent(
                        res.servicesStatus[i].serviceId,
                        true,
                        '',
                        res.servicesStatus[i].inconsistentId
                    );
                    continue;
                }

                if (
                    !isUpdComb &&
                    !CheckDepService.canTriggerInconsistency(serviceId, res.servicesStatus[i].serviceId)
                ) {
                    continue;
                }

                const isATransport = SearchHelpers.isTransport(serviceId);
                // when TO is modified we do not ask for partial update TRAZ-5470
                if (
                    isUpdComb &&
                    SearchHelpers.isPrimaryService(serviceId) &&
                    (res.servicesStatus[i].serviceId === serviceId ||
                        (SearchHelpers.isTransport(res.servicesStatus[i].serviceId) && isATransport)) &&
                    res.servicesStatus[i].changedData &&
                    res.servicesStatus[i].changedData.length
                ) {
                    if (
                        (isATransport && res.servicesStatus[i].changedData[0].fieldName !== 'from') ||
                        (!isATransport && res.servicesStatus[i].changedData[0].fieldName === 'tripCal')
                    ) {
                        mainChange = {
                            changedData: this.translateService.instant('Fields.destination'),
                        };
                        noPartial = true;
                    } else if (
                        res.servicesStatus[i].changedData[0].fieldName === 'rooms' &&
                        serviceId === ServiceId.ACC
                    ) {
                        // when only rooms is changed we want to ignore all inconsistencies
                        ignoreAll = true;
                        roomChanged = true;
                    }
                }
                if (!ignoreAll && !noPartial) {
                    if (!res.servicesStatus[i].changedData) {
                        if (mainChange) {
                            mainChange.servicesChanged +=
                                sep + this.translateService.instant('Services.' + res.servicesStatus[i].serviceId);
                        } else {
                            mainChange = {
                                servicesChanged: this.translateService.instant(
                                    'Services.' + res.servicesStatus[i].serviceId
                                ),
                            };
                        }
                        sep = ' ,';
                    } else {
                        for (let j = 1; j < res.servicesStatus[i].changedData.length; j++) {
                            if (res.servicesStatus[i].changedData[j].ui) {
                                if (
                                    serviceId === res.servicesStatus[i].serviceId ||
                                    (isATransport && SearchHelpers.isTransport(res.servicesStatus[i].serviceId))
                                ) {
                                    if (mainChange) {
                                        mainChange.changedData = this.translateService.instant(
                                            'Fields.' + res.servicesStatus[i].changedData[0].fieldName
                                        );
                                        mainChange.fieldChanged = this.translateService.instant(
                                            'Fields.hl.' + res.servicesStatus[i].changedData[0].fieldName
                                        );
                                    } else {
                                        mainChange = {
                                            changedData: this.translateService.instant(
                                                'Fields.' + res.servicesStatus[i].changedData[0].fieldName
                                            ),
                                            servicesChanged: '',
                                            fieldChanged: this.translateService.instant(
                                                'Fields.hl.' + res.servicesStatus[i].changedData[0].fieldName
                                            ),
                                        };
                                    }
                                } else {
                                    if (mainChange) {
                                        if (
                                            (serviceId !== res.servicesStatus[i].serviceId && !isATransport) ||
                                            (isATransport &&
                                                !SearchHelpers.isTransport(res.servicesStatus[i].serviceId))
                                        ) {
                                            mainChange.servicesChanged +=
                                                sep +
                                                this.translateService.instant(
                                                    'Services.' + res.servicesStatus[i].serviceId
                                                );
                                            sep = ', ';
                                        }
                                    } else {
                                        mainChange = {
                                            servicesChanged: this.translateService.instant(
                                                'Services.' + res.servicesStatus[i].serviceId
                                            ),
                                        };
                                    }
                                }
                                break;
                            }
                        }
                    }
                }

                if (this.cartService.isVirtual(res.servicesStatus[i].serviceId)) {
                    if (isUpdComb) {
                        preCheckInconsistent.push({
                            serviceId: res.servicesStatus[i].serviceId,
                            infos: serviceInfo,
                        });
                    } else {
                        this.tabRoutingService.setConsistent(
                            res.servicesStatus[i].serviceId,
                            false,
                            serviceInfo,
                            res.servicesStatus[i].inconsistentId
                        );
                    }
                    if (fromPrimary || !SearchHelpers.isPrimaryService(res.servicesStatus[i].serviceId)) {
                        serviceIdMapToUpd[res.servicesStatus[i].serviceId] = true;
                    }
                    continue;
                }
                if (!fromPrimary && (isUpdComb || SearchHelpers.isPrimaryService(res.servicesStatus[i].serviceId))) {
                    continue;
                }
                if (
                    res.servicesStatus[i].serviceId !== serviceId &&
                    (!SearchHelpers.isTransport(res.servicesStatus[i].serviceId) ||
                        (!SearchHelpers.isTransport(serviceId) &&
                            SearchHelpers.isTransport(res.servicesStatus[i].serviceId)))
                ) {
                    validatedModified++;
                }
                if (isUpdComb) {
                    if (!ignoreAll || (res.servicesStatus[i].serviceId === ServiceId.ACC && roomChanged)) {
                        preCheckInconsistent.push({
                            serviceId: res.servicesStatus[i].serviceId,
                            infos: serviceInfo,
                        });
                        serviceIdMapToUpd[res.servicesStatus[i].serviceId] = true;
                    }
                } else {
                    this.tabRoutingService.setConsistent(
                        res.servicesStatus[i].serviceId,
                        false,
                        serviceInfo,
                        res.servicesStatus[i].inconsistentId
                    );
                    serviceIdMapToUpd[res.servicesStatus[i].serviceId] = true;
                }
                if (serviceInfo !== '') {
                    details +=
                        '<br>' +
                        this.translateService.instant('Services.' + res.servicesStatus[i].serviceId) +
                        ':' +
                        serviceInfo;
                }
                serviceListTxt += sep + this.translateService.instant('Services.' + res.servicesStatus[i].serviceId);
                sep = ', ';
                details += serviceInfo;
            }
        }
        if (!ignoreAll && serviceListTxt !== '') {
            if (isUpdComb) {
                if (validatedModified >= 1) {
                    console.log('no partial:' + noPartial);
                    let dialogObservableReference;
                    if (mainChange) {
                        if (!mainChange.changedData) {
                            mainChange.changedData = this.translateService.instant('Fields.search');
                        }
                        if (!mainChange.fieldChanged) {
                            mainChange.fieldChanged = this.translateService.instant('Fields.hl.search');
                        }
                    }
                    if (noPartial) {
                        dialogObservableReference = this.dialogService.openConfirmationDialog({
                            title: this.translateService.instant('POPUPS.RESET_NO_PARTIAL.TITLE'),
                            content: this.translateService.instant('POPUPS.RESET_NO_PARTIAL.BODY', mainChange),
                            confirmBtnTitle: this.translateService.instant('POPUPS.RESET_NO_PARTIAL.1_YES'),
                            cancelBtnTitle: this.translateService.instant('POPUPS.RESET_NO_PARTIAL.2_NO'),
                            showCloseBtn: false,
                            icon: { name: 'reset-no-partial-dialog', iconStyle: { 'width.px': 80, 'height.px': 80 } },
                        });
                    } else {
                        dialogObservableReference = this.dialogService.openConfirmationDialog({
                            content: this.translateService.instant('POPUPS.RESET_BOOKED.BODY', mainChange),
                            title: this.translateService.instant('POPUPS.RESET_BOOKED.TITLE'),
                            confirmBtnTitle: this.translateService.instant('POPUPS.RESET_BOOKED.CONFIRM'),
                            cancelBtnTitle: this.translateService.instant('POPUPS.RESET_BOOKED.CANCEL'),
                            closeBtnTitle: this.translateService.instant('POPUPS.RESET_BOOKED.CLOSE'),
                            icon: { name: 'reset-booked-dialog', iconStyle: { 'width.px': 80, 'height.px': 93.16 } },
                        });
                    }
                    dialogObservableReference.afterClosed().subscribe((dialogReturn: TzDialogReturn) => {
                        if (dialogReturn === CONFIRM) {
                            // recompute all
                            this.tabRoutingService.unsetBookedDependencies(serviceIdMapToUpd);
                            resolver({
                                hasDep: true,
                                resetList: serviceIdMapToUpd,
                                toReset: true,
                                preCheckInconsistent,
                            });
                        } else if (dialogReturn === CANCEL && !noPartial) {
                            //partial case
                            if (!SearchHelpers.isTransport(serviceId)) {
                                // in partial case, we do not want to trigger inconsistency
                                // on transport when acc is changed. Remove transport from list
                                // of inconsistent
                                this.tabRoutingService.setConsistent(ServiceId.FLIGHT_TRAIN, true, '');
                                for (let i = 0; i < preCheckInconsistent.length; i++) {
                                    if (SearchHelpers.isTransport(preCheckInconsistent[i].serviceId)) {
                                        preCheckInconsistent.splice(i, 1);
                                        i--;
                                    }
                                }
                            }
                            resolver({
                                hasDep: true,
                                resetList: {},
                                toReset: true,
                                preCheckInconsistent,
                            });
                        } else {
                            //cancel
                            resolver({
                                hasDep: true,
                                resetList: serviceIdMapToUpd,
                                toReset: false,
                            });
                        }
                    });
                } else {
                    this.tabRoutingService.unsetBookedDependencies(serviceIdMapToUpd);
                    resolver({
                        hasDep: true,
                        resetList: serviceIdMapToUpd,
                        toReset: true,
                        preCheckInconsistent,
                    });
                }
            } else if (isNewService) {
                resolver({
                    hasDep: true,
                    resetList: undefined,
                    toReset: false,
                });
            } else {
                resolver({
                    hasDep: true,
                    resetList: serviceIdMapToUpd,
                    toReset: false,
                });
            }
        } else {
            if (hasModified) {
                // when it is an edit search and there is no identified confirmed service
                // we set resetlist to undefined to check all services again
                // this is done to solve issue on flight / acc where the date does not change
                // but from change making the start date of the hotel being the next day
                // if (ignoreAll) {
                //     preCheckInconsistent = [ServiceId.ACC];
                //     serviceIdMapToUpd[ServiceId.ACC] = true;
                // }
                resolver({
                    hasDep: true,
                    resetList: serviceIdMapToUpd, //isUpdComb ? undefined : serviceIdMapToUpd,
                    toReset: true,
                    preCheckInconsistent,
                });
            } else {
                resolver({
                    hasDep: res && res.servicesStatus && res.servicesStatus.length > 0,
                    resetList: undefined,
                    toReset: false,
                });
            }
        }
    }

    private getInconsistentInfos(data: any) {
        let details = '';

        if (data.changedData && data.changedData.length) {
            // eslint-disable-next-line @typescript-eslint/prefer-for-of
            for (let j = 0; j < data.changedData.length; j++) {
                if (data.changedData[j].ui) {
                    if (data.changedData[j].oldVal !== '' && data.changedData[j].newVal !== '') {
                        details +=
                            '<li>' +
                            this.translateService.instant('INCONSISTENT_MSG.' + data.changedData[j].fieldName, {
                                oldVal: data.changedData[j].oldVal,
                                newVal: data.changedData[j].newVal,
                                service: this.translateService.instant('Services.' + data.serviceId),
                            }) +
                            '</li>';
                    } else {
                        details +=
                            '<li>' +
                            this.translateService.instant('INCONSISTENT_MSG.SearchInfo', {
                                fieldName: this.translateService.instant('FIELD_NAME.' + data.changedData[j].fieldName),
                                changedValue:
                                    data.changedData[j].newVal !== ''
                                        ? data.changedData[j].newVal
                                        : data.changedData[j].oldVal,
                                service: this.translateService.instant('Services.' + data.serviceId),
                            }) +
                            '</li>';
                    }
                }
            }
        }

        if (details !== '') {
            details = '</span><br><ul class="inconsistent">' + details + '</ul>';
        }
        return details;
    }
}
