import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    OnDestroy,
    OnInit,
    Output,
    Renderer2,
} from '@angular/core';
import { Event, NavigationEnd, Router } from '@angular/router';

import { UserService } from '@services/modules/user/services/user.service';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';
import { SettingsService } from '@services/services/settings-service';
import { CurrencyService } from '@services/services/currency-service';
import { ILanguage } from '@schemas/modules/core/ILanguage';
import { LANGUAGES } from '@core/constants/languages.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { ListenersService } from '@services/modules/core/listeners-service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { BehaviorSubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ICart } from '@schemas/modules/cart/cart';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { PlatformService } from '@services/platform.service';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HeaderComponent implements OnInit {
    @Output() signOut: EventEmitter<void> = new EventEmitter<void>();
    @Output() reloadAccDetails: EventEmitter<void> = new EventEmitter<void>();

    public currentState = '';
    public currencies = this.currencyService.currencies;
    public selectedCurCode = this.currencyService.currencyCode;
    public hideCurrency$ = new BehaviorSubject<boolean>(false);
    public languages: ILanguage[] = LANGUAGES.list;
    public selectedLanguage: ILanguage;
    public isSearchDropDownOpen = false;

    constructor(
        private router: Router,
        public userService: UserService,
        private settingsService: SettingsService,
        private currencyService: CurrencyService,
        private searchDataService: SearchDataService,
        private listenersService: ListenersService,
        private readonly changeDetectorRef: ChangeDetectorRef,
        private cartService: CartService,
        private searchErrorService: SearchErrorService,
        private renderer: Renderer2,
        private scrollService: ScrollService,
        public platformService: PlatformService
    ) {
        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                this.scrollService.scrollToTop();
                this.onStateChange(event.url);
            }
        });
        this.selectedLanguage = this.settingsService.selectedLanguageObject;
    }

    ngOnInit(): void {
        this.searchDataService.isSearchDropDownOpen$.subscribe((val: boolean) => {
            this.isSearchDropDownOpen = val;
            this.changeDetectorRef.detectChanges();
        });
    }

    public gotoRoute(path: string): void {
        if (path.includes('/about') && !this.currentState.includes('/about')) {
            const targetUrl = this.router.serializeUrl(this.router.createUrlTree([path]));
            window.open(targetUrl, '_blank');
            return;
        }

        this.router.navigate([path]);
    }

    public onTrazlerLogoClicked($event: MouseEvent): boolean {
        if (this.router.url === '/search') {
            this.listenersService.NotifyListeners(ListenerId.LISTEN_SERVICE_REINIT);
        } else {
            this.searchErrorService.unsetErrorFlag();
            this.router.navigate(['/search'], { skipLocationChange: false });
        }
        $event.stopPropagation();
        $event.preventDefault();
        this.renderer.removeClass(document.body, 'body-lock-scroll');

        return true;
    }

    public onLanguageChange(language: ILanguage): void {
        this.settingsService.setLanguage(language);
    }

    public onHeaderClick(): void {
        if (!this.isSearchDropDownOpen) {
            this.searchDataService.closeSearchEditingMode$.next();
        }
    }

    public onCurrencyChange(currency: { code: string }): void {
        this.currencyService.setCurrency(currency.code);
    }

    public onSignOut(): void {
        this.signOut.emit();
    }

    public logIn(): void {
        this.userService.openLoginModel().then(
            (result) => {
                this.changeDetectorRef.detectChanges();
                this.reloadAccDetails.emit();
            },
            (reason) => {
                console.log(reason);
            }
        );
    }

    private onStateChange(url: string): void {
        this.currentState = url;

        const isInMyTripOrCheckout = url.includes('/bookings') || url.includes('/checkout');
        const cartData = this.cartService.getCartData() as ICart;
        const cartCur = StorageManagerUtil.get('cartCurrency');
        const isCartFinalised =
            (cartData !== undefined && cartData.cartCurrency !== undefined && cartData.cartCurrency !== '') ||
            (cartCur !== null && cartCur !== '');

        this.hideCurrency$.next(isInMyTripOrCheckout || isCartFinalised);
    }
}
